import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Breadcrumb, SimpleCard } from "app/components";
import { Box, styled, Grid, TextField, Select, MenuItem } from "@mui/material";
import useOracleSettings from "app/hooks/useOracleSettings";
import { exportToERPOptions, enableBOSTransferOptions } from "../adminConfig/common";
import Button from "@mui/material/Button";
import Autocomplete from '@mui/material/Autocomplete';
import { BUService } from "services/buService";
import { PlantService } from "services/plantService";
import CustomAlertBox from "app/components/CustomAlertBox";
import Loader from 'app/components/Loader';
import { sortDataByKey } from "app/utils/mcoUtils/commonUtils";
const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));

export default function CreateBU() {
  const {
    refreshData,
    templatesInfo
  } = useOracleSettings();

  const [buForm, setBuFrom] = useState({
    exportToERPdisabled: "N",
    exportToERPbyPassed: "N",
    enableBOSTransfer: "Y",
  });
  const [plantData, setPlantData] = useState([]);
  const [selectedplants, setSelectedPlants] = useState([]);
  const [DoNotSendToERP, setDoNotSendToERP] = useState([]);
  const [OracleExisting, setOracleExisting] = useState([]);
  const [MakeWithoutBOMList, setMakeWithoutBOMList] = useState([]);
  const [NotOwningDivisionList, setNotOwningDivisionList] = useState([]);
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  let navigate = useNavigate();
  const buService = new BUService();

  const plantService = new PlantService();
  useEffect(() => {
    setIsLoading(true);
    plantService.getAllPlantsList().then((result) => {
      setPlantData(result);
      setIsLoading(false);
    });
  }, []);
  const handleAddbuName = () => {
    if (
      buForm.buName?.trim() === "" ||
      !buForm.buName ||
      selectedplants.length === 0
    ) {
      handlesnackBarClick("Please fill the require details", "error");
      return;
    }
    const newbuName = {
      id: buForm.buName,
      buName: buForm.buName,
      exportToERPdisabled: buForm.exportToERPdisabled,
      contextDEF: buForm.contextDEF,
      EMRDivisionORGValue: buForm.EMRDivisionORGValue,
      productHierarchy: buForm.productHierarchy,
      exportToERPbyPassed: buForm.exportToERPbyPassed,
      enableBOSTransfer: buForm.enableBOSTransfer,
      plants: selectedplants?.map((option) => {
        return {
          id: option?.id,
          plantName: option?.plantName
        }
      }) || [],
      doNotSendToERP: DoNotSendToERP?.map((option) => (
        option.templateName
      )) || [],
      oracleExisting: OracleExisting?.map((option) => (
        option.templateName
      )) || [],
      makeWithoutBOMList: MakeWithoutBOMList?.map((option) => (
        option.templateName
      )) || [],
      notOwningDivisionList: NotOwningDivisionList?.map((option) => (
        option.templateName
      )) || [],
    };
    setIsLoading(true);
    buService.addnewBU(newbuName).then((resp) => {
      setIsLoading(false);
      if (resp.status === 'success') {
        setTimeout(() => {
          navigate("/buList", { state: { message: resp.msg, status: resp.status } });
        }, 200);
      } else if (resp.status === 'error') {
        handlesnackBarClick(resp.msg, "error");
      }
    });
  };

  const handlebuFormChange = (event) => {
    const { name, value } = event.target;
    const updatedForm = {
      ...buForm,
      [name]: value,
    };
    setBuFrom(updatedForm);
  };
  const handlePlantsChange = (selectedObj) => {
    const newselectedObj = selectedObj?.filter(
      (item) => plantData.some((existing) => existing.plantName === item.plantName)
    ) || [];
    setSelectedPlants(newselectedObj);
  };

  const handleTemplateChange = (name, selectedObj) => {
    console.log(`New Value: ${name} - `, selectedObj);
    const newselectedObj = selectedObj?.filter(
      (item) => templatesInfo.some((existing) => existing.templateName === item.templateName)
    ) || [];
    if (name === 'DoNotSendToERP') {
      setDoNotSendToERP(newselectedObj)
    }
    else if (name === 'OracleExisting') {
      setOracleExisting(newselectedObj);
    }
    else if (name === 'MakeWithoutBOMList') {
      setMakeWithoutBOMList(newselectedObj)
    }
    else if (name === 'NotOwningDivisionList') {
      setNotOwningDivisionList(newselectedObj)
    }
  };

  const handlesnackBarClick = (message, severity) => {
    setOpenAlert(true);
    setOpenDesc({
      severity: severity,
      desc: message,
    });
  };
  const handleCancel = () => {
    setTimeout(() => {
      navigate(-1)
    }, 200);
  }

  sortDataByKey(plantData, 'plantName');
  sortDataByKey(templatesInfo, 'templateName');

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Create Design Organization" }]} />
      </Box>

      <form>
        <SimpleCard>
          <Grid item md={9} sm={6} xs={6}>
            <h4>Create Design Organization</h4>
            <Grid container spacing={3} alignItems="center">
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                Design Organization <span style={{ color: "#FF3D57" }}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="buName"
                  value={buForm.buName}
                  onChange={handlebuFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>Export to ERP Disabled </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Select
                  labelId="exportToERPdisabled"
                  id="exportToERPdisabled"
                  name="exportToERPdisabled"
                  fullWidth
                  value={buForm.exportToERPdisabled}
                  onChange={handlebuFormChange}
                >
                  {exportToERPOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Export To ERP Bypassed
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Select
                  labelId="exportToERPbyPassed"
                  id="exportToERPbyPassed"
                  name="exportToERPbyPassed"
                  fullWidth
                  value={buForm.exportToERPbyPassed}
                  onChange={handlebuFormChange}
                >
                  {exportToERPOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>Enable BOS Transfer </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Select
                  labelId="enableBOSTransfer"
                  id="enableBOSTransfer"
                  name="enableBOSTransfer"
                  fullWidth
                  value={'Y'}
                  onChange={handlebuFormChange}
                >
                  {enableBOSTransferOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Context DFF
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="contextDEF"
                  value={buForm.contextDEF}
                  onChange={handlebuFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  EMR Division ARG
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="EMRDivisionORGValue"
                  value={buForm.EMRDivisionORGValue}
                  onChange={handlebuFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Product Hierarchy
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="productHierarchy"
                  value={buForm.productHierarchy}
                  onChange={handlebuFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Plant Name <span style={{ color: "#FF3D57" }}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Autocomplete
                  multiple
                  value={selectedplants}
                  getOptionLabel={(option) => option.plantName}
                  autoHighlight
                  onChange={(e, newValue) => {
                    handlePlantsChange(newValue)
                  }}
                  options={plantData}
                  renderInput={(params) => <TextField {...params} className={"sampleeeee"} />}
                  renderOption={(props, option) => {
                    return (
                      <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                        {option.plantName}
                      </Box>
                    )
                  }}
                />
              </Grid>

              {/* add templates  */}
              {/* DoNotSendToERP */}
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Do Not Send To ERP
                </span>
              </Grid>
              <Grid item md={9} sm={8} xs={12}>
                <Autocomplete
                  multiple
                  value={DoNotSendToERP}
                  getOptionLabel={(option) => option.templateName}
                  autoHighlight
                  onChange={(e, newValue) => {
                    handleTemplateChange("DoNotSendToERP", newValue)
                  }}
                  options={templatesInfo}
                  renderInput={(params) => <TextField {...params} className={"sampleeeee"} />}
                  renderOption={(props, option) => {
                    return (
                      <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                        {option.templateName}
                      </Box>
                    )
                  }}
                />
              </Grid>

              {/* OracleExisting */}
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Oracle Existing
                </span>
              </Grid>
              <Grid item md={9} sm={8} xs={12}>
                <Autocomplete
                  multiple
                  value={OracleExisting}
                  getOptionLabel={(option) => option.templateName}
                  autoHighlight
                  onChange={(e, newValue) => {
                    handleTemplateChange("OracleExisting", newValue)
                  }}
                  options={templatesInfo}
                  renderInput={(params) => <TextField {...params} className={"sampleeeee"} />}
                  renderOption={(props, option) => {
                    return (
                      <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                        {option.templateName}
                      </Box>
                    )
                  }}
                />
              </Grid>

              {/* MakeWithoutBOMList */}
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Make Without BOM List
                </span>
              </Grid>
              <Grid item md={9} sm={8} xs={12}>
                <Autocomplete
                  multiple
                  value={MakeWithoutBOMList}
                  getOptionLabel={(option) => option.templateName}
                  autoHighlight
                  onChange={(e, newValue) => {
                    handleTemplateChange("MakeWithoutBOMList", newValue)
                  }}
                  options={templatesInfo}
                  renderInput={(params) => <TextField {...params} className={"sampleeeee"} />}
                  renderOption={(props, option) => {
                    return (
                      <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                        {option.templateName}
                      </Box>
                    )
                  }}
                />
              </Grid>

              {/* NotOwningDivisionList */}
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Not Owning Division List
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Autocomplete
                  multiple
                  value={NotOwningDivisionList}
                  getOptionLabel={(option) => option.templateName}
                  autoHighlight
                  onChange={(e, newValue) => {
                    handleTemplateChange("NotOwningDivisionList", newValue)
                  }}
                  options={templatesInfo}
                  renderInput={(params) => <TextField {...params} className={"sampleeeee"} />}
                  renderOption={(props, option) => {
                    return (
                      <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                        {option.templateName}
                      </Box>
                    )
                  }}
                />
              </Grid>

              {/* add templates  */}
              <Grid item md={12} sm={12} xs={12} className="font-medium" style={{ textAlign: "right" }}>
                <Button
                  variant="contained"
                  color="primary"
                  margin="2px"
                  onClick={handleCancel}
                  style={{ marginRight: "20px" }}
                >
                  Cancel
                </Button>
                <Button
                  variant="contained"
                  color="primary"
                  margin="2px"
                  onClick={handleAddbuName}
                >
                  Submit
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </SimpleCard>
        {(openDesc) ? (
          <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
        ) : (<></>)}

        {
          (refreshData || isLoading) ? (
            <div style={{ textAlign: 'center' }}>
              <Loader show={refreshData} />
            </div>
          ) : <></>
        }
      </form>
    </Container>
  );
}
