import { Breadcrumb, SimpleCard } from "app/components";

import React, { useEffect, useState, useRef } from "react";
import Button from "@mui/material/Button";
import { Link as RouterLink, useNavigate } from "react-router-dom";

import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";

import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";

import {
  Box,
  styled,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  InputLabel,
  Grid,
  TextField,
  Autocomplete,
} from "@mui/material";

import { SourceListService } from "services/sourceListService";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import { MappingsService } from "services/mappingsService";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));

const StyledTable = styled(Table)(() => ({
  whiteSpace: "pre",
  "& thead": {
    "& tr": { "& th": { paddingLeft: 0, paddingRight: 0 } },
  },
  "& tbody": {
    "& tr": { "& td": { paddingLeft: 0, textTransform: "capitalize" } },
  },
}));

const Mappings = () => {
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [selectedMapping, setSelectedMapping] = useState();
  const [mappingData, setMappingData] = useState();
  const [searchValue, setSearchValue] = useState("");
  const [filteredRows, setFilteredRows] = useState([]);
  const [snackBar, setsnackBar] = useState(false);
  const [deleteDialog, setDeleteDialog] = useState(false);
  const mappingService = new MappingsService();
  useEffect(() => {
    mappingService.getAllMappings().then((result) => {
      setMappingData(result);
    });
  }, []);
  //console.log(mappingData, "mappingData");

  const [filteredMappingData, setFilteredMappingData] = useState([]);

  useEffect(() => {
    setFilteredMappingData(mappingData);
  }, [mappingData]);

  const handleSearchInputChange = (event) => {
    const newSearchValue = event.target.value;
    setSearchValue(newSearchValue);

    if (newSearchValue === "") {
      setFilteredMappingData(mappingData);
    } else {
      const filteredData = mappingData?.filter(
        (subscriber) =>
          subscriber?.Name?.toLowerCase().includes(
            newSearchValue.toLowerCase()
          ) ||
          subscriber.ERPType?.toLowerCase().includes(
            newSearchValue.toLowerCase()
          )
      );
      setFilteredMappingData(filteredData);
    }
  };

  const handleChangePage = (_, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  const handleSearch = () => {
    if (searchValue === "") {
      setFilteredMappingData(mappingData);
    } else {
      const filteredData = mappingData.filter((subscriber) =>
        subscriber?.Name.toLowerCase().includes(searchValue.toLowerCase())
      );
      setFilteredMappingData(filteredData);
    }
  };

  const navigate = useNavigate();
  const plantModeRef = useRef("initialValue");

  const handleCreateMappings = () => {
    plantModeRef.current = "create";
    setTimeout(() => {
      navigate("/createMappings", {
        state: { plantMode: plantModeRef.current },
      });
    }, 200);
  };

  const handleEditMapping = (editselectedMapping) => {
    plantModeRef.current = "edit";

    const selectedMappingData = editselectedMapping;
    setTimeout(() => {
      navigate("/editMappings", {
        state: { plantMode: plantModeRef.current, selectedMappingData },
      });
    }, 200);
  };

  const handleDeleteMappingDilaog = (DeleteSource) => {
    setDeleteDialog(true);

    setSelectedMapping(DeleteSource);
  };
  const handleCloseDelete = () => {
    setDeleteDialog(false);
  };

  const handleDeleteMapping = async () => {
    setDeleteDialog(false);
    // console.log(selectedMapping.id, " selected deleted plant ");

    try {
      await mappingService.deleteMapping(selectedMapping.id);
    } catch (error) {
      console.error("Failed to delete plant:", error);
    }

    mappingService.getAllMappings().then((result) => {
      setMappingData(result);
    });

    handlesnackBarClick();
  };

  const handlesnackBarClick = () => {
    setsnackBar(true);
  };

  const handlesnackbarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setsnackBar(false);
  };
  // ****************filters**********************
  const [filterValues, setFilterValues] = useState({
    Name: [],

    ERPType: [],
  });

  const handleFilterChange = (column, selectedValues) => {
    setFilterValues((prevFilterValues) => ({
      ...prevFilterValues,
      [column]: selectedValues,
    }));
  };

  useEffect(() => {
    let dataToShow = mappingData;

    if (filterValues["Name"].length > 0) {
      dataToShow = dataToShow.filter((row) =>
        filterValues["Name"].includes(row["Name"])
      );
    }

    if (filterValues["ERPType"].length > 0) {
      dataToShow = dataToShow.filter((row) =>
        filterValues["ERPType"].includes(row["ERPType"])
      );
    }

    setFilteredRows(dataToShow);
  }, [filterValues]);

  const getUniqueValues = (column) => {
    const values = new Set();
    mappingData?.forEach((subscriber) => values.add(subscriber[column]));
    return Array.from(values);
  };

  // *******filters end***********

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Mappings" }]} />
      </Box>

      <SimpleCard>
        <Grid container spacing={1} alignItems="center">
          <Grid item md={3} sm={6} xs={12} className="font-medium">
            <TextField
              type="text"
              name="search"
              size="small"
              onChange={handleSearchInputChange}
              value={searchValue}
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid item container md={9} sm={6} xs={12} alignItems="center">
            <Button variant="contained" onClick={handleSearch}>
              Search
            </Button>
            <div style={{ flex: 1 }} />
            <Button
              variant="contained"
              component={RouterLink}
              onClick={handleCreateMappings}
            >
              Create
            </Button>
          </Grid>
          <Box width="100%" overflow="auto">
            <StyledTable>
              <TableHead>
                <TableRow>
                  {/* <TableCell align="left">Id</TableCell> */}
                  {/*<TableCell align="left">ID</TableCell>*/}
                  <TableCell align="left">
                    <InputLabel
                      style={{
                        fontWeight: "bold",
                        color: "rgba(52, 49, 76, 1)",
                      }}
                    >
                      Mapping Name
                    </InputLabel>

                    <br />
                    <Autocomplete
                      style={{ width: "70%" }}
                      multiple
                      options={getUniqueValues("Name")}
                      value={filterValues.Name}
                      onChange={(_, newValue) =>
                        handleFilterChange("Name", newValue)
                      }
                      renderValue={() => null}
                      renderInput={(params) => (
                        <TextField
                          {...params}
                          variant="outlined"
                          label="Filter Name"
                          placeholder="Search..."
                        />
                      )}
                      PaperProps={{
                        style: {
                          maxHeight: "200px",
                        },
                      }}
                      disableCloseOnSelect
                      isOptionEqualToValue={(option, value) => option === value}
                      getOptionLabel={(option) => option}
                      getOptionSelected={(option, value) =>
                        filterValues.Name.includes(value)
                      }
                      ChipProps={{
                        style: {
                          fontSize: "8px",
                        },
                      }}
                    />
                  </TableCell>

                  <TableCell align="left">
                    <InputLabel
                      style={{
                        fontWeight: "bold",
                        color: "rgba(52, 49, 76, 1)",
                      }}
                    >
                      ERP Type
                    </InputLabel>

                    <br />
                    <Autocomplete
                      style={{ width: "70%" }}
                      multiple
                      options={getUniqueValues("ERPType")}
                      value={filterValues.ERPType}
                      onChange={(_, newValue) =>
                        handleFilterChange("ERPType", newValue)
                      }
                      renderValue={() => null}
                      renderInput={(params) => (
                        <TextField
                          {...params}
                          variant="outlined"
                          label="Filter ERP Type"
                          placeholder="Search..."
                        />
                      )}
                      PaperProps={{
                        style: {
                          maxHeight: "200px",
                        },
                      }}
                      disableCloseOnSelect
                      isOptionEqualToValue={(option, value) => option === value}
                      getOptionLabel={(option) => option}
                      getOptionSelected={(option, value) =>
                        filterValues.ERPType.includes(value)
                      }
                      ChipProps={{
                        style: {
                          fontSize: "8px",
                        },
                      }}
                    />
                  </TableCell>
                  <TableCell align="left">
                    <InputLabel
                      style={{
                        paddingBottom: "85px",
                        fontWeight: "bold",
                        color: "rgba(52, 49, 76, 1)",
                      }}
                    >
                      Actions
                    </InputLabel>
                  </TableCell>

                  {/* <TableCell align="right">Action</TableCell> */}
                </TableRow>
              </TableHead>
              <TableBody>
                {filteredMappingData?.length > 0 ? (
                  filteredMappingData
                    .filter(
                      (subscriber) =>
                        (searchValue === "" ||
                          subscriber.Name.toLowerCase().includes(
                            searchValue.toLowerCase()
                          ) ||
                          subscriber?.ERPType.toLowerCase().includes(
                            searchValue.toLowerCase()
                          )) &&
                        (filterValues.Name.length === 0 ||
                          filterValues.Name.includes(subscriber.Name)) &&
                        (filterValues.ERPType.length === 0 ||
                          filterValues.ERPType.includes(subscriber.ERPType))
                    )
                    .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    .map((subscriber, index) => {
                      return (
                        <TableRow key={index}>
                          {/* <TableCell align="left">{subscriber.id}</TableCell>*/}
                          {/* <TableCell align="left">{subscriber.name}</TableCell> */}
                          <TableCell
                            align="left"
                            style={{ textTransform: "none" }}
                          >
                            {subscriber.Name}
                          </TableCell>

                          <TableCell
                            align="left"
                            style={{ textTransform: "none" }}
                          >
                            {subscriber.ERPType}
                          </TableCell>
                          <TableCell
                            align="left"
                            style={{ textTransform: "none" }}
                          >
                            <EditIcon
                              id={subscriber.id}
                              onClick={() => handleEditMapping(subscriber)}
                            />{" "}
                            <DeleteIcon
                              onClick={() =>
                                handleDeleteMappingDilaog(subscriber)
                              }
                            />
                          </TableCell>
                        </TableRow>
                      );
                    })
                ) : (
                  <>
                    <TableCell
                      align="left"
                      colSpan={5}
                      style={{ textAlign: "center" }}
                    >
                      {"No result found!"}
                    </TableCell>
                  </>
                )}
              </TableBody>
            </StyledTable>
            {mappingData?.length ? (
              <TablePagination
                sx={{ px: 2 }}
                page={page}
                component="div"
                rowsPerPage={rowsPerPage}
                count={mappingData?.length}
                onPageChange={handleChangePage}
                rowsPerPageOptions={[10, 25, 50]}
                onRowsPerPageChange={handleChangeRowsPerPage}
                nextIconButtonProps={{ "aria-label": "Next Page" }}
                backIconButtonProps={{ "aria-label": "Previous Page" }}
              />
            ) : (
              <></>
            )}
          </Box>
        </Grid>
      </SimpleCard>

      <Snackbar
        open={snackBar}
        autoHideDuration={3000}
        onClose={handlesnackbarClose}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
      >
        <Alert
          onClose={handlesnackbarClose}
          severity="success"
          sx={{ width: "100%" }}
        >
          Mapping deleted successfully!
        </Alert>
      </Snackbar>

      <Dialog open={deleteDialog}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Delete template
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            Do you want to delete the source <b>{selectedMapping?.Name}</b>{" "}
            having ERP type <b>{selectedMapping?.ERPType}</b>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCloseDelete}>
            Cancel
          </Button>
          <Button onClick={handleDeleteMapping}>delete</Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default Mappings;
