import React, { useState, useEffect, useCallback, useRef } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  Select,
  MenuItem,
  Button,
  Tooltip
} from "@mui/material";
import { Scrollbars } from 'react-custom-scrollbars-2';
import ReactDataGrid from "@inovua/reactdatagrid-community";
import Constant from '../../../constant'
import "@inovua/reactdatagrid-community/index.css";
import { unionBy } from 'lodash';
const gridStyle = { minHeight: 248 }
const ExpenseMappingDialog = ({
  open,
  handleorgAllowedDialogClose,
  orgAllowedForm,
  handleorgAllowedFormChange,
  handleAddOrgAllowedValues,
  isEdit,
  templatesInfo,
  bussinessUnitLists,
  selectedBU,
  setPlantValueList,
  plantValueList
}) => {
  const [filteredBUOptions, setFilteredBUOptions] = useState([])
  const [dataSource, setDataSource] = useState([]);
  useEffect(() => {
    setFilteredBUOptions([selectedBU])
  }, [orgAllowedForm.EXPENSE_TEMPLATE, selectedBU]);
  useEffect(() => {
    let filteredPlants = bussinessUnitLists?.find((plant) => plant.buName === orgAllowedForm.BUID)?.plants || [];
    filteredPlants.map((plant, index) => {
      plant.expense = ''
      plant.id = index
      return Object.assign({}, plant)
    })
    filteredPlants = unionBy(plantValueList, filteredPlants, 'plantName');
    setDataSource(filteredPlants)
    setPlantValueList(filteredPlants)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [orgAllowedForm.BUID, selectedBU, bussinessUnitLists, setPlantValueList]);

  const [templateOption, setTemplateOption] = useState([])
  useEffect(() => {
    if (selectedBU) {
      let tempData = templatesInfo.filter(temp => temp?.BU?.includes(selectedBU))
      setTemplateOption(tempData)
    }
    setFilteredBUOptions([selectedBU])
  }, [selectedBU, templatesInfo]);

  const Input = (props) => {
    const inputRef = useRef(null);
    useEffect(() => {
      if (inputRef && inputRef.current) {
        if (props.autoFocus) {
          inputRef.current.focus();
        }
      }
    }, [props.autoFocus]);

    return <input {...props} inputRef={inputRef} />;
  };
  const expenseColumn = {
    name: 'expense',
    header: 'Expense',
    defaultFlex: 1, minWidth: 100,
    rendersInlineEditor: true,
    editable: true,
    render: ({ value }, { cellProps }) => {
      let v = cellProps.editProps.inEdit
        ? cellProps.editProps.value
        : value;
      return (
        <Input
          type="number"
          autoFocus={cellProps.inEdit}
          value={v}
          onBlur={e => {
            cellProps.editProps.onComplete(e);
          }}
          onChange={cellProps.editProps.onChange}
          onFocus={() => cellProps.editProps.startEdit()}
          onKeyDown={e => {
            if (e.key === 'Escape') {
              cellProps.editProps.onCancel(e);
            }
            if (e.key === 'Enter') {
              cellProps.editProps.onComplete(e);
            }
          }}
        />
      );
    }
  };
  const gridRef = useRef(null);
  const columns = [
    { name: 'id', header: 'Id', defaultVisible: false, defaultWidth: 60, type: 'string' },
    {
      name: "plantName",
      defaultWidth: 180,
      header: <Tooltip title={Constant.adminConstant.expensemapList.tooltip.PlantName}>Plant Name</Tooltip>,
      render: ({ data }) => <div>{data?.plantName}</div>,
    },
    expenseColumn
  ]

  const onEditComplete = useCallback(({ value, columnId, rowId }) => {
    const data = [...dataSource];
    data[rowId] = Object.assign({}, data[rowId], { [columnId]: value });
    setDataSource(data);
    setPlantValueList(data)
  }, [dataSource, setPlantValueList])
  return (
    <Dialog open={open}>
      <DialogTitle>
        {" "}
        {isEdit ? "Edit Expense Mapping " : "Add Expense Mapping"}
      </DialogTitle>
      <Scrollbars
        autoHeight
        autoHeightMin={100}
        autoHeightMax={500}
        autoHide
        autoHideTimeout={1000}
        autoHideDuration={300}
      >
        <DialogContent>
          <Grid container spacing={3} alignItems="center">
          <Grid item md={3} sm={4} xs={12}>
              <span>
                Design Organization <span style={{ color: "red" }}>*</span>
              </span>
            </Grid>
            <Grid item md={9} sm={8} xs={12}>
              <Select
                id="DataType-select"
                name="BUID"
                fullWidth
                value={selectedBU}
                onChange={handleorgAllowedFormChange}
                disabled
              >
                {filteredBUOptions?.map((option) => (
                  <MenuItem key={option} value={option}>
                    {option}
                  </MenuItem>
                ))}
              </Select>
            </Grid>
            <Grid item md={3} sm={4} xs={12}>
              <span>
                Template <span style={{ color: "red" }}>*</span>
              </span>
            </Grid>
            <Grid item md={9} sm={8} xs={12}>
              <Select
                name="EXPENSE_TEMPLATE"
                fullWidth
                value={orgAllowedForm.EXPENSE_TEMPLATE}
                onChange={handleorgAllowedFormChange}
                disabled={isEdit}
              >
                {templateOption?.map((option) => (
                  <MenuItem key={option.templateName} value={option.templateName}>
                    {option.templateName}
                  </MenuItem>
                ))}
              </Select>
            </Grid>

            <Grid item md={3} sm={4} xs={12}>
              <span>
                Plant Expenses <span style={{ color: "red" }}>*</span>
              </span>
            </Grid>

            <Grid item md={9} sm={8} xs={12}>
              <ReactDataGrid
                idProperty="id"
                showZebraRows={false}
                handle={ref => gridRef.current = ref ? ref.current : null}
                style={gridStyle}
                onEditComplete={onEditComplete}
                columns={columns}
                dataSource={dataSource}
              />
            </Grid>
          </Grid>
        </DialogContent>
      </Scrollbars>
      <DialogActions>
        <Button onClick={handleorgAllowedDialogClose}>Cancel</Button>
        <Button onClick={handleAddOrgAllowedValues}>
          {isEdit ? "Update" : "Add"}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default ExpenseMappingDialog;
