import React from 'react';
import BUPlantWrapper from "./buplantWrapper";
import PrivateRoute from "app/privateRoutes";

const buplantroutes = [
  {
    path: "/buplantList",
    element: (
      <PrivateRoute>
        <BUPlantWrapper />
      </PrivateRoute>
    ),
  },
];

export default buplantroutes;
