import React from 'react';

const sortedExportDataHOC = (WrappedComponent) => {
    return ({ data = [], sortInfo = {}, initialData = [], ...props }) => {
        const sortData = (data, sortInfo) => {
            if (!Array.isArray(data)) {
                return [];
            }

            if (!sortInfo || !sortInfo.id) {
                return initialData; // Return initial data if sortInfo is null or empty
            }

            const field = sortInfo.id || sortInfo.name || sortInfo.columnName;
            const direction = sortInfo.dir === 1 ? 1 : -1;

            return [...data].sort((a, b) => {
                const aValue = String(a[field] || '').toLowerCase();
                const bValue = String(b[field] || '').toLowerCase();

                if (aValue < bValue) return -direction;
                if (aValue > bValue) return direction;
                return 0;
            });
        };

        const sortedData = sortData(data, sortInfo);
        return <WrappedComponent data={sortedData} sortInfo={sortInfo} {...props} />;
    };
};

export default sortedExportDataHOC;