import { createContext, useState, useEffect } from "react";

const SetTempCompContext = createContext({
  comptempInfo: [],
  updatecomptempList: () => [],
});

export const SetTempCompProvider = ({ children }) => {
  const [comptempInfo, setcomptempInfo] = useState([]);

  const updatecomptempList = (values) => {
    setcomptempInfo(values);
   // console.log(values, "inside update context");
  };

  useEffect(() => {
   // console.log(comptempInfo, "templatesContext");
  }, [comptempInfo]);

  return (
    <SetTempCompContext.Provider
      value={{
        comptempInfo,
        updatecomptempList,
      }}
    >
      {children}
    </SetTempCompContext.Provider>
  );
};

export default SetTempCompContext;
