import Loadable from 'app/components/Loadable';
import React, { lazy } from 'react';

const CaList = Loadable(lazy(() => import('./CaList')));
const CaDetails = Loadable(lazy(() => import('./CaDetails')));
const McoList = Loadable(lazy(() => import('./McoList')));
const Mcodetails = Loadable(lazy(() => import('./Mcodetails')));
const TrackerProgressBar = Loadable(lazy(() => import('./TrackerProgressBar')));
const SpecCreationList = Loadable(lazy(() => import('./SpecCreationList')));
const ObsoletePartList = Loadable(lazy(() => import('./ObsoletePartList')));
const MyReportList = Loadable(lazy(() => import('./MyReports')));

const McoListRoutes = [
    { path: '/calist', element: <CaList /> },
    { path: '/caDetails/:ca_id', element: <CaDetails /> },
    { path: '/mcolist', element: <McoList /> },
    { path: '/mcodetail/:mco_id?', element: <Mcodetails /> },
    { path: '/:mcoName/tracker', element: <TrackerProgressBar /> },
    { path: '/specCreationList', element: <SpecCreationList /> },
    { path: '/obsoletePartList', element: <ObsoletePartList /> },
    { path: '/myreport', element: <MyReportList /> }
];

export default McoListRoutes;
