
export const formatMessage = (template, replaceArray) => {
    let formattedMessage = template;
    // Replace placeholders in the message template
    replaceArray.forEach((value, index) => {
        const placeholder = new RegExp(`\\{${index}\\}`, 'g');
        formattedMessage = formattedMessage?.replace(placeholder, value);
    });
    return formattedMessage;
}

export const sortDataByKey = (data, key) => {
  if (!Array.isArray(data)) {
    console.error('Invalid data: Expected an array');
    return [];
  }

  return data.sort((a, b) => {
    const valueA = a[key] !== undefined ? a[key].toString().toLowerCase() : '';
    const valueB = b[key] !== undefined ? b[key].toString().toLowerCase() : '';

    if (!isNaN(valueA) && !isNaN(valueB)) {
      return parseFloat(valueA) - parseFloat(valueB);
    }

    if (valueA < valueB) return -1;
    if (valueA > valueB) return 1;
    return 0;
  });
};

export const sortDataByKeyAllowableOrgTemplates = (data, key) => {
  return data.sort((a, b) => {
    const valueA = a.details[key] || '';
    const valueB = b.details[key] || '';
    return valueA.toLowerCase().localeCompare(valueB.toLowerCase());
  });
}; 

export const sortByLabel = (data) => {
  return data.sort((a, b) => {
    const labelA = a.label ? a.label.toLowerCase() : '';
    const labelB = b.label ? b.label.toLowerCase() : '';
    return labelA.localeCompare(labelB);
  });
};