import { Breadcrumb, SimpleCard } from "app/components";
import React, { useEffect, useState, useRef, useCallback } from "react";
import Button from "@mui/material/Button";
import { Link as RouterLink, useNavigate } from "react-router-dom";
import CustomAlertBox from "app/components/CustomAlertBox";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import { CircularProgress } from "@mui/material";
import {
  Box,
  styled,
  Grid,
  TextField,
  Tooltip
} from "@mui/material";
import { PlantService } from "services/plantService";
import DataGrid from "@inovua/reactdatagrid-community";
import ExportDialogBox from "./exportPlants";
import "@inovua/reactdatagrid-community/index.css";
import sortedExportDataHOC from "app/components/sortedExportDataHOC";
import { sortDataByKey } from "app/utils/mcoUtils/commonUtils";

const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));

const Plant = () => {
  const plantService = new PlantService();
  const [deleteDialog, setDeleteDialog] = useState(false);
  const [searchValue, setSearchValue] = useState("");
  const [plantData, setPlantData] = useState([]);
  const [initialData, setInitialData] = useState([]);
  const [selectedDelPlant, setSelectedDelPlant] = useState();
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [sortInfo, setSortInfo] = useState({}); // Add this line

  // ==============table===========
  const [selected, setSelected] = useState({});
  const [selectedRows, setSelectedRows] = useState([]);
  const gridRef = useRef(null);
  const dataMap = gridRef && gridRef.current && gridRef.current.dataMap ? gridRef.current.dataMap : null;

  const onSelectionChange = useCallback(({ selected }) => {
    setSelected(selected);
  }, []);

  const [isLoading, setIsLoading] = useState(true);
  const handleDeleteDialog = () => {
    handleDeletePlantDialog(selectedRows[0])
    setDeleteDialog(true);
    // Perform delete operation here using the rowId or rowData
  };
  const handleEditMappingDialog = () => {
    handleEditPlant(selectedRows[0]);
  };
  // ==============table===========

  const fetchPlants = () => {
    setIsLoading(true);
    plantService.getAllPlantsList()
      .then((result) => {
        setPlantData(result);
        setInitialData(result); // Set initial data
        setFilteredPlantData(result);
      })
      .finally(() => {
        setIsLoading(false);
      });
  };

  useEffect(() => {
    fetchPlants();
  }, []);

  const [filteredPlantData, setFilteredPlantData] = useState([]);
  let sortedPlanteData = sortDataByKey([...filteredPlantData], "plantName");
    
  useEffect(() => {
    setFilteredPlantData(plantData);
  }, [plantData]);
  // ***** Global search functionality ********
  const handleSearch = () => {
  };

  const handleSearchInputChange = (event) => {
    const newSearchValue = event.target.value;
    setSearchValue(newSearchValue);
  };

  // ***** Global search functionality ends********

  const navigate = useNavigate();
  const plantModeRef = useRef("initialValue");
  const handleCreatePlant = () => {
    plantModeRef.current = "create"; // Update the ref with the new value of plantMode
    setTimeout(() => {
      navigate("/createPlant", {
        state: { plantMode: plantModeRef.current },
      });
    }, 200);
  };
  const handleDeletePlantDialog = (DeletePlant) => {
    setDeleteDialog(true);
    setSelectedDelPlant(DeletePlant);
  };

  const handleCloseDelete = () => {
    setDeleteDialog(false);
  };

  const handleDeletePlant = async () => {
    setDeleteDialog(false);

    try {
      const selRowsId = selectedRows?.map(item => (item?.id));
      await plantService.deletePlant(selRowsId);
      setSelected({});
      setSelectedRows([]);
    } catch (error) {
      return error;
    }

    plantService.getAllPlantsList().then((result) => {
      setPlantData(result);
    });
    handlesnackBarClick("Plant deleted successfully", "success");
  };

  const handleEditPlant = (editSelectedPlant) => {
    plantModeRef.current = "edit";
    const selectedPlantData = editSelectedPlant;
    setTimeout(() => {
      navigate(`/editPlant/${selectedPlantData?.id}`, {
        state: { plantMode: plantModeRef.current, selectedPlantData },
      });
    }, 200);
  };

  const handlesnackBarClick = (message, severity) => {
    setOpenAlert(true);
    setOpenDesc({
      severity: severity,
      desc: message,
    });
  };

  useEffect(() => {
    const selRowsData = selected === true ? dataMap : selected;
    const selRowsDetails = (selRowsData) && Object.keys(selRowsData).map(key => (selRowsData[key]));
    setSelectedRows(selRowsDetails);
  }, [selected, dataMap]);

  useEffect(() => {
    const new_FilteredData = plantData.filter((subscriber) => {
      return (searchValue === "" ||
        subscriber.plantName
          .toLowerCase()
          .includes(searchValue.toLowerCase()) ||
        subscriber.instance
          .toLowerCase()
          .includes(searchValue.toLowerCase()) ||
        subscriber.ERPType.toLowerCase().includes(
          searchValue.toLowerCase()
        ))
    });
    setFilteredPlantData(new_FilteredData);
  }, [searchValue, plantData]);

  const handleSortInfoChange = (sortInfo) => {
    setSortInfo(sortInfo);
    const SortedPlant = sortedExportDataHOC(Plant);
    const sortedData = SortedPlant({ data: plantData, sortInfo, sortedPlanteData, onSortInfoChange: handleSortInfoChange, initialData });
    setPlantData(sortedData.props.data);
    setFilteredPlantData(sortedData.props.data);
  };

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Plant" }]} />
      </Box>

      <SimpleCard>
        <Grid container spacing={1} alignItems="center">
          <Grid item md={3} sm={6} xs={12} className="font-medium">
            <TextField
              type="text"
              name="search"
              size="small"
              onChange={handleSearchInputChange}
              value={searchValue}
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid item container md={9} sm={6} xs={12} alignItems="center">
            <Button variant="contained" onClick={handleSearch}>
              Search
            </Button>
            <div style={{ flex: 1 }} />
            <Button
              variant="contained"
              component={RouterLink}
              onClick={handleCreatePlant}
            >
              Create
            </Button>
            <Button
              variant="contained"
              disabled={selectedRows?.length === 1 ? false : true}
              onClick={handleEditMappingDialog}
              style={{ margin: '0 0px 0 10px' }}
            >
              Edit
            </Button>
            <Button
              variant="contained"
              disabled={selectedRows?.length ? false : true}
              onClick={handleDeleteDialog}
              style={{ margin: '0 10px' }}
            >
              Delete
            </Button>

            <ExportDialogBox
              plantData={sortInfo && Object.keys(sortInfo).length === 0 ? sortedPlanteData : plantData}
              filterTemplateData={selectedRows} />
          </Grid>


          <Box width="100%" overflow="auto">
            <br />
            {isLoading ? (
              // Display loading spinner while data is being fetched
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  minHeight: 550
                }}
              >
                <CircularProgress />
              </Box>
            ) : (
              // Render DataGrid once data is loaded
              <DataGrid
                idProperty="id"
                showZebraRows={false}
                style={{ minHeight: 550 }}
                defaultLimit={100}
                columns={[
                  {
                    name: "plantName",
                    header: <Tooltip title={'Name'}>Plant Name</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                    render: ({ data }) => <div>{data?.plantName}</div>,
                  },
                  {
                    name: "ERPType",
                    header: <Tooltip title={"ERP Type"}>ERP Type</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                    render: ({ data }) => <div>{data?.ERPType}</div>,
                  },
                  {
                    name: "instance",
                    header: <Tooltip title={"instance"}>Instance</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                  },
                  {
                    name: "exportToERP",
                    header: <Tooltip title={"Export To ERP"}>Export To ERP</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                  },
                  // {
                  //   name: "orgId",
                  //   header: <Tooltip title={"Org Id"}>Org Id</Tooltip>,
                  //   defaultFlex: 1,
                  //   defaultWidth: 50,
                  // },
                  // {
                  //   name: "BU",
                  //   header: <Tooltip title={"Design Organization"}>Design Organization</Tooltip>,
                  //   defaultFlex: 1,
                  //   defaultWidth: 50,
                  //   render: ({ data }) => <div>{(data?.BU) ? data.BU.map((item) => item).join(", ") : ''}</div>,
                  // },
                ]}
                  dataSource={sortInfo === null ? filteredPlantData : sortedPlanteData}
                // rowHeight={null}
                checkboxColumn
                selected={selected}
                onSelectionChange={onSelectionChange}
                handle={ref => gridRef.current = ref ? ref.current : null}
                pagination
                renderScroller={() => gridRef?.current?.setScrollTop(selectedRows?.length == 0 ? 0 : gridRef?.current?.getScrollTop())}
                onSortInfoChange={handleSortInfoChange}
              />
            )}
          </Box>
        </Grid>
      </SimpleCard>

      {(openDesc) ? (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      ) : (<></>)}
      <Dialog open={deleteDialog}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Delete Plant
        </DialogTitle>
        <DialogContent>
          {
            (selectedRows.length === 1) ? (<>
              <DialogContentText>
                Do you want to delete the plant <b>{selectedDelPlant?.plantName}</b>{" "}
                with instance <b>{selectedDelPlant?.instance}</b> having Design Organizations{" "}
                {/* <b>
                  {selectedDelPlant?.BU.map((buname) => {
                    return buname;
                  }).join(",")}
                </b> */}
              </DialogContentText>
            </>) : (<>
              <DialogContentText>
                Do you want to delete the seleted plants?
              </DialogContentText>
            </>)
          }
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCloseDelete}>
            Cancel
          </Button>
          <Button onClick={handleDeletePlant}>delete</Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default Plant;