import React, { useEffect, useState } from "react";

import Button from "@mui/material/Button";

import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import shortId from 'shortid';
import { BUService } from "services/buService";
import { MassUpdate } from "services/massUpdateService";
import CloudUploadIcon from '@mui/icons-material/CloudUpload';

import {
    Table,
    TableBody,
    TableCell,
    TableRow,
    Box,
    styled,
    InputLabel,
    Select,
    MenuItem,
    FormControl
} from "@mui/material";


import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import AlertTitle from '@mui/material/AlertTitle';

//import { MassUpdate } from "services/massUpdateService";
import useOracleSettings from "app/hooks/useOracleSettings";

// import download from 'downloadjs';
// import CustomSelect from "./CustomSelect";
const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const StyledTable = styled(Table)(() => ({
    whiteSpace: "pre",
    "& thead": {
        "& tr": { "& th": { paddingLeft: 0, paddingRight: 0 } },
    },
    "& tbody": {
        "& tr": { "& td": { paddingLeft: 0, textTransform: "capitalize" } },
    },
}));

const VisuallyHiddenInput = styled('input')({
    clip: 'rect(0 0 0 0)',
    clipPath: 'inset(50%)',
    height: 1,
    overflow: 'hidden',
    position: 'absolute',
    bottom: 0,
    left: 0,
    whiteSpace: 'nowrap',
    width: 1,
});
const BootstrapDialog = styled(Dialog)(({ theme }) => ({
    '& .MuiDialog-paper-root': {
        backgroundColor: '#ffffff',
    },
    '& .MuiDialogActions-root': {
        padding: theme.spacing(1),
    },
}));

const MassUpdatePartDescriptionInEnoviaAndERP = (props) => {
    const buService = new BUService();
    const massUpdate = new MassUpdate();
    const { importMassupdateFile, setImportMassupdateFile, setOpenAlert, setOpenDesc, setIsLoading } = props;
    const [noFileError, setNoFileError] = useState(false);
    const [importResponse, setImportResponse] = useState(false);
    const [importfileBar, setimportfileBar] = useState(false);
    const [importedFileData, setImportedFileData] = useState(null)
    const [importedFileName, setImportedFileName] = useState(null);
    const [sampleFileUrl, setSampleFileUrl] = useState(null);
    // const [bussinessUnitLists, setBussinessUnitLists] = useState([]);
    const instance = localStorage.getItem('instance')
    const { bussinessUnitLists } = useOracleSettings();

    // const [ERPTypeError, setERPTypeError] = useState(false);
    // const [instanceError, setinstanceError] = useState(false);
    const [BUError, setBUError] = useState(false);
    const [importSelectionValues, setImportSelectionValues] = useState({
        BU: '',
    });

    const importFileData = (e) => {
        console.log('file selected')
        const file = e.target.files[0];
        if (file) {
            const fileType = file.name.split('.').pop();
            if (fileType === 'xlsx' || fileType === 'xls') {
                // if (e.target.files[0].name.split('.').pop() === 'xlsx' || e.target.files[0].name.split('.').pop() === 'xls') {
                setImportedFileData(e.target.files[0])
                setImportedFileName(e.target.files[0].name)
                setNoFileError(false)
            } else {
                setNoFileError(true)
                setImportedFileName(null)
                setImportedFileData(null)
            }
        } else {
            setNoFileError(true)
            setImportedFileName(null)
            setImportedFileData(null)
        }
    }
    const handleCloseImport = () => {
        setImportedFileData(null)
        setImportMassupdateFile(false);
    };

    const handleImportOptionSelection = (event) => {
        setImportSelectionValues((prevselectedValues) => {
            return {
                ...prevselectedValues,
                [event.target.name]: event.target.value,
            };
        })
        if (event.target.name === 'BU') { setBUError(false) }
        //console.info(event.target, 'importSelectionValues')
    }
    const handleImportFile = () => {
        let hasError = false;
        if (importSelectionValues.BU === null || importSelectionValues.BU.length < 1) {
            setBUError(true);
            hasError = true
        }
        if (importedFileData === null || importedFileData.length < 1) {
            setNoFileError(true);
            hasError = true
        }
        if (hasError) { return }

        if (importedFileData !== null) {
            //alert('file is upl7oaded')
            if (window.confirm(`You have requested a report that will take some time to complete. This report will continue to run if you close this browser window. To access this report later, use 'Mass Update Part Description' > My Report.`)) {
                setImportMassupdateFile(false);
                setIsLoading(true);

                console.log('ok for mass update')
                let postData = {
                    'file': importedFileData,
                    data: {
                        BU: importSelectionValues.BU,
                        Instance: instance,
                    }
                };

                // console.log(importSelectionValues, 'importSelectionValues');
                console.log(postData, 'postData');
                massUpdate.massUpdatePartDescription(postData).then((response) => {
                    console.log(response, 'response 11111');
                    setImportResponse(response)
                    setimportfileBar(true);
                    setImportedFileData(null)
                    setImportedFileName(null)
                    setImportMassupdateFile(false)

                    setIsLoading(false);
                    setOpenAlert(false);
                    if (response?.status === "success") {
                        setOpenAlert(true);
                        setOpenDesc({
                            severity: "success",
                            desc: response?.msg,
                        });
                    } else if (response?.status === "failed") {
                        setOpenAlert(true);
                        setOpenDesc({
                            severity: "error",
                            desc: response?.msg,
                        });
                    }

                    // if (!importResponse?.errorInTemplateCount || !importResponse?.errorInPlantCount) {
                    //     updateRefreshData(true);
                    //     fetchData()
                    // }
                });
            }
            console.log('cancle for mass update')
        }
    }
    const handleImportfileBarClose = (event, reason) => {
        if (reason === "clickaway") {
            return;
        }
        setimportfileBar(false);
    };
    const getUniqueValues = (column) => {
        const values = new Set();
        bussinessUnitLists?.forEach(item => {
            values.add(item[column]);
        })
        return Array.from(values);
    };
    useEffect(() => {
        if (bussinessUnitLists?.length === 0) {
            // buService.getAllBuList().then((result) => {
            //   setBussinessUnitLists(result.data)
            // });
        }
    }, [bussinessUnitLists])
    const handleDownload = () => {
        let filelocation = 'massUpdate/sample-massUpdatepartDescription.xlsx'
        massUpdate.downloadSampleFile(filelocation)
    };
    return (
        <>
            <Dialog open={importMassupdateFile}
                PaperProps={{
                    style: {
                        backgroundColor: '#fff',
                        boxShadow: 'none',
                    },
                }} >
                <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
                    Mass Update Part Description in Enovia and ERP
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        <Box width="100%" overflow="auto">
                            <StyledTable>
                                <TableBody>
                                    <TableRow>
                                        <TableCell align="left">
                                            {/* <InputLabel
                                                style={{
                                                    fontWeight: "bold",
                                                    color: "rgba(52, 49, 76, 1)",
                                                }}
                                            > */}
                                            Design Organization
                                            {/* </InputLabel> */}
                                        </TableCell>
                                        <TableCell align="left">
                                            <FormControl fullWidth>
                                                <InputLabel id="bussinessUnit">Select RDO</InputLabel>
                                                <Select
                                                    labelId="bussinessUnit"
                                                    id="bussinessUnit"
                                                    name="BU"
                                                    size="medium"
                                                    label="Select RDO"
                                                    fullWidth
                                                    style={{ width: "70%", maxHeight: "200px", }} // Set the width here
                                                    value={importSelectionValues.BU ? importSelectionValues.BU : ''}
                                                    onChange={handleImportOptionSelection}

                                                >
                                                    {bussinessUnitLists?.map((option) => (
                                                        <MenuItem key={option.buName} value={option.buName}
                                                            style={{ backgroundColor: '#fff' }}
                                                        >
                                                            {option.buName}
                                                        </MenuItem>
                                                    ))}
                                                </Select>
                                            </FormControl>
                                            {BUError && (<p style={{ color: 'red' }}>please select Design Organization</p>)}
                                        </TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left">
                                            Instance
                                        </TableCell>
                                        <TableCell align="left">
                                            <InputLabel id="bussinessUnit">{instance}</InputLabel>
                                        </TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left">
                                            Upload File (Excel File)
                                        </TableCell>
                                        <TableCell align="left">
                                            <div style={{ paddingTop: '10px' }}>
                                                <Button component="label" variant="contained" startIcon={<CloudUploadIcon />}>
                                                    Upload file
                                                    <VisuallyHiddenInput type="file" name="file" accept=".xls, .xlsx" onChange={importFileData} />
                                                </Button>
                                                {noFileError && (<p>Please Select xlsx file to Upload </p>)}
                                            </div>
                                            {importedFileName && (<p>{importedFileName}</p>)}
                                        </TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell colSpan={2}>
                                            <div style={{ paddingTop: '10px' }}>
                                                <a
                                                    // href={sampleFileUrl}
                                                    onClick={handleDownload}
                                                    download="Example-XLSX-document"
                                                    rel="noreferrer"
                                                    // target="_blank"
                                                    //style={{ color: "blue" }}
                                                    style={{ color: "blue", cursor: "pointer" }}
                                                >
                                                    Click Here
                                                </a> to download the sample Excel template for the Part Description Update.

                                                {/* <Button component="label" variant="contained" startIcon={<CloudDownloadIcon />} onClick={() => { download(sampleFileUrl) }}>
                                                Download sample file
                                            </Button> */}
                                                {/* </a> */}
                                            </div>
                                            <div style={{ paddingTop: '10px' }}>
                                                <b>Note:</b> Use only the Excel template, downloaded from this page. Other Excel templates
                                                will not be accepted. The Excel file has four columns with Header labels in the
                                                following order. Fields mark with * are the mandatory fields in the input excel.
                                            </div>
                                            <div style={{ paddingTop: '10px' }}>
                                                Part Type*<br />
                                                Part Name*<br />
                                                Part Revision*<br />
                                                Part Description*<br />
                                            </div>
                                        </TableCell>
                                    </TableRow>
                                </TableBody>
                            </StyledTable>

                        </Box>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleImportFile}>Done</Button>
                    <Button autoFocus onClick={handleCloseImport}>
                        Cancel
                    </Button>
                </DialogActions>
            </Dialog>

            <Snackbar
                open={importfileBar}
                autoHideDuration={6000}
                onClose={handleImportfileBarClose}
                anchorOrigin={{ vertical: "top", horizontal: "right" }}
            >
                <Alert
                    onClose={handleImportfileBarClose}
                    severity={(importResponse && (!importResponse?.errorInTemplate?.length && !importResponse?.errorInPlants?.length)) ? "success" : "error"}
                    sx={{ width: "100%" }}
                >
                    <AlertTitle>{(importResponse && (!importResponse?.errorInTemplate?.length && !importResponse?.errorInPlants?.length)) ? "Success" : "Error"}</AlertTitle>
                    {
                        (importResponse && (importResponse?.errorInTemplate?.length || importResponse?.errorInPlants?.length)) ? (
                            <>
                                <div>
                                    {importResponse?.msg} <br />
                                    {
                                        (importResponse?.errorInTemplateCount) ? (<>
                                            Total Template Count: {importResponse?.errorInTemplateCount} <br />
                                            Templates are: <br />
                                            <ul>
                                                {
                                                    importResponse?.errorInTemplate?.map((eitem) => {
                                                        return (
                                                            <li key={shortId.generate()}>
                                                                {eitem}
                                                            </li>
                                                        )
                                                    })
                                                }
                                            </ul>
                                        </>) : (<></>)
                                    }

                                    {
                                        (importResponse?.errorInPlantCount) ? (
                                            <>
                                                Total Plant Count: {importResponse?.errorInPlantCount} <br />
                                                Plants are: <br />
                                                <ul>
                                                    {
                                                        importResponse?.errorInPlants?.map((eitem) => {
                                                            return (
                                                                <li key={shortId.generate()}>
                                                                    {eitem}
                                                                </li>
                                                            )
                                                        })
                                                    }
                                                </ul>
                                            </>
                                        ) : (<></>)
                                    }

                                </div>
                            </>
                        ) : (
                            <>
                                <div>
                                    {importResponse.msg} <br />
                                    Parts Description Updated: {importResponse.updateTemplates} <br />
                                </div>
                            </>
                        )
                    }
                    {/* Template imported successfully! */}
                </Alert>
            </Snackbar >
        </>
    )
}

export default MassUpdatePartDescriptionInEnoviaAndERP;