import { Breadcrumb, SimpleCard } from "app/components";
import Constant from '../../../constant'
import React, { useEffect, useState, useRef, useCallback } from "react";
import Button from "@mui/material/Button";
import { Link as RouterLink } from "react-router-dom";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import OrgAllowedDialog from "./orgallowedDialog";
import CircularProgress from "@mui/material/CircularProgress";
import DataGrid from "@inovua/reactdatagrid-community";
import "@inovua/reactdatagrid-community/index.css";
import { Box, styled, Grid, TextField, Tooltip } from "@mui/material";
import useOracleSettings from "app/hooks/useOracleSettings";
import { TemplateMappingService } from "services/templateMappingService";
import { HistoryManagementService } from "services/historyManagementService";
import CustomSelect from "../../../components/CustomSelect";
import { BUOptions } from "../../adminConfig/common";
import { MVOtempcompService } from "services/mvotempcompService";
import ImportMappingsDialog from "../../../components/importMappingsDialog";
import ExportMappingDialog from '../../../components/exportMappingTemplate';
import CustomAlertBox from "app/components/CustomAlertBox";
import { sortDataByKey } from "app/utils/mcoUtils/commonUtils";

const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));
const OrgAllowedValuesList = () => {
  const { refreshData } = useOracleSettings();
  const {
    templatesInfo,
    bussinessUnit,
    bussinessUnitLists,
    mappingTemplatesInfo,
    updateBussinessUnit,
    updateMappingTemplatesInfo
  } = useOracleSettings();

  const [searchValue, setSearchValue] = useState("");
  const [deleteDialog, setDeleteDialog] = useState(false);
  const [orgallowedaddDialog, setOrgallowedaddDialog] = useState(false);
  const [orgAllowedForm, setOrgAllowedForm] = useState({});
  const [importTemplate, setImportTemplate] = useState(false);
  const [selectedPlants, setSelectedPlants] = useState([]);
  const mvotempcompservice = new MVOtempcompService();
  const tempMapService = new TemplateMappingService();
  const draftMapService = new HistoryManagementService();
  const [selectedDeleteMapping, setSelectedDeleteMapping] = useState();
  const [isEdit, setIsEdit] = useState(false);
  const [orgallowedData, setorgallowedData] = useState([]);
  const [selectedRows, setSelectedRows] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const gridStyle = { minHeight: 550 };
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [selected, setSelected] = useState({});
  const [filteredDraftTabledata, setFilteredDraftTabledata] = useState([]);
  const gridRef = useRef(null);
  const [sortInfo, setSortInfo] = useState({});
  const dataMap = gridRef && gridRef.current && gridRef.current.dataMap ? gridRef.current.dataMap : null;
  const handleSearchInputChange = (event) => {
    const newSearchValue = event.target.value;
    setSearchValue(newSearchValue);
  };

  const handleSearch = () => {
  };

  const handleCloseDelete = () => {
    setDeleteDialog(false);
  };

  const handleDeleteTempMapping = async () => {
    try {
      await mvotempcompservice.softDeleteTempCompMapping({ data: selectedRows }).then(result => {
        handleCloseDelete(false)
        setSelectedRows([])
        setSelectedDeleteMapping([])
        setSelected({})
      });
    } catch (error) {
      console.error("Failed to delete mapping:", error);
    }
    handlesnackBarClick("Template Mapping deleted Successfully", "success");
    fetchData();
    setDeleteDialog(false);
  };

  const handlesnackBarClick = (message, severity) => {
    setOpenAlert(true);
    setOpenDesc({
      severity: severity,
      desc: message,
    });
  };

  // ****************filters**********************

  // *********org allowed values dialog methods**********
  const handleorgAllowedFormChange = (event) => {
    const { name, value } = event.target;

    if (name === "BUID") {
      // Business unit is changed, reset selected plants
      setSelectedPlants([]);
    }

    if (name === "ALLOWABLE_ORG_TEMPLATES") {
      // Template Name is changed, reset selected Business Unit
      setOrgAllowedForm({
        ...orgAllowedForm,
        'BUID': bussinessUnit,
      });
      setSelectedPlants([]);
    }

    // If the event comes from the multi-select dropdown
    if (name === "plants") {
      setSelectedPlants(value);
      // Generate payload
    } else {
      // If it's a form field change
      const updatedForm = {
        ...orgAllowedForm,
        [name]: value,
      };
      setOrgAllowedForm(updatedForm);
    }
  }

  const handleAddOrgAllowedValues = () => {
    const templateid = templatesInfo?.filter(
      (template) =>
        template.templateName === orgAllowedForm.ALLOWABLE_ORG_TEMPLATES
    );
    const filteredPlants =
      bussinessUnitLists?.find((plant) => plant.buName === orgAllowedForm.BUID)?.plants ||
      [];

    const resultPayload = {};

    // Iterate over filteredPlants
    filteredPlants.forEach((plant) => {
      const plantName = plant.plantName;
      resultPayload[plantName] = selectedPlants?.includes(plantName) ? "Y" : "N";
    });
    if (
      !orgAllowedForm.ALLOWABLE_ORG_TEMPLATES ||
      !orgAllowedForm.BUID ||
      selectedPlants?.length < 1
    ) {
      handlesnackBarClick("Please Enter the required fields", "error");
      return;
    }

    if (orgAllowedForm) {
      const neworgallowedvalue = {
        BUID: orgAllowedForm.BUID,
        MappingType: "orgallowedvalues",
        id: templateid[0].id + "_" + orgAllowedForm.BUID.trim(),
        details: {
          ALLOWABLE_ORG_TEMPLATES: orgAllowedForm.ALLOWABLE_ORG_TEMPLATES,
          Has_MBOM: orgAllowedForm.Has_MBOM !== undefined ? orgAllowedForm.Has_MBOM : 'Y',
          EBOM_Allowed: orgAllowedForm.EBOM_Allowed !== undefined ? orgAllowedForm.EBOM_Allowed : 'Y',
          Optional: orgAllowedForm.Optional !== undefined ? orgAllowedForm.Optional : 'Y',
          PO_Org_Exist_Check: orgAllowedForm.PO_Org_Exist_Check !== undefined ? orgAllowedForm.PO_Org_Exist_Check : 'Y',
          On_MBOM: orgAllowedForm.On_MBOM !== undefined ? orgAllowedForm.On_MBOM : 'Y',
          ALLOWABLE_ORG_TEMPLATES_ID: templateid[0].id,
          Plants: resultPayload,
        },
      };
      if (isEdit === false) {
        mvotempcompservice
          .addnewTempCompMapping(neworgallowedvalue)
          .then((resp) => {
            if (resp?.status === 'success') {
              fetchData();
              setTimeout(() => {
                setOrgallowedaddDialog(false);
              }, 200);
              handlesnackBarClick("Org Allowed Values added successfully", "success");
            } else if (resp?.status === 'error') {
              handlesnackBarClick(resp?.msg, "error");
            }
          });
      } else {
        mvotempcompservice
          .updateTempCompMapping(neworgallowedvalue)
          .then((resp) => {
            if (resp.status === "success") {
              setTimeout(() => {
                setOrgallowedaddDialog(false);
                fetchDraftData();
                fetchData();
              }, 200);
              setIsEdit(false);
              handlesnackBarClick(
                "Org Allowed Values updated successfully",
                "success"
              );
            } else if (resp?.status === "error") {
              handlesnackBarClick(resp?.msg, "error");
            }
          });
      }
    }
    handleorgAllowedDialogClose();
  };

  const handleorgAllowedDialogOpen = () => {
    setOrgAllowedForm({ BUID: bussinessUnit });
    setSelectedPlants([]);
    setOrgallowedaddDialog(true);
  };
  const handleorgAllowedDialogClose = () => {
    setOrgallowedaddDialog(false);
    setOrgAllowedForm("");
    setSelectedPlants([]);
    setSelectedRows([]);
    setSelected({})
    setIsEdit(false);
  };

  const handleorgAllowedDialogAdd = () => {
    setOrgallowedaddDialog(false);
  };
  const handleDeleteDialog = () => {
    setSelectedDeleteMapping(selectedRows[0]);
    setDeleteDialog(true);
  };
  const handleEditOrgAllowedValues = () => {
    const selectedDetails = selectedRows[0]?.details;
    const BUID = selectedRows[0]?.BUID;
    for (const key in selectedRows[0]?.details?.Plants) {
      if (selectedRows[0]?.details?.Plants[key] === "Y") {
        selectedPlants.push(key);
      }
    }
    setOrgAllowedForm({
      ...selectedDetails,
      BUID,
    });

    setIsEdit(true);
    setOrgallowedaddDialog(true);
  };

  const prepareTableColumn = (orgallowedData) => {
    let plantColumnsSet = new Set(); // Define it outside
    if (orgallowedData && orgallowedData.length > 0) {
      orgallowedData.forEach((dataItem) => {
        const plants = dataItem.details?.Plants;
        if (plants) {
          const plantKeys = Object.keys(plants);
          plantKeys.forEach((plant) => {
            plantColumnsSet.add(plant);
          });
        }
      });
    }

    const plantColumns = Array.from(plantColumnsSet).map((plant) => {
      return {
        name: plant,
        header: plant,
        defaultWidth: 100,
        render: (params) => (
          <div dangerouslySetInnerHTML={{ __html: params.value }} />
        ),
      };
    });
    const columns = [
      {
        name: "ALLOWABLE_ORG_TEMPLATES",
        defaultWidth: 250,
        header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.AllowableOrgTemplate}>Template Name</Tooltip>,
        render: (params) => (
          <div dangerouslySetInnerHTML={{ __html: params.value }} />
        )
      },

      {
        name: "BUID",
        header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.RDO}>Design Organization</Tooltip>,
        defaultWidth: 150,
      },
      {
        name: "Has_MBOM",
        header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.HasMBom}>Has MBOM</Tooltip>,
        defaultWidth: 120,
        render: (params) => (
          <div dangerouslySetInnerHTML={{ __html: params.value }} />
        )
      },
      {
        name: "On_MBOM",
        header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.OnMbom}>On MBOM</Tooltip>,
        defaultWidth: 120,
        render: (params) => (
          <div dangerouslySetInnerHTML={{ __html: params.value }} />
        )
      },
      {
        name: "EBOM_Allowed",
        header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.EBOM}>EBOM Allowed</Tooltip>,
        defaultWidth: 100,
        render: (params) => (
          <div dangerouslySetInnerHTML={{ __html: params.value }} />
        )
      },
      {
        name: "Optional",
        header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.Optional}>Optional</Tooltip>,
        defaultWidth: 100,
        render: (params) => (
          <div dangerouslySetInnerHTML={{ __html: params.value }} />
        )
      },
      {
        name: "PO_Org_Exist_Check",
        header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.PoCheck}>PO org Check</Tooltip>,
        defaultWidth: 120,
        render: (params) => (
          <div dangerouslySetInnerHTML={{ __html: params.value }} />
        )
      },
      ...plantColumns,
    ];

    return columns;
  }

  const fetchData = () => {
    setIsLoading(true)
    tempMapService.getAlltemplateMappings().then((result) => {
      updateMappingTemplatesInfo(result?.data?.templateMappingList);
      setIsLoading(false);
    });
  }
  const onSelectionChange = useCallback(({ selected }) => {
    setSelected(selected);
  }, []);

  useEffect(() => {
    const selRowsData = selected === true ? dataMap : selected;
    const selRowsDetails = (selRowsData) && Object.keys(selRowsData).map(key => (selRowsData[key]));
    setSelectedRows(selRowsDetails);
  }, [selected, dataMap]);

  useEffect(() => {
    setIsLoading(refreshData);
  }, [refreshData]);
  const [tableTempData, setTableTempData] = useState([])
  const [unSortTableTempData, setUnSortTableTempData] = useState([])

  const sortedFilterOrgAllowedData = sortDataByKey(tableTempData, "ALLOWABLE_ORG_TEMPLATES");

  useEffect(() => {
    fetchDraftData();
  }, []);

  useEffect(() => {
    setIsLoading(true)
    if (mappingTemplatesInfo) {
      let finalTempMapList = mappingTemplatesInfo;
      finalTempMapList = finalTempMapList.filter(
        (item) => item.MappingType === "orgallowedvalues" && item.actionFlag !== "Delete"
      );

      if (bussinessUnit) {
        finalTempMapList = finalTempMapList?.filter((fitem) => {
          return fitem.BUID === bussinessUnit;
        });
      }
      if (searchValue && searchValue !== "") {
        finalTempMapList = finalTempMapList?.filter((fitem) => {
          return (
            JSON.stringify(fitem)?.includes(searchValue) ||
            JSON.stringify(fitem)?.toLocaleLowerCase()?.includes(searchValue)
          );
        });
      }
      if (filteredDraftTabledata.length > 0) {
        const filteredOrgAllowedDraftData = filteredDraftTabledata.filter((i) => { return i.MappingType == "orgallowedvalues" });
        if (filteredOrgAllowedDraftData.length > 0 && filteredOrgAllowedDraftData[0].MappingType === 'orgallowedvalues') {
          finalTempMapList.forEach(obj1 => {
            filteredOrgAllowedDraftData[0].changes.forEach(obj2 => {
              if (obj1['TEMPLATE_MAPPING'] === obj2['id']) {
                obj1.details.draftValue_ALLOWABLE_ORG_TEMPLATES = obj2.details.ALLOWABLE_ORG_TEMPLATES !== obj1.details.ALLOWABLE_ORG_TEMPLATES ? obj2.details.ALLOWABLE_ORG_TEMPLATES : null;
                obj1.details.draftValue_Has_MBOM = obj2.details.Has_MBOM !== obj1.details.Has_MBOM ? obj2.details.Has_MBOM : null;
                obj1.details.draftValue_On_MBOM = obj2.details.On_MBOM !== obj1.details.On_MBOM ? obj2.details.On_MBOM : null;
                obj1.details.draftValue_EBOM_Allowed = obj2.details.EBOM_Allowed !== obj1.details.EBOM_Allowed ? obj2.details.EBOM_Allowed : null;
                obj1.details.draftValue_Optional = obj2.details.Optional !== obj1.details.Optional ? obj2.details.Optional : null;
                obj1.details.draftValue_PO_Org_Exist_Check = obj2.details.PO_Org_Exist_Check !== obj1.details.PO_Org_Exist_Check ? obj2.details.PO_Org_Exist_Check : null;
                obj1.details.draft_Plants = {};
                for (let key in obj1.details.Plants) {
                  obj1.details.draft_Plants[key] = obj2.details.Plants[key] !== obj1.details.Plants[key] ? obj2.details.Plants[key] : null;
                }
              }
            });
          });
        }
      }

      setTimeout(() => {
        setorgallowedData(finalTempMapList);
        let data = finalTempMapList.map(item => {
          item["ALLOWABLE_ORG_TEMPLATES"] = item?.details?.draftValue_ALLOWABLE_ORG_TEMPLATES ? item?.details?.draftValue_ALLOWABLE_ORG_TEMPLATES : item?.details?.ALLOWABLE_ORG_TEMPLATES
          item["Has_MBOM"] = item?.details?.draftValue_Has_MBOM
            ? `${item?.details?.draftValue_Has_MBOM} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through">${item?.details.Has_MBOM}</span>`
            : item?.details.Has_MBOM;
          item["On_MBOM"] = item?.details?.draftValue_On_MBOM ? `${item?.details?.draftValue_On_MBOM} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through">${item?.details.On_MBOM}</span>`
            : item?.details.On_MBOM;
          item["EBOM_Allowed"] = item?.details?.draftValue_EBOM_Allowed ? `${item?.details?.draftValue_EBOM_Allowed} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through">${item?.details.EBOM_Allowed}</span>`
            : item?.details.EBOM_Allowed;
          item["Optional"] = item?.details?.draftValue_Optional ? `${item?.details?.draftValue_Optional} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through">${item?.details.Optional}</span>`
            : item?.details.Optional;
          item["PO_Org_Exist_Check"] = item?.details?.draftValue_PO_Org_Exist_Check ? `${item?.details?.draftValue_PO_Org_Exist_Check} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through">${item?.details.PO_Org_Exist_Check}</span>`
            : item?.details.PO_Org_Exist_Check;

          Object.keys(item?.details?.Plants).map(plant => {
            item?.details?.draft_Plants ? item[plant] = item?.details?.draft_Plants[plant] ? `${item?.details?.draft_Plants[plant]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.Plants[plant]} <span>` : item?.details?.Plants[plant]
              : item[plant] = item?.details?.Plants[plant]
          }
          )
          return item
        })
        setUnSortTableTempData(finalTempMapList)
        setTableTempData(data);
        setIsLoading(false)
      }, 500)
    }
    else {
      if (!bussinessUnit) {
        updateBussinessUnit(BUOptions ? BUOptions[0]?.value : "");
      }
    }
  }, [mappingTemplatesInfo, bussinessUnit, searchValue, filteredDraftTabledata]);

  const fetchDraftData = () => {
    setIsLoading(true);
    draftMapService.getAlldraftChanges().then((result) => {
      let BUID = bussinessUnit ? bussinessUnit : 'Micro Motion';
      if (result.data) {
        let filterData = result.data.filter(element => element.BUID === BUID || element.BUID === 'Another Name');
        setFilteredDraftTabledata(filterData);
      } else {
        setFilteredDraftTabledata([]);
      }
      setIsLoading(false);
    });
  }

  // *********org allowed values dialog methods ends**********
  const rowStyle = ({ data }) => {
    const isSelected = Object.keys(selected).includes(data.id);
    return {
      backgroundColor: data.actionFlag !== undefined ? 'rgb(25 118 210 / 20%)' : '',
      zIndex: 99,
      background: (isSelected || selected === true) ? '#dee1f2' : 'initial',
    }
  }
  const handleSortInfoChange = (sortInfo) => {
    setSortInfo(sortInfo);
  };
  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Org Allowed Templates" }]} />
      </Box>

      <SimpleCard>
        <Grid container spacing={1} alignItems="top">
          {/* Search Input and Search Button */}
          <Grid item md={6} sm={6} xs={12} className="font-medium">
            <Grid container spacing={1} alignItems="center">
              <Grid item md={6} sm={6} xs={12} className="font-medium">
                <TextField
                  type="text"
                  name="search"
                  size="small"
                  onChange={handleSearchInputChange}
                  value={searchValue}
                  style={{ width: "100%" }}
                />
              </Grid>
              <Grid item md={6} sm={6} xs={12} className="font-medium">
                <Button variant="contained" onClick={handleSearch}>
                  Search
                </Button>
              </Grid>
            </Grid>
          </Grid>

          {/* Design Organization Text and Dropdown */}
          <Grid item md={6} sm={6} xs={12} className="font-medium">
            <Grid container alignItems="center" justify="flex-end" style={{ display: "none" }}>
              <Grid item xs={12} sm={6} md={6} className="font-medium" style={{ textAlign: 'right' }}>
                <label style={{ whiteSpace: "nowrap", paddingTop: "11px" }}>Design Organization:</label>
              </Grid>
              <Grid item xs={12} sm={6} md={6} className="font-medium" style={{ textAlign: 'left' }}>
                <CustomSelect
                  onChange={handleSearchInputChange}
                  selectedvalue={bussinessUnit}
                  style={{ width: "100%" }}
                  optionsValues={BUOptions}
                  // defaultValue={
                  //   BUOptions && !bussinessUnit ? BUOptions[0]?.value : ""
                  // }
                  onChangeFunc={(selectedOptionValue) => {
                    setSelected({});
                    setSelectedRows([]);

                    updateBussinessUnit(selectedOptionValue);
                    setSearchValue("");
                    //setSelectedRows([]);
                  }}
                />
              </Grid>
            </Grid>


            {/* Right-aligned Buttons */}
            <Grid item md={12} sm={12} xs={12} className="font-medium" style={{ marginTop: '0px', textAlign: 'right' }}>
              <Button
                variant="contained"
                component={RouterLink}
                onClick={handleorgAllowedDialogOpen}
                style={{ margin: "0 0px 0 10px" }}
              >
                Add
              </Button>
              <Button
                variant="contained"
                component={RouterLink}
                disabled={selectedRows?.length === 1 ? false : true}
                onClick={handleEditOrgAllowedValues}
                style={{ margin: "0 0px 0 10px" }}
              >
                Edit
              </Button>
              <Button
                variant="contained"
                component={RouterLink}
                disabled={selectedRows?.length !== 0 ? false : true}
                //onClick={handleDeleteOrgAllowedValues}
                onClick={handleDeleteDialog}
                style={{ margin: "0 0px 0 10px" }}
              >
                Delete
              </Button>
              <Button
                variant="contained"
                onClick={() => {
                  setImportTemplate(true);
                }}
                style={{ margin: "0 10px 0 10px" }}
              >
                Import
              </Button>
              {/* export btn */}
              <ExportMappingDialog
                title={"Org Allowed Templates"}
                MappingType={"orgallowedvalues"}
                filterTemplateData={selectedRows}
                sortInfo={sortInfo}
              />
            </Grid>
          </Grid>

          {/* DataGrid */}
          <Grid item xs={12} style={{ marginTop: '10px' }}>
            <Box width="100%" overflow="auto" marginTop="10px">
              {isLoading && (
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    height: "100vh",
                  }}
                >
                  <CircularProgress />
                </Box>
              )}
              {!isLoading && (
                <DataGrid
                  idProperty="id"
                  showZebraRows={false}
                  style={gridStyle}
                  columns={prepareTableColumn(orgallowedData) || []}
                  //dataSource={orgallowedData}
                  dataSource={sortInfo === null ? unSortTableTempData : sortedFilterOrgAllowedData}
                  rowHeight={null}
                  checkboxColumn
                  defaultLimit={100}
                  selected={selected}
                  onSelectionChange={onSelectionChange}
                  handle={ref => gridRef.current = ref ? ref.current : null}
                  rowStyle={rowStyle}
                  pagination
                  renderScroller={()=> gridRef?.current?.setScrollTop(selectedRows?.length==0 ? 0 : gridRef?.current?.getScrollTop())}
                    onSortInfoChange={handleSortInfoChange}
                />
              )}
            </Box>
          </Grid>
        </Grid>
      </SimpleCard >
      {
        (openDesc) ? (
          <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
        ) : (<></>)
      }
      <Dialog open={deleteDialog}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Delete Mapping
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            {'Do you want to delete the OrgAllowedTemplates Mapping(s) for Design Organization'} <b>{selectedDeleteMapping?.BUID}</b>
            <br />
            <br />
            <b>Note:</b> {Constant.adminConstant.common.deleteNote}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDelete}>Cancel</Button>
          <Button onClick={handleDeleteTempMapping}>delete</Button>
        </DialogActions>
      </Dialog>

      <OrgAllowedDialog
        open={orgallowedaddDialog}
        handleorgAllowedDialogAdd={handleorgAllowedDialogAdd}
        handleorgAllowedDialogClose={handleorgAllowedDialogClose}
        orgAllowedForm={orgAllowedForm}
        handleorgAllowedFormChange={handleorgAllowedFormChange}
        handleAddOrgAllowedValues={handleAddOrgAllowedValues}
        selectedPlants={selectedPlants}
        isEdit={isEdit}
        selectedBU={bussinessUnit}
      />

      <ImportMappingsDialog
        title={"Import RDO Allowed Template Mappings"}
        MappingType={"orgallowedvalues"}
        selectedBU={bussinessUnit}
        importTemplate={importTemplate}
        fetchData={fetchData}
        setImportTemplate={setImportTemplate}
      />
    </Container >
  );
};

export default OrgAllowedValuesList;
