import React, { useState, useEffect, useRef } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  Select,
  MenuItem,
  Button,
  Tooltip
} from "@mui/material";

import { Scrollbars } from 'react-custom-scrollbars-2';
import ReactDataGrid from "@inovua/reactdatagrid-community";
import Constant from '../../../constant'
import "@inovua/reactdatagrid-community/index.css";
import { unionBy } from 'lodash';
const gridStyle = { minHeight: 248 }

const plantGroupOptions = [
  { name: "MASTER ORG", value: "MASTER ORG" },
  { name: "Boulder OU", value: "Boulder OU" },
  { name: "Nanjing OU", value: "Nanjing OU" },
  { name: "EDE OU", value: "EDE OU" },
  { name: "Cluj OU", value: "Cluj OU" },
  { name: "Others", value: "Others" }
]
const bomStatusOptions = [
  { name: "BOM", value: "BOM" },
  { name: "NO BOM", value: "NO BOM" },
];
const rduOptions = [
  { name: "YES", value: "YES" },
  { name: "NO", value: "NO" },
];
const entitydropdown = [{ name: "PlantGroup", value: "PlantGroup" },
{ name: "BOM or NO BOM", value: "BOM or NO BOM" },
{ name: "none or PO Org", value: "none or PO Org" },
{ name: "RDO OU Org Set", value: "RDO OU Org Set" }]
const BUPlantDialog = ({
  open,
  handleorgAllowedDialogClose,
  orgAllowedForm,
  handleorgAllowedFormChange,
  handleAddBuPlant,
  isEdit,
  bussinessUnitLists,
  bussinessUnit,
  setPlantValueList,
  plantValueList,
  setDisplayOrder
}) => {
  const [filteredPlants, setFilteredPlants] = useState([]);
  const [dropdownOptions, setDropdownOptions] = useState([]);
  const [entityOption, setEntityOption] = useState([]);
  const [dataSource, setDataSource] = useState([]);
  useEffect(() => {
    const Plants = (
      bussinessUnitLists?.find((plant) => plant.buName === bussinessUnit)?.plants || []
    ).concat({ plantName: "None", id: "None" });
    let plist = Plants.map((item) => {
      let res = {
        "name": item.plantName,
        "value": item.plantName
      }
      return res;
    })
    setFilteredPlants(plist)
    let filteredPlantsLeftSide = bussinessUnitLists?.find((plant) => plant.buName === bussinessUnit)?.plants || [];
    filteredPlantsLeftSide.map((plant, index) => {
      plant.value = ''
      plant.id = index
      return Object.assign({}, plant)
    })
    filteredPlantsLeftSide = unionBy(plantValueList, filteredPlantsLeftSide, 'plantName');
    setDataSource(filteredPlantsLeftSide)
    setPlantValueList(filteredPlantsLeftSide)

    console.log(plantValueList, 'plantValueList plantValueList')
    setEntityOption(entitydropdown)
    if (isEdit) {
      console.log(orgAllowedForm.Entity, 'orgAllowedForm.Entity')
      switch (orgAllowedForm.Entity) {
        case "PlantGroup":
          setDropdownOptions(plantGroupOptions)
          setDisplayOrder(1)
          break;
        case "BOM or NO BOM":
          setDropdownOptions(bomStatusOptions)
          setDisplayOrder(2)
          break;
        case "none or PO Org":
          setDropdownOptions(filteredPlants)
          setDisplayOrder(3)
          break;
        case "RDO OU Org Set":
          setDropdownOptions(rduOptions)
          setDisplayOrder(4)
          break;
        default:
          setDropdownOptions([])
          break;
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bussinessUnit, bussinessUnitLists, orgAllowedForm.Entity, isEdit]);
  const plantColumn = {
    name: 'value',
    header: 'Value',
    defaultFlex: 1, minWidth: 100,
    rendersInlineEditor: true,
    editable: true,
    sortable: false,
    render: ({ value }, { cellProps }) => {
      let v = cellProps.editProps.inEdit
        ? cellProps.editProps.value
        : value;
      return (
        <Select
          style={{ width: "100%" }}
          size="small"
          autoFocus={cellProps.inEdit}
          name={cellProps.data.plantName}
          value={v}
          onBlur={e => {
            cellProps.editProps.onComplete(e);
          }}
          onChange={handleChanges}
          onFocus={() => cellProps.editProps.startEdit()}
        >
          {dropdownOptions?.map((option) => (
            <MenuItem key={option.name} value={option.name}>
              {option.name}
            </MenuItem>
          ))}
        </Select>
      );
    }
  };

  const gridRef = useRef(null);
  const columns = [
    { name: 'id', header: 'Id', defaultVisible: false, defaultWidth: 60, type: 'string' },
    {
      name: "plantName",
      defaultWidth: 180,
      header: <Tooltip title={Constant.adminConstant.expensemapList.tooltip.PlantName}>Plant Name</Tooltip>,
      render: ({ data }) => <div>{data?.plantName}</div>,
    },
    plantColumn
  ]

  const handleChanges = (event) => {
    const data = [...dataSource];
    let newData = data.map(item => {
      if (item.plantName === event.target.name) {
        item.value = event.target.value
      }
      return Object.assign({}, item)
    })
    setDataSource(newData)
    setPlantValueList(newData)
  }
  const handleEntityChange = (event) => {
    handleorgAllowedFormChange(event)
    let dropdownselect = event.target.value;
    switch (dropdownselect) {
      case "PlantGroup":
        setDropdownOptions(plantGroupOptions)
        setDisplayOrder(1)
        break;
      case "BOM or NO BOM":
        setDropdownOptions(bomStatusOptions)
        setDisplayOrder(2)
        break;
      case "none or PO Org":
        setDropdownOptions(filteredPlants)
        setDisplayOrder(3)
        break;
      case "RDO OU Org Set":
        setDropdownOptions(rduOptions)
        setDisplayOrder(4)
        break;
      default:
        setDropdownOptions([])
        break;
    }
  }
  return (
    <Dialog open={open}>
      <DialogTitle>
        {" "}
        {isEdit ? "Edit RDO Plant Mapping " : "Add RDO Plant Mapping"}
      </DialogTitle>
      <Scrollbars
        autoHeight
        autoHeightMin={100}
        autoHeightMax={500}
        autoHide
        autoHideTimeout={1000}
        autoHideDuration={300}
      >
        <DialogContent>
          <Grid container spacing={3} alignItems="center">
            <Grid item md={3} sm={4} xs={12}>
              <span>
                Design Organization <span style={{ color: "red" }}>*</span>
              </span>
            </Grid>
            <Grid item md={9} sm={8} xs={12}>
              <Select
                id="DataType-select"
                name="BUID"
                fullWidth
                value={bussinessUnit}
                onChange={handleorgAllowedFormChange}
                disabled
              >
                {bussinessUnitLists?.map((option) => (
                  <MenuItem key={option.businessUnit} value={option.businessUnit}>
                    {option.businessUnit}
                  </MenuItem>
                ))}
              </Select>
            </Grid>

            <Grid item md={3} sm={4} xs={12}>
              <span>
                Entity <span style={{ color: "red" }}>*</span>
              </span>
            </Grid>
            <Grid item md={9} sm={8} xs={12}>
              <Select
                style={{ width: "100%" }}
                name="Entity"
                value={orgAllowedForm.Entity}
                onChange={handleEntityChange}
                disabled={isEdit}
              >
                {entityOption?.map((option) => (
                  <MenuItem key={option.name} value={option.value}>
                    {option.name}
                  </MenuItem>
                ))}
              </Select>
            </Grid>

            <Grid item md={3} sm={4} xs={12}>
              <span>
                Plant Values <span style={{ color: "red" }}>*</span>
              </span>
            </Grid>

            <Grid item md={9} sm={8} xs={12}>
              <ReactDataGrid
                idProperty="id"
                showZebraRows={false}
                handle={ref => gridRef.current = ref ? ref.current : null}
                style={gridStyle}
                columns={columns}
                dataSource={dataSource}
              />
            </Grid>
          </Grid>
        </DialogContent>
      </Scrollbars>
      <DialogActions>
        <Button onClick={handleorgAllowedDialogClose}>Cancel</Button>
        <Button onClick={handleAddBuPlant}>{isEdit ? "Update" : "Add"}</Button>
      </DialogActions>
    </Dialog>
  );
};

export default BUPlantDialog;
