import { createContext, useState, useEffect } from 'react'

const MVOtemplatecompContext = createContext({
    mvotemplatecompInfo: [],
    updateMVOtempcompLists: () => [],
})

export const MVOtempcompProvider = ({ children }) => {
    const [mvotemplatecompInfo, setMvotemplatecompInfo] = useState([]);

    const updateMVOtempcompLists = (values) => {
        setMvotemplatecompInfo(values);
       // console.log(values, "inside update  333");
    };

    useEffect(() => {
       // console.log(mvotemplatecompInfo, "templatesContext");
    }, [mvotemplatecompInfo]);

    return (
        <MVOtemplatecompContext.Provider
            value={{
                mvotemplatecompInfo,
                updateMVOtempcompLists,
            }}
        >
            {children}
        </MVOtemplatecompContext.Provider>
    );
};

export default MVOtemplatecompContext;