import { Breadcrumb, SimpleCard } from "app/components";
import Constant from '../../../constant'
import React, { useEffect, useState, useRef, useCallback } from "react";
import Button from "@mui/material/Button";
import { Link as RouterLink } from "react-router-dom";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import DataGrid from "@inovua/reactdatagrid-community";
import "@inovua/reactdatagrid-community/index.css";
import { Box, styled, Grid, TextField, Tooltip } from "@mui/material";
import { TemplateMappingService } from "services/templateMappingService";
import CustomSelect from "../../../components/CustomSelect";
import { BUOptions } from "../../adminConfig/common";
import PartTypeTemplateDialog from "./parttypetemplateDialog";
import { MVOtempcompService } from "services/mvotempcompService";
import { HistoryManagementService } from "services/historyManagementService";
import useOracleSettings from "app/hooks/useOracleSettings";
import CircularProgress from "@mui/material/CircularProgress";
import ImportMappingsDialog from '../../../components/importMappingsDialog';
import ExportMappingDialog from '../../../components/exportMappingTemplate';
import CustomAlertBox from "app/components/CustomAlertBox";
import { sortDataByKey } from "app/utils/mcoUtils/commonUtils";
const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));

const PartTypeToTemplateList = () => {
  const { refreshData } = useOracleSettings();
  const {
    templatesInfo,
    bussinessUnit,
    mappingTemplatesInfo,
    updateBussinessUnit,
    updateMappingTemplatesInfo
  } = useOracleSettings();

  const [importTemplate, setImportTemplate] = useState(false);
  const [searchValue, setSearchValue] = useState("");
  const [deleteDialog, setDeleteDialog] = useState(false);
  const [orgallowedaddDialog, setOrgallowedaddDialog] = useState(false);
  const [orgAllowedForm, setOrgAllowedForm] = useState({});
  const [orgAllowedData, setOrgAllowedData] = useState([]);
  const [selectedPlants, setSelectedPlants] = useState([]);
  const tempMapService = new TemplateMappingService();
  const draftMapService = new HistoryManagementService();
  const [selectedDeleteMapping, setSelectedDeleteMapping] = useState();
  const mvotempcompservice = new MVOtempcompService();
  const [isLoading, setIsLoading] = useState(true);
  const [isEdit, setIsEdit] = useState(false);
  const [valuesInputList, setValuesInputList] = useState([]);
  const [newKey, setNewKey] = useState("");
  const [newValue, setNewValue] = useState("");
  const [keyerror, setKeyerror] = useState("");
  const [selected, setSelected] = useState({});
  const [formTemplateSelection, setFormTemplateSelection] = useState({});
  const [filteredBUOptions, setFilteredBUOptions] = useState([]);
  const [filteredDraftTabledata, setFilteredDraftTabledata] = useState([]);
  const [formError, setFormError] = useState({})
  const [RDO, setRDO] = useState("");
  const [statusObject, setStatusObject] = useState({});
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const gridRef = useRef(null);
  const [sortInfo, setSortInfo] = useState({});
  const dataMap = gridRef && gridRef.current && gridRef.current.dataMap ? gridRef.current.dataMap : null;

  const handleSearchInputChange = (event) => {
    const newSearchValue = event.target.value;
    setSearchValue(newSearchValue);
  };



  const handleSearch = () => {
  };

  const handleCloseDelete = () => {
    setDeleteDialog(false);
  };

  const handlesnackBarClick = (message, severity) => {
    setOpenAlert(true);
    setOpenDesc({
      severity: severity,
      desc: message,
    });
  };

  //********Dialog code ********
  const handleorgAllowedFormChange = (event) => {
    const { name, value } = event.target;

    if (name === "newKey") {
      setNewKey(value);
    }
    if (name === "newValue") {
      setNewValue(value);
    }
    if (name === "BUID") {
      // Business unit is changed, reset selected plants
      setSelectedPlants([]);
    }

    if (name === "ENGG_ITEM_TYPE") {
      // Template Name is changed, reset selected Business Unit
      setOrgAllowedForm({
        ...orgAllowedForm,
        BUID: "",
      });
      setFormError(prevState => {
        const { templateError, ...rest } = prevState;
        return rest
      });
      setSelectedPlants([]);
    }
    if (name === "plants") {
      setSelectedPlants(value);
      // Generate payload
    } else {
      // If it's a form field change
      const updatedForm = {
        ...orgAllowedForm,
        [name]: value,
      };
      setOrgAllowedForm(updatedForm);
    }
  };

  const handleAddRow = () => {
    const keyExists = valuesInputList.some((row) => row.key === newKey);
    const valueExists = valuesInputList.some((row) => row.value === newValue);
    const templateid = templatesInfo?.filter(
      (template) => template.templateName === newKey
    );
    if (keyExists || valueExists) {
      setKeyerror("The key or value already exists");
      return;
    }
    if (!newKey || !newValue) {
      setKeyerror("Template and its status required");
      return;
    }

    setValuesInputList((prevList) => [
      ...prevList,
      {
        org_allowed_template: newKey,
        exclusive_use: newValue,
        id: templateid[0].id,
      },
    ]);
    setNewKey("");
    setNewValue("");
    setKeyerror("");
  };
  const handleDeleteRow = (index) => {
    setValuesInputList((prevList) => {
      const newList = [...prevList];
      newList.splice(index, 1);
      return newList;
    });
  };

  const handleAddMVOtempComp = () => {
    const templateid = templatesInfo?.filter(
      (template) => template.templateName === orgAllowedForm.ENGG_ITEM_TYPE
    )
    if (orgAllowedForm) {
      if (RDO === undefined || RDO === "") {
        setFormError(prevState => ({
          ...prevState,
          "RDOError": "Design Organization is required",
        }));
      }
      if (orgAllowedForm.ENGG_ITEM_TYPE === undefined || orgAllowedForm.ENGG_ITEM_TYPE === "") {
        setFormError(prevState => ({
          ...prevState,
          "templateError": "Template Name is required",
        }));
      }
      let templatedata = [];
      let new_formError = { ...formError };
      delete new_formError["AllowableOrgTempError"];
      if (Object.keys(formTemplateSelection).length) {
        templatedata = Object.values(formTemplateSelection).map((value) => value)
        templatedata = templatedata.map(item => {
          return {
            org_allowed_template: item.details.ALLOWABLE_ORG_TEMPLATES,
            exclusive_use: statusObject[item.details.ALLOWABLE_ORG_TEMPLATES] === undefined ? 'Yes' : statusObject[item.details.ALLOWABLE_ORG_TEMPLATES],
            id: item.details.ALLOWABLE_ORG_TEMPLATES_ID,
          }
        })
      } else {
        new_formError["AllowableOrgTempError"] = "Please select at least one template";
      }
      setFormError(new_formError);
      if (Object.keys(new_formError).length || Object.keys(formError).length) {
        console.log("Resultant Payload:", orgAllowedForm);
        return;
      } else {
        const neworgallowedvalue = {
          MappingType: "parttypetotemplate",
          BUID: RDO,
          details: {
            ENGG_ITEM_TYPE: orgAllowedForm.ENGG_ITEM_TYPE,
            ENOVIA_PART_TYPE_ID: templateid[0]?.id,
            allowableOrgTemplates: templatedata,
          },

          id:
            orgAllowedForm.ENGG_ITEM_TYPE +
            "_" +
            RDO.trim() +
            "_" +
            "parttypetotemplate",
        };
        if (isEdit === false) {
          mvotempcompservice
            .addnewTempCompMapping(neworgallowedvalue)
            .then((resp) => {
              if (resp?.status === 'success') {
                fetchData();
                setSelected({})
                setTimeout(() => {
                  setOrgallowedaddDialog(false);
                }, 200);
                handlesnackBarClick("Parttype to Template added successfully", "success");
              } else if (resp?.status === 'error') {
                handlesnackBarClick(resp?.msg, "error");
              }
            });
        } else {
          mvotempcompservice
            .updateTempCompMapping(neworgallowedvalue)
            .then((resp) => {
              if (resp.status === "success") {
                fetchDraftData();
                fetchData();
                setSelected({})
                setTimeout(() => {
                  setOrgallowedaddDialog(false);
                }, 200);
                setIsEdit(false);
                handlesnackBarClick(
                  "MVO template compatibility updated successfully",
                  "success"
                );
              } else if (resp?.status === "error") {
                handlesnackBarClick(resp?.msg, "error");
              }
            });
        }
        handleorgAllowedDialogClose();
      }
    } else {
      let orgtemperror = {}
      if (Object.keys(formTemplateSelection).length === 0) {
        orgtemperror = { "AllowableOrgTempError": "Please select at least one template" }
      }
      setFormError({
        ...orgtemperror,
        "templateError": "Template Name is required"
      })
    }
  };

  const handleorgAllowedDialogOpen = () => {
    setOrgAllowedForm("");
    setSelectedPlants([]);
    setNewKey("");
    setNewValue("");
    setKeyerror("");
    setValuesInputList([]);
    setOrgallowedaddDialog(true);
    setRDO(bussinessUnit);
  };
  const handleorgAllowedDialogClose = () => {
    setOrgallowedaddDialog(false);
    setOrgAllowedForm("");
    setSelectedPlants([]);
    setSelectedRows([]);
    setSelected({})
    setFormTemplateSelection({});
    setFormError({})
    setIsEdit(false);
  };

  const handleorgAllowedDialogAdd = () => {
    setOrgallowedaddDialog(false);
  };
  const handleEditPartTypeTotemplate = () => {
    const selectedDetails = selectedRows[0]?.details;
    const BUID = selectedRows[0]?.BUID;
    setOrgAllowedForm({
      ...selectedDetails,
      BUID,
    });
    setRDO(BUID)
    const selectedData = selectedRows[0]?.details?.allowableOrgTemplates.map((fitem) => {
      const filterTableData = orgAllowedData?.filter(
        (item) => item.details.ALLOWABLE_ORG_TEMPLATES === fitem.org_allowed_template
      );
      return filterTableData;
    }).flat();
    let object = selectedData.reduce(
      (obj, item) => Object.assign(obj, { [item.id]: item }), {});
    setFormTemplateSelection(object);
    setValuesInputList(selectedRows[0]?.details?.allowableOrgTemplates);
    setIsEdit(true);
    setOrgallowedaddDialog(true);
  };

  const handleDeleteDialog = () => {
    setSelectedDeleteMapping(selectedRows[0]);
    setDeleteDialog(true);
  };
  const handleDeletePartTypeToTemplate = async () => {
    try {
      await mvotempcompservice.softDeleteTempCompMapping({ data: selectedRows }).then(result => {
        handleCloseDelete(false)
        setSelectedRows([])
        setSelectedDeleteMapping([])
        setSelected({})
      });
    } catch (error) {
      console.error("Failed to delete mapping:", error);
    }

    handlesnackBarClick("Parttype to Template deleted Successfully", "success");
    fetchData();
    setSelectedRows([]);
  };

  const fetchData = () => {
    setIsLoading(true)
    tempMapService.getAlltemplateMappings().then((result) => {
      updateMappingTemplatesInfo(result?.data?.templateMappingList);
      setIsLoading(false);
    });
  }
  useEffect(() => {
    setIsLoading(refreshData);
  }, [refreshData]);
  const [tableTempData, setTableTempData] = useState([])
  const [unSortTableTempData, setUnSortTableTempData] = useState([])
  const sortedFilterTempData = sortDataByKey(tableTempData, "ENGG_ITEM_TYPE");

  useEffect(() => {
    fetchDraftData();
  }, []);

  const fetchDraftData = () => {
    setIsLoading(true);
    draftMapService.getAlldraftChanges().then((result) => {
      let BUID = bussinessUnit ? bussinessUnit : 'Micro Motion';
      let filterData = result.data.filter(element => element.BUID === BUID);
      setFilteredDraftTabledata(filterData);
      setIsLoading(false);
    });
  }

  useEffect(() => {
    setIsLoading(true)
    if (mappingTemplatesInfo) {
      let finalTempMapList = mappingTemplatesInfo;
      finalTempMapList = finalTempMapList.filter(
        (item) => item.MappingType === "parttypetotemplate" && item.actionFlag !== "Delete"
      );

      if (bussinessUnit) {
        finalTempMapList = finalTempMapList?.filter((fitem) => {
          return fitem.BUID === bussinessUnit;
        });
      }
      if (searchValue && searchValue !== "") {
        finalTempMapList = finalTempMapList?.filter((fitem) => {
          return (
            JSON.stringify(fitem)?.includes(searchValue) ||
            JSON.stringify(fitem)?.toLocaleLowerCase()?.includes(searchValue)
          );
        });
      }

      if (filteredDraftTabledata.length > 0) {
        const filteredMVOTempDraftData = filteredDraftTabledata.filter((i) => { return i.MappingType == "parttypetotemplate" });
        if (filteredMVOTempDraftData.length > 0 && filteredMVOTempDraftData[0].MappingType === 'parttypetotemplate') {
          finalTempMapList.forEach(obj1 => {
            filteredMVOTempDraftData[0].changes.forEach(obj2 => {
              if (obj1['id'] === obj2['id']) {
                obj1.details.draft_allowableOrgTemplates = obj2.details.allowableOrgTemplates;
                const templateSet = new Set(obj2.details.allowableOrgTemplates.map(i => i.org_allowed_template));
                const missingFromArrObj1 = obj1.details.allowableOrgTemplates.filter(j => !templateSet.has(j.org_allowed_template));
                obj1.details.deletedTemplateLists = missingFromArrObj1;
                let newTemplate = obj1.details.draft_allowableOrgTemplates.filter(item1 => 
                    !obj1.details.allowableOrgTemplates.some(item2 => 
                        item1.org_allowed_template === item2.org_allowed_template && 
                        item1.exclusive_use === item2.exclusive_use
                    )
                );
                obj1.details.newTemplateLists = newTemplate;
                let mergeNewOldTemplates = [...obj1.details.allowableOrgTemplates,  ...obj1.details.newTemplateLists]
                obj1.details.allowableOrgTemplates = mergeNewOldTemplates;
              }
            });
          });
        }
      }
      let data = finalTempMapList.map(item => {
        item["ENGG_ITEM_TYPE"] = item?.details?.ENGG_ITEM_TYPE
        item['org_allowed_template'] = item?.details?.allowableOrgTemplates
          .filter((plant, index, self) =>
            index === self.findIndex(p => p.org_allowed_template === plant.org_allowed_template)
          )
          .map(plant => {
            const deletedItems = item?.details?.deletedTemplateLists || [];

            const deletedText = deletedItems
              .map(draft_Plant => (
                plant.org_allowed_template === draft_Plant.org_allowed_template
                  ? `<span style="color:#f00; font-weight:bold;text-decoration: line-through">${draft_Plant.org_allowed_template}</span>`
                  : ''
              ))
              .join(' '); // Join deleted items without any separator

            return `<div>${plant.org_allowed_template} - ${plant.exclusive_use.charAt(0).toUpperCase() + plant.exclusive_use.slice(1)} &nbsp;&nbsp; ${deletedText}</div>`;
          })
          .join(''); // Join all plant strings without a comma or any separator
        return item
      })
      setTableTempData(data)
      setUnSortTableTempData(finalTempMapList)
      let buFilterData = mappingTemplatesInfo.filter((item) => {
        return item.MappingType === "orgallowedvalues" && item.actionFlag !== "Delete"
      });
      if (bussinessUnit) {
        buFilterData = buFilterData?.filter((fitem) => {
          return fitem.BUID === bussinessUnit;
        });
      }
      setTimeout(() => {
        setOrgAllowedData(buFilterData)
        setIsLoading(false)
      }, 500)
    } else {
      if (!bussinessUnit) {
        updateBussinessUnit(BUOptions ? BUOptions[0]?.value : "");
      }
    }
  }, [mappingTemplatesInfo, bussinessUnit, searchValue, filteredDraftTabledata]);

  // *********org allowed values dialog methods ends**********

  // *******react data grid table code *******//
  const gridStyle = { minHeight: 550 };
  const [selectedRows, setSelectedRows] = useState([]);
  const columns = [

    {
      name: "ENGG_ITEM_TYPE",
      header: <Tooltip title={Constant.adminConstant.parttypetemplateList.tooltip.TemplateOptionsMVO}>Enovia Part Type</Tooltip>,
      defaultFlex: 1,
      render: ({ data }) => <div>
        {data?.ENGG_ITEM_TYPE}
      </div>,
    },
    {
      name: "org_allowed_template",
      header: <Tooltip title={Constant.adminConstant.parttypetemplateList.tooltip.allowableOrgTemplatesExclusive}>Org Allowed Templates - Exclusive use</Tooltip>,
      defaultFlex: 1,
      sortable: false,
      // render: ({ data }) =>
      //   data?.details?.allowableOrgTemplates.map((template, index) => (
      //     <div key={index}>
      //       {template.org_allowed_template} - {template.exclusive_use}
      //     </div>
      //   )),
      render: (params) => (
        <div dangerouslySetInnerHTML={{ __html: params.value }} />
      ),
    },
    {
      name: "BUID",
      header: <Tooltip title={Constant.adminConstant.parttypetemplateList.tooltip.RDO}>Design Organization</Tooltip>,
      defaultFlex: 1,

      defaultWidth: 50,
    },
  ];
  const onSelectionChange = useCallback(({ selected }) => {
    setSelected(selected);
  }, []);

  useEffect(() => {
    const selRowsData = selected === true ? dataMap : selected;
    const selRowsDetails = (selRowsData) && Object.keys(selRowsData).map(key => (selRowsData[key]));
    setSelectedRows(selRowsDetails);
  }, [selected, dataMap]);

  const onFormSelectionChange = useCallback(({ selected }) => {
    setFormTemplateSelection(selected);
    setFormError(prevState => {
      const { AllowableOrgTempError, ...rest } = prevState;
      return rest
    });
  }, []);
  const handleStatusChange = (event) => {
    const updatedStatus = {
      ...statusObject,
      [event.target.name]: event.target.value,
    };
    setStatusObject(updatedStatus);
  }
  const rowStyle = ({ data }) => {
    const isSelected = Object.keys(selected).includes(data.id);
    return {
      backgroundColor: data.actionFlag !== undefined ? 'rgb(25 118 210 / 20%)' : '',
      zIndex: 99,
      background: (isSelected || selected === true) ? '#dee1f2' : 'initial',
      height: "auto !important",
      whiteSpace: "normal"
    }
  }
  const handleSortInfoChange = (sortInfo) => {
    setSortInfo(sortInfo);
  };

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Part Type to Template" }]} />
      </Box>
      <SimpleCard>
        <Grid container spacing={1} alignItems="top">
          {/* Search Input and Search Button */}
          <Grid item md={4} sm={6} xs={12} className="font-medium">
            <Grid container spacing={1} alignItems="center">
              <Grid item md={6} sm={6} xs={12} className="font-medium">
                <TextField
                  type="text"
                  name="search"
                  size="small"
                  onChange={handleSearchInputChange}
                  value={searchValue}
                  style={{ width: "100%" }}
                />
              </Grid>
              <Grid item md={6} sm={6} xs={12} className="font-medium">
                <Button variant="contained" onClick={handleSearch}>
                  Search
                </Button>
              </Grid>
            </Grid>
          </Grid>

          {/* Design Organization Text and Dropdown */}
          <Grid item md={8} sm={6} xs={12} className="font-medium">
            <Grid container alignItems="center" justify="flex-end" style={{ display: "none" }}>
              <Grid item xs={12} sm={6} md={6} className="font-medium" style={{ textAlign: 'right' }}>
                <label style={{ whiteSpace: "nowrap", paddingTop: "11px" }}>Design Organization:</label>
              </Grid>
              <Grid item xs={12} sm={6} md={6} className="font-medium" style={{ textAlign: 'left' }}>
                <CustomSelect
                  onChange={handleSearchInputChange}
                  selectedvalue={bussinessUnit}
                  style={{ width: "100%" }}
                  optionsValues={BUOptions}
                  // defaultValue={
                  //   BUOptions && !bussinessUnit ? BUOptions[0]?.value : ""
                  // }
                  onChangeFunc={(selectedOptionValue) => {
                    setSelected({});
                    setSelectedRows([]);

                    updateBussinessUnit(selectedOptionValue);
                    setSearchValue("");
                    setSelectedRows([]);
                  }}
                />
              </Grid>
            </Grid>

            {/* Right-aligned Buttons */}
            <Grid item xs={12} className="font-medium" style={{ marginTop: '0px', textAlign: 'right' }}>
              <Button
                variant="contained"
                component={RouterLink}
                onClick={handleorgAllowedDialogOpen}
                style={{ margin: '0 0px 0 10px' }}
              >
                Add
              </Button>
              <Button
                variant="contained"
                component={RouterLink}
                disabled={selectedRows?.length === 1 ? false : true}
                onClick={handleEditPartTypeTotemplate}
                style={{ margin: '0 0px 0 10px' }}
              >
                Edit
              </Button>
              <Button
                variant="contained"
                component={RouterLink}
                disabled={selectedRows?.length !== 0 ? false : true}
                onClick={handleDeleteDialog}
                //onClick={handleDeletePartTypeToTemplate}
                style={{ margin: '0 0px 0 10px' }}
              >
                Delete
              </Button>
              <Button
                variant="contained"
                onClick={() => {
                  setImportTemplate(true)
                }}
                style={{ margin: '0 10px 0 10px' }}
              >
                Import
              </Button>
              {/* export btn */}
              <ExportMappingDialog
                title={"Part Type To Template"}
                MappingType={"parttypetotemplate"}
                filterTemplateData={selectedRows}
                sortInfo={sortInfo} // Pass sortInfo to ExportMappingDialog
              />
            </Grid>
          </Grid>

          {/* DataGrid */}
          <Grid item xs={12} style={{ marginTop: '10px' }}>
            <Box width="100%" overflow="auto" marginTop="10px">
              {isLoading && (
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    height: "100vh",
                  }}
                >
                  <CircularProgress />
                </Box>
              )}
              {!isLoading && (
                <DataGrid
                  idProperty="id"
                  showZebraRows={false}
                  style={gridStyle}
                  rowStyle={rowStyle}
                  columns={columns}
                  dataSource={sortInfo === null ? unSortTableTempData : sortedFilterTempData}
                  rowHeight={"auto"}
                  defaultLimit={100}
                  checkboxColumn
                  selected={selected}
                  onSelectionChange={onSelectionChange}
                  handle={ref => gridRef.current = ref ? ref.current : null}
                  pagination
                  renderScroller={()=> gridRef?.current?.setScrollTop(selectedRows?.length==0 ? 0 : gridRef?.current?.getScrollTop())}
                    onSortInfoChange={handleSortInfoChange}
                />
              )}
            </Box>
          </Grid>
        </Grid>
      </SimpleCard>
      {
        (openDesc) ? (
          <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
        ) : (<></>)
      }
      <Dialog open={deleteDialog}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Delete Mapping
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            {'Do you want to delete the PartTypeToTemplate Mapping(s) for Design Organization'} <b>{selectedDeleteMapping?.BUID}</b>
            <br />
            <br />
            <b>Note:</b> {Constant.adminConstant.common.deleteNote}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDelete}>Cancel</Button>
          <Button onClick={handleDeletePartTypeToTemplate}>delete</Button>
        </DialogActions>
      </Dialog>
      <PartTypeTemplateDialog
        open={orgallowedaddDialog}
        handleorgAllowedDialogAdd={handleorgAllowedDialogAdd}
        handleorgAllowedDialogClose={handleorgAllowedDialogClose}
        orgAllowedForm={orgAllowedForm}
        handleorgAllowedFormChange={handleorgAllowedFormChange}
        handleAddMVOtempComp={handleAddMVOtempComp}
        selectedPlants={selectedPlants}
        isEdit={isEdit}
        newKey={newKey}
        newValue={newValue}
        handleAddRow={handleAddRow}
        handleDeleteRow={handleDeleteRow}
        keyerror={keyerror}
        valuesInputList={valuesInputList}
        onSelectionChange={onFormSelectionChange}
        selected={formTemplateSelection}
        orgAllowedTempList={orgAllowedData}
        filteredBUOptions={filteredBUOptions}
        setFilteredBUOptions={setFilteredBUOptions}
        formError={formError}
        RDO={RDO}
        selectedRow={selectedRows[0]}
        handleStatusChange={handleStatusChange}
      />

      <ImportMappingsDialog
        title={"Import Parttype To Template Mappings"}
        MappingType={"parttypetotemplate"}
        selectedBU={bussinessUnit}
        importTemplate={importTemplate}
        fetchData={fetchData}
        setImportTemplate={setImportTemplate} />
    </Container >
  );
};

export default PartTypeToTemplateList;
