import { MCOService } from '../../../services/mcoService';

export const mcoService = new MCOService();

export const GET_MCOLIST = 'GET_MCOLIST';
export const GET_MCOINFO = 'GET_MCOINFO';
export const GET_UPDATEMCODETAILS = 'GET_UPDATEMCODETAILS';
export const GET_UPDATE_MCO_RELATED_VALUES = 'GET_UPDATE_MCO_RELATED_VALUES';
export const SET_ALL_CA_MCO_DETAILS = 'SET_ALL_CA_MCO_DETAILS';
export const SET_UPDATED_ROUTES = "SET_UPDATED_ROUTES";

export const RESET_VALUES = 'RESET_VALUES';

export const setUpdatedRoutes = (routes) => ({
  type: SET_UPDATED_ROUTES,
  payload: routes,
});

export const getMCOLists = (data) => async (dispatch) => {
  try {
    // await mcoService.searchMCO(search).then((result) => {
    await dispatch({
      type: GET_MCOLIST,
      payload: data,
    })
    // })
  } catch (e) {
    console.error(e)
  }
};
export const updateResponseData = (data) => async (dispatch) => {
  try {
    await dispatch({
      type: GET_MCOINFO,
      payload: data,
    })
  } catch (e) {
    console.error(e)
  }
};
export const updateMcoDetails = (data) => async (dispatch) => {
  try {
    await dispatch({
      type: GET_UPDATEMCODETAILS,
      payload: data,
    })
  } catch (e) {
    console.error(e)
  }
};
export const update_generalValues = (data) => async (dispatch) => {
  try {
    await dispatch({
      type: GET_UPDATE_MCO_RELATED_VALUES,
      payload: data,
    })
  } catch (e) {
    console.error(e)
  }
};
export const resetValues = () => async (dispatch) => {
  try {
    await dispatch({
      type: RESET_VALUES,
    })
  } catch (e) {
    console.error(e)
  }
};

export const setAllCaMCODetails = (data) => async (dispatch) => {
  try {
    await dispatch({
      type: SET_ALL_CA_MCO_DETAILS,
      payload: data,
    });
  } catch (e) {
    console.error(e);
  }
};
