import React, { useEffect, useState } from "react";
import { useNavigate, useLocation, useParams } from "react-router-dom";
import { Breadcrumb, SimpleCard } from "app/components";
import { styled as makeStyles } from '@mui/material/styles';
import { Formik, Form } from "formik";
import EditIcon from "@mui/icons-material/Edit";
import CustomAlertBox from "app/components/CustomAlertBox";
import Button from "@mui/material/Button";
import {
  instanceOptions,
  ERPtypeOptions,
  bomusageOptions,
  BUOptions,
  exportToERPOptions,
} from "../adminConfig/common";
import { PlantService } from "services/plantService";
import ConnectivityDetailsDialog from "./connectivityDetailsDialog";
import { Grid, TextField, Select, MenuItem, Box, styled } from "@mui/material";
import Loader from 'app/components/Loader';
import useOracleSettings from "app/hooks/useOracleSettings";
const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));
const useStyles = makeStyles({
  spanRed: {
    color: "#FF3D57",
  },
});
export default function EditPlant() {
  const { refreshData } = useOracleSettings();
  const plantService = new PlantService();
  const [allPlantData, setallPlantData] = useState();
  const [diaBox, setDiabox] = useState(false);
  const classes = useStyles();
  const initialValues = {
    id: "",
    plants: "",
    ERPType: "",
    instance: "",
    Connectivitydetails: {},
  };
  const [isLoading, setIsLoading] = useState(true);

  const [connectivityError, setconnectivityError] = useState();
  const location = useLocation();
  const navigate = useNavigate();
  const params = useParams();

  // const selectedPlant = location.state?.selectedPlantData;
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [plantForm, setPlantForm] = useState({});
  const [connDetails, setConnDetails] = useState({});
  const [selectedBU, setSelectedBU] = useState([]);
  const [selectedPlant, setSelectedPlant] = useState({});

  const materialMasterOption = [
    {
      value: "Y",
      label: "Yes",
    },
    {
      value: "N",
      label: "No",
    },
  ];

  useEffect(() => {
    plantService.getAllPlantsList().then((result) => {
      setallPlantData(result.plant);
    });
  }, []);

  //plantform methods

  const handleAddPlant = async () => {
    setIsLoading(true);
    const newPlant = {
      id: plantForm.id,
      plants: plantForm.plants,
      plantName: plantForm.plantName,
      ERPType: plantForm.ERPType,
      SalesOrg: plantForm.SalesOrg,
      aliasPlant: plantForm.aliasPlant,
      material_master: plantForm.material_master,
      ECN_required: plantForm.ECN_required,
      bomusage: plantForm.bomusage,
      ConnectivityDetails: connDetails,
      // BU: plantForm.ERPType === "SAP" ? "" : selectedBU,
      // exportToERP: plantForm.ERPType === "SAP" ? "" : plantForm.exportToERP,
      exportToERP: plantForm.exportToERP,
    };

    if (plantForm?.instance) {
      newPlant["instance"] = plantForm?.instance;
    }
    if (
      plantForm.plantName?.trim() === "" || !plantForm.plantName ||
      plantForm.ERPType?.trim() === "" || !plantForm.ERPType ||
      plantForm.instance?.trim() === "" || !plantForm.instance ||
      plantForm.exportToERP?.trim() === "" || !plantForm.exportToERP ||
      (plantForm.ERPType === "SAP" && (plantForm.plantName?.trim() === "" || !plantForm.plantName)) ||
      (plantForm.ERPType === "SAP" && (plantForm.material_master?.trim() === "" || !plantForm.material_master)) ||
      (plantForm.ERPType === "SAP" && (plantForm.bomusage?.trim() === "" || !plantForm.bomusage)) ||
      (plantForm.ERPType === "SAP" && !plantForm.instance)
    ) {
      handlesnackBarClick("Please fill required details ", "error");
      setIsLoading(false);
    } else {
      setIsLoading(true);
      await plantService.updatePlant(newPlant).then((resp) => {
        if (resp?.data?.id) {
          handlesnackBarClick("Plant updated successfully", "success");
          setTimeout(() => {
            setIsLoading(false);
            navigate("/plants");
          }, 2000);
        } else if (resp.msg) {
          setIsLoading(false);
          // console.log(resp.msg, "res.msg");
        }
      });
    }
  };

  const handleCDChange = (e) => {
    setConnDetails({
      ...connDetails,
      [e.target.name]: e.target.value,
    });
  };

  const handleAddCD = () => {
    setDiabox(false);
  };

  const handlePlantFromChange = (event) => {
    const { name, value } = event.target;
    const updatedForm = {
      ...plantForm,
      [name]: value,
    };
    setPlantForm(updatedForm);
  };

  const handleBUChange = (event) => {
    setSelectedBU(event.target.value);
  };
  const handleDialogClose = () => {
    setDiabox(false);
    setconnectivityError("");
  };

  // snackbar code
  const handlesnackBarClick = (message, severity) => {
    setOpenAlert(true);
    setOpenDesc({
      severity: severity,
      desc: message,
    });
  };
  function hasValues(obj) {
    for (let key in obj) {
      if (obj.hasOwnProperty(key) && obj[key] !== "" && obj[key] !== undefined) {
        return true;
      }
    }
    return false;
  }
  const handleCancel = () => {
    setTimeout(() => {
      navigate(-1)
    }, 200);
  }

  useEffect(() => {
    if (params?.plantId) {
      setIsLoading(true);
      plantService.getPlantById({ id: params?.plantId }).then((resp) => {
        setIsLoading(false);
        if (resp?.data && Object?.keys(resp?.data)?.length) {
          const selectedPlantObj = resp?.data;
          setSelectedPlant(
            selectedPlantObj
          );
          setSelectedBU(
            selectedPlantObj?.BU
          );
          setPlantForm({
            id: selectedPlantObj?.id,
            plants: selectedPlantObj?.plants,
            plantName: selectedPlantObj?.plantName,
            SalesOrg: selectedPlantObj?.SalesOrg,
            aliasPlant: selectedPlantObj?.aliasPlant,
            ERPType: selectedPlantObj?.ERPType,
            instance: selectedPlantObj?.instance,
            material_master: selectedPlantObj?.material_master,
            ECN_required: selectedPlantObj?.ECN_required,
            bomusage: selectedPlantObj?.bomusage,
            exportToERP: selectedPlantObj?.exportToERP,

            Connectivitydetails: {
              url: selectedPlantObj?.ConnectivityDetails?.url,
              username: selectedPlantObj?.ConnectivityDetails?.username,
              password: selectedPlantObj?.ConnectivityDetails?.password,
              authkey: selectedPlantObj?.ConnectivityDetails?.authkey,
              port: selectedPlantObj?.ConnectivityDetails?.port,
            },
          });
          setConnDetails({
            url: selectedPlantObj?.ConnectivityDetails?.url,
            username: selectedPlantObj?.ConnectivityDetails?.username,
            password: selectedPlantObj?.ConnectivityDetails?.password,
            authkey: selectedPlantObj?.ConnectivityDetails?.authkey,
            port: selectedPlantObj?.ConnectivityDetails?.port,
            sapsysnum: selectedPlantObj?.ConnectivityDetails?.sapsysnum,
            sapclientid: selectedPlantObj?.ConnectivityDetails?.sapclientid,
          });
        } else {
          navigate("/plants", { state: { message: "Plant not found!", status: "error" } });
        }
      });
    } else {
      setTimeout(() => {
        navigate("/plants", { state: { message: "Plant not found!", status: "error" } });
      }, 200);
    }
  }, []);

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Edit Plant" }]} />
      </Box>
      <Formik
        initialValues={initialValues}
        onSubmit={() => {
        }}
      >
        <Form>
          <SimpleCard>
            <Grid item md={9} sm={6} xs={6}>
              <h4>Edit Plant</h4>

              <Grid container spacing={3} alignItems="center">
                <Grid item md={3} sm={4} xs={12} className="font-medium">
                  <span>
                    Plant Name <span className={classes.spanRed}>*</span>
                  </span>
                </Grid>

                <Grid item md={9} sm={8} xs={12}>
                  <TextField
                    disabled
                    name="plantName"
                    value={plantForm.plantName}
                    onChange={handlePlantFromChange}
                    fullWidth
                    margin="normal"
                  />
                </Grid>

                <Grid item md={3} sm={4} xs={12} className="font-medium">
                  <span>
                    ERP Type <span className={classes.spanRed}>*</span>
                  </span>
                  {/* <span className={classes.spanRed}>*</span> */}
                </Grid>
                <Grid item md={9} sm={8} xs={12}>
                  <Select
                    id="ERPType"
                    name="ERPType"
                    label="ERPType"
                    fullWidth
                    disabled
                    displayEmpty
                    value={[plantForm?.ERPType]}
                    onChange={handlePlantFromChange}
                  >
                    {ERPtypeOptions.map((option) => (
                      <MenuItem key={option.value} value={option.value}>
                        {option.label}
                      </MenuItem>
                    ))}
                  </Select>
                </Grid>

                {(plantForm.ERPType === "SAP" ||
                  plantForm.ERPType === "ORACLE") && (
                    <>
                      <Grid item md={3} sm={4} xs={12} className="font-medium">
                        <span>
                          Instance <span className={classes.spanRed}>*</span>
                        </span>
                      </Grid>

                      <Grid item md={9} sm={8} xs={12}>
                        <Select
                          label="instance"
                          id="instance"
                          name="instance"
                          disabled
                          fullWidth
                          displayEmpty
                          value={plantForm.instance}
                          onChange={handlePlantFromChange}
                        >
                          {instanceOptions.map((option) => (
                            <MenuItem key={option.value} value={option.value}>
                              {option.label}
                            </MenuItem>
                          ))}
                        </Select>
                      </Grid>
                    </>
                  )}

                {["ORACLE", "SAP"]?.includes(plantForm.ERPType) && (
                  <>
                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>Export To ERP</span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <Select
                        id="exportToERP"
                        name="exportToERP"
                        fullWidth
                        displayEmpty
                        value={plantForm.exportToERP}
                        onChange={handlePlantFromChange}
                      >
                        {exportToERPOptions.map((option) => (
                          <MenuItem key={option.value} value={option.value}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </Grid>
                  </>
                )}

                {/* {plantForm.ERPType === "ORACLE" && (
                  <>
                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>
                        Design Organization
                      </span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <Select
                        style={{ width: "100%" }}
                        multiple
                        value={selectedBU}
                        onChange={handleBUChange}
                        renderValue={(selected) => selected.join(", ")}
                        MenuProps={{
                          sx: {
                            "&& .Mui-selected": {
                              backgroundColor: "rgb(25 118 210 / 67%)"
                            }
                          }
                        }}
                      >
                        {BUOptions.map((option) => (
                          <MenuItem key={option.value} value={option.value}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </Grid>
                  </>
                )} */}

                {plantForm.ERPType === "SAP" && (
                  <>
                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>
                        Default Sales Org
                      </span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <TextField
                        label="SalesOrg"
                        name="SalesOrg"
                        defaultValue={plantForm.SalesOrg}
                        onChange={handlePlantFromChange}
                        fullWidth
                        margin="normal"
                      />
                    </Grid>
                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>
                        Material Master
                      </span>
                      {/* <span className={classes.spanRed}>*</span> */}
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <Select
                        id="material_master"
                        name="material_master"
                        label="material_master"
                        fullWidth
                        displayEmpty
                        value={plantForm.material_master}
                        onChange={handlePlantFromChange}
                      >
                        {materialMasterOption.map((option) => (
                          <MenuItem key={option.value} value={option.value}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </Grid>

                    {plantForm.material_master === "Y" && (
                      <>
                        <Grid item md={3} sm={4} xs={12} className="font-medium">
                          <span>
                            Plantname Alias
                          </span>
                        </Grid>

                        <Grid item md={9} sm={8} xs={12}>
                          <TextField
                            label="aliasPlant"
                            name="aliasPlant"
                            value={plantForm.aliasPlant}
                            onChange={handlePlantFromChange}
                            fullWidth
                            margin="normal"
                          />
                        </Grid>
                      </>
                    )}

                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>
                        ECN Required
                      </span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <Select
                        id="ECN_required"
                        name="ECN_required"
                        label="ECN_required"
                        fullWidth
                        displayEmpty
                        value={plantForm.ECN_required}
                        onChange={handlePlantFromChange}
                      >
                        {materialMasterOption.map((option) => (
                          <MenuItem key={option.value} value={option.value}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </Grid>

                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>
                        BOM Usage
                      </span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <Select
                        id="bomusage"
                        name="bomusage"
                        fullWidth
                        displayEmpty
                        value={plantForm.bomusage}
                        onChange={handlePlantFromChange}
                      >
                        {bomusageOptions.map((option) => (
                          <MenuItem key={option.value} value={option.value}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </Grid>
                    {hasValues(connDetails) && (<>

                      <Grid item md={3} sm={4} xs={12} className="font-medium">
                        <span>Connectivity Details </span>
                      </Grid>

                      <Grid item md={9} sm={8} xs={12}>
                        <span>
                          {Object.keys(connDetails).length === 0 ||
                            (selectedPlant?.ConnectivityDetails === null && (
                              <Button
                                variant="contained"
                                color="primary"
                                margin="2px"
                                onClick={() => {
                                  setDiabox(true);
                                }}
                              >
                                Add Connectivity Details
                              </Button>
                            ))}
                          {(Object.keys(connDetails).length > 0 ||
                            selectedPlant?.ConnectivityDetails !== null) && (
                              <Grid item md={12} sm={8} xs={12}>
                                <>
                                  <div style={{ display: "flex" }}>
                                    <p>
                                      <b>URL :</b> {connDetails.url}
                                    </p>{" "}
                                    &nbsp;
                                    <p>
                                      <b>Username :</b> {connDetails.username}
                                    </p>{" "}
                                    &nbsp;
                                    <p>
                                      <b>Pass :</b> {connDetails.password}
                                    </p>{" "}
                                    &nbsp;
                                    <p>
                                      <b>authkey :</b> {connDetails.authkey}
                                    </p>{" "}
                                    &nbsp;
                                    <p>
                                      <b>Port :</b> {connDetails.port}
                                    </p>{" "}
                                    &nbsp;
                                    {plantForm.ERPType === "SAP" && (
                                      <>
                                        <p>
                                          <b>sapsysnum :</b> {connDetails.sapsysnum}
                                        </p>{" "}
                                        &nbsp;
                                        <p>
                                          <b>sapclientid :</b> {connDetails.sapclientid}
                                        </p>{" "}
                                      </>
                                    )}
                                    <p>
                                      {" "}
                                      <EditIcon
                                        onClick={() => {
                                          setDiabox(true);
                                        }}
                                      />
                                    </p>
                                  </div>
                                </>
                              </Grid>
                            )}
                        </span>
                      </Grid>
                    </>)}
                  </>
                )}
                <Grid item md={3} sm={4} xs={12} className="font-medium">
                  <span>
                    <Button
                      type="submit"
                      variant="contained"
                      color="primary"
                      margin="2px"
                      onClick={handleAddPlant}
                    >
                      Update
                    </Button>
                  </span>
                </Grid>
                <Grid item md={3} sm={4} xs={12} className="font-medium">
                  <span>
                    <Button
                      variant="contained"
                      color="primary"
                      margin="2px"
                      onClick={handleCancel}
                    >
                      Cancel
                    </Button>
                  </span>
                </Grid>
              </Grid>
            </Grid>
          </SimpleCard>
        </Form>
      </Formik>
      {(openDesc) ? (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      ) : (<></>)}

      {
        (refreshData || isLoading) ? (
          <div style={{ textAlign: 'center' }}>
            <Loader show={true} />
          </div>
        ) : <></>
      }

      {/* dialogBox */}
      <ConnectivityDetailsDialog
        open={diaBox}
        onClose={handleDialogClose}
        connDetails={connDetails}
        handleDialogClose={handleDialogClose}
        handleCDChange={handleCDChange}
        handleAddCD={handleAddCD}
        connectivityError={connectivityError}
        plantForm={plantForm}
        classes={classes}
      />
    </Container>
  );
}
