import { BACKEND_API_URL, REACT_APP_BACKEND_API_HEADER } from "../config";
import ApiInstance from './ApiInstance';

export class SystemSettingsService {
    getAllsystemSetupList = () => {
        return ApiInstance
            .get(
                `${BACKEND_API_URL}/systemSettings/getSystemSettings`,
                REACT_APP_BACKEND_API_HEADER
            )
            .then((res) => {
                if (res.status === 200) {
                    return res.data;
                }
                throw res.data;
            })
            .catch((e) => {
                console.error("getSystemSettings", e);
                return [];
            });
    };
    getSystemSettingsbyid = (data) => {
        return ApiInstance
            .post(
                `${BACKEND_API_URL}/systemSettings/getSystemSettingsbyid`,
                data,
                REACT_APP_BACKEND_API_HEADER
            )
            .then((res) => {
                if (res.status === 200) {
                    return res.data;
                }
                throw res.data;
            })
            .catch((e) => {
                console.error("getSystemSettings", e);
                return [];
            });
    };

    addSystemSettings = (data) => {
        return ApiInstance
            .post(
                `${BACKEND_API_URL}/systemSettings/addSystemSettings`,
                data,
                REACT_APP_BACKEND_API_HEADER
            )
            .then((res) => {
                if (res.status === 200) {
                    return res.data;
                }
                throw res.data;
            })
            .catch((e) => {
                console.error("addnewSystemSettings", e);
                return [];
            });
    };

    updateSystemSettings = (data) => {
        return ApiInstance
            .put(
                `${BACKEND_API_URL}/systemSettings/updateSystemSettings`,
                data,
                REACT_APP_BACKEND_API_HEADER
            )
            .then((res) => {
                if (res.status === 200) {
                    return res.data;
                }
                throw res.data;
            })
            .catch((e) => {
                console.error("updateSystemSettings", e);
                return [];
            });
    };

    deleteSystemSettings = (ids) => {
        return ApiInstance
            .delete(
                `${BACKEND_API_URL}/systemSettings/deleteSystemSettings`,
                { data: ids },
                REACT_APP_BACKEND_API_HEADER
            )
            .then((res) => {
                if (res.status === 200) {
                    return res.data;
                }
                throw res.data;
            })
            .catch((error) => {
                console.error("Error deleting SystemSettings:", error);
                throw error;
            });
    };
}
