import React, { useEffect, useState } from "react";

import Button from "@mui/material/Button";

import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import { BUService } from "services/buService";
import useOracleSettings from "app/hooks/useOracleSettings";
//import { MassUpdate } from "services/massUpdateService";
import { TemplateMappingService } from "services/templateMappingService";

import {
    Table,
    TableBody,
    TableCell,
    TableRow,
    Box,
    styled,
    InputLabel,
    Select,
    MenuItem,
    FormControl,
    Radio,
    RadioGroup,
    FormControlLabel
} from "@mui/material";

//import MuiAlert from "@mui/material/Alert";
import CustomAlertBox from "./CustomAlertBox";

const StyledTable = styled(Table)(() => ({
    whiteSpace: "pre",
    "& thead": {
        "& tr": { "& th": { paddingLeft: 0, paddingRight: 0 } },
    },
    "& tbody": {
        "& tr": { "& td": { paddingLeft: 0, textTransform: "capitalize" } },
    },
}));

const ExportAllTemplateMapping = (props) => {
    const buService = new BUService();
    const templateMappingService = new TemplateMappingService();
    const { exportAllTempMapping, setExportAllTempMapping } = props;
    // const [noFileError, setNoFileError] = useState(false);
    // const [importResponse, setImportResponse] = useState(false);
    // const [importfileBar, setimportfileBar] = useState(false);
    // const [importedFileData, setImportedFileData] = useState(null)
    const [importedFileName, setImportedFileName] = useState(null);
    // const [sampleFileUrl, setSampleFileUrl] = useState(null);
    //const [bussinessUnitLists, setBussinessUnitLists] = useState([]);
    //const instance = localStorage.getItem('instance')
    const [openAlert, setOpenAlert] = useState(false);
    const [openDesc, setOpenDesc] = useState(null);
    const { bussinessUnitLists } = useOracleSettings();
    // const [ERPTypeError, setERPTypeError] = useState(false);
    // const [instanceError, setinstanceError] = useState(false);
    const [BUError, setBUError] = useState(false);
    const [importSelectionValues, setImportSelectionValues] = useState({
        BU: '',
        dataToExport: 'masterData'
    });

    // const importFileData = (e) => {
    //     console.log('file selected')
    //     if (e.target.files[0].name.split('.').pop() === 'xlsx' || e.target.files[0].name.split('.').pop() === 'xls') {
    //         setImportedFileData(e.target.files[0])
    //         setImportedFileName(e.target.files[0].name)
    //         setNoFileError(false)
    //     } else {
    //         setNoFileError(true)
    //         setImportedFileName(null)
    //         setImportedFileData(null)
    //     }
    // }
    const handleCloseImport = () => {
        // setImportedFileData(null)
        setExportAllTempMapping(false);
    };

    const handleImportOptionSelection = (event) => {
        setImportSelectionValues((prevselectedValues) => {
            return {
                ...prevselectedValues,
                [event.target.name]: event.target.value,
            };
        })
        if (event.target.name === 'BU') { setBUError(false) }
        console.info(event.target, 'importSelectionValues')
    }
    const handleImportFile = () => {
        let hasError = false;
        if (importSelectionValues.BU === null || importSelectionValues.BU.length < 1) {
            setBUError(true);
            hasError = true
        }
        if (hasError) { return }

        //if (importedFileData !== null) {
        //alert('file is upl7oaded')
        //if (window.confirm(`You have requested a report that will take some time to complete. This report will continue to run if you close this browser window. To access this report later, use 'Mass Update Part Description' > My Report.`)) {
        //console.log('ok for mass update')
        let postData = {
            rdo: importSelectionValues.BU,
            dataToExport: importSelectionValues.dataToExport,
        }

        // console.log(importSelectionValues, 'importSelectionValues');
        console.log(postData, 'postData');
        templateMappingService.exportAllTemplateMapping(postData).then((response) => {
            console.log(response, 'response 11111');
            // setImportResponse(response)
            // setimportfileBar(true);
            // setImportedFileData(null)
            setImportedFileName(null)
            //console.log(response, ';resprespresp')
            setOpenAlert(true);
            setOpenDesc({
                severity: response?.status,
                desc: response?.msg,
            });
            setExportAllTempMapping(false)
        });
        // }
        // console.log('cancle for mass update')
        //}
    }

    // const handleImportfileBarClose = (event, reason) => {
    //     if (reason === "clickaway") {
    //         return;
    //     }
    //     setimportfileBar(false);
    // };
    // const getUniqueValues = (column) => {
    //     const values = new Set();
    //     bussinessUnitLists?.forEach(item=>{
    //         values.add(item[column]);
    //     })
    //     return Array.from(values);
    // };
    useEffect(() => {
        if (bussinessUnitLists?.length === 0) {
            // buService.getAllBuList().then((result) => {
            //   setBussinessUnitLists(result.data)
            // });
        }
    }, [])
    return (
        <>
            <Dialog open={exportAllTempMapping}
                PaperProps={{
                    style: {
                        backgroundColor: '#fff',
                        boxShadow: 'none',
                    },
                }} >
                <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
                    Export All Template Mappings
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        <Box width="100%" overflow="auto">
                            <StyledTable>
                                <TableBody>
                                    <TableRow>
                                        <TableCell align="left">
                                            {/* <InputLabel
                                                style={{
                                                    fontWeight: "bold",
                                                    color: "rgba(52, 49, 76, 1)",
                                                }}
                                            > */}
                                            Design Organization
                                            {/* </InputLabel> */}
                                        </TableCell>
                                        <TableCell align="left">
                                            <FormControl fullWidth>
                                                <InputLabel id="bussinessUnit">Select RDO</InputLabel>
                                                <Select
                                                    labelId="bussinessUnit"
                                                    id="bussinessUnit"
                                                    name="BU"
                                                    size="medium"
                                                    label="Select RDO"
                                                    fullWidth
                                                    style={{ width: "70%", maxHeight: "200px", }} // Set the width here
                                                    value={importSelectionValues.BU ? importSelectionValues.BU : ''}
                                                    onChange={handleImportOptionSelection}

                                                >
                                                    {bussinessUnitLists?.map((option) => (
                                                        <MenuItem key={option.buName} value={option.buName}
                                                            style={{ backgroundColor: '#fff' }}
                                                        >
                                                            {option.buName}
                                                        </MenuItem>
                                                    ))}
                                                </Select>
                                            </FormControl>
                                            {BUError && (<p style={{ color: 'red' }}>please select Design Organization</p>)}
                                        </TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left">
                                            Data to Export
                                        </TableCell>
                                        <TableCell align="left">
                                            <div style={{ paddingTop: '10px' }}>
                                                <FormControl>
                                                    {/* <FormLabel id="demo-row-radio-buttons-group-label">Gender</FormLabel> */}
                                                    <RadioGroup
                                                        row
                                                        aria-labelledby="demo-row-radio-buttons-group-label"
                                                        name="dataToExport"
                                                        defaultValue="masterData"
                                                        // value={value}
                                                        onChange={handleImportOptionSelection}
                                                    >
                                                        <FormControlLabel value="masterData" control={<Radio />} label="Master Data" />
                                                        <FormControlLabel value="draftData" control={<Radio />} label="Proposed Changes" />
                                                    </RadioGroup>
                                                </FormControl>
                                            </div>
                                            {importedFileName && (<p>{importedFileName}</p>)}
                                        </TableCell>
                                    </TableRow>
                                </TableBody>
                            </StyledTable>
                        </Box>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleImportFile}>Done</Button>
                    <Button autoFocus onClick={handleCloseImport}>
                        Cancel
                    </Button>
                </DialogActions>
            </Dialog>

            {(openDesc) ? (
                <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
            ) : (<></>)}
        </>
    )
}

export default ExportAllTemplateMapping;