import React, { useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { Breadcrumb, SimpleCard } from "app/components";
import { Box, styled, Grid, TextField, Select, MenuItem } from "@mui/material";
import { is_authorizedOptions } from "../adminConfig/common";
import { userGroupOptions } from "../adminConfig/common";
import Button from "@mui/material/Button";
import { UserService } from "services/usersService";
import CustomAlertBox from "app/components/CustomAlertBox";
const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));
const useStyles = {
  spanRed: {
    color: "#FF3D57",
  },
};

export default function AddEditUser() {
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const location = useLocation();
  const isEdit = location.state?.isEdit;
  const selectedUser = location.state?.selectedUserData !== undefined ? location.state?.selectedUserData : {};
  const [userForm, setUserFrom] = useState({
    id: selectedUser.userId !== undefined ? selectedUser.userId : Date.now().toString(),
    userName: selectedUser.userName,
    name: selectedUser.name,
    email: selectedUser.email,
    is_authorized: selectedUser.is_authorized !== undefined ? selectedUser.is_authorized : 'Y',
    userinfo: selectedUser.userinfo !== undefined ? selectedUser.userinfo : []
  });
  const classes = useStyles;
  let navigate = useNavigate();
  const userService = new UserService();

  const handleAddbuName = () => {
    if (
      userForm.userName?.trim() === "" ||
      !userForm.userName ||
      userForm.name?.trim() === "" ||
      !userForm.name ||
      userForm.email?.trim() === "" ||
      !userForm.email ||
      userGroup.length === 0 ||
      userForm.is_authorized?.trim() === "" ||
      !userForm.is_authorized
    ) {
      handlesnackBarClick("Please fill the require details", "error");
      return;
    }

    const newbuName = {
      id: userForm.id,
      userName: userForm.userName,
      name: userForm.name,
      email: userForm.email,
      is_authorized: userForm.is_authorized,
      userinfo: userGroup,
    };
    if (isEdit) {
      userService.updateUser({ userData: newbuName }).then((resp) => {
        if (resp.status === 'success') {
          setTimeout(() => {
            navigate("/userList", { state: { message: resp.msg, status: resp.status } });
          }, 200);
        } else if (resp.status === 'error') {
          handlesnackBarClick(resp.msg, resp.status);
        }
      })
    } else {
      userService.addnewUser({ userData: newbuName }).then((resp) => {
        if (resp.status === 'success') {
          setTimeout(() => {
            navigate("/userList", { state: { message: resp.msg, status: resp.status } });
          }, 200);
        } else if (resp.status === 'error') {
          handlesnackBarClick(resp.msg, resp.status);
        }
      })
    }
  };

  const handleUserFormChange = (event) => {
    const { name, value } = event.target;
    const updatedForm = {
      ...userForm,
      [name]: value,
    };
    setUserFrom(updatedForm);
  };
  const [userGroup, setUserGroup] = useState(selectedUser.userinfo || []);
  const handleuserGroupChange = (event) => {
    const selectedValues = event.target.value;
    const userGroupInfo = userGroupOptions.filter((option) =>
      selectedValues.includes(option)
    );
    setUserGroup(userGroupInfo);
  };

  const handlesnackBarClick = (message, severity) => {
    setOpenAlert(true);
    setOpenDesc({
      severity: severity,
      desc: message,
    });
  };
  const handleCancel = () => {
    setTimeout(() => {
      navigate(-1)
    }, 200);
  }
  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: isEdit ? "Edit User" : "Create User" }]} />
      </Box>
      <form>
        <SimpleCard>
          <Grid item md={9} sm={6} xs={6}>
            <h4>{isEdit ? "Edit User" : "Create User"}</h4>
            <Grid container spacing={3} alignItems="center">
            <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Name <span className={classes.spanRed}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="name"
                  value={userForm.name}
                  onChange={handleUserFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  User Name <span className={classes.spanRed}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="userName"
                  // disabled={isEdit}
                  value={userForm.userName}
                  onChange={handleUserFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Email <span className={classes.spanRed}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="email"
                  disabled={isEdit}
                  value={userForm.email}
                  onChange={handleUserFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>Is Authorized</span>{" "}
                <span className={classes.spanRed}>*</span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Select
                  id="is_authorized"
                  name="is_authorized"
                  fullWidth
                  value={userForm.is_authorized ? userForm.is_authorized : ''}
                  onChange={handleUserFormChange}
                >
                  {is_authorizedOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>


              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  User Group <span className={classes.spanRed}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Select
                  style={{ width: "100%" }}
                  multiple
                  value={userGroup.map((item) => item)}
                  onChange={handleuserGroupChange}
                  renderValue={(selected) => selected.join(", ")}
                  MenuProps={{
                    sx: {
                      "&& .Mui-selected": {
                        backgroundColor: "rgb(25 118 210 / 67%)"
                      }
                    }
                  }}
                >
                  {userGroupOptions?.map((option) => (
                    <MenuItem key={option} value={option}>
                      {option}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>

            </Grid>
            <br />
            <Grid container spacing={3} alignItems="center">
              <Grid item md={3} sm={4} xs={6} className="font-medium">
                <span>
                  <Button
                    variant="contained"
                    color="primary"
                    margin="2px"
                    onClick={handleAddbuName}
                  >
                    {isEdit ? "Update" : "Create"}
                  </Button>
                </span>
              </Grid>
              <Grid item md={3} sm={4} xs={6} className="font-medium">
                <span>
                  <Button
                    variant="contained"
                    color="primary"
                    margin="2px"
                    onClick={handleCancel}
                  >
                    Cancel
                  </Button>
                </span>
              </Grid>
            </Grid>
          </Grid>
        </SimpleCard>
      </form>
      {(openDesc) ? (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      ) : (<></>)}
    </Container>
  );
}
