import { SimpleCard } from "app/components"
import React, { useEffect, useState } from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import {
  Grid,
  Box,
} from "@mui/material";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import { templateService } from "services/temlplateService";
import useOracleSettings from "app/hooks/useOracleSettings";
import useMVOtempComp from "app/hooks/useMVOtempComp";
import usePartTypeTemplate from "app/hooks/usePartTypeTemplate";
import useMVoallowedchanges from "app/hooks/useMVOallowedChanges";
import useCompTemp from "app/hooks/useCompTemp";
import useBUPlant from "app/hooks/useBUPlant";
import useExpenseMap from "app/hooks/useExpenseMap";
import CustomAlertBox from "./CustomAlertBox";
import { sortAndReorderData } from "app/utils/utils";
//import { isArray } from "lodash";
const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const ExportMappingTemplate = (props) => {
  //console.log(props.templateData, 'props')
  const tempService = new templateService();
  const [exportTemplate, setExportTemplate] = useState(false);
  const [exportfileBar, setexportfileBar] = useState(false);
  const [exportResponse, setExportResponse] = useState(false);
  const [dataToExport, setDataToExport] = useState([]);
  const { templatesInfo, bussinessUnit, mappingTemplatesInfo } = useOracleSettings();
  const { mvotemplatecompInfo } = useMVOtempComp();
  const { parttypetemplateinfo } = usePartTypeTemplate();
  const { mvoallowedchangesinfo } = useMVoallowedchanges();
  const { comptempInfo } = useCompTemp();
  const { buplantsInfo } = useBUPlant();
  const { expensemapInfo } = useExpenseMap();
  const [value, setValue] = useState('allData');
  //const AllTempData = templatesInfo;
  const filteredTempData = props.filterTemplateData;
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  let bu = localStorage.getItem('bussinessUnit');

  const handleExportTemplateDialog = () => {
    setExportTemplate(true)
  }

  const handleCloseExportTemplate = () => {
    setExportTemplate(false)
  };
  const handleExportfileBarClick = () => {
    setexportfileBar(true);
  };
  const handleExportfileBarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setexportfileBar(false);
  };
  
  function isObjectBlank(obj) { 
    return obj && Object.keys(obj).length === 0 && obj.constructor === Object; 
  }

  useEffect(() => {

    if (mappingTemplatesInfo) {
      mappingTemplatesInfo.forEach(template => {
        const details = template.details;
        if (details) {
          details.allowableOrgTemplates && details.allowableOrgTemplates.sort((a, b) => {
            const key = a.ALLOWABLE_ORG_TEMPLATE ? 'ALLOWABLE_ORG_TEMPLATE' : 'org_allowed_template';
            return a[key] < b[key] ? -1 : a[key] > b[key] ? 1 : 0;
          });

          details.MVO_TEMPLATES_TO && details.MVO_TEMPLATES_TO.sort((a, b) => {
            return a.MVO_TEMPLATE_TO < b.MVO_TEMPLATE_TO ? -1 : a.MVO_TEMPLATE_TO > b.MVO_TEMPLATE_TO ? 1 : 0;
          });

          details.TEMPLATE_ORG_MODIFYING && details.TEMPLATE_ORG_MODIFYING.sort((a, b) => {
            return a.TEMPLATE_ORG_MODIFYING < b.TEMPLATE_ORG_MODIFYING ? -1 : a.TEMPLATE_ORG_MODIFYING > b.TEMPLATE_ORG_MODIFYING ? 1 : 0;
          });
        }
      });
    }

    let mappingType = props.MappingType;

    if (mappingType === "mvoallowedchanges" && isObjectBlank(props?.sortInfo)) {
      props.sortInfo.dir = 1;
      props.sortInfo.name = 'MVO_TEMPLATE_FROM';
      props.sortInfo.columnName = 'MVO_TEMPLATE_FROM';
    }
    if (mappingType === "mvoTemplateCompatibility" && isObjectBlank(props?.sortInfo)) {
      props.sortInfo.dir = 1;
      props.sortInfo.name = 'MVO_TEMPLATE_OPTION';
      props.sortInfo.columnName = 'MVO_TEMPLATE_OPTION';
    }
    if (mappingType === "parttypetotemplate" && isObjectBlank(props?.sortInfo)) {
      props.sortInfo.dir = 1;
      props.sortInfo.name = 'ENGG_ITEM_TYPE';
      props.sortInfo.columnName = 'ENGG_ITEM_TYPE';
    }
    // if (mappingType === "orgallowedvalues" && isObjectBlank(props?.sortInfo)) {
    //   props.sortInfo.dir = 1;
    //   props.sortInfo.name = 'ALLOWABLE_ORG_TEMPLATES';
    //   props.sortInfo.columnName = 'ALLOWABLE_ORG_TEMPLATES';
    // }
    if (mappingType === "setcompatibletemplates" && isObjectBlank(props?.sortInfo)) {
      props.sortInfo.dir = 1;
      props.sortInfo.name = 'TEMPLATE_ORG_EXISTING';
      props.sortInfo.columnName = 'TEMPLATE_ORG_EXISTING';
    }
    if (mappingType === "expensemapping" && isObjectBlank(props?.sortInfo)) {
      props.sortInfo.dir = 1;
      props.sortInfo.name = 'EXPENSE_TEMPLATE';
      props.sortInfo.columnName = 'EXPENSE_TEMPLATE';
    }
    let AllTempData = mappingType && mappingTemplatesInfo ? mappingTemplatesInfo.filter(
      (item) => item.MappingType === mappingType) : [];
    // switch (mappingType) {
    //   case "orgallowedvalues":
    //     AllTempData = templatesInfo
    //   break;
    //   case "mvoTemplateCompatibility":
    //     AllTempData = mvotemplatecompInfo
    //   break;
    //   case "parttypetotemplate":
    //     AllTempData = parttypetemplateinfo
    //   break;
    //   case "mvoallowedchanges":
    //     AllTempData = mvoallowedchangesinfo
    //   break;
    //   case "setcompatibletemplates":
    //     AllTempData = comptempInfo
    //   break;
    //   case "buplantmapping":
    //     AllTempData = buplantsInfo
    //   break;
    //   case "expensemapping":
    //     AllTempData = expensemapInfo
    //   break;
    //   default:  AllTempData = templatesInfo
    //     break;
    // } 
    if (value === 'allData' && props.sortInfo) {
      AllTempData.sort((a, b) => {
        const field = props.sortInfo.id || props.sortInfo.name || props.sortInfo.columnName;
        const aValue = a[field]?.toLowerCase() || '';
        const bValue = b[field]?.toLowerCase() || '';
        if (aValue < bValue) {
          return props.sortInfo.dir === 1 ? -1 : 1;
        }
        if (aValue > bValue) {
          return props.sortInfo.dir === 1 ? 1 : -1;
        }
        return 0;
      });
    
    }
    let data = value === 'allData' ? AllTempData : props?.filterTemplateData;
    data = data?.filter(item => item.BUID === bussinessUnit);

    let exportData = data.map(item => {
      if (mappingType === "orgallowedvalues" && item.actionFlag !== "Delete") {
        let res = {
          "Template Name": item?.details?.ALLOWABLE_ORG_TEMPLATES,
          "Design Organization": item.BUID,
          "Has MBOM": item?.details?.Has_MBOM,
          "On MBOM": item?.details?.On_MBOM,
          "EBOM Allowed": item?.details?.EBOM_Allowed,
          "Optional": item?.details?.Optional,
          "PO Org Check": item?.details?.PO_Org_Exist_Check,
          ...item?.details?.Plants
        };
        // // res = {...res, ...item?.details?.Plants}
        //  console.log(res, 'newRes')
        //  console.log(item?.details?.Plants, '...item?.details?.Plants')
        //  //let sorted = {...res, ...item?.details?.Plants}
        //  const addObjectResource = Object.assign(res, item?.details?.Plants);
        //  console.log(addObjectResource, 'addObjectResource')
        return res
      } else if (mappingType === 'mvoTemplateCompatibility' && item.actionFlag !== "Delete") {
        return item.details?.allowableOrgTemplates?.map((item1, index) => {
          let res = {
            "Template MVO": index === 0 ? item.details.MVO_TEMPLATE_OPTION : null,
            "Org Allowed Templates": item1.ALLOWABLE_ORG_TEMPLATE,
            "Status:": item1.status,
            "Design Organization": index===0?item.BUID:null,

          }
          return res
        })
      } else if (mappingType === 'parttypetotemplate' && item.actionFlag !== "Delete") {
        return item.details?.allowableOrgTemplates?.map((item1, index) => {
          let res = {
            "Enovia Part Type": index === 0 ? item.details.ENGG_ITEM_TYPE : null,
            "Org Allowed Templates": item1.org_allowed_template,
            "Exclusive Use": item1.exclusive_use,
            "Design Organization": index===0?item.BUID:null,
          }
          return res
        })
      } else if (mappingType === 'mvoallowedchanges' && item.actionFlag !== "Delete") {
        return item.details?.MVO_TEMPLATES_TO?.map((item1, index) => {
          let res = {
            "MVO Template - From": index === 0 ? item.details.MVO_TEMPLATE_FROM : null,
            "MVO Template - To": item1.MVO_TEMPLATE_TO,
            "Design Organization": index===0?item.BUID:null,
          }
          return res
        })
      } else if (mappingType === 'setcompatibletemplates' && item.actionFlag !== "Delete") {
        return item.details?.TEMPLATE_ORG_MODIFYING?.map((item1, index) => {
          let res = {
            "Template for Org(s) Existing": index === 0 ? item.details.TEMPLATE_ORG_EXISTING : null,
            "Template for Org Modifying": item1.TEMPLATE_ORG_MODIFYING,
            "Design Organization": index===0?item.BUID:null
          }
          return res
        })
      } else if (mappingType === 'buplantmapping' && item.actionFlag !== "Delete") {
        //console.log(item, 'inside export data')
        let res = {
          // "Design Organization": item.BUID,
          // "Plants": item.details.PLANT,
          // "BOM/NOBOM": item.details.BOM_STATUS,
          // "PO Org": item.details.PO_ORG,
          // "RDO OU Org set": item.details.RDU_OU_ORG_SET,
          Entity: item.details.Entity,
          ...Object.keys(item.details.PLANT).sort().reduce((acc, key) => {
            acc[key] = item.details.PLANT[key];
            return acc;
          }, {})
        };
        return res
      } else if (mappingType === 'expensemapping' && item.actionFlag !== "Delete") {
        let res = {
          "Template": item.details.EXPENSE_TEMPLATE,
          "Design Organization": bussinessUnit,
          ...item.details.EXPENSE_PLANTS
        }
        return res
      }
    })

let filterData = exportData.flat().filter(item => item !== null && item !== undefined);

setDataToExport(filterData);
  }, [bussinessUnit,
    templatesInfo,
    mvotemplatecompInfo,
    parttypetemplateinfo,
    mvoallowedchangesinfo,
    comptempInfo,
    buplantsInfo,
    expensemapInfo,
    value,
    mappingTemplatesInfo,
    props.filterTemplateData,
    props.sortInfo
  ])

  /* export template data start */
  const exportTemplateHandle = () => {
    //console.log(dataToExport, 'dataToExport dataToExport dataToExport')
    if (dataToExport) {
      let dataToPass = dataToExport;
      if (props.title === "RDO Plant Mapping") {
        dataToPass = sortAndReorderData(dataToExport);
      }
      if (props.title === "Org Allowed Templates") {
        if (props.MappingType === "orgallowedvalues" && isObjectBlank(props?.sortInfo)) {
          props.sortInfo.dir = 1;
          props.sortInfo.name = 'ALLOWABLE_ORG_TEMPLATES';
          props.sortInfo.columnName = 'ALLOWABLE_ORG_TEMPLATES';
        }
        if (props.sortInfo) {
          dataToPass.sort((a, b) => {
            let field = props.sortInfo.id || props.sortInfo.name || props.sortInfo.columnName;
            // Check if the field is 'ALLOWABLE_ORG_TEMPLATES' and sort by 'Template Name'
            if (field === 'ALLOWABLE_ORG_TEMPLATES') {
              field = 'Template Name';
            }
            const aValue = a[field]?.toLowerCase() || '';
            const bValue = b[field]?.toLowerCase() || '';
            if (aValue < bValue) {
              return props.sortInfo.dir === 1 ? -1 : 1;
            }
            if (aValue > bValue) {
              return props.sortInfo.dir === 1 ? 1 : -1;
            }
            return 0;
          });
        }
      }
      tempService.exportTemplate({ 'data': dataToPass, 'sheet_name': props.title.substring(0, 26), 'bussiness_unit': bu }).then((response) => {
        setExportResponse(response)
        //console.log(response, ';resprespresp')
        handleExportfileBarClick("Template exported successfully!", "success");
        setOpenAlert(true);
        setOpenDesc({
          severity: response?.status,
          desc: response?.msg,
        });
      });
    }
    setExportTemplate(false)
  }

  const handleChange = (event) => {
    setValue(event.target.value)
    //console.log(event.target.value, 'event.target.value')
  }
  /* export template data end */
  return (
    <>
      <Button
        variant="contained"
        onClick={handleExportTemplateDialog}
      >
        Export
      </Button>
      <Dialog open={exportTemplate}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Export Template
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <Box width="100%" overflow="auto">
              <SimpleCard>
                <Grid item md={12} sm={12} xs={12}>
                  <Grid container alignItems="center">
                    <FormControl>
                      <FormLabel id="demo-controlled-radio-buttons-group">Select Data to Export</FormLabel>
                      <RadioGroup
                        aria-labelledby="demo-controlled-radio-buttons-group"
                        name="controlled-radio-buttons-group"
                        value={value}
                        onChange={handleChange}
                      >
                        <FormControlLabel value='allData' control={<Radio />} label="All Data" />
                        <FormControlLabel value='selectedData' control={<Radio />} label="Selected Data" />
                      </RadioGroup>
                    </FormControl>
                  </Grid>

                </Grid>
              </SimpleCard>
            </Box>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCloseExportTemplate}>
            Cancel
          </Button>
          <Button onClick={exportTemplateHandle}>Export</Button>
        </DialogActions>
      </Dialog>
      {/* <Snackbar
        open={exportfileBar}
        autoHideDuration={3000}
        onClose={handleExportfileBarClose}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
      >
        <Alert
          onClose={handleExportfileBarClose}
          severity="success"
          sx={{ width: "100%" }}
        >
          {exportResponse && (
            <div>
              {exportResponse.msg} <br />
            </div>
          )}
          
        </Alert>
      </Snackbar> */}
      {(openDesc) ? (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      ) : (<></>)}
    </>
  );
};

export default ExportMappingTemplate;
