import React, { useEffect, useState } from 'react';
import CustomSelect from "./components/CustomSelect";
import { Grid, Select, MenuItem } from "@mui/material";
import { useSelector } from 'react-redux';
import { InstanceOptions } from "./views/adminConfig/common.jsx";

const Homepage = () => {
  const globalBUandInstance = useSelector((state) => state.globalBUandInstance);
  const [bussinessUnit, setBussinessUnit] = useState('')
  const [instance, setInstance] = useState('')
  const handleSelectChange = (event) => {
    const { name, value } = event.target;
    if (name === 'Instance') {
      setInstance(value)
    }
  }
  useEffect(() => {
    if (globalBUandInstance.bu !== '') {
      setBussinessUnit(globalBUandInstance.bu)
    }
    if (globalBUandInstance.instanse !== '') {
      setInstance(globalBUandInstance.instanse)
    }
  }, []);

  return (
    <div style={styles.container}>

      <h1 style={styles.title}>Select RDO and Instance</h1>
      <Grid container item md={6} sm={6} xs={12} className="font-medium"
        style={{ backgroundColor: 'lightblue', padding: '20px' }}
      >
        <Grid item md={12} sm={12} xs={12} className="font-medium">
          <span>Design Organization:</span>
          <CustomSelect
            selectedvalue={bussinessUnit}
            style={{ width: "50%" }}
            // optionsValues={BUOptions}
            defaultValue={bussinessUnit ? bussinessUnit : ""}
            onChangeFunc={(selectedOptionValue) => {
              setBussinessUnit(selectedOptionValue);
              console.log(selectedOptionValue, 'selectedOptionValue')
              //handleSelectChange()
            }}
          />
        </Grid>

        <Grid item md={12} sm={12} xs={12} className="font-medium">
          <span>Select Instance:</span>
          <Select
            labelId="Instance"
            id="Instance"
            name="Instance"
            fullWidth
            style={{ width: "50%" }} // Set the width here
            value={instance ? instance : ''}
            onChange={handleSelectChange}
          >
            {InstanceOptions.map((option) => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </Select>
        </Grid>
      </Grid>
    </div>

  );
};

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
    height: '100vh',
    backgroundColor: '#f8f9fa',
  },
  title: {
    fontSize: '2rem',
    color: '#333',
  },
  message: {
    fontSize: '1rem',
    color: '#666',
    textAlign: 'center',
    marginTop: '10px',
  },
  // Optional button styles
  button: {
    marginTop: '20px',
    padding: '10px 20px',
    fontSize: '1rem',
    backgroundColor: '#007bff',
    color: '#fff',
    border: 'none',
    borderRadius: '5px',
    cursor: 'pointer',
    transition: 'background-color 0.3s ease',
    textDecoration: 'none',
    outline: 'none',
    ':hover': {
      backgroundColor: '#0056b3',
    },
  },
};

export default Homepage;
