import { Breadcrumb, SimpleCard } from "app/components";

import React, { useEffect, useState, useRef } from "react";
import Button from "@mui/material/Button";
import DataGrid from "@inovua/reactdatagrid-community";
import "@inovua/reactdatagrid-community/index.css";
import {
  Box,
  styled,
  Table,
  Grid,
  TextField,
  Select,
  MenuItem
} from "@mui/material";

import MuiAlert from "@mui/material/Alert";

import useOracleSettings from "app/hooks/useOracleSettings";
import CircularProgress from "@mui/material/CircularProgress";

import CustomSelect from "../../components/CustomSelect";
import { BUOptions } from "../adminConfig/common";

import { HistoryManagementService } from "services/historyManagementService";

import historyData from "./sampledata.json";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));

const HistoryLIst = () => {
  const { bussinessUnit, updateBussinessUnit } = useOracleSettings();
  const [tabledata, setTabledata] = useState(historyData);
  const [filteredTabledata, setFilteredTabledata] = useState([]);
  const [searchValue, setSearchValue] = useState("");
  const [isLoading, setIsLoading] = useState(true);
  const gridStyle = { minHeight: 550 };
  const [selectedRows, setSelectedRows] = useState([]);
  const [mappingOptions, setMappingOptions] = useState([]);
  const historyManagementService = new HistoryManagementService();
  const [mappingType, setMappingType] = useState('orgallowedvalues')
  const gridRef = useRef(null);

  const columns = [
    {
      name: "version",
      header: "Version",
      defaultFlex: 0.6,
      defaultWidth: 20,
      render: ({ data }) => <div>{data?.version}</div>,
    },
    {
      name: "dateTime",
      header: "Change Date",
      defaultFlex: 2,
      render: ({ data }) => {
        let date = new Date(parseInt(data?.dateTime));
        console.log(date, 'date')
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, '0'); // Months are zero-indexed
        const day = String(date.getDate()).padStart(2, '0');
        const hours = String(date.getHours()).padStart(2, '0');
        const minutes = String(date.getMinutes()).padStart(2, '0');
        const seconds = String(date.getSeconds()).padStart(2, '0');
        const meridiem = hours >= 12 ? 'PM' : 'AM';
        const formattedHours = hours % 12 || 12; // Convert 24-hour to 12-hour format
        const formattedDate = `${year}-${month}-${day} ${formattedHours}:${minutes}:${seconds} ${meridiem}`;
        return <div>{formattedDate}</div>
      },
    },
    {
      name: "userName",
      header: "Requested By",
      defaultFlex: 1,
      render: ({ data }) => <div>{data?.userName}</div>,
    },
    {
      name: "MappingType ",
      header: "Mapping Type",
      defaultFlex: 1,
      defaultWidth: 200,
      render: ({ data }) => <div>{data?.MappingType}</div>,
    },
    {
      name: "reason",
      header: "Change Details",
      defaultFlex: 3,
      defaultWidth: 200,
      render: ({ data }) => <div>{data?.reason}</div>,
    },

  ];

  const handleSearch = () => {
    let filteredData = [];
    // input box filter
    if (searchValue === "") {
      filteredData = tabledata;
    } else {
      filteredData = tabledata?.filter((thisItem) => {
        const thisRowData = Object.values(thisItem).join()?.toLowerCase();
        return thisRowData.includes(searchValue?.toLowerCase());
      });
    }

    // dropdown filter
    // filteredData = filteredData?.filter((thisItem) => {
    //   const thisRowData = Object.values(thisItem).join()?.toLowerCase();
    //   return thisRowData.includes(bussinessUnit?.toLowerCase());
    // });
    // console.log(filteredData, 'filteredDatafilteredData')
    setFilteredTabledata(filteredData);
  };

  const handleRowClick = (rowData) => {
    const isSelected = selectedRows.some((row) => row.id === rowData.id);

    if (isSelected) {
      setSelectedRows((prevSelectedRows) =>
        prevSelectedRows.filter((row) => row.id !== rowData.id)
      );
    } else {
      setSelectedRows([rowData]);
    }
  };

  useEffect(() => {
    setIsLoading(true);
    historyManagementService.getAllmappingHistory().then((result) => {
      setTabledata(result?.data);
      setFilteredTabledata(result?.data);
      // setMappingType()
      setIsLoading(false);
      const values = new Set();
      result?.data?.forEach((subscriber) => {
        if (Array.isArray(subscriber['MappingType'])) {
          subscriber['MappingType'].forEach((data) => {
            values.add(data.MappingType);
          });
        } else {
          values.add(subscriber['MappingType']);
        }
      });
      console.log(Array.from(values), 'array of mapping')
      setMappingOptions(Array.from(values))
    });
  }, []);

  useEffect(() => {
    handleSearch();
  }, [searchValue, bussinessUnit]);


  useEffect(() => {
    // let dataToShow = tabledata;
    // console.log(dropdownFilter, 'dropdownFilter')
    let filteredData = tabledata;
    // dropdown filter
    filteredData = filteredData?.filter((thisItem) => {
      const thisRowData = Object.values(thisItem).join()?.toLowerCase();
      return thisRowData.includes(bussinessUnit?.toLowerCase()) &&
        thisRowData.includes(mappingType?.toLowerCase());
    });
    setFilteredTabledata(filteredData);
  }, [mappingType, bussinessUnit, tabledata])
  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Changes History " }]} />
      </Box>

      <SimpleCard>
        <Grid container spacing={1} alignItems="center">
          <Grid item md={6} sm={6} xs={12} className="font-medium">
            <Grid container spacing={1} alignItems="center">
              <Grid item md={6} sm={6} xs={12} className="font-medium">
                <TextField
                  type="text"
                  name="search"
                  size="small"
                  onChange={(e) => {
                    setSearchValue(e.target.value);
                  }}
                  value={searchValue}
                  style={{ width: "100%" }}
                />
              </Grid>
              <Grid item md={6} sm={6} xs={12} className="font-medium">
                <Button variant="contained" onClick={handleSearch}>
                  Search
                </Button>
              </Grid>
            </Grid>
          </Grid>
          <Grid item md={6} sm={6} xs={12} className="font-medium">
            <Grid container alignItems="center"
              style={{
                // marginLeft: "198px",
                textAlign: "right"
              }}
            >
              <Grid
                item
                md={12}
                sm={12}
                xs={12}
                className="font-medium"
                style={{ paddingRight: "10px" }}
              >
                <label style={{ textWrap: "nowrap", paddingTop: "10px" }}>Mapping Type:</label>

                <Select
                  className="form-control"
                  defaultValue={mappingOptions[0]}
                  value={mappingType}
                  onChange={(e) => setMappingType(e.target.value)}
                  style={{ width: "50%" }}
                  size={'medium'}
                  displayEmpty // Enable displayEmpty prop
                  renderValue={(selected) => {
                    // Render the selected value
                    let selectedOption = [];
                    selectedOption = mappingOptions?.find(
                      (option) => (option === selected)
                    );

                    return (selectedOption) ? selectedOption : '';
                  }}
                >
                  {mappingOptions?.map((option, index) => {
                    const listKey = "dpKey" + index;
                    return (
                      <MenuItem value={option} key={listKey}>
                        {option}
                      </MenuItem>
                    )
                  })}
                </Select>
              </Grid>
            </Grid>
          </Grid>
          <Grid item md={12} sm={6} xs={12} className="font-medium" style={{ display: "none" }}>
            <Grid container alignItems="center"
              style={{
                // marginLeft: "198px",
                textAlign: "right"
              }}
            >
              <Grid
                item
                md={12}
                sm={12}
                xs={12}
                className="font-medium"
                style={{ paddingRight: "10px" }}
              >
                <label style={{ textWrap: "nowrap", paddingTop: "10px" }}>Design Organization:</label>

                <CustomSelect
                  selectedvalue={bussinessUnit}
                  style={{ width: "50%" }}
                  optionsValues={BUOptions}
                  // defaultValue={
                  //   BUOptions && !bussinessUnit ? BUOptions[0]?.value : ""
                  // }
                  onChangeFunc={(selectedOptionValue) => {
                    updateBussinessUnit(selectedOptionValue);
                    // handleOptionChange({bu: selectedOptionValue})
                    setSearchValue("");
                  }}
                />
              </Grid>
            </Grid>
          </Grid>

          <Box width="100%" overflow="auto" marginTop="10px">
            {isLoading && (
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "100vh",
                }}
              >
                <CircularProgress />
              </Box>
            )}
            {!isLoading && (
              <DataGrid
                showZebraRows={false}
                style={gridStyle}
                columns={columns}
                dataSource={filteredTabledata}
                defaultLimit={100}
                // rowHeight={null}
                onRowClick={(rowData) => handleRowClick(rowData.data)}
                pagination
                renderScroller={()=> gridRef?.current?.setScrollTop(selectedRows?.length==0 ? 0 : gridRef?.current?.getScrollTop())}
                />
            )}
          </Box>
        </Grid>
      </SimpleCard>
    </Container>
  );
};

export default HistoryLIst;
