import { createContext, useState, useEffect } from 'react'

const BUPlantContext = createContext({
    buplantsInfo: [],
    updatebuplantsInfo: () => [],
})

export const BUPlantProvider = ({ children }) => {
    const [buplantsInfo, setBuplantInfo] = useState([]);

    const updatebuplantsInfo = (values) => {
        setBuplantInfo(values);
       // console.log(values, "inside update context");
    };

    useEffect(() => {
       // console.log(buplantsInfo, "templatesContext");
    }, [buplantsInfo]);

    return (
        <BUPlantContext.Provider
            value={{
                buplantsInfo,
                updatebuplantsInfo,
            }}
        >
            {children}
        </BUPlantContext.Provider>
    );
};

export default BUPlantContext;