export const GET_BU_AND_INSTANSE = 'GET_BU_AND_INSTANSE';

export const updateGlobalBUandInstance = (data) => async (dispatch) => {
    try {
      console.log(data, 'updateGlobalBUandInstance data')
      dispatch({
        type: GET_BU_AND_INSTANSE,
        payload: data,
      })
    } catch (e) {
      console.error(e)
    }
  };