import React, { useEffect, useState } from "react";
import { useNavigate, useLocation, useParams } from "react-router-dom";
import { Breadcrumb, SimpleCard } from "app/components";
import { Box, styled, Grid, TextField, Select, MenuItem } from "@mui/material";
import InfoIcon from '@mui/icons-material/Info';
import { styled as makeStyles } from '@mui/material/styles';
import useOracleSettings from "app/hooks/useOracleSettings";
import { exportToERPOptions, enableBOSTransferOptions } from "../adminConfig/common";
import Button from "@mui/material/Button";
import Autocomplete from '@mui/material/Autocomplete';
import { BUService } from "services/buService";
import { PlantService } from "services/plantService";
import { TemplateMappingService } from "services/templateMappingService";
import CustomAlertBox from "app/components/CustomAlertBox";
import Loader from 'app/components/Loader';
import { sortDataByKey } from "app/utils/mcoUtils/commonUtils";
const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));
const useStyles = makeStyles({
  spanRed: {
    color: "#FF3D57",
  },
});

export default function EditBU() {
  const {
    refreshData,
    templatesInfo,
    updateMappingTemplatesInfo
  } = useOracleSettings();
  const [plantData, setPlantData] = useState([]);
  const location = useLocation();
  // const selectBU = location.state?.selectedBUData;
  const [selectBU, setSelectBU] = useState({});
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [buForm, setBuFrom] = useState({});
  const [isLoading, setIsLoading] = useState(true);
  const classes = useStyles();
  let navigate = useNavigate();
  const params = useParams();

  const buService = new BUService();
  const tempMapService = new TemplateMappingService();
  const plantService = new PlantService();
  useEffect(() => {
    setIsLoading(true);
    plantService.getAllPlantsList().then((result) => {
      setPlantData(result);
      setIsLoading(false);
    });
  }, []);

  const handleAddbuName = () => {
    if (
      buForm.buName?.trim() === "" ||
      !buForm.buName ||
      selectedplants.length === 0
    ) {
      handlesnackBarClick("Please fill the require details", "error");
      return;
    }

    const newbuName = {
      id: buForm.id,
      buName: buForm.buName,
      exportToERPdisabled: buForm.exportToERPdisabled,
      contextDEF: buForm.contextDEF,
      EMRDivisionORGValue: buForm.EMRDivisionORGValue,
      productHierarchy: buForm.productHierarchy,
      exportToERPbyPassed: buForm.exportToERPbyPassed,
      enableBOSTransfer: buForm.enableBOSTransfer,
      plants: selectedplants?.map((option) => {
        return {
          id: option?.id,
          plantName: option?.plantName
        }
      }) || [],
      doNotSendToERP: DoNotSendToERP?.map((option) => (
        option.templateName
      )) || [],
      oracleExisting: OracleExisting?.map((option) => (
        option.templateName
      )) || [],
      makeWithoutBOMList: MakeWithoutBOMList?.map((option) => (
        option.templateName
      )) || [],
      notOwningDivisionList: NotOwningDivisionList?.map((option) => (
        option.templateName
      )) || [],
    };
    setIsLoading(true);
    buService.updateBU(newbuName).then((resp) => {
      setIsLoading(false);
      if (resp.status === 'success') {
        setIsLoading(false);
        tempMapService.getAlltemplateMappings().then((result) => {
          updateMappingTemplatesInfo(result?.data?.templateMappingList);
          setIsLoading(true);
        });
        setTimeout(() => {
          navigate("/buList", { state: { message: resp.msg, status: resp.status } });
        }, 200);
      } else if (resp.status === 'error') {
        handlesnackBarClick(resp.msg, resp.status);
      }
    });
  };

  const handlebuFormChange = (event) => {
    const { name, value } = event.target;
    const updatedForm = {
      ...buForm,
      [name]: value,
    };
    console.log(updatedForm, "===================updatedForm");
    setBuFrom(updatedForm);
  };

  const [selectedplants, setSelectedPlants] = useState([]);
  const [DoNotSendToERP, setDoNotSendToERP] = useState([]);
  const [OracleExisting, setOracleExisting] = useState([]);
  const [MakeWithoutBOMList, setMakeWithoutBOMList] = useState([]);
  const [NotOwningDivisionList, setNotOwningDivisionList] = useState([]);
  const handlePlantsChange = (selectedObj) => {
    const newselectedObj = selectedObj?.filter(
      (item) => plantData.some((existing) => existing.plantName === item.plantName)
    ) || [];
    setSelectedPlants(newselectedObj);
  };
  const handleTemplateChange = (name, selectedObj) => {
    console.log("New Value:", selectedObj);
    const newselectedObj = selectedObj?.filter(
      (item) => templatesInfo.some((existing) => existing.templateName === item.templateName)
    ) || [];
    if (name === 'DoNotSendToERP') {
      setDoNotSendToERP(newselectedObj)
    }
    else if (name === 'OracleExisting') {
      setOracleExisting(newselectedObj);
    }
    else if (name === 'MakeWithoutBOMList') {
      setMakeWithoutBOMList(newselectedObj)
    }
    else if (name === 'NotOwningDivisionList') {
      setNotOwningDivisionList(newselectedObj)
    }
  };

  const handlesnackBarClick = (message, severity) => {
    setOpenAlert(true);
    setOpenDesc({
      severity: severity,
      desc: message,
    });
  };
  const handleCancel = () => {
    setTimeout(() => {
      navigate(-1)
    }, 200);
  }

  const getTemplValues = (fieldValue) => {
    const newselectedObj = templatesInfo?.filter(
      (item) => fieldValue.some((templateName) => templateName === item.templateName)
    ) || [];
    return newselectedObj;
  }

  useEffect(() => {
    if (selectBU?.plants) {
      const newselectedObj = plantData?.filter(
        (item) => selectBU?.plants.some((eitem) => eitem?.plantName === item?.plantName)
      ) || [];
      setSelectedPlants(newselectedObj)
    }
    if (selectBU?.doNotSendToERP) {
      const newselectedObj = getTemplValues(selectBU?.doNotSendToERP);
      setDoNotSendToERP(newselectedObj)
    }
    if (selectBU?.oracleExisting) {
      const newselectedObj = getTemplValues(selectBU?.oracleExisting);
      setOracleExisting(newselectedObj)
    }
    if (selectBU?.makeWithoutBOMList) {
      const newselectedObj = getTemplValues(selectBU.makeWithoutBOMList);
      setMakeWithoutBOMList(newselectedObj)
    }
    if (selectBU?.notOwningDivisionList) {
      const newselectedObj = getTemplValues(selectBU.notOwningDivisionList);
      setNotOwningDivisionList(newselectedObj)
    }
  }, [selectBU, plantData, templatesInfo]);

  useEffect(() => {
    if (params?.buId) {
      setIsLoading(true);
      buService.getBusinessUnitbyid([{ id: params?.buId }]).then((resp) => {
        setIsLoading(false);
        if (resp?.data?.length && (resp?.data?.[0] && Object?.keys(resp?.data?.[0])?.length)) {
          setSelectBU(resp?.data[0]);
          const selectBUValue = resp?.data[0];
          setBuFrom({
            id: selectBUValue.id,
            buName: selectBUValue.buName,
            exportToERPdisabled: selectBUValue.exportToERPdisabled,
            exportToERPbyPassed: selectBUValue.exportToERPbyPassed,
            enableBOSTransfer: selectBUValue.enableBOSTransfer,
            contextDEF: selectBUValue.contextDEF,
            EMRDivisionORGValue: selectBUValue.EMRDivisionORGValue,
            productHierarchy: selectBUValue.productHierarchy,
          });
        } else {
          navigate("/buList", { state: { message: "Design Organization not found!", status: "error" } });
        }
      });
    } else {
      setTimeout(() => {
        navigate("/buList", { state: { message: "Design Organization not found!", status: "error" } });
      }, 200);
    }
  }, []);

  sortDataByKey(plantData, 'plantName');

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Edit Design Organization" }]} />
      </Box>

      <form>
        <SimpleCard>
          <Grid item md={9} sm={6} xs={6}>
            <h4>Edit Design Organization</h4>
            <Grid container spacing={3} alignItems="center">
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Design Organization <span className={classes.spanRed}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="buName"
                  disabled
                  value={buForm.buName}
                  onChange={handlebuFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>Export to ERP Disabled </span>
              </Grid>
              <Grid item md={9} sm={8} xs={12}>
                <Select
                  id="exportToERPdisabled"
                  name="exportToERPdisabled"
                  fullWidth
                  value={buForm?.exportToERPdisabled ? buForm?.exportToERPdisabled : "N"}
                  onChange={handlebuFormChange}
                >
                  {exportToERPOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Export To ERP ByPassed
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Select
                  id="exportToERPbyPassed"
                  name="exportToERPbyPassed"
                  fullWidth
                  value={buForm?.exportToERPbyPassed ? buForm?.exportToERPbyPassed : "N"}
                  onChange={handlebuFormChange}
                >
                  {exportToERPOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>Enable BOS Transfer </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Select
                  labelId="enableBOSTransfer"
                  id="enableBOSTransfer"
                  name="enableBOSTransfer"
                  fullWidth
                  value={buForm?.enableBOSTransfer ? buForm?.enableBOSTransfer : 'Y'}
                  onChange={handlebuFormChange}
                >
                  {enableBOSTransferOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Context DFF
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="contextDEF"
                  value={buForm.contextDEF}
                  onChange={handlebuFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  EMR Division ARG
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="EMRDivisionORGValue"
                  value={buForm.EMRDivisionORGValue}
                  onChange={handlebuFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Product Hierarchy
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="productHierarchy"
                  value={buForm.productHierarchy}
                  onChange={handlebuFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Plant Name <span className={classes.spanRed}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Autocomplete
                  multiple
                  value={selectedplants}
                  getOptionLabel={(option) => option.plantName}
                  autoHighlight
                  onChange={(e, newValue) => {
                    handlePlantsChange(newValue)
                  }}
                  options={plantData}
                  renderInput={(params) => <TextField {...params} className={"sampleeeee"} />}
                  renderOption={(props, option) => {
                    return (
                      <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                        {option.plantName}
                      </Box>
                    )
                  }}
                />
                <div style={{ marginTop: '5px' }}>
                  <InfoIcon style={{ marginTop: '5px' }} color="secondary" fontSize="small" /><span style={{ marginBottom: '20px' }}> Changing Plants will affect the Template Mappings.</span>
                </div>
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Do Not Send To ERP
                </span>
              </Grid>
              <Grid item md={9} sm={8} xs={12}>
                <Autocomplete
                  multiple
                  value={DoNotSendToERP}
                  getOptionLabel={(option) => option.templateName}
                  autoHighlight
                  onChange={(e, newValue) => {
                    handleTemplateChange("DoNotSendToERP", newValue)
                  }}
                  options={templatesInfo}
                  renderInput={(params) => <TextField {...params} className={"sampleeeee"} />}
                  renderOption={(props, option) => {
                    return (
                      <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                        {option.templateName}
                      </Box>
                    )
                  }}
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Oracle Existing
                </span>
              </Grid>
              <Grid item md={9} sm={8} xs={12}>
                <Autocomplete
                  multiple
                  value={OracleExisting}
                  getOptionLabel={(option) => option.templateName}
                  autoHighlight
                  onChange={(e, newValue) => {
                    handleTemplateChange("OracleExisting", newValue)
                  }}
                  options={templatesInfo}
                  renderInput={(params) => <TextField {...params} className={"sampleeeee"} />}
                  renderOption={(props, option) => {
                    return (
                      <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                        {option.templateName}
                      </Box>
                    )
                  }}
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Make Without BOM List
                </span>
              </Grid>
              <Grid item md={9} sm={8} xs={12}>
                <Autocomplete
                  multiple
                  value={MakeWithoutBOMList}
                  getOptionLabel={(option) => option.templateName}
                  autoHighlight
                  onChange={(e, newValue) => {
                    handleTemplateChange("MakeWithoutBOMList", newValue)
                  }}
                  options={templatesInfo}
                  renderInput={(params) => <TextField {...params} className={"sampleeeee"} />}
                  renderOption={(props, option) => {
                    return (
                      <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                        {option.templateName}
                      </Box>
                    )
                  }}
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Not Owning Division List
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Autocomplete
                  multiple
                  value={NotOwningDivisionList}
                  getOptionLabel={(option) => option.templateName}
                  autoHighlight
                  onChange={(e, newValue) => {
                    handleTemplateChange("NotOwningDivisionList", newValue)
                  }}
                  options={templatesInfo}
                  renderInput={(params) => <TextField {...params} className={"sampleeeee"} />}
                  renderOption={(props, option) => {
                    return (
                      <Box component="li" sx={{ '& > img': { mr: 2, flexShrink: 0 } }} {...props}>
                        {option.templateName}
                      </Box>
                    )
                  }}
                />
              </Grid>

              <Grid item md={12} sm={12} xs={12} className="font-medium" style={{ textAlign: "right" }}>
                <Button
                  variant="contained"
                  color="primary"
                  margin="2px"
                  onClick={handleCancel}
                  style={{ marginRight: "20px" }}
                >
                  Cancel
                </Button>
                <Button
                  variant="contained"
                  color="primary"
                  margin="2px"
                  onClick={handleAddbuName}
                >
                  Update
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </SimpleCard>
        {(openDesc) ? (
          <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
        ) : (<></>)}

        {
          (refreshData || isLoading) ? (
            <div style={{ textAlign: 'center' }}>
              <Loader show={true} />
            </div>
          ) : <></>
        }
      </form>
    </Container >
  );
}
