import React from 'react';
import ExpenseMapWrapper from "./expensemapWrapper";
import PrivateRoute from "app/privateRoutes";

const expensemaproutes = [
  {
    path: "/expensemapList",
    element: (
      <PrivateRoute>
        <ExpenseMapWrapper />
      </PrivateRoute>
    ),
  },
];

export default expensemaproutes;
