import jwt from 'jwt-simple';
import { Buffer } from 'buffer';

export const getToken = () => {
    return new Promise((resolve, reject) => {
        setTimeout(() => {
            const getToken = window.localStorage.getItem('okta-token-storage')
            const sessionToken = (getToken) ? JSON.parse(getToken)?.accessToken?.accessToken : null;
            resolve(sessionToken);
        }, 500);
    });
}

export const getOKTAAccessKey = () => {
    const getToken = window.localStorage.getItem('okta-token-storage');
    if (!getToken) {
        console.error('Token storage item not found');
        return null;
    }

    const parsedToken = JSON.parse(getToken);
    const accessToken = parsedToken?.accessToken?.accessToken;
    const curruid = parsedToken?.accessToken?.claims?.uid;
    const userinfo = parsedToken?.idToken?.claims;

    if (!accessToken || !userinfo) {
        console.error('Access token or userinfo not found in token storage item');
        return null;
    }

    const payload = {
        exp: Math.floor(Date.now() / 1000) + (60 * 60), // expires in 1 hour
        data: {
            accessToken: accessToken,
            userinfo: {
                username: userinfo?.name,
                email: userinfo?.email,
                uid: curruid
            }
        },
    };

    const secret = 'EMRPRoDucTDaTa';
    let token = null;
    try {
        window.Buffer = Buffer;
        token = jwt.encode(payload, secret, 'HS512')
    } catch (error) {
        console.error('Error encoding JWT:', error);
        return null;
    }
    return token;
}