import { BACKEND_API_URL, REACT_APP_BACKEND_API_HEADER } from "../config";
import ApiInstance from './ApiInstance';

export class BUService {
  getAllBuList = () => {
    return ApiInstance
      .get(
        `${BACKEND_API_URL}/bu/getBusinessUnit`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getAllBuList", e);
        return [];
      });
  };

  getBusinessUnitbyid = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/bu/getBusinessUnitbyid`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getBusinessUnitbyid", e);
        return [];
      });
  };
  addnewBU = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/bu/addBusinessUnit`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("addnewBU", e);
        return [];
      });
  };

  updateBU = (data) => {
    return ApiInstance
      .put(
        `${BACKEND_API_URL}/bu/updateBusinessUnit`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("updateBU", e);
        return [];
      });
  };

  deleteBU = (ids) => {
    return ApiInstance
      .delete(
        `${BACKEND_API_URL}/bu/deleteBusinessUnit`,
        { data: { ids } },
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error("Error deleting bu:", error);
        throw error;
      });
  };
}
