import React, { useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import getBaseUrl from "app/utils/getBaseUrl";
import Button from '@mui/material/Button';
import { UserService } from "services/usersService";
import { useOktaAuth } from '@okta/okta-react';
const UnauthorizedPage = () => {
    const userService = new UserService();
    const { authState, oktaAuth } = useOktaAuth();
    const navigate = useNavigate();
    const location = useLocation();
    const userInfo = location.state?.userInfo !== undefined ? location.state?.userInfo : false;
    const NotAuthorized = location.state?.NotAuthorized !== undefined ? location.state?.NotAuthorized : false;
    console.log(location.state?.NotAuthorized, 'location.state?.NotAuthorized')
    console.log(NotAuthorized, 'NotAuthorized')
    const [requestforAccess, setRequestforAccess] = useState(false);
    const requestAccess = () => {
        if (!requestforAccess) {
            userService.requestForAccess({ userData: userInfo }).then((result) => {
                setRequestforAccess(true);
                // setIsLoading(false)
                setTimeout(() => {
                    logout()
                }, 2000);
            });
        }
    }
    const logout = async () => {
        try {
            if (authState.isAuthenticated) {
                // await oktaAuth.signOut();
                await oktaAuth.signOut({
                    // This needs to be changed here
                    postLogoutRedirectUri: window.location.origin,
                });
            }
            // handle successful authentication here
        } catch (error) {
            // handle error appropriately, e.g., display error message or redirect to login page
        }
    };
    return (
        <div style={styles.container}>
            <h1 style={styles.title}>Access Denied</h1>
            {!NotAuthorized ?
                <><p style={styles.message}>Sorry, you don't have permission to access the page.</p>
                    {/* You can optionally add a button to redirect users to another page */}
                    <Button variant="contained" onClick={() => navigate('/calist')}>Go to Home</Button>
                    {/* <button onClick={() => navigate(getBaseUrl() + '/calist')} style={styles.button}>Go to Home</button> */}
                </>
                :
                <>
                    <p style={styles.message}>Sorry, you don't have permission to access the application.</p>
                    <p style={styles.message}>To access the application Please request by click on the button below.</p>
                    <Button variant="contained" onClick={requestAccess}>Request Access</Button>
                </>
            }
        </div>
    );
};

const styles = {
    container: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        height: '100vh',
        backgroundColor: '#f8f9fa',
    },
    title: {
        fontSize: '2rem',
        color: '#333',
    },
    message: {
        fontSize: '1rem',
        color: '#666',
        textAlign: 'center',
        marginTop: '10px',
    },
    // Optional button styles
    button: {
        marginTop: '20px',
        padding: '10px 20px',
        fontSize: '1rem',
        backgroundColor: '#007bff',
        color: '#fff',
        border: 'none',
        borderRadius: '5px',
        cursor: 'pointer',
        transition: 'background-color 0.3s ease',
        textDecoration: 'none',
        outline: 'none',
        ':hover': {
            backgroundColor: '#0056b3',
        },
    },
};

export default UnauthorizedPage;
