import {
  GET_BU_AND_INSTANSE,
} from '../actions/GlobalBUandInstanceAction';

const initialState = {
  bu: '',
  instance: '',
  systemName: ""
};

const GlobalBUandInstanceReducer = function (state = initialState, action) {
  switch (action.type) {
    case GET_BU_AND_INSTANSE: {
      return { ...state, ...action.payload };
    }
    default: {
      return { ...state };
    }
  }
};

export default GlobalBUandInstanceReducer;