import React from 'react';
import PrivateRoute from "app/privateRoutes";
import CreateSourceList from "./createSourceList";
import EditSourceList from "./editSourceList";
import SourceList from "./sourceList";

const sourceListRoutes = [
  {
    path: "/sourceList",
    element: (
      <PrivateRoute>
        <SourceList />
      </PrivateRoute>
    )
  },
  {
    path: "/createSourceList",
    element: (
      <PrivateRoute>
        <CreateSourceList />
      </PrivateRoute>
    )
  },
  {
    path: "/editSourceList/:sourceId",
    element: (
      <PrivateRoute>
        <EditSourceList />
      </PrivateRoute>
    )
  },
];

export default sourceListRoutes;
