import {
  GET_CURRENT_INSTANCE,
} from '../actions/GlobalDataAction';

import { getBasename } from "app/utils/baseurl";

const baseValues = getBasename();

const initialState = {
  segment: baseValues?.segment || {},
  instanceType: baseValues?.instanceType || {},
};

const GlobalDataReducer = function (state = initialState, action) {
  switch (action.type) {
    case GET_CURRENT_INSTANCE: {
      return { ...state };
    }
    default: {
      return { ...state };
    }
  }
};

export default GlobalDataReducer;
