import React, { useEffect } from "react";
import { useRoutes } from "react-router-dom";

import AuthGuard from "app/auth/AuthGuard";
import NotFound from "app/views/sessions/NotFound";
import UnauthorizedPage from "app/unAuthorizedPage";
import Homepage from "app/homepage";
import McoListRoutes from "app/views/mcolist/McoListRoutes";

import MatxLayout from "./components/MatxLayout/MatxLayout";
import templateRoutes from "app/views/template/templateRoutes";
import plantRoutes from "app/views/plants/plantRoutes";
import sourceListroutes from "app/views/sourceList/sourceListroutes";
import mappingsroutes from "app/views/mappings/mappingsroutes";
import buListRoutes from "app/views/businessUnit/buRoutes";
import userListRoutes from "app/views/users/usersRoutes";

//Mapping pages
import orgallowedroutes from "app/views/mappingPages/orgAllowedTemplates/orgallowedroutes";
import mvocompatibilityRoutes from "app/views/mappingPages/mvoTemplateCompatibility/mvocompatibiltyroutes";
import parttypetotemplateroutes from "app/views/mappingPages/partTypeToTemplate/parttypeTotemplateroutes";
import mvoallowedchangesroutes from "app/views/mappingPages/mvoAllowedChanges/mvoallowedchangesroutes";
import setcomptemproutes from "app/views/mappingPages/setCompatibleTemplates/setcomptemproutes";
import buplantroutes from "app/views/mappingPages/buPlantMapping/buplantRoutes";
import expensemaproutes from "app/views/mappingPages/expenseMapping/expensemapRoutes";

import draftChangesroutes from "app/views/draftChanges/draftChangesroutes";
import historyroutes from "app/views/history/historyroutes";

import systemSetupRoutes from "app/views/systemSetup/systemSetupRoutes";

import RedirectRoute from "./RedirectRoute";
import CustomLoginCallback from "./CustomLoginCallback";

const allChildroutes = [
  ...McoListRoutes,
  ...templateRoutes,
  ...plantRoutes,
  ...sourceListroutes,
  ...mappingsroutes,
  ...buListRoutes,
  ...mvocompatibilityRoutes,
  ...parttypetotemplateroutes,
  ...mvoallowedchangesroutes,
  ...setcomptemproutes,
  ...orgallowedroutes,
  ...buplantroutes,
  ...expensemaproutes,
  ...draftChangesroutes,
  ...historyroutes,
  ...userListRoutes,
  ...systemSetupRoutes,
];

export default function Router() {
  useEffect(() => {
    console.log(" [Router] Routing loaded.");
  }, []);
  const routes = [
    {
      element: (
        <>
          <AuthGuard>
            <MatxLayout />
          </AuthGuard>
        </>
      ),
      children: allChildroutes,
    },
    {
      path: "/",
      element: <RedirectRoute />,
    },
    {
      path: "/login/callback",
      element: <CustomLoginCallback />,
    },
    {
      path: "/accessDenied",
      element: <UnauthorizedPage />,
    },
    {
      path: "/homepage",
      element: <Homepage />,
    },
    {
      path: "*",
      element: <NotFound />,
    },
  ];

  let element = useRoutes(routes);
  return element;
}
