import { Breadcrumb, SimpleCard } from "app/components";
import React, { useEffect, useState, useRef, useCallback } from "react";
import Button from "@mui/material/Button";
import { Link as RouterLink, useNavigate } from "react-router-dom";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import { CircularProgress } from "@mui/material";
import CustomAlertBox from "app/components/CustomAlertBox";
import {
  Box,
  styled,
  Grid,
  TextField,
  Tooltip
} from "@mui/material";

import { SourceListService } from "services/sourceListService";
import DataGrid from "@inovua/reactdatagrid-community";
import ExportDialogBox from "./exportSource";
import "@inovua/reactdatagrid-community/index.css";
import sortedExportDataHOC from "app/components/sortedExportDataHOC";
import { sortDataByKey } from "app/utils/mcoUtils/commonUtils";

const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));

const SourceList = () => {
  const [selectedSource, setselectedSource] = useState();
  const [sourceData, setsourceData] = useState([]);
  const [initialData, setInitialData] = useState([]);
  const [searchValue, setSearchValue] = useState("");
  const [deleteDialog, setDeleteDialog] = useState(false);
  const sourceListService = new SourceListService();
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [sortInfo, setSortInfo] = useState({}); // Add this line
  const [unSortSourceData, setUnSortSourceData] = useState([])
  // ==============table===========
  const [selected, setSelected] = useState({});
  const [selectedRows, setSelectedRows] = useState([]);
  const gridRef = useRef(null);
  const dataMap = gridRef && gridRef.current && gridRef.current.dataMap ? gridRef.current.dataMap : null;

  const onSelectionChange = useCallback(({ selected }) => {
    setSelected(selected);
  }, []);

  const [isLoading, setIsLoading] = useState(true);
  const handleDeleteDialog = () => {
    handleDeleteSourceDilaog(selectedRows[0])
    setDeleteDialog(true);
    // Perform delete operation here using the rowId or rowData
  };
  const handleEditMappingDialog = () => {
    handleEditSource(selectedRows[0]);
  };
  // ==============table===========
  useEffect(() => {
    setIsLoading(true);
    sourceListService.getAllSourceList()
      .then((result) => {
        setsourceData(result);
        setInitialData(result); // Set initial data
        setFilteredSourceData(result);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }, []);

  const [filteredSourceData, setFilteredSourceData] = useState([]);

  useEffect(() => {
    setFilteredSourceData(sourceData);
  }, [sourceData]);

  let sortedSourceData = sortDataByKey([...filteredSourceData], "source");

  const handleSearchInputChange = (event) => {
    const newSearchValue = event.target.value;
    setSearchValue(newSearchValue);
  };
  const handleSearch = () => {
  };
  const navigate = useNavigate();
  const plantModeRef = useRef("initialValue");
  const handleCreateSourceList = () => {
    plantModeRef.current = "create";
    setTimeout(() => {
      navigate("/createSourceList", {
        state: { plantMode: plantModeRef.current },
      });
    }, 200);
  };

  const handleEditSource = (editSelectedSource) => {
    plantModeRef.current = "edit";
    const selectedSourceData = editSelectedSource;
    setTimeout(() => {
      navigate(`/editSourceList/${selectedSourceData?.id}`, {
        state: { plantMode: plantModeRef.current, selectedSourceData },
      });
    }, 200);
  };

  const handleDeleteSourceDilaog = (DeleteSource) => {
    setDeleteDialog(true);
    setselectedSource(DeleteSource);
  };
  const handleCloseDelete = () => {
    setDeleteDialog(false);
  };

  const handleDeleteSource = async () => {
    setDeleteDialog(false);

    try {
      const selRowsId = selectedRows?.map(item => (item?.id));
      await sourceListService.deleteSource(selRowsId).then(result => {
        setSelected({});
        setSelectedRows([]);
        setOpenAlert(true);
        setOpenDesc({
          severity: result?.status,
          desc: result?.msg,
        });
      });
    } catch (error) {
      return error;
    }
    sourceListService.getAllSourceList().then((result) => {
      setsourceData(result);
    });
  };

  useEffect(() => {
    const selRowsData = selected === true ? dataMap : selected;
    const selRowsDetails = (selRowsData) && Object.keys(selRowsData).map(key => (selRowsData[key]));
    setSelectedRows(selRowsDetails);
  }, [selected, dataMap]);

  useEffect(() => {
    const new_FilteredData = sourceData.filter((subscriber) => {
      return (searchValue === "" ||
        subscriber.source
          .toLowerCase()
          .includes(searchValue.toLowerCase()) ||
        subscriber.environment
          .toLowerCase()
          .includes(searchValue.toLowerCase()) ||
        subscriber.instance
          .toLowerCase()
          .includes(searchValue.toLowerCase()) ||
        subscriber.ERPType.toLowerCase().includes(
          searchValue.toLowerCase()
        ))
    })

    setFilteredSourceData(new_FilteredData);
  }, [searchValue, sourceData]);

  const handleSortInfoChange = (sortInfo) => {
    setSortInfo(sortInfo);
    const SortedSource = sortedExportDataHOC(SourceList);
    const sortedData = SortedSource({ data: sourceData, sortInfo, sortedSourceData, onSortInfoChange: handleSortInfoChange, initialData });
    setsourceData(sortedData.props.data);
    setFilteredSourceData(sortedData.props.data);
  };

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Source" }]} />
      </Box>

      <SimpleCard>
        <Grid container spacing={1} alignItems="center">
          <Grid item md={3} sm={6} xs={12} className="font-medium">
            <TextField
              type="text"
              name="search"
              size="small"
              onChange={handleSearchInputChange}
              value={searchValue}
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid item container md={9} sm={6} xs={12} alignItems="center">
            <Button variant="contained" onClick={handleSearch}>
              Search
            </Button>
            <div style={{ flex: 1 }} />
            <Button
              variant="contained"
              component={RouterLink}
              onClick={handleCreateSourceList}
            >
              Create
            </Button>
            <Button
              variant="contained"
              disabled={selectedRows?.length === 1 ? false : true}
              onClick={handleEditMappingDialog}
              style={{ margin: '0 0px 0 10px' }}
            >
              Edit
            </Button>
            <Button
              variant="contained"
              disabled={selectedRows?.length ? false : true}
              onClick={handleDeleteDialog}
              style={{ margin: '0 10px 0 10px' }}
            >
              Delete
            </Button>
            <ExportDialogBox sourceData={sourceData} filterTemplateData={selectedRows} />
          </Grid>
          <Box width="100%" overflow="auto">
            <br></br>
            {isLoading ? (
              // Display loading spinner while data is being fetched
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  minHeight: 550
                }}
              >
                <CircularProgress />
              </Box>
            ) : (
              // Render DataGrid once data is loaded
              <DataGrid
                idProperty="id"
                showZebraRows={false}
                style={{ minHeight: 550 }}
                defaultLimit={100}
                columns={[
                  {
                    name: "source",
                    header: <Tooltip title={'Name'}>Source</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                    render: ({ data }) => <div>{data?.source}</div>,
                  },
                  {
                    name: "instance",
                    header: <Tooltip title={"Instance"}>Instance</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                  },
                  {
                    name: "environment",
                    header: <Tooltip title={"Environment"}>Environment</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                  },
                  {
                    name: "ERPType",
                    header: <Tooltip title={"ERP Type"}>ERP Type</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                    render: ({ data }) => <div>{data?.ERPType}</div>,
                  }
                ]}
                dataSource={sortInfo==null ? filteredSourceData : sortedSourceData}
                checkboxColumn
                selected={selected}
                onSelectionChange={onSelectionChange}
                handle={ref => gridRef.current = ref ? ref.current : null}
                pagination
                renderScroller={() => gridRef?.current?.setScrollTop(selectedRows?.length == 0 ? 0 : gridRef?.current?.getScrollTop())}
                onSortInfoChange={handleSortInfoChange}
              />
            )}
          </Box>
        </Grid>
      </SimpleCard>
      {(openDesc) ? (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      ) : (<></>)}
      <Dialog open={deleteDialog}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Delete Source
        </DialogTitle>
        <DialogContent>
          {
            (selectedRows.length === 1) ? (<>
              <DialogContentText>
                Do you want to delete the source <b>{selectedSource?.source}</b>{" "}
                having instance <b>{selectedSource?.instance}</b> and ERPType{" "}
                <b>{selectedSource?.ERPType}</b>
              </DialogContentText>
            </>) : (<>
              <DialogContentText>
                Do you want to delete the seleted sources?
              </DialogContentText>
            </>)
          }
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCloseDelete}>
            Cancel
          </Button>
          <Button onClick={handleDeleteSource}>delete</Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default SourceList;