import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Breadcrumb, SimpleCard } from "app/components";
import { Box, styled, Grid, TextField, Select, MenuItem } from "@mui/material";
import { styled as makeStyles } from '@mui/material/styles';
import { ERPtypeOptions } from "../adminConfig/common";
import Button from "@mui/material/Button";
import { SourceListService } from "services/sourceListService";
import CustomAlertBox from "app/components/CustomAlertBox";
const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));
const useStyles = makeStyles({
  spanRed: {
    color: "#FF3D57",
  },
});

export default function CreateSourceList() {
  const [sourceForm, setSourceForm] = useState({});
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const classes = useStyles();
  let navigate = useNavigate();
  const sourceListService = new SourceListService();
  const handleAddSource = () => {
    if (
      sourceForm.source?.trim() === "" ||
      !sourceForm.source ||
      sourceForm.environment?.trim() === "" ||
      !sourceForm.environment ||
      sourceForm.instance?.trim() === "" ||
      !sourceForm.instance ||
      sourceForm.ERPType?.trim() === "" ||
      !sourceForm.ERPType
    ) {
      handlesnackBarClick("Please fill the require details", "error");
      return;
    }

    const newSource = {
      source: sourceForm.source,
      environment: sourceForm.environment,
      instance: sourceForm.instance,
      ERPType: sourceForm.ERPType,
    };
    sourceListService.addnewSource(newSource).then((resp) => {
      if (resp?.data?.id) {
        handlesnackBarClick("Source added successfully!", "success");

        setTimeout(() => {
          navigate("/sourceList");
        }, 2000);
      } else if (resp?.data[0]?.msg) {
        //console.log(resp?.data[0]?.msg, "res.msg");

        handlesnackBarClick(resp.data[0].msg, "error");
      }
    });
  };

  const handleSourceFormChange = (event) => {
    const { name, value } = event.target;
    const updatedForm = {
      ...sourceForm,
      [name]: value,
    };
    setSourceForm(updatedForm);
  };

  const handlesnackBarClick = (message, severity) => {
    setOpenAlert(true);
    setOpenDesc({
      severity: severity,
      desc: message,
    });
  };

  const handleCancel = () => {
    setTimeout(() => {
      navigate(-1)
    }, 200);
  }
  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Create Source" }]} />
      </Box>

      <form>
        <SimpleCard>
          <Grid item md={9} sm={6} xs={6}>
            <h4>Create Source</h4>
            <Grid container spacing={3} alignItems="center">
            <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Source <span className={classes.spanRed}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="source"
                  value={sourceForm.source}
                  onChange={handleSourceFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Instance <span className={classes.spanRed}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="instance"
                  value={sourceForm.instance}
                  onChange={handleSourceFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Environment <span className={classes.spanRed}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="environment"
                  value={sourceForm.environment}
                  onChange={handleSourceFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>ERP Type </span>{" "}
                <span className={classes.spanRed}>*</span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Select
                  labelId="ERPType-label"
                  id="ERPType-select"
                  name="ERPType"
                  fullWidth
                  value={sourceForm.ERPType}
                  onChange={handleSourceFormChange}
                >
                  {ERPtypeOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>
            </Grid>
            <br />
            <Grid container spacing={3} alignItems="center">
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  <Button
                    variant="contained"
                    color="primary"
                    margin="2px"
                    onClick={handleAddSource}
                  >
                    Submit
                  </Button>
                </span>
                {/* <span className={classes.spanRed}>*</span> */}
              </Grid>
              <Grid item md={2} sm={4} xs={6} className="font-medium">
                <span>
                  <Button
                    variant="contained"
                    color="primary"
                    margin="2px"
                    onClick={handleCancel}
                  >
                    Cancel
                  </Button>
                </span>
              </Grid>
            </Grid>
          </Grid>
        </SimpleCard>
        {(openDesc) ? (
          <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
        ) : (<></>)}
      </form>
    </Container>
  );
}
