import React, { useEffect, useState, useRef, useCallback } from "react";
import Button from "@mui/material/Button";
import { Link as RouterLink, useLocation } from "react-router-dom";
import Loader from 'app/components/Loader';
import { Breadcrumb } from "app/components";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import {
  Box,
  styled,
  Grid,
  TextField,
} from "@mui/material";

import { SystemSettingsService } from "services/systemSettingsService"; // Change UserService to SystemSetupService
import CustomAlertBox from "app/components/CustomAlertBox";
import DataGrid from "@inovua/reactdatagrid-community";
import ExportDialogBox from "./exportSetup";
import "@inovua/reactdatagrid-community/index.css";
import SystemSetupForm from "./systemSetupForm";
import sortedExportDataHOC from "app/components/sortedExportDataHOC";
import { sortDataByKey } from "app/utils/mcoUtils/commonUtils";

const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));

const SystemSetup = () => {
  const childCompRef = useRef(null);
  const [showForm, setShowForm] = useState(false);
  const [showFormType, setShowFormType] = useState("Create");
  const [formValues, setFormValues] = useState(null);
  const [systemData, setSystemData] = useState([]); // Change userData to systemData
  const [orgSystemData, setOrgSystemData] = useState([]); // Change userData to systemData
  const [searchValue, setSearchValue] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [deleteDialog, setDeleteDialog] = useState(false);
  const systemSettingsService = new SystemSettingsService(); // Change userService to systemSettingsService
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [selected, setSelected] = useState({});
  const [selectedRows, setSelectedRows] = useState([]);
  const [initialData, setInitialData] = useState([]);
  const [sortInfo, setSortInfo] = useState([]);
  const gridRef = useRef(null);
  const dataMap = gridRef && gridRef.current && gridRef.current.dataMap ? gridRef.current.dataMap : null;
  const onSelectionChange = useCallback(({ selected }) => {
    setSelected(selected);
  }, []);

const sortedSystemData = systemData.sort((a, b) => {
  if (a.ERPType < b.ERPType) return -1;
  if (a.ERPType > b.ERPType) return 1;
  return 0;
});

  const columns = [ // Adjust columns according to your system setup data
    {
      name: "ERPType",
      header: "ERP Type",
      defaultFlex: 1,
      defaultWidth: 50,
      render: ({ data }) => <div>{data?.ERPType}</div>,
    },
    {
      name: "instance",
      header: "Instance",
      defaultFlex: 1,
      defaultWidth: 50,
      render: ({ data }) => <div>{data?.instance}</div>,
    },
    {
      name: "file1",
      header: "Validation Key 1",
      defaultFlex: 1,
      defaultWidth: 50,
      render: ({ data }) => <div>{data?.file1}</div>,
    },
    {
      name: "file2",
      header: "Validation Key 2",
      defaultFlex: 1,
      defaultWidth: 50,
      render: ({ data }) => <div>{data?.file2}</div>,
    },
  ]

  const handleSearchInputChange = (event) => {
    const newSearchValue = event.target.value;
    setSearchValue(newSearchValue);
  };

  const handleDeleteDialog = () => {
    setDeleteDialog(true);
  };

  const handleCloseDelete = () => {
    setDeleteDialog(false);
  };

  const handleDeleteSetting = async () => {
    setDeleteDialog(false);
    try {
      setIsLoading(true)
      const selRowsId = selectedRows?.map(item => (item?.id));
      await systemSettingsService.deleteSystemSettings(selRowsId).then(response => {
        setIsLoading(false)
        setOpenAlert(true);
        setOpenDesc({
          severity: response?.status,
          desc: response?.msg,
        });
        setSelected({});
        setSelectedRows([]);
      });
    } catch (error) {
      console.error("Failed to delete Setting:", error);
    }

    systemSettingsService.getAllsystemSetupList().then((result) => {
      console.log(result.data, "result.data");
      setSystemData(result.data);
      setInitialData(result.data);
      setOrgSystemData(result.data);
    });
  };

  // Function to trigger the form submission in the child component
  const saveForm = async (submittedValues) => {
    console.log(submittedValues, 'submittedValues 222');


    if (submittedValues) {
      // Update the formValues state only if the submission is successful
      if (submittedValues?.formisValid) {
        setShowForm(false);
        setIsLoading(true);
        setFormValues(submittedValues);
        let updatedData = null;
        if (showFormType === "Edit" && selectedRows?.length) {
          updatedData = await systemSettingsService.updateSystemSettings(submittedValues?.formData);
        } else {
          updatedData = await systemSettingsService.addSystemSettings(submittedValues?.formData);
        }
        if (updatedData?.status === "success") {
          setSelected({});
          setSelectedRows([]);
          await systemSettingsService.getAllsystemSetupList().then((result) => {
            console.log(result.data, "result.data");
            setSystemData(result.data);
            setInitialData(result.data);
            setOrgSystemData(result.data);
          });

          setOpenAlert(true);
          setOpenDesc({
            severity: "success",
            desc: updatedData?.msg,
          });
        } else {
          setOpenAlert(true);
          setOpenDesc({
            severity: "error",
            desc: updatedData?.msg,
          });
        }
      }
    }
    setIsLoading(false);
  }
  const handleSubmitForm = async () => {
    if (childCompRef.current) {
      // Call the submitForm function exposed by the child component
      await childCompRef.current.submitForm();
    }
  };

  const fetchAllDetails = async () => {
    const listValue = await systemSettingsService.getAllsystemSetupList();
    console.log(listValue, 'listValue');
    if (listValue?.data?.length) {
      setSystemData(listValue?.data);
      setInitialData(listValue?.data);
      setOrgSystemData(listValue?.data);
      return listValue?.data;
    } else {
      return [];
    }
  }
  const applyFilterData = async (searchValue) => {
    let systemDataValue = [];
    setIsLoading(true);
    if (orgSystemData.length === 0) {
      systemDataValue = await fetchAllDetails();
    } else {
      systemDataValue = orgSystemData;
    }
    if (systemDataValue) {
      const newFilteredData = systemDataValue.filter((setting) => {
        return (!searchValue ? true : (JSON.stringify(setting).toLowerCase().includes(searchValue.toLowerCase())))
      }) || [];
      if (newFilteredData.length) {
        setSystemData(newFilteredData);
        setInitialData(newFilteredData);
      } else {
        setSystemData([]);
      }
    }
    setIsLoading(false);
  }

  const handleBackdropClick = (event) => {
    // Prevent closing the dialog when clicking on the backdrop
    event.stopPropagation();
  };


  useEffect(() => {
    fetchAllDetails();
  }, []);

  useEffect(() => {
    const selRowsData = selected === true ? dataMap : selected;
    const selRowsDetails = (selRowsData) && (Object.keys(selRowsData)?.map(key => (selRowsData[key])) || []);
    if (selRowsDetails?.length) {
      setSelectedRows(selRowsDetails);
    }
  }, [selected]);

  useEffect(() => {
    console.log(searchValue, 'parent comp 2');
    applyFilterData(searchValue);
  }, [searchValue]);

  console.log('parent comp');

  const handleSortInfoChange = (sortInfo) => {
    setSortInfo(sortInfo);
    const SortedUserData = sortedExportDataHOC(SystemSetup);
    const sortedData = SortedUserData({ data: systemData, sortInfo, onSortInfoChange: handleSortInfoChange, initialData });
    setSystemData(sortedData.props.data);
  };

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "System Setup" }]} />
      </Box>

      <div>
        <Grid container spacing={1} alignItems="center">
          <Grid item md={3} sm={6} xs={12} className="font-medium">
            <TextField
              type="text"
              name="search"
              size="small"
              onChange={handleSearchInputChange}
              value={searchValue}
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid item container md={9} sm={6} xs={12} alignItems="center">
            <Button variant="contained">
              Search
            </Button>
            <div style={{ flex: 1 }} />
            <Button
              variant="contained"
              component={RouterLink}
              onClick={() => {
                setShowForm(true);
                setShowFormType("Create");
              }}
            >
              Create
            </Button>
            <Button
              variant="contained"
              disabled={selectedRows?.length === 1 ? false : true}
              onClick={() => {
                setShowForm(true);
                setShowFormType("Edit");
              }}
              style={{ margin: '0 0px 0 10px' }}
            >
              Edit
            </Button>
            <Button
              variant="contained"
              disabled={selectedRows?.length ? false : true}
              onClick={handleDeleteDialog}
              style={{ margin: '0 10px 0 10px' }}
            >
              Delete
            </Button>
            <ExportDialogBox 
             systemData={systemData}
             filterTemplateData={selectedRows} />
          </Grid>
        </Grid>

        <br />

        {isLoading ? (
          <div style={{ textAlign: 'center' }}>
            <Loader show={isLoading} />
          </div>
        ) : (
          <DataGrid
            idProperty="id"
            showZebraRows={false}
            style={{ minHeight: 550 }}
            columns={columns}
            defaultLimit={100}
              dataSource={sortInfo === null ? systemData : sortedSystemData}
            // rowHeight={null}
            checkboxColumn
            selected={selected}
            onSelectionChange={onSelectionChange}
            handle={ref => gridRef.current = ref ? ref.current : null}
            pagination
            renderScroller={()=> gridRef?.current?.setScrollTop(selectedRows?.length==0 ? 0 : gridRef?.current?.getScrollTop())}
            onSortInfoChange={handleSortInfoChange}
            />
        )}
      </div>

      {openDesc && (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      )}

      <Dialog open={deleteDialog}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Delete Setting
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            Do you want to delete the selected Setting(s)?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCloseDelete}>
            Cancel
          </Button>
          <Button onClick={handleDeleteSetting}>Delete</Button>
        </DialogActions>
      </Dialog>

      {/* MODAL BOX */}
      <Dialog
        open={showForm}
        onClose={() => { setShowForm(false) }}
        scroll={'paper'}
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
        maxWidth={'md'}
        fullWidth={true}
        onClick={handleBackdropClick}
      >
        <DialogTitle id="scroll-dialog-title">
          {`${showFormType} system setup`}
        </DialogTitle>
        <DialogContent dividers={true}>
          <DialogContentText id="scroll-dialog-description" tabIndex={-1}>
            {/* Display the form values from the state if they exist */}
            {/* {formValues && (
              <div>
                <h2>Form Values:</h2>
                <pre>{JSON.stringify(formValues, null, 2)}</pre>
              </div>
            )} */}
            <SystemSetupForm ref={childCompRef} saveForm={saveForm} selectedRows={selectedRows} editForm={showFormType} />
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => { setShowForm(false) }}>Cancel</Button>
          { }
          <Button onClick={handleSubmitForm}>Save</Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default SystemSetup;
