// export default App;

import React, { useEffect } from "react";
import { Provider } from "react-redux";
import { HashRouter, useNavigate } from "react-router-dom";
import { OktaAuth, toRelativeUrl } from "@okta/okta-auth-js";
import { Security } from "@okta/okta-react"; // Import useOktaAuth
import config from "./../config/config";
import { Store } from "./redux/Store";
import Router from "./routes";
import { GlobalCss, MatxSuspense, MatxTheme } from "app/components";
import { ErrorBoundary } from "react-error-boundary";
import Fallback from "./components/Fallback/Fallback";
import { OracleMappingsProvider } from "./contexts/OracleSettingsContext";
import { SettingsProvider } from "./contexts/SettingsContext";
import { createRoot } from "react-dom/client";
import { StyledEngineProvider } from "@mui/system";
import { CssBaseline } from "@mui/material";

const oktaAuth = new OktaAuth(config.oidc);
oktaAuth.options.transformAuthState = async (oktaAuth, authState) => {
  console.log(" [OKTA] transformAuthState called");
  console.log("AuthState:", JSON.stringify(authState, null, 2));
};

oktaAuth.options.devMode = true; // This turns on some internal dev logging
console.log(" Okta Auth Config:", JSON.stringify(config.oidc, null, 2));
console.log(" oktaAuth instance:", oktaAuth);

const App = () => {
  const navigate = useNavigate();
  const restoreOriginalUri = (oktaAuth, originalUri) => {
    console.log(" [OKTA] restoreOriginalUri fired");
    console.log(" originalUri:", originalUri);
    console.log(" window.location.origin:", window.location.origin);
    // const relativeUrl = toRelativeUrl(originalUri || "/", window.location.origin);
    // console.log(" Redirecting to relativeUrl:", relativeUrl);
  
    navigate("/calist");
  };
  const onAuthRequired = function () {
    console.warn(" [OKTA] onAuthRequired triggered. User is unauthenticated.");
    console.log(" Redirecting to /calist (MemoryRouter safe)");
    navigate("/calist");
  };
  // if (!sessionStorage.getItem("original_uwaUrl")) {
  //   const currentUrl = window.location.href;
  //   console.log(" Initial Widget URL:", currentUrl);
  //   sessionStorage.setItem("original_uwaUrl", currentUrl);
  // }

  useEffect(() => {
    console.log(" App mounted.");
    console.log(" Current Hash:", window.location.hash);
    console.log(" Current URL:", window.location.href);
    console.log(" Process Env:", process.env.NODE_ENV);

    const timer = setTimeout(() => {
      const performanceMarkName = "FirstContentfulPaint";
      performance.mark(performanceMarkName);
    }, 1000);

    console.log(process.env.NODE_ENV, "process.env.NODE_ENV");

    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    console.log(" App mounted.");
    console.log(" window.location.hash:", window.location.hash);
  }, []);

  return (
    <ErrorBoundary
      FallbackComponent={Fallback}
      onReset={(details) => {
        // Reset the state of your app so the error doesn't happen againn
      }}
    >
      <Provider store={Store}>
        <SettingsProvider>
          <MatxTheme>
            <GlobalCss />
            <MatxSuspense>
              <Security
                oktaAuth={oktaAuth} // Use authService instead of creating a new instance
                restoreOriginalUri={restoreOriginalUri}
                onAuthRequired={onAuthRequired}
              >
                <OracleMappingsProvider>
                  <Router />
                </OracleMappingsProvider>
              </Security>
            </MatxSuspense>
          </MatxTheme>
        </SettingsProvider>
      </Provider>
    </ErrorBoundary>
  );
};

export default App;
