import React, { useEffect, useState, useRef } from "react";

import Button from "@mui/material/Button";

import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";

import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import CloudDownloadIcon from '@mui/icons-material/CloudDownload';

import {
    Grid,
    TextField,
    Table,
    TableBody,
    TableCell,
    TableHead,
    TablePagination,
    TableRow,
    Box,
    styled,
    InputLabel,
    Autocomplete,
    Select,
    MenuItem
} from "@mui/material";


import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";

import { templateService } from "services/temlplateService";
import useOracleSettings from "app/hooks/useOracleSettings";
import CustomAlertBox from "app/components/CustomAlertBox";
// import * as sampleTemplate from '../../../assets/sample-template.xlsx';

import download from 'downloadjs';
import {
    instanceOptions,
    ERPtypeOptions,
    existInOracleOptions,
    exportToERPOptions,
    notOwningDivisionOptions,
    BUOptions,
} from "../adminConfig/common";
import { textAlign } from "@mui/system";
const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const StyledTable = styled(Table)(() => ({
    whiteSpace: "pre",
    "& thead": {
        "& tr": { "& th": { paddingLeft: 0, paddingRight: 0 } },
    },
    "& tbody": {
        "& tr": { "& td": { paddingLeft: 0, textTransform: "capitalize" } },
    },
}));

const VisuallyHiddenInput = styled('input')({
    clip: 'rect(0 0 0 0)',
    clipPath: 'inset(50%)',
    height: 1,
    overflow: 'hidden',
    position: 'absolute',
    bottom: 0,
    left: 0,
    whiteSpace: 'nowrap',
    width: 1,
});

const ImportDialogBox = (props) => {
    const { allTemplatesInfo, updateAllTemplate, bussinessUnitLists } = useOracleSettings();

    const tempService = new templateService();
    const { importTemplate, setImportTemplate, setTemplateData, setIsLoading } = props;
    const [ERPTypeError, setERPTypeError] = useState(false);
    const [instanceError, setinstanceError] = useState(false);
    const [BUError, setBUError] = useState(false);
    const [noFileError, setNoFileError] = useState(false);
    const [importResponse, setImportResponse] = useState(false);

    const [importfileBar, setimportfileBar] = useState(false);
    const [importedFileData, setImportedFileData] = useState(null)
    const [importedFileName, setImportedFileName] = useState(null)

    const [importSelectionValues, setImportSelectionValues] = useState({
        ERPType: [],
        instance: [],
        BU: [],
    });
    const [openAlert, setOpenAlert] = useState(false);
    const [openDesc, setOpenDesc] = useState(null);
    const fileName = 'sample-template.xlsx';
    const handleDownload = () => {
        tempService.downloadSampleFile(fileName)
    };

    const handleImportTemplateSelection = (column, selectedValues) => {
        console.log(column, '==values ==', selectedValues)
        setImportSelectionValues((prevselectedValues) => {
            return {
                ...prevselectedValues,
                [column]: column === 'BU' ? selectedValues : selectedValues.value,
            };
        })
        if (column === 'ERPType') { setERPTypeError(false) }
        if (column === 'instance') { setinstanceError(false) }
        if (column === 'BU') { setBUError(false) }
        console.info(importSelectionValues, 'importSelectionValues')
    }

    const getUniqueValues = (column) => {
        const values = new Set();

        //console.log(allTemplatesInfo, 'allTemplatesInfo vvvvv');

        /*  allTemplatesInfo?.forEach((subscriber) => {
             if (column === "plants") {
                 subscriber[column]?.forEach((plant) => {
                     values.add(plant.plantName);
                 });
             } else if (column === "BU") {
                 subscriber[column]?.forEach((BU) => {
                     values.add(BU);
                 });
             } else {
                 values.add(subscriber[column]);
             }
         });
         return Array.from(values); */
        bussinessUnitLists?.forEach(item => {
            values.add(item[column]);
        })
        return Array.from(values);
    };

    const importFileData = (e) => {
        //  console.log(e.target.files[0].name, 'rowData')
        //  console.log(e.target.files[0].type, 'rowData')
        //  console.log(e.target.files[0].name.split('.').pop(), 'file name')
        const file = e.target.files[0];
        if (file) {
            const fileType = file.name.split('.').pop();
            if (fileType === 'xlsx' || fileType === 'xls') {
                // if (e.target.files[0].name.split('.').pop() === 'xlsx' || e.target.files[0].name.split('.').pop() === 'xls') {
                setImportedFileData(e.target.files[0]);
                setImportedFileName(e.target.files[0].name);
                setNoFileError(false);
            } else {
                setNoFileError(true);
                setImportedFileName(null);
                setImportedFileData(null);
            }
        } else {
            setNoFileError(true);
            setImportedFileName(null);
            setImportedFileData(null);
        }
    }

    const handleCloseImportTemplate = () => {
        setImportSelectionValues((prev) => {
            return {
                ERPType: [],
                instance: [],
                BU: []
            }
        })
        setImportedFileData(null)
        setImportTemplate(false)
        setImportedFileName(null)
        setNoFileError(false)
        setERPTypeError(false)
        setinstanceError(false)
        setBUError(false)
    };
    const handleImportfileBarClick = () => {
        setimportfileBar(true);
    };

    const handleImportTemplate = () => {
        if (importSelectionValues.ERPType === null || importSelectionValues.ERPType.length < 1) { setERPTypeError(true) }
        if (importSelectionValues.instance === null || importSelectionValues.instance.length < 1) { setinstanceError(true) }
        if (importSelectionValues.BU === null || importSelectionValues.BU.length < 1) { setBUError(true) }
        if (importedFileData === null || importedFileData.length < 1) { setNoFileError(true) }
        // console.log(importSelectionValues, 'importSelectionValueshandleImportTemplate')
        if (importSelectionValues.ERPType.length > 1 && importSelectionValues.instance.length > 1 && importSelectionValues.BU.length > 1 && importedFileData !== null) {
            let postData = { 'file': importedFileData, data: importSelectionValues };
            setIsLoading(true);
            setImportTemplate(false)
            tempService.importTemplate(postData).then(async (response) => {
                setImportResponse(response)
                let desc;
                let severity;
                if (response.errorCount > 0) {
                    desc = (
                        <div style={{ color: 'red' }}>
                            {/* Failure <br /> */}
                            New Template Added: {response.newTemplateAdded} <br />
                            Template Updated: {response.updateTemplates} <br />
                            Error in Template: {response.errorCount} <br />
                        </div>
                    );
                    severity = 'error';
                    setOpenAlert(true);
                    setOpenDesc({
                        severity: severity,
                        desc: desc,
                    });
                    setIsLoading(false);
                } else {
                    desc = (
                        <div>
                            Success <br />
                            New Template Added: {response.newTemplateAdded} <br />
                            Template Updated: {response.updateTemplates} <br />
                            Error in Template: {response.errorCount} <br />
                        </div>
                    );
                    severity = 'success';
                    setIsLoading(false);

                }
                setOpenAlert(true);
                setOpenDesc({
                    severity: severity,
                    desc: desc,
                });
                setImportSelectionValues((prev) => {
                    return {
                        ERPType: [],
                        instance: [],
                        BU: []
                    }
                })
                setImportedFileData(null)
                setImportedFileName(null)
                // setTimeout(() => {
                //     setImportTemplate(false)
                // }, 200);

                await tempService.getAllTemplateList().then((result) => {
                    setTemplateData(result.template);
                    updateAllTemplate(result.template);
                });
                setIsLoading(false);
            });
        }
    }
    const handleImportfileBarClose = (event, reason) => {
        if (reason === "clickaway") {
            return;
        }
        setImportSelectionValues((prev) => {
            return {
                ERPType: [],
                instance: [],
                BU: []
            }
        })
        setimportfileBar(false);
    };

    useEffect(() => {
        // console.log(allTemplatesInfo, 'allTemplatesInfo cccc');
    }, [allTemplatesInfo])

    return (
        <>
            <Dialog open={importTemplate}>
                <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
                    Import Template
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        <Box width="100%" overflow="auto">
                            <StyledTable>
                                <TableHead>
                                    <TableRow>
                                        <TableCell align="left">
                                            <InputLabel
                                                style={{
                                                    fontWeight: "bold",
                                                    color: "rgba(52, 49, 76, 1)",
                                                }}
                                            >
                                                Design Organization
                                            </InputLabel>
                                        </TableCell>
                                        <TableCell align="left">
                                            <Autocomplete
                                                style={{ width: "70%" }}
                                                multiple={false}
                                                options={getUniqueValues("buName")}
                                                value={importSelectionValues.BU}
                                                onChange={(_, newValue) =>
                                                    handleImportTemplateSelection("BU", newValue)
                                                }
                                                renderValue={() => null}
                                                renderInput={(params) => (
                                                    <TextField
                                                        {...params}
                                                        variant="outlined"
                                                        label="Select RDO"
                                                        placeholder="Search..."
                                                    />
                                                )}
                                                PaperProps={{
                                                    style: {
                                                        maxHeight: "200px",
                                                    },
                                                }}
                                                disableCloseOnSelect={false}
                                                disableClearable
                                                isOptionEqualToValue={(option, value) => option === value}
                                                getOptionSelected={(option, value) =>
                                                    importSelectionValues.BU.includes(value)
                                                }
                                                ChipProps={{
                                                    style: {
                                                        fontSize: "8px",
                                                    },
                                                }}
                                            />
                                            {BUError && (<p style={{ textAlign: "left", color: "red" }}>please select Design Organization</p>)}
                                        </TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left">
                                            <InputLabel
                                                style={{
                                                    fontWeight: "bold",
                                                    color: "rgba(52, 49, 76, 1)",
                                                }}
                                            >
                                                ERP Type
                                            </InputLabel>
                                        </TableCell>
                                        <TableCell align="right">
                                            <Autocomplete
                                                style={{ width: "70%" }}
                                                multiple={false}
                                                options={ERPtypeOptions}
                                                value={importSelectionValues.ERPType}
                                                onChange={(_, newValue) =>
                                                    handleImportTemplateSelection("ERPType", newValue)
                                                }
                                                renderValue={() => null}
                                                renderInput={(params) => (
                                                    <TextField
                                                        {...params}
                                                        variant="outlined"
                                                        label="Select ERP Type"
                                                        placeholder="Search..."
                                                    />
                                                )}
                                                PaperProps={{
                                                    style: {
                                                        maxHeight: "200px",
                                                    },
                                                }}
                                                disableCloseOnSelect={false}
                                                disableClearable
                                                isOptionEqualToValue={(option, value) => option === value}
                                                getOptionSelected={(option, value) =>
                                                    importSelectionValues.ERPType.includes(value)
                                                }
                                                ChipProps={{
                                                    style: {
                                                        fontSize: "8px",
                                                    },
                                                }}
                                            />
                                            {ERPTypeError && (<p style={{ textAlign: "left", color: "red" }}>please select ERP Type</p>)}
                                        </TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align="left">
                                            <InputLabel
                                                style={{
                                                    fontWeight: "bold",
                                                    color: "rgba(52, 49, 76, 1)",
                                                }}
                                            >
                                                Instance
                                            </InputLabel>
                                        </TableCell>
                                        <TableCell align="right">
                                            <Autocomplete
                                                style={{ width: "70%" }}
                                                multiple={false}
                                                options={instanceOptions}
                                                value={importSelectionValues.instance}
                                                onChange={(_, newValue) =>
                                                    handleImportTemplateSelection("instance", newValue)
                                                }
                                                renderValue={() => null}
                                                renderInput={(params) => (
                                                    <TextField
                                                        {...params}
                                                        variant="outlined"
                                                        label="Select Instance"
                                                        placeholder="Search..."
                                                    />
                                                )}
                                                PaperProps={{
                                                    style: {
                                                        maxHeight: "200px",
                                                    },
                                                }}
                                                disableCloseOnSelect={false}
                                                disableClearable
                                                isOptionEqualToValue={(option, value) => option === value}
                                                // getOptionLabel={(option) => option}
                                                getOptionSelected={(option, value) =>
                                                    importSelectionValues.instance.includes(value)
                                                }
                                                ChipProps={{
                                                    style: {
                                                        fontSize: "8px",
                                                    },
                                                }}
                                            />
                                            {instanceError && (<p style={{ textAlign: "left", color: "red" }}>please select Instance</p>)}
                                        </TableCell>
                                    </TableRow>

                                </TableHead>
                                <TableBody>
                                    <div style={{ paddingTop: '10px' }}>
                                        <Button component="label" variant="contained" startIcon={<CloudUploadIcon />}>
                                            Upload file
                                            <VisuallyHiddenInput type="file" name="file" accept=".xls, .xlsx" onChange={importFileData} />
                                        </Button>
                                        {noFileError && (<p style={{ textAlign: "left", color: "red" }}>Please Select xlsx file to Upload </p>)}
                                    </div>
                                    {importedFileName && (<p>{importedFileName}</p>)}
                                    <div style={{ paddingTop: '10px' }}>
                                        {/* <a
                                            href={sampleTemplate}
                                            download="Example-XLSX-document"
                                            target="_blank"
                                            rel="noreferrer"
                                        > */}
                                        <Button component="label" variant="contained" startIcon={<CloudDownloadIcon />} onClick={handleDownload}>
                                            Download sample file
                                        </Button>
                                        {/* </a> */}
                                    </div>
                                </TableBody>
                            </StyledTable>

                        </Box>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={handleCloseImportTemplate}>
                        Cancel
                    </Button>
                    <Button onClick={handleImportTemplate}>Import</Button>
                </DialogActions>
            </Dialog>

            {/* <Snackbar
                open={importfileBar}
                autoHideDuration={3000}
                onClose={handleImportfileBarClose}
                anchorOrigin={{ vertical: "top", horizontal: "right" }}
            >
                <Alert
                    onClose={handleImportfileBarClose}
                    severity="success"
                    sx={{ width: "100%" }}
                >
                    {importResponse && (
                        <div>
                            {importResponse.msg} <br />
                            New Template Added: {importResponse.newTemplateAdded} <br />
                            Template Updated: {importResponse.updateTemplates} <br />
                            Error in Template: {importResponse.errorCount} <br />
                        </div>
                    )}
                </Alert>
            </Snackbar> */}
            {(openDesc) ? (
                <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} sx={{ '& .MuiAlertTitle-root': { backgroundColor: 'red' } }} />
            ) : (<></>)}
        </>
    )
}

export default ImportDialogBox;