import { SimpleCard } from "app/components"
import React, { useState } from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import {
  Grid,
  Box,
} from "@mui/material";
//import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import { templateService } from "services/temlplateService";
import CustomAlertBox from "app/components/CustomAlertBox";
//import { isArray } from "lodash";
const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const ExportTemplate = (props) => {
  const tempService = new templateService();
  const [exportTemplate, setExportTemplate] = useState(false);
  const [dataToExport, setDataToExport] = useState(props?.sourceData || []);
  const [value, setValue] = useState('allData');

  const [exportfileBar, setexportfileBar] = useState(false);
  const [exportResponse, setExportResponse] = useState(false);
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const handleExportTemplateDialog = () => {
    setExportTemplate(true)
  }

  const handleCloseExportTemplate = () => {
    setExportTemplate(false)
  };
  const handleExportfileBarClick = () => {
    setexportfileBar(true);
  };
  console.log('props', props);
  const exportTemplateHandle = () => {
    setExportTemplate(false)
    let data = ((value !== "allData") ? props?.filterTemplateData : props?.sourceData) || [];
    let exportData = data?.map(item => {
      let res = {
        Source: item.source,
        Instance: item.instance,
        Environment: item.environment,
        'ERP Type': item.ERPType
      }
      return res
    })
    tempService.exportTemplate({ 'data': exportData, 'sheet_name': 'Source' }).then((response) => {
      setExportResponse(response)
      handleExportfileBarClick("Template exported successfully!", "success");
      setOpenAlert(true);
      setOpenDesc({
        severity: response?.status,
        desc: response?.msg,
      });
    });
  }

  const handleChange = (event) => {
    setValue(event.target.value)
    if (event.target.value === 'allData') {
      setDataToExport(props?.sourceData)
    } else {
      setDataToExport(props?.filterTemplateData)
    }
  }
  /* export template data end */
  return (
    <>
      <Button
        variant="contained"
        onClick={handleExportTemplateDialog}
      >
        Export
      </Button>
      <Dialog open={exportTemplate}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Export Template
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <Box width="100%" overflow="auto">
              <SimpleCard>
                <Grid item md={12} sm={12} xs={12}>
                  <Grid container alignItems="center">
                    <FormControl>
                      <FormLabel id="demo-controlled-radio-buttons-group">Select Data to Export</FormLabel>
                      <RadioGroup
                        aria-labelledby="demo-controlled-radio-buttons-group"
                        name="controlled-radio-buttons-group"
                        value={value}
                        onChange={handleChange}
                      >
                        <FormControlLabel value='allData' control={<Radio />} label="All Data" />
                        <FormControlLabel value='selectedData' control={<Radio />} label="Selected Data" />
                      </RadioGroup>
                    </FormControl>
                  </Grid>

                </Grid>
              </SimpleCard>
            </Box>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCloseExportTemplate}>
            Cancel
          </Button>
          <Button onClick={exportTemplateHandle}>Export</Button>
        </DialogActions>
      </Dialog>
      
      {(openDesc) ? (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      ) : (<></>)}
    </>
  );
};

export default ExportTemplate;
