import { createContext, useState, useEffect } from "react";

const PartTypeTemplateContext = createContext({
  parttypetemplateinfo: [],
  updateParttypeTemplate: () => [],
});

export const ParttypetemplateProvider = ({ children }) => {
  const [parttypetemplateinfo, setPartTypeTemplateinfo] = useState([]);

  const updateParttypeTemplate = (values) => {
    setPartTypeTemplateinfo(values);
    //console.log(values, "inside update context 2222");
  };

  useEffect(() => {
   // console.log(parttypetemplateinfo, "templatesContext");
  }, [parttypetemplateinfo]);

  return (
    <PartTypeTemplateContext.Provider
      value={{
        parttypetemplateinfo,
        updateParttypeTemplate,
      }}
    >
      {children}
    </PartTypeTemplateContext.Provider>
  );
};

export default PartTypeTemplateContext;
