import React, { useState, useEffect } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  Select,
  MenuItem,
  Button,
} from "@mui/material";
import { notOwningDivisionOptions } from "../../adminConfig/common";
import useOracleSettings from "app/hooks/useOracleSettings";
const OrgAllowedDialog = ({
  open,
  handleorgAllowedDialogClose,
  selectedPlants,
  orgAllowedForm,
  handleorgAllowedFormChange,
  handleAddOrgAllowedValues,
  isEdit,
  selectedBU
}) => {

  const {
    templatesInfo,
    bussinessUnitLists,
  } = useOracleSettings();
  const filteredPlants = () => {
    return bussinessUnitLists?.find((plant) => plant.buName === selectedBU)?.plants || []
  };
  const [templateOption, setTemplateOption] = useState([])
  const [filteredBUOptions, setFilteredBUOptions] = useState([])
  useEffect(() => {
    if (selectedBU) {
      let tempData = templatesInfo.filter(temp => temp?.BU?.includes(selectedBU))
      console.log(tempData, 'tempData tempData tempData')
      setTemplateOption(tempData)
    }
    setFilteredBUOptions([selectedBU])
  }, [selectedBU, templatesInfo]);
  return (
    <Dialog open={open}>
      <DialogTitle>
        {" "}
        {isEdit ? "Edit Org Allowed Templates " : "Add OrgAllowed Templates"}
      </DialogTitle>
      <DialogContent>
        <Grid container spacing={3} alignItems="center">
          <Grid item md={3} sm={4} xs={12}>
            <span>
              Design Organization <span style={{ color: "red" }}>*</span>
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <Select
              id="DataType-select"
              name="BUID"
              fullWidth
              value={selectedBU}
              onChange={handleorgAllowedFormChange}
              disabled
            >
              {filteredBUOptions?.map((option) => (
                <MenuItem key={option} value={option}>
                  {option}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              Template Name <span style={{ color: "red" }}>*</span>
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <Select
              name="ALLOWABLE_ORG_TEMPLATES"
              fullWidth
              value={orgAllowedForm.ALLOWABLE_ORG_TEMPLATES}
              onChange={handleorgAllowedFormChange}
              disabled={isEdit}
            >
              {templateOption?.map((option) => (
                <MenuItem key={option.templateName} value={option.templateName}>
                  {option.templateName}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              Has MBOM <span style={{ color: "red" }}>*</span>
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <Select
              id="Has_MBOM"
              name="Has_MBOM"
              fullWidth
              displayEmpty
              value={orgAllowedForm.Has_MBOM !== undefined ? orgAllowedForm.Has_MBOM : 'Y'}
              onChange={handleorgAllowedFormChange}
            >
              {notOwningDivisionOptions.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              On MBOM <span style={{ color: "red" }}>*</span>
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <Select
              id="On_MBOM"
              name="On_MBOM"
              fullWidth
              displayEmpty
              value={orgAllowedForm.On_MBOM !== undefined ? orgAllowedForm.On_MBOM : 'Y'}
              onChange={handleorgAllowedFormChange}
            >
              {notOwningDivisionOptions.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              EBOM Allowed <span style={{ color: "red" }}>*</span>
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <Select
              id="EBOM_Allowed"
              name="EBOM_Allowed"
              fullWidth
              displayEmpty
              value={orgAllowedForm.EBOM_Allowed !== undefined ? orgAllowedForm.EBOM_Allowed : 'Y'}
              onChange={handleorgAllowedFormChange}
            >
              {notOwningDivisionOptions.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              Optional <span style={{ color: "red" }}>*</span>
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <Select
              id="Optional"
              name="Optional"
              fullWidth
              displayEmpty
              value={orgAllowedForm.Optional !== undefined ? orgAllowedForm.Optional : 'Y'}
              onChange={handleorgAllowedFormChange}
            >
              {notOwningDivisionOptions.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              PO Org Check: <span style={{ color: "red" }}>*</span>
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <Select
              id="PO_Org_Exist_Check"
              name="PO_Org_Exist_Check"
              fullWidth
              displayEmpty
              value={orgAllowedForm.PO_Org_Exist_Check !== undefined ? orgAllowedForm.PO_Org_Exist_Check : 'Y'}
              onChange={handleorgAllowedFormChange}
            >
              {notOwningDivisionOptions.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              Plants <span style={{ color: "red" }}>*</span>
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <Select
              style={{ width: "100%" }}
              multiple
              name="plants"
              value={selectedPlants}
              onChange={handleorgAllowedFormChange}
              renderValue={(selected) => selected?.join(", ")}
              MenuProps={{
                sx: {
                  "&& .Mui-selected": {
                    backgroundColor: "rgb(25 118 210 / 67%)"
                  }
                }
              }}
            >
              {filteredPlants()?.map((option) => (
                <MenuItem key={option.plantName} value={option.plantName}>
                  {option.plantName}
                </MenuItem>
              ))}
            </Select>
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleorgAllowedDialogClose}>Cancel</Button>
        <Button onClick={handleAddOrgAllowedValues}>
          {isEdit ? "Update" : "Add"}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default OrgAllowedDialog;
