import {
  GET_MCOLIST,
  GET_MCOINFO,
  GET_UPDATEMCODETAILS,
  GET_UPDATE_MCO_RELATED_VALUES,
  RESET_VALUES,
  SET_ALL_CA_MCO_DETAILS,
  SET_UPDATED_ROUTES
} from '../actions/MCODetailsAction';

const initialState = {
  isPageHavingIssue: false,
  stepData: 'Create',
  RSMEList: {},
  RMEList: {},
  responseData: {
    itemERPStatus: [],
  },
  affectedPartsData: [],
  exportAffectedPartsData: [],
  currentMcoDetails: null,
  isPropertyFormEdited: false,
  isMaterialFormEdited: false,
  selectedRowData: {},
  selectedRowFormdata: {},
  selectedRowIndex: null,
  templates: [],
  source: [],
  plants: [],
  materialData: [],
  masterData: {},
  templateMappingsInfo: [],
  ORGAllowedTemplateMappings: [],
  previousMcosDetails: [],
  // eachPartMRDetails: {},
  mrDetails: [],
  massUpdateChanges: {},
  lifeCycleData: {},
  systemSetingsDetail: [],
  eachTemplateOptions: {},
  bulkPartTemplates: [],
  caRelatedEachMCODetails: {},
  allCaMCODetails: {},
  mcoSearchLists: {},
  showMaterialForm: false,
  parentMCODetails: {},
  caLifeCycleData: {},
};

const MCODetailsReducer = function (state = initialState, action) {
  switch (action.type) {
    case SET_UPDATED_ROUTES:
      return {
        ...state,
        updatedRoutes: action.payload,
    };
    case GET_MCOLIST: {
      return { ...action.payload };
    }
    case GET_MCOINFO: {
      return {
        ...state,
        responseData: {
          ...state.responseData,
          ...action.payload
        },
      };
    }
    case GET_UPDATEMCODETAILS: {
      return {
        ...state,
        responseData: {
          ...state.responseData,
          ...action.payload
        },
      };
    }
    case GET_UPDATE_MCO_RELATED_VALUES: {
      return {
        ...state,
        ...action.payload
      };
    }
    case SET_ALL_CA_MCO_DETAILS: {
      return {
        ...state,
        allCaMCODetails: action.payload, // Update the Redux state
      };
    }
    case RESET_VALUES:
      return {
        // Reset to initial state
        ...initialState,
        updatedRoutes: state.updatedRoutes,
      };
  
      
    default: {
      return { ...state };
    }
  }
};

export default MCODetailsReducer;
