import { Breadcrumb, SimpleCard } from "app/components";
import React, { useEffect, useState, useRef, useCallback } from "react";
import Button from "@mui/material/Button";
import { Link as RouterLink, useNavigate, useLocation } from "react-router-dom";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import { CircularProgress } from "@mui/material";
import Loader from 'app/components/Loader';
import {
  Grid,
  TextField,
  Box,
  styled,
  Tooltip
} from "@mui/material";
import { templateService } from "services/temlplateService";
import useOracleSettings from "app/hooks/useOracleSettings";
import ImportDialogBox from "./importDialog";
import ExportDialogBox from "./exportTemplate";
import CustomAlertBox from "app/components/CustomAlertBox";
import DataGrid from "@inovua/reactdatagrid-community";
import "@inovua/reactdatagrid-community/index.css";
import sortedExportDataHOC from "app/components/sortedExportDataHOC";
import { sortDataByKey } from "app/utils/mcoUtils/commonUtils";

const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));

const Template = () => {
  const location = useLocation();
  let toastState = location.state;
  const { updateAllTemplate } = useOracleSettings();
  const tempService = new templateService();
  const [deleteDialog, setDeleteDialog] = useState(false);
  const [importTemplate, setImportTemplate] = useState(false);
  const [templateData, setTemplateData] = useState([]);
  const [initialData, setInitialData] = useState([]);
  const [selectedDelTemplate, setselectedDelTemplate] = useState();
  const [searchValue, setSearchValue] = useState("");
  const [filteredTempData, setFilteredTempData] = useState([]);
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [sortInfo, setSortInfo] = useState({}); // Add this line

  // ==============table===========
  const [selected, setSelected] = useState({});
  const [selectedRows, setSelectedRows] = useState([]);
  const gridRef = useRef(null);
  const dataMap = gridRef && gridRef.current && gridRef.current.dataMap ? gridRef.current.dataMap : null;
  const [isLoading, setIsLoading] = useState(true);

  const sortedFilterTempData = sortDataByKey(filteredTempData, "templateName");

  const onSelectionChange = useCallback(({ selected }) => {
    setSelected(selected);
  }, []);

  const handleDeleteDialog = () => {
    handleDeleteTemplateDialog(selectedRows[0])
    setDeleteDialog(true);
  };
  const handleEditMappingDialog = () => {
    handleEditTemplate(selectedRows[0]);
  };
  // ==============table===========

  useEffect(() => {
    setIsLoading(true);
    tempService.getAllTemplateList()
      .then((result) => {
        setTemplateData(result.template);
        setInitialData(result.template); // Set initial data
        setFilteredTempData(result.template);
        updateAllTemplate(result.template);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }, []);

  useEffect(() => {
    setFilteredTempData(templateData);
  }, [templateData]);

  useEffect(() => {
    if (toastState?.status) {
      setOpenAlert(true);
      setOpenDesc({
        severity: toastState?.status,
        desc: toastState?.message,
      });
    }
    toastState = {};

    // Clear the state value on page refresh
    window.onbeforeunload = () => {
      window.history.replaceState({}, document.title);
    };
  }, [toastState]);

  //************Search code************* */

  const handleSearch = () => {
  };

  const handleSearchInputChange = (event) => {
    const newSearchValue = event.target.value;
    setSearchValue(newSearchValue);
  };
  // *************Search code ends *******/
  const navigate = useNavigate();
  const templateModeRef = useRef("initialValue");
  const handleCreateTemplate = () => {
    templateModeRef.current = "create";
    setTimeout(() => {
      navigate("/createTemplate");
    }, 200);
  };
  const handleEditTemplate = (editSelectedTemplate) => {
    templateModeRef.current = "edit";
    const selectedTempalteData = editSelectedTemplate;
    setTimeout(() => {
      navigate(`/editTemplate/${selectedTempalteData?.id}`, {
        state: { templateMode: templateModeRef.current, selectedTempalteData },
      });
    }, 200);
  };

  const handleDeleteTemplateDialog = (Deltemplate) => {
    setDeleteDialog(true);
    setselectedDelTemplate(Deltemplate);
  };

  const handleCloseDelete = () => {
    setselectedDelTemplate(null);
    setDeleteDialog(false);
  };

  const handleDeleteTemplate = async () => {
    setDeleteDialog(false);
    try {
      setIsLoading(true);
      const selRowsId = selectedRows?.map(item => (item?.id));
      await tempService.deleteTemplate(selRowsId).then(async (response) => {
        setOpenAlert(true);
        setOpenDesc({
          severity: response?.status,
          desc: response?.msg,
        });
        setSelected({});
        setSelectedRows([]);

        await tempService.getAllTemplateList().then((result) => {
          setTemplateData(result.template);
          setInitialData(result.template); // Set initial data
          setFilteredTempData(result.template);
          updateAllTemplate(result.template);
        });
      })
    } catch (error) {
      return error;
    } finally {
      setIsLoading(false);
    }
  };

  const handleImportTemplateDialog = () => {
    setImportTemplate(true);
  };

  useEffect(() => {
    const selRowsData = selected === true ? dataMap : selected;
    const selRowsDetails = (selRowsData) && Object.keys(selRowsData).map(key => (selRowsData[key]));
    setSelectedRows(selRowsDetails);
  }, [selected, dataMap]);

  useEffect(() => {
    if (templateData) {
      const new_FilteredData = templateData.filter((subscriber) => {
        return (searchValue === "" ||
          subscriber?.templateName?.toLowerCase()
            .includes(searchValue.toLowerCase()) ||
          subscriber.desc
            .toLowerCase()
            .includes(searchValue.toLowerCase()) ||
          subscriber.plants?.some((plant) =>
            plant.plantName
              .toLowerCase()
              .includes(searchValue.toLowerCase())
          ) ||
          subscriber.BU?.some((BU) =>
            BU.toLowerCase().includes(searchValue.toLowerCase())
          ) ||
          subscriber.instance
            .toLowerCase()
            .includes(searchValue.toLowerCase()) ||
          subscriber.ERPType.toLowerCase().includes(
            searchValue.toLowerCase()
          ))
      })
      setFilteredTempData(new_FilteredData);
    }
  }, [searchValue, templateData]);

  const handleSortInfoChange = (sortInfo) => {
    setSortInfo(sortInfo);
    const SortedTemplate = sortedExportDataHOC(Template);
    // const sortedData = SortedTemplate({ data: templateData, sortInfo, filteredTempData, onSortInfoChange: handleSortInfoChange, initialData });
    const sortedData = SortedTemplate({ data: templateData, sortInfo, onSortInfoChange: handleSortInfoChange, initialData });
    setTemplateData(sortedData.props.data);
    //setFilteredTempData(sortedData.props.data);
  };

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Template" }]} />
      </Box>

      <SimpleCard>
        <Grid container spacing={1} alignItems="center">
          <Grid item md={3} sm={6} xs={12} className="font-medium">
            <TextField
              type="text"
              name="search"
              size="small"
              onChange={handleSearchInputChange}
              value={searchValue}
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid item container md={9} sm={6} xs={12} alignItems="center">
            <Button variant="contained" onClick={handleSearch}>
              Search
            </Button>
            <div style={{ flex: 1 }} />
            <Button
              variant="contained"
              component={RouterLink}
              onClick={handleCreateTemplate}
              style={{ marginRight: "10px" }}
            >
              Create
            </Button>
            <Button
              variant="contained"
              disabled={selectedRows?.length === 1 ? false : true}
              onClick={handleEditMappingDialog}
              style={{ margin: '0 10px 0 0px' }}
            >
              Edit
            </Button>
            <Button
              variant="contained"
              disabled={selectedRows?.length ? false : true}
              onClick={handleDeleteDialog}
              style={{ margin: '0 10px 0 0px' }}
            >
              Delete
            </Button>

            <Button
              variant="contained"
              onClick={handleImportTemplateDialog}
              style={{ marginRight: '10px' }}
            >
              Import
            </Button>

            <ExportDialogBox templateData={templateData} filterTemplateData={selectedRows} />
          </Grid>
          <Box width="100%" overflow="auto">
            <br></br>
            <DataGrid
              idProperty="id"
              showZebraRows={false}
              style={{ minHeight: 550 }}
              defaultLimit={100}
              columns={[
                {
                  name: "templateName",
                  header: <Tooltip title={'Name'}>Template Name</Tooltip>,
                  defaultFlex: 1,
                  defaultWidth: 50,
                  render: ({ data }) => <div>{data?.templateName}</div>,
                },
                {
                  name: "desc",
                  header: <Tooltip title={"Description"}>Description</Tooltip>,
                  defaultFlex: 1,
                  defaultWidth: 50,
                  render: ({ data }) => <div>{data?.desc}</div>,
                },
                {
                  name: "ERPType",
                  header: <Tooltip title={"ERP Type"}>ERP Type</Tooltip>,
                  defaultFlex: 1,
                  defaultWidth: 50,
                },
                {
                  name: "instance",
                  header: <Tooltip title={"Instance"}>Instance</Tooltip>,
                  defaultFlex: 1,
                  defaultWidth: 50,
                },
                {
                  name: "BU",
                  header: <Tooltip title={"Design Organization"}>Design Organization</Tooltip>,
                  defaultFlex: 1,
                  defaultWidth: 50,
                  render: ({ data }) => <div>{(data?.BU) ? data.BU.sort().map((item) => item).join(", ") : ''}</div>,
                },
              ]}
              dataSource={sortedFilterTempData}
              checkboxColumn
              selected={selected}
              onSelectionChange={onSelectionChange}
              handle={ref => gridRef.current = ref ? ref.current : null}
              pagination
              renderScroller={() => gridRef?.current?.setScrollTop(selectedRows?.length == 0 ? 0 : gridRef?.current?.getScrollTop())}
              onSortInfoChange={handleSortInfoChange}
            />
          </Box>
        </Grid>
      </SimpleCard>

      <Dialog open={deleteDialog}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Delete template
        </DialogTitle>
        <DialogContent>
          {
            (selectedRows.length === 1) ? (<>
              <DialogContentText>
                Do you want to delete the template{" "}
                <b>{selectedDelTemplate?.templateName}</b> with instance{" "}
                <b>{selectedDelTemplate?.instance}</b>
              </DialogContentText>
            </>) : (<>
              <DialogContentText>
                Do you want to delete the seleted templates?
              </DialogContentText>
            </>)
          }
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCloseDelete}>
            Cancel
          </Button>
          <Button onClick={handleDeleteTemplate}>delete</Button>
        </DialogActions>
      </Dialog>

      <ImportDialogBox
        importTemplate={importTemplate}
        setImportTemplate={setImportTemplate}
        setTemplateData={setTemplateData}
        setIsLoading={setIsLoading}
      />

      {(openDesc) ? (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      ) : (<></>)}

      {
        isLoading ? (
          <div style={{ textAlign: 'center' }}>
            <Loader show={isLoading} />
          </div>
        ) : <></>
      }

    </Container>
  );
};

export default Template;