import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Breadcrumb, SimpleCard } from "app/components";
import {
  Box,
  styled,
  IconButton,
  Grid,
  TextField,
  Paper,
  Select,
  MenuItem,
} from "@mui/material";
import CustomAlertBox from "app/components/CustomAlertBox";
import { styled as makeStyles } from "@mui/material/styles";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import Button from "@mui/material/Button";

import { templateService } from "services/temlplateService";
import { PlantService } from "services/plantService";

import AttributeDialog from "./attributeDialog";
import {
  instanceOptions,
  ERPtypeOptions,
  existInOracleOptions,
  exportToERPOptions,
  notOwningDivisionOptions,
  BUOptions,
} from "../adminConfig/common";
import useOracleSettings from "app/hooks/useOracleSettings";
import { caseInsensitiveSort, generateRandomId } from "../../utils/utils";
import shortId from 'shortid';
import { sortDataByKey } from "app/utils/mcoUtils/commonUtils";

const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));
const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const useStyles = makeStyles({
  spanRed: {
    color: "#FF3D57",
  },
});

const defaultFormValue = {
  name: null,
  mandatory: null,
  material_master: null,
  dataType: null,
  inputType: null,
  values: null,
  defaultValues: null,
};

export default function CreateTemplate() {
  const tempService = new templateService();
  const { bussinessUnitLists } = useOracleSettings();
  const plantService = new PlantService();
  const [plantData, setPlantData] = useState();

  const [templateForm, setTemplateForm] = useState({
    exportToERP: "Y",
    existInOracle: "N",
    notOwningDivision: "Y",
  });

  const classes = useStyles();

  let navigate = useNavigate();

  const [snackBar, setsnackBar] = useState(false);
  const [snackbarmessage, setSnackbarMessage] = useState();
  const [snackbarseverity, setSnackbarSeverity] = useState();

  //**********AttributeComplete States************************** */
  const [diabox, setDiabox] = useState(false);
  const [attributeData, setAttributeData] = useState([]);
  const [attributeForm, setAttributeForm] = useState(null);
  const [tempObject, setTempObject] = useState({});
  const [valuesInputList, setValuesInputList] = useState([]);
  const [newKey, setNewKey] = useState("");
  const [newValue, setNewValue] = useState("");
  const [keyerror, setKeyerror] = useState("");
  //**********AttributeComplete States end ************************** */
  const [selectedBU, setSelectedBU] = useState([]);
  const [selectedplants, setSelectedPlants] = useState([]);
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  useEffect(() => {
    plantService.getAllPlantsList().then((result) => {
      setPlantData(result);
    });
  }, []);

  const handleDialogClose = () => {
    setDiabox(false);
  };

  // const handleBUChange = (event) => {
  //   setSelectedBU(event.target.value);
  // };

  const handleSelectChange = (event, type) => {
    const selectedValues = event.target.value;

    if (type === "BU") {
      setSelectedBU(selectedValues);
    } else if (type === "plants") {
      const selectedPlantsInfo = plantData.filter((option) =>
        selectedValues.includes(option.plantName)
      );

      const extractedInfo = selectedPlantsInfo.map((option) => ({
        plantName: option.plantName,
        id: option.id,
      }));

      setSelectedPlants(extractedInfo);
    }
  };

  //********all atrribute functions */
  const handleAttributeChange = (e) => {
    const { name, value } = e.target;
    if (name === "newKey") {
      setNewKey(value);
    } else if (name === "newValue") {
      setNewValue(value);
    } else {
      setTempObject((prevObject) => ({
        ...prevObject,
        [name]: value,
      }));
    }
  };
  const handleAddRow = () => {
    const keyExists = valuesInputList.some((row) => row.key === newKey);
    const valueExists = valuesInputList.some((row) => row.value === newValue);

    if (keyExists || valueExists) {
      setKeyerror("The key or value already exists");
      return;
    }

    setValuesInputList((prevList) => [
      ...prevList,
      { key: newKey, value: newValue },
    ]);
    setNewKey("");
    setNewValue("");
    setKeyerror("");
  };

  const handleDeleteRow = (index) => {
    setValuesInputList((prevList) => {
      const newList = [...prevList];
      newList.splice(index, 1);
      return newList;
    });
  };

  const handleAddAttribute = () => {
    const updatedTempObject = {
      ...tempObject,
      values: valuesInputList.reduce((acc, curr) => {
        acc[curr.key] = curr.value;
        return acc;
      }, {}),
    };

    if (updatedTempObject.material_master === "") {
      updatedTempObject["material_master"] = "Y";
    }
    if (updatedTempObject.mandatory === "") {
      updatedTempObject["mandatory"] = "Y";
    }
    if (Object.keys(updatedTempObject.values).length) {
      updatedTempObject["values"] = [updatedTempObject.values];
    }

    if (attributeForm !== null) {
      const updatedAttributeData = [...attributeData];
      updatedAttributeData[attributeForm] = updatedTempObject;
      setAttributeData(updatedAttributeData);
    } else {
      setAttributeData((prevAttributeData) => [
        ...prevAttributeData,
        updatedTempObject,
      ]);
    }

    setDiabox(false);
    setTempObject(defaultFormValue);
    setValuesInputList([]);
    setAttributeForm(null);
  };

  const handleDeleteAttribute = (index) => {
    setAttributeData((prevData) => {
      const updatedData = [...prevData];
      updatedData.splice(index, 1);
      return updatedData;
    });
  };
  const handleEditAttribute = (index) => {
    if (index >= 0 && index < attributeData.length) {
      setAttributeForm(index);
      setDiabox(true);
      setTempObject({ ...attributeData[index] });

      const attindex = attributeData[index]?.values[0];
      const valuesArray = Object.entries(attindex || {}).map(
        ([key, value]) => ({
          key,
          value,
        })
      );

      setValuesInputList(valuesArray);
    }
  };

  const handleAddNewAttribute = () => {
    setAttributeForm(null);
    setDiabox(true);
    setTempObject({
      name: "",
      mandatory: "",
      material_master: "",
      dataType: "",
      inputType: "",
      defaultValues: "",
    });
    setValuesInputList([]);
  };

  //********all atrribute functions ends */

  const handleTemplateFormChange = (event) => {
    const { name, value } = event.target;
    let updatedForm = {};
  
    if (name === "ERPtype") {
      updatedForm = {
        ...templateForm,
        plantId: null,
        plants: null,
        url: null,
        username: null,
        port: null,
        authkey: null,
        material_master: null,
        SalesOrg: null,
        ECN_required: null,
        bomusage: null,
  
        [name]: value,
      };
    } else if (name === "plant") {
      const {
        id,
        plantName,
        material_master,
        SalesOrg,
        ECN_required,
        bomusage,
        ConnectivityDetails,
      } = value;
  
      const { url, username, password, port, authkey } = ConnectivityDetails;
  
      updatedForm = {
        ...templateForm,
        plantId: id,
        plantName: plantName,
        SalesOrg: SalesOrg,
        url: url,
        material_master: material_master,
        username: username,
        password: password,
        port: port,
        authkey: authkey,
        ECN_required: ECN_required,
        bomusage: bomusage,
      };
    } else {
      updatedForm = {
        ...templateForm,
        [name]: value,
      };
    }
    // if (updatedForm.ERPType === "SAP") {
    //   updatedForm.exportToERP = "";
    //   updatedForm.existInOracle = "";
    //   updatedForm.notOwningDivision = "";
    // }
    setTemplateForm(updatedForm);
  };
  
  const handleTemplateNameBlur = (event) => {
    const { name, value } = event.target;
    const trimmedValue = value.trim().replace(/\s+/g, ' '); // Trim and replace multiple spaces
    setTemplateForm((prevForm) => ({
      ...prevForm,
      [name]: trimmedValue,
    }));
  };

  const filteredPlant = plantData?.filter(
    (plant) =>
      plant.ERPType === templateForm.ERPType &&
      plant.instance === templateForm.instance
  );
  //console.log(filteredPlant, "2323232 plant ");

  const handlesnackBarClick = (message, severity) => {
    // setSnackbarMessage(message);
    // setSnackbarSeverity(severity);
    // setsnackBar(true);
    setOpenAlert(true);
    setOpenDesc({
      severity: severity,
      desc: message,
    });
  };

  const handlesnackbarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setsnackBar(false);
  };

  const handleSubmitTempForm = async () => {
    const randomid = Math.floor(100000 + generateRandomId() * 900000);

    if (
      templateForm.templateName?.trim() === "" ||
      !templateForm.templateName ||
      templateForm.desc?.trim() === "" ||
      !templateForm.desc ||
      // templateForm.plantName?.trim() === "" ||
      // !templateForm.plantName ||
      templateForm.ERPType?.trim() === "" ||
      !templateForm.ERPType ||
      (templateForm.ERPType === "SAP" && !templateForm.instance)
    ) {
      handlesnackBarClick("Please fill the required details", "error");
      return;
    }

    const updatedForm = {
      ...templateForm,
      id: randomid.toString(),
      attributes: attributeData,
      BU: selectedBU,
      plants: selectedplants,
    };

    tempService.addnewTemplate(updatedForm).then((resp) => {
      if (resp && Object.keys(resp)?.length && resp?.status === "failed") {
        const altHtml = resp?.message?.map((citem) => {
          return (<li key={shortId.generate()}>{citem}</li>)
        });
        handlesnackBarClick(altHtml, "error");
        return;
      }
      // handlesnackBarClick("Template added successfully!", "success");

      setTimeout(() => {
        navigate("/template", { state: { message: 'Template added successfully', status: "success" } });
      }, 200);
    });
  };
  const handleCancel = () => {
    setTimeout(() => {
      navigate(-1)
    }, 200);
  }

  const sortedBusinessUnitList = sortDataByKey(bussinessUnitLists, "businessUnit");
  
  // console.log(templateForm, "tempform data");
  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Create Template" }]} />
      </Box>

      <SimpleCard>
        <Grid item md={9} sm={6} xs={6}>
          <form>
            <h4>Create Template</h4>

            <Grid container spacing={3} alignItems="center">
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>Template Name</span> <span className={classes.spanRed}>*</span>
                {/* <span className={classes.spanRed}>*</span> */}
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="templateName"
                  value={templateForm.templateName}
                  onChange={handleTemplateFormChange}
                  onBlur={handleTemplateNameBlur}
                  fullWidth
                  margin="normal"
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>Description</span>{" "}
                <span className={classes.spanRed}>*</span>
                {/* <span className={classes.spanRed}>*</span> */}
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  name="desc"
                  value={templateForm.desc}
                  onChange={handleTemplateFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>ERP Type </span>{" "}
                <span className={classes.spanRed}>*</span>
                {/* <span className={classes.spanRed}>*</span> */}
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Select
                  labelId="ERPType-label"
                  id="ERPType-select"
                  name="ERPType"
                  fullWidth
                  value={templateForm.ERPType}
                  onChange={handleTemplateFormChange}
                >
                  {ERPtypeOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>

              {(templateForm.ERPType === "SAP" ||
                templateForm.ERPType === "ORACLE") && (
                  <>
                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>Instance</span>{" "}
                      <span className={classes.spanRed}>*</span>
                      {/* <span className={classes.spanRed}>*</span> */}
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <Select
                        // label="instance"
                        id="instance"
                        name="instance"
                        fullWidth
                        displayEmpty
                        value={templateForm.instance}
                        onChange={handleTemplateFormChange}
                      >
                        {instanceOptions.map((option) => (
                          <MenuItem key={option.value} value={option.value}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </Grid>
                  </>
                )}

              {/* {templateForm.ERPType === "ORACLE" && (
                <>
                  <Grid item md={3} sm={4} xs={12} className="font-medium">
                    <span>Export to ERP </span>{" "}
                  </Grid>

                  <Grid item md={9} sm={8} xs={12}>
                    <Select
                      id="exportToERP"
                      name="exportToERP"
                      fullWidth
                      displayEmpty
                      value={templateForm.exportToERP}
                      onChange={handleTemplateFormChange}
                    >
                      {exportToERPOptions.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                          {option.label}
                        </MenuItem>
                      ))}
                    </Select>
                  </Grid>
                </>
              )}

              {templateForm.ERPType === "ORACLE" && (
                <>
                  <Grid item md={3} sm={4} xs={12} className="font-medium">
                    <span>Exist in Oracle </span>{" "}
                  </Grid>

                  <Grid item md={9} sm={8} xs={12}>
                    <Select
                      id="existInOracle"
                      name="existInOracle"
                      fullWidth
                      displayEmpty
                      value={templateForm.existInOracle}
                      onChange={handleTemplateFormChange}
                    >
                      {existInOracleOptions.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                          {option.label}
                        </MenuItem>
                      ))}
                    </Select>
                  </Grid>
                </>
              )}

              {templateForm.ERPType === "ORACLE" && (
                <>
                  <Grid item md={3} sm={4} xs={12} className="font-medium">
                    <span>Not Owning Division </span>{" "}
                  </Grid>

                  <Grid item md={9} sm={8} xs={12}>
                    <Select
                      id="notOwningDivision"
                      name="notOwningDivision"
                      fullWidth
                      displayEmpty
                      value={templateForm.notOwningDivision}
                      onChange={handleTemplateFormChange}
                    >
                      {notOwningDivisionOptions.map((option) => (
                        <MenuItem key={option.value} value={option.value}>
                          {option.label}
                        </MenuItem>
                      ))}
                    </Select>
                  </Grid>
                </>
              )} */}

              {(templateForm.ERPType === "ORACLE" ||
                templateForm.ERPType === "SAP") && (
                  <>
                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>
                        Design Organization <span className={classes.spanRed}>*</span>
                      </span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <Select
                        style={{ width: "100%" }}
                        multiple
                        name="BU"
                      value={selectedBU.sort(caseInsensitiveSort)}
                        onChange={(e) => handleSelectChange(e, "BU")}
                      renderValue={(selected) => selected.sort(caseInsensitiveSort).join(", ")}
                        MenuProps={{
                          sx: {
                            "&& .Mui-selected": {
                              backgroundColor: "rgb(25 118 210 / 67%)"
                            }
                          }
                        }}
                      >
                        {bussinessUnitLists.map((option) => (
                          <MenuItem key={option.businessUnit} value={option.businessUnit}>
                            {option.businessUnit}
                          </MenuItem>
                        ))}
                      </Select>
                    </Grid>
                  </>
                )}

              {/* <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>Plant</span> <span className={classes.spanRed}>*</span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Select
                  style={{ width: "100%" }}
                  multiple
                  name="plants"
                  value={selectedplants.map((item) => item.plantName)}
                  onChange={(e) => handleSelectChange(e, "plants")}
                  renderValue={(selected) => selected.join(", ")}
                >
                  {filteredPlant?.map((option) => (
                    <MenuItem key={option.id} value={option.plantName}>
                      {option.plantName}
                    </MenuItem>
                  ))}
                </Select>
              </Grid> */}

              {(templateForm.ERPType === "SAP") && (
                <>
                  <Grid item md={3} sm={4} xs={12} className="font-medium">
                    <span>Attributes</span>
                  </Grid>

                  <Grid item md={9} sm={8} xs={12}>
                    <span>
                      <Button
                        variant="contained"
                        color="primary"
                        margin="2px"
                        onClick={handleAddNewAttribute}
                      >
                        Add Attribute
                      </Button>
                    </span>
                    <br />
                    &nbsp;
                    <Grid md={4}>
                      <span>
                        <div
                          style={{
                            height: "154px",
                            overflowY: "auto",
                            border: "1px solid #ccc",
                            borderRadius: "4px",
                            width: "500px",
                          }}
                        >
                          {attributeData.map((attrivar, aindex) => (
                            <Paper
                              key={aindex}
                              style={{
                                paddingLeft: "50px",
                                marginBottom: "8px",
                                display: "flex",
                                justifyContent: "space-between",
                                alignItems: "center",
                                height: "46px",
                              }}
                            >
                              <Grid container alignItems="center" spacing={3}>
                                <Grid item md={9} sm={8} xs={12}>
                                  <p>
                                    <b>Name:</b> {attrivar.name}
                                    <IconButton
                                      onClick={() => handleEditAttribute(aindex)}
                                    >
                                      <EditIcon />
                                    </IconButton>
                                    <IconButton
                                      onClick={() => handleDeleteAttribute(aindex)}
                                    >
                                      <DeleteIcon />
                                    </IconButton>
                                  </p>
                                </Grid>
                              </Grid>
                            </Paper>
                          ))}
                        </div>
                      </span>
                    </Grid>
                  </Grid>
                </>
              )}
            </Grid>
            <br />
            <Grid container spacing={3} alignItems="center">
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  <Button
                    variant="contained"
                    color="primary"
                    margin="2px"
                    onClick={handleSubmitTempForm}
                  >
                    Submit
                  </Button>
                </span>
                {/* <span className={classes.spanRed}>*</span> */}
              </Grid>
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  <Button
                    variant="contained"
                    color="primary"
                    margin="2px"
                    onClick={handleCancel}
                  >
                    Cancel
                  </Button>
                </span>
              </Grid>
            </Grid>

          </form>

          <Snackbar
            open={snackBar}
            autoHideDuration={3000}
            onClose={handlesnackbarClose}
            anchorOrigin={{ vertical: "top", horizontal: "right" }}
          >
            <Alert
              onClose={handlesnackbarClose}
              severity={snackbarseverity}
              sx={{ width: "100%" }}
            >
              {snackbarmessage}
            </Alert>
          </Snackbar>
          {(openDesc) ? (
            <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
          ) : (<></>)}
          {/* add attribute dialog box code */}
          <AttributeDialog
            open={diabox}
            handleDialogClose={handleDialogClose}
            attributeForm={attributeForm}
            tempObject={tempObject}
            handleAttributeChange={handleAttributeChange}
            handleAddAttribute={handleAddAttribute}
            newKey={newKey}
            newValue={newValue}
            handleAddRow={handleAddRow}
            handleDeleteRow={handleDeleteRow}
            keyerror={keyerror}
            valuesInputList={valuesInputList}
          />
        </Grid>
      </SimpleCard>
    </Container>
  );
}
