import React from 'react';
import PrivateRoute from "app/privateRoutes";
import BUList from "./buList";
import CreateBU from "./createBU";
import EditBU from "./editBU";

const buListRoutes = [
  {
    path: "/buList",
    element: (
      <PrivateRoute>
        <BUList />
      </PrivateRoute>
    )
  },
  {
    path: "/createBU",
    element: (
      <PrivateRoute>
        <CreateBU />
      </PrivateRoute>
    )
  },
  {
    path: "/editBU/:buId",
    element: (
      <PrivateRoute>
        <EditBU />
      </PrivateRoute>
    )
  }
];
export default buListRoutes;
