import { Box, ButtonBase, Icon, styled, useMediaQuery } from '@mui/material';
import { useTheme } from '@mui/system';
import useSettings from 'app/hooks/useSettings';
import React, { Fragment, useState } from 'react';
import { NavLink } from 'react-router-dom';
import { Paragraph, Span } from '../Typography';
import MatxVerticalNavExpansionPanel from './MatxVerticalNavExpansionPanel';
import shortId from 'shortid';
// import { getBasename } from "app/utils/baseurl";
import MassUpdatePartDescriptionInEnoviaAndERP from '../massUpdatePartDescriptionInEnoviaAndERP';
import ExportAllTemplateMapping from '../exportAllTemplateMapping';
import ExportAllGeneralPropertiesDialogBox from '../exportAllAdminGeneralProperties';
import CustomAlertBox from "app/components/McoPages/CustomAlertBox";
import Loader from 'app/components/Loader';
import ExportAllSystemSetupsDialogBox from '../exportAllAdminSystemSetups';
const ListLabel = styled(Paragraph)(({ theme, mode }) => ({
  fontSize: '12px',
  marginTop: '20px',
  marginLeft: '15px',
  marginBottom: '10px',
  textTransform: 'uppercase',
  display: mode === 'compact' && 'none',
  color: theme.palette.text.secondary,

}));

const ExtAndIntCommon = {
  display: 'flex',
  overflow: 'hidden',
  borderRadius: '4px',
  height: 44,
  whiteSpace: 'pre',
  marginBottom: '8px',
  textDecoration: 'none',
  justifyContent: 'space-between',
  transition: 'all 150ms ease-in',
  '&:hover': { background: 'rgba(255, 255, 255, 0.08)' },
  '&.compactNavItem': {
    overflow: 'hidden',
    justifyContent: 'center !important',
  },
  '& .icon': {
    fontSize: '18px',
    paddingLeft: '16px',
    paddingRight: '16px',
    verticalAlign: 'middle',
  },
};
const ExternalLink = styled('a')(({ theme }) => ({
  ...ExtAndIntCommon,
  color: theme.palette.text.primary,
}));

const InternalLink = styled(Box)(({ theme }) => ({
  '& a': {
    ...ExtAndIntCommon,
    color: theme.palette.text.primary,
  },
  '& .navItemActive': {
    backgroundColor: 'rgba(255, 255, 255, 0.16)',
  },
}));

const StyledText = styled(Span)(({ mode, theme }) => ({
  fontSize: '0.875rem',
  paddingLeft: '0.8rem',
  display: mode === 'compact' && 'none',
  color: theme.palette.common.white,
}));

//for mass update start
const StyledTextforMassUpdate = styled(Span)(({ mode, theme }) => ({
  fontSize: '0.875rem',
  paddingLeft: '0.8rem',
  display: mode === 'compact' && 'none',
  color: theme.palette.common.white,
}));
//for mass update end
const BulletIcon = styled('div')(({ theme }) => ({
  padding: '2px',
  marginLeft: '24px',
  marginRight: '8px',
  overflow: 'hidden',
  borderRadius: '300px',
  background: theme.palette.common.white,
}));

const BadgeValue = styled('div')(() => ({
  padding: '1px 8px',
  overflow: 'hidden',
  borderRadius: '300px',
}));

const MatxVerticalNav = ({ items }) => {
  const { settings, updateSettings } = useSettings();
  const [isLoading, setIsLoading] = useState(false);
  const { mode } = settings.layout1Settings.leftSidebar;
  const theme = useTheme();
  const isMdScreen = useMediaQuery(theme.breakpoints.down('md'));

  // const getBasenameVal = getBasename();
  // const routePaths = getBasenameVal?.segment;
  const [importMassupdateFile, setImportMassupdateFile] = useState(false)
  const [exportAllTempMapping, setExportAllTempMapping] = useState(false)
  const [exportAllAdminGeneralPropertiesDialog, setExportAllAdminGeneralPropertiesDialog] = useState(false)
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [exportAllAdminSystemSetupsDialog, setExportAllAdminSystemSetupsDialog] = useState(false)
  const handleClickImportButton = (event) => {
    if (event === 'massUpdate') {
      setImportMassupdateFile(true)
    } else if (event === 'exportAll') {
      setExportAllTempMapping(true)
    } else if (event === 'exportAllAdminGeneralProperties') {
      setExportAllAdminGeneralPropertiesDialog(true)
    } else if (event === 'exportAllAdminSystemSetups') {
      setExportAllAdminSystemSetupsDialog(true)
    }
  }
  // For NavLink components
  const handleNavLinkClick = () => {
    if (isMdScreen) {
      updateSidebarMode({ mode: 'close' }); // Close the sidebar
    }
  };

  const updateSidebarMode = (sidebarSettings) => {
    let activeLayoutSettingsName = settings.activeLayout + 'Settings';
    let activeLayoutSettings = settings[activeLayoutSettingsName];

    updateSettings({
      ...settings,
      [activeLayoutSettingsName]: {
        ...activeLayoutSettings,
        leftSidebar: {
          ...activeLayoutSettings.leftSidebar,
          ...sidebarSettings,
        },
      },
    });
  };

  const renderLevels = (data) => {
    return data.map((item, index) => {
      if (item.type === 'label')
        return (
          <ListLabel key={shortId.generate()} mode={mode} className="sidenavHoverShow">
            {item.label}
          </ListLabel>
        );

      if (item.children) {
        return (
          <MatxVerticalNavExpansionPanel mode={mode} item={item}>
            <div style={{ marginLeft: '20px' }}>
              {renderLevels(item.children)}
            </div>
          </MatxVerticalNavExpansionPanel>
        );
      } else if (item.type === 'extLink') {
        return (
          <ExternalLink
            key={shortId.generate()}
            href={item.path}
            className={`${mode === 'compact' && 'compactNavItem'}`}
            rel="noopener noreferrer"
            target="_blank"
          >
            <ButtonBase key={item.name} name="child" sx={{ width: '100%' }}>
              {(() => {
                if (item.icon) {
                  return <Icon className="icon">{item.icon}</Icon>;
                } else {
                  return <span className="item-icon icon-text">{item.iconText}</span>;
                }
              })()}
              <StyledText mode={mode} className="sidenavHoverShow">
                {item.name}
              </StyledText>
              <Box mx="auto"></Box>
              {item.badge && <BadgeValue>{item.badge.value}</BadgeValue>}
            </ButtonBase>
          </ExternalLink>
        );
      } else if (item.type === 'popup3') {
        return (
          <InternalLink key={shortId.generate()}>
            {
              (item.type === undefined || item.type === "popup3" || item.type !== "popup") ? (
                <>
                  {
                    (item.type === "popup3") ? (
                      <>
                        <NavLink>
                          <ButtonBase onClick={() => {
                            handleClickImportButton(item.path)
                          }} key={item.name} name="child" sx={{ width: '100%' }}>
                            {item?.icon ? (
                              <Icon className="icon" sx={{ width: 36 }}>
                                {item.icon}
                              </Icon>
                            ) : (
                              <Fragment>
                                <BulletIcon
                                  className={`nav-bullet`}
                                  sx={{ display: mode === 'compact' && 'none' }}
                                />
                                <Box
                                  className="nav-bullet-text"
                                  sx={{
                                    ml: '20px',
                                    fontSize: '11px',
                                    display: mode !== 'compact' && 'none',
                                  }}
                                >
                                  {item.iconText}
                                </Box>
                              </Fragment>
                            )}
                            <StyledText mode={mode} className="sidenavHoverShow">
                              {item.name}
                            </StyledText>

                            <Box mx="auto" />

                            {item.badge && (
                              <BadgeValue className="sidenavHoverShow">{item.badge.value}</BadgeValue>
                            )}
                          </ButtonBase>
                        </NavLink>
                        <ExportAllGeneralPropertiesDialogBox
                          exportAllAdminGeneralPropertiesDialog={exportAllAdminGeneralPropertiesDialog}
                          setExportAllAdminGeneralPropertiesDialog={setExportAllAdminGeneralPropertiesDialog}
                        />
                      </>
                    ) : (
                      <NavLink
                        onClick={handleNavLinkClick}
                        to={`${item.path}`}
                        className={({ isActive }) =>
                          isActive
                            ? `navItemActive ${mode === 'compact' && 'compactNavItem'}`
                            : `${mode === 'compact' && 'compactNavItem'}`
                        }
                      >
                        <ButtonBase key={item.name} name="child" sx={{ width: '100%' }}>
                          {item?.icon ? (
                            <Icon className="icon" sx={{ width: 36 }}>
                              {item.icon}
                            </Icon>
                          ) : (
                            <Fragment>
                              <BulletIcon
                                className={`nav-bullet`}
                                sx={{ display: mode === 'compact' && 'none' }}
                              />
                              <Box
                                className="nav-bullet-text"
                                sx={{
                                  ml: '20px',
                                  fontSize: '11px',
                                  display: mode !== 'compact' && 'none',
                                }}
                              >
                                {item.iconText}
                              </Box>
                            </Fragment>
                          )}
                          <StyledText mode={mode} className="sidenavHoverShow">
                            {item.name}
                          </StyledText>

                          <Box mx="auto" />

                          {item.badge && (
                            <BadgeValue className="sidenavHoverShow">{item.badge.value}</BadgeValue>
                          )}
                        </ButtonBase>
                      </NavLink>
                    )
                  }
                </>
              ) :
                (
                  <>
                    <InternalLink key={shortId.generate()}>
                      <NavLink>
                        <ButtonBase onClick={() => {
                          handleClickImportButton(item.path)
                        }} key={item.name} name="child" sx={{ width: '100%' }}>
                          <Fragment>
                            <BulletIcon
                              className={`nav-bullet`}
                              sx={{ display: mode === 'compact' && 'none' }}
                            />
                            <StyledTextforMassUpdate mode={mode} className="sidenavHoverShow">
                              {item.name}
                            </StyledTextforMassUpdate>
                          </Fragment>
                        </ButtonBase>
                      </NavLink>
                    </InternalLink>
                    <MassUpdatePartDescriptionInEnoviaAndERP
                      importMassupdateFile={importMassupdateFile}
                      setImportMassupdateFile={setImportMassupdateFile}
                      setOpenAlert={setOpenAlert}
                      setOpenDesc={setOpenDesc}
                      setIsLoading={setIsLoading}
                    />
                    {
                      (openDesc) ? (
                        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
                      ) : (<></>)
                    }
                    {
                      isLoading ? (
                        <div className="textAlignCenter">
                          <Loader show={isLoading} />
                        </div>
                      ) : <></>
                    }
                  </>
                )
            }
          </InternalLink>
        );
      } else if (item.type === 'popup6') {
        return (
          <InternalLink key={shortId.generate()}>
            {
              (item.type === undefined || item.type === "popup6" || item.type !== "popup") ? (
                <>
                  {
                    (item.type === "popup6") ? (
                      <>
                        <NavLink>
                          <ButtonBase onClick={() => {
                            handleClickImportButton(item.path)
                          }} key={item.name} name="child" sx={{ width: '100%' }}>
                            {item?.icon ? (
                              <Icon className="icon" sx={{ width: 36 }}>
                                {item.icon}
                              </Icon>
                            ) : (
                              <Fragment>
                                <BulletIcon
                                  className={`nav-bullet`}
                                  sx={{ display: mode === 'compact' && 'none' }}
                                />
                                <Box
                                  className="nav-bullet-text"
                                  sx={{
                                    ml: '20px',
                                    fontSize: '11px',
                                    display: mode !== 'compact' && 'none',
                                  }}
                                >
                                  {item.iconText}
                                </Box>
                              </Fragment>
                            )}
                            <StyledText mode={mode} className="sidenavHoverShow">
                              {item.name}
                            </StyledText>

                            <Box mx="auto" />

                            {item.badge && (
                              <BadgeValue className="sidenavHoverShow">{item.badge.value}</BadgeValue>
                            )}
                          </ButtonBase>
                        </NavLink>
                        <ExportAllSystemSetupsDialogBox
                          exportAllAdminSystemSetupsDialog={exportAllAdminSystemSetupsDialog}
                          setExportAllAdminSystemSetupsDialog={setExportAllAdminSystemSetupsDialog}
                        />
                      </>
                    ) : (
                      <NavLink
                        onClick={handleNavLinkClick}
                        to={`${item.path}`}
                        className={({ isActive }) =>
                          isActive
                            ? `navItemActive ${mode === 'compact' && 'compactNavItem'}`
                            : `${mode === 'compact' && 'compactNavItem'}`
                        }
                      >
                        <ButtonBase key={item.name} name="child" sx={{ width: '100%' }}>
                          {item?.icon ? (
                            <Icon className="icon" sx={{ width: 36 }}>
                              {item.icon}
                            </Icon>
                          ) : (
                            <Fragment>
                              <BulletIcon
                                className={`nav-bullet`}
                                sx={{ display: mode === 'compact' && 'none' }}
                              />
                              <Box
                                className="nav-bullet-text"
                                sx={{
                                  ml: '20px',
                                  fontSize: '11px',
                                  display: mode !== 'compact' && 'none',
                                }}
                              >
                                {item.iconText}
                              </Box>
                            </Fragment>
                          )}
                          <StyledText mode={mode} className="sidenavHoverShow">
                            {item.name}
                          </StyledText>

                          <Box mx="auto" />

                          {item.badge && (
                            <BadgeValue className="sidenavHoverShow">{item.badge.value}</BadgeValue>
                          )}
                        </ButtonBase>
                      </NavLink>
                    )
                  }
                </>
              ) :
                (
                  <>
                    <InternalLink key={shortId.generate()}>
                      <NavLink>
                        <ButtonBase onClick={() => {
                          handleClickImportButton(item.path)
                        }} key={item.name} name="child" sx={{ width: '100%' }}>
                          <Fragment>
                            <BulletIcon
                              className={`nav-bullet`}
                              sx={{ display: mode === 'compact' && 'none' }}
                            />
                            <StyledTextforMassUpdate mode={mode} className="sidenavHoverShow">
                              {item.name}
                            </StyledTextforMassUpdate>
                          </Fragment>
                        </ButtonBase>
                      </NavLink>
                    </InternalLink>
                    <MassUpdatePartDescriptionInEnoviaAndERP
                      importMassupdateFile={importMassupdateFile}
                      setImportMassupdateFile={setImportMassupdateFile}
                      setOpenAlert={setOpenAlert}
                      setOpenDesc={setOpenDesc}
                      setIsLoading={setIsLoading}
                    />
                    {
                      (openDesc) ? (
                        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
                      ) : (<></>)
                    }
                    {
                      isLoading ? (
                        <div className="textAlignCenter">
                          <Loader show={isLoading} />
                        </div>
                      ) : <></>
                    }
                  </>
                )
            }
          </InternalLink>
        );
      } else {
        return (
          <InternalLink key={shortId.generate()}>
            {
              (item.type === undefined || item.type === "popup2" || item.type !== "popup") ? (
                <>
                  {
                    (item.type === "popup2") ? (
                      <>
                        <NavLink>
                          <ButtonBase onClick={() => {
                            handleClickImportButton(item.path)
                          }} key={item.name} name="child" sx={{ width: '100%' }}>
                            {item?.icon ? (
                              <Icon className="icon" sx={{ width: 36 }}>
                                {item.icon}
                              </Icon>
                            ) : (
                              <Fragment>
                                <BulletIcon
                                  className={`nav-bullet`}
                                  sx={{ display: mode === 'compact' && 'none' }}
                                />
                                <Box
                                  className="nav-bullet-text"
                                  sx={{
                                    ml: '20px',
                                    fontSize: '11px',
                                    display: mode !== 'compact' && 'none',
                                  }}
                                >
                                  {item.iconText}
                                </Box>
                              </Fragment>
                            )}
                            <StyledText mode={mode} className="sidenavHoverShow">
                              {item.name}
                            </StyledText>

                            <Box mx="auto" />

                            {item.badge && (
                              <BadgeValue className="sidenavHoverShow">{item.badge.value}</BadgeValue>
                            )}
                          </ButtonBase>
                        </NavLink>
                        <ExportAllTemplateMapping
                          exportAllTempMapping={exportAllTempMapping}
                          setExportAllTempMapping={setExportAllTempMapping}
                        />
                      </>
                    ) : (
                      <NavLink
                        onClick={handleNavLinkClick}
                        to={`${item.path}`}
                        className={({ isActive }) =>
                          isActive
                            ? `navItemActive ${mode === 'compact' && 'compactNavItem'}`
                            : `${mode === 'compact' && 'compactNavItem'}`
                        }
                      >
                        <ButtonBase key={item.name} name="child" sx={{ width: '100%' }}>
                          {item?.icon ? (
                            <Icon className="icon" sx={{ width: 36 }}>
                              {item.icon}
                            </Icon>
                          ) : (
                            <Fragment>
                              <BulletIcon
                                className={`nav-bullet`}
                                sx={{ display: mode === 'compact' && 'none' }}
                              />
                              <Box
                                className="nav-bullet-text"
                                sx={{
                                  ml: '20px',
                                  fontSize: '11px',
                                  display: mode !== 'compact' && 'none',
                                }}
                              >
                                {item.iconText}
                              </Box>
                            </Fragment>
                          )}
                          <StyledText mode={mode} className="sidenavHoverShow">
                            {item.name}
                          </StyledText>

                          <Box mx="auto" />

                          {item.badge && (
                            <BadgeValue className="sidenavHoverShow">{item.badge.value}</BadgeValue>
                          )}
                        </ButtonBase>
                      </NavLink>
                    )
                  }
                </>
              ) :
                (
                  <>
                    <InternalLink key={shortId.generate()}>
                      <NavLink>
                        <ButtonBase onClick={() => {
                          handleClickImportButton(item.path)
                        }} key={item.name} name="child" sx={{ width: '100%' }}>
                          <Fragment>
                            <BulletIcon
                              className={`nav-bullet`}
                              sx={{ display: mode === 'compact' && 'none' }}
                            />
                            <StyledTextforMassUpdate mode={mode} className="sidenavHoverShow">
                              {item.name}
                            </StyledTextforMassUpdate>
                          </Fragment>
                        </ButtonBase>
                      </NavLink>
                    </InternalLink>
                    <MassUpdatePartDescriptionInEnoviaAndERP
                      importMassupdateFile={importMassupdateFile}
                      setImportMassupdateFile={setImportMassupdateFile}
                      setOpenAlert={setOpenAlert}
                      setOpenDesc={setOpenDesc}
                      setIsLoading={setIsLoading}
                    />
                    {
                      (openDesc) ? (
                        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
                      ) : (<></>)
                    }
                    {
                      isLoading ? (
                        <div className="textAlignCenter">
                          <Loader show={isLoading} />
                        </div>
                      ) : <></>
                    }
                  </>
                )
            }
          </InternalLink>
        );
      }
    });
  };

  return <div className="navigation">{renderLevels(items)}</div>;
};

export default React.memo(MatxVerticalNav);