// import { useEffect, useState } from "react";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import Button from "@mui/material/Button";

import {
    Grid,
} from "@mui/material";

import { Formik, Form, Field, ErrorMessage } from 'formik';
import * as yup from 'yup';

const validationSchema = yup.object().shape({
    description: yup
        .string()
        .required('This field is required')
        .max(500, 'Input should not exceed 500 characters'),
});


const CommitChangesForm = (props) => {
    return (
        <Formik
            initialValues={{ description: null }}
            validationSchema={validationSchema}
            enableReinitialize
            validateOnChange={false}
            validateOnBlur={false}
            onSubmit={(values, { resetForm }) => {
                // handle form submission here
                props.saveApplyChanges(values);
                resetForm();
            }}
        >
            {({
                errors,
                touched,
                submitForm,
                setFieldValue,
                setErrors,
                setTouched
            }) => {
                const closeDialogBox = () => {
                    props?.setApplyChangesDialog(false);
                    setErrors({});
                    setTouched({}, false);
                }
                return (
                    <Form>
                        <Dialog
                            open={props?.applyChangesDialog}
                            maxWidth={'sm'}
                            fullWidth={true}
                        >
                            <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
                                Do you want to apply your changes?
                            </DialogTitle>
                            <DialogContent>
                                <DialogContentText>

                                    <Grid container spacing={1} alignItems="center">
                                        <Grid item md={12} sm={12} xs={12} className="font-medium">
                                            Comments<span style={{ color: "#FF3D57" }}>*</span>:
                                        </Grid>
                                        <Grid item md={12} sm={12} xs={12} className="font-medium">
                                            <Field as="textarea" id="description" name="description" rows="10" style={{ width: "100%", borderRadius: "10px" }} />
                                            <ErrorMessage name="description" component="div" style={{ color: "red" }} />
                                        </Grid>
                                    </Grid>
                                </DialogContentText>
                            </DialogContent>
                            <DialogActions>
                                <Button onClick={closeDialogBox}>Cancel</Button>
                                <Button onClick={submitForm} >Apply</Button>
                            </DialogActions>
                        </Dialog>
                    </Form>
                )
            }}
        </Formik >
    )
}

export default CommitChangesForm;