import { BACKEND_API_URL, REACT_APP_BACKEND_API_HEADER, REACT_APP_BACKEND_API_HEADER_FOR_FILE } from "../config";
import ApiInstance from './ApiInstance';
export class MassUpdate {
    massUpdatePartDescription = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/massUpdate/massUpdatePartDescription`,
        data,
        REACT_APP_BACKEND_API_HEADER_FOR_FILE
      )
      .then((res) => {
        // console.log(data, "data in post call");
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("bulkAddTemplate", e);
        return [];
      });
  }
  getMyReport = (data) => {
    return ApiInstance
      .get(
        `${BACKEND_API_URL}/massUpdate/myReport`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        // console.log(data, "data in post call");
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("myReport", e);
        return [];
      });
  }
  deleteReport = (ids) => {
    return ApiInstance
      .delete(
        `${BACKEND_API_URL}/massUpdate/deleteMyReport`,
        { data: { ids } },
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error("Error deleting bu:", error);
        throw error;
      });
  };
  downloadSampleFile=(fileName)=>{
    window.open(`${BACKEND_API_URL}/${fileName}`);
  }
}