
import Snackbar from '@mui/material/Snackbar';
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';

const CustomAlertBox = ({ openAlert, openDesc, setOpenAlert }) => {
    const vertical = 'top';
    const horizontal = 'center';

    return (
        <>
            <Snackbar
                anchorOrigin={{ vertical, horizontal }}
                open={openAlert}
                // autoHideDuration={30 * 1000} // 30 sec
                onClose={(event, reason) => {
                    if (reason === 'clickaway') {
                        return;
                    }
                    setOpenAlert(false);
                }}
                key={vertical + horizontal}
                sx={{ top: '70px!important' }} // Set top to 70px
            >

                <Alert
                    onClose={(event, reason) => {
                        if (reason === 'clickaway') {
                            return;
                        }
                        setOpenAlert(false);
                    }}
                    severity={openDesc.severity}
                    sx={{ width: '100%', maxHeight: "calc(100vh - 100px)" }}
                >
                    {(openDesc.severity === "success") ? (<AlertTitle>Success</AlertTitle>) : (<></>)}
                    {(openDesc.severity === "warning") ? (<AlertTitle>Warning</AlertTitle>) : (<></>)}
                    {(openDesc.severity === "error") ? (<AlertTitle>Error</AlertTitle>) : (<></>)}
                    {
                        (openDesc?.type === 'validationcheck') ? (
                            <>
                                <div style={{ fontWeight: "bold" }}>
                                    Please verify the below items.
                                </div>
                                <ul>
                                    {openDesc.desc}
                                </ul>
                            </>
                        ) : (
                            <>
                                {openDesc.desc}
                            </>
                        )
                    }
                </Alert>
            </Snackbar>
        </>
    )
}

export default CustomAlertBox;