import React from "react";
import {
  Dialog,
  DialogActions,
  DialogTitle,
  DialogContent,
  Grid,
  TextField,
  Button,
} from "@mui/material";

export default function ConnectivityDetailsDialog({
  open,
  connDetails,
  handleCDChange,
  handleAddCD,
  connectivityError,
  handleDialogClose,
  plantForm,
}) {
  return (
    <Dialog open={open}>
      <DialogTitle>Add Connectivity details</DialogTitle>

      <DialogContent>
        <Grid container spacing={3} alignItems="center">
          <Grid item md={3} sm={4} xs={12} className="font-medium">
            <span>URL :</span>
          </Grid>

          <Grid item md={9} sm={8} xs={12}>
            <TextField
              label="URL"
              name="url"
              value={connDetails.url}
              onChange={handleCDChange}
              fullWidth
              margin="normal"
            />
          </Grid>

          <Grid item md={3} sm={4} xs={12} className="font-medium">
            <span>Username :</span>
          </Grid>

          <Grid item md={9} sm={8} xs={12}>
            <TextField
              label="Username"
              name="username"
              value={connDetails.username}
              onChange={handleCDChange}
              fullWidth
              margin="normal"
            />
          </Grid>

          <Grid item md={3} sm={4} xs={12} className="font-medium">
            <span>Password :</span>
          </Grid>

          <Grid item md={9} sm={8} xs={12}>
            <TextField
              label="Password"
              name="password"
              value={connDetails?.password || ""}
              onChange={handleCDChange}
              fullWidth
              margin="normal"
            />
          </Grid>

          <Grid item md={3} sm={4} xs={12} className="font-medium">
            <span>AuthKey :</span>
          </Grid>

          <Grid item md={9} sm={8} xs={12}>
            <TextField
              label="Authkey"
              name="authkey"
              value={connDetails.authkey}
              onChange={handleCDChange}
              fullWidth
              margin="normal"
            />
          </Grid>

          <Grid item md={3} sm={4} xs={12} className="font-medium">
            <span>Port :</span>
          </Grid>

          <Grid item md={9} sm={8} xs={12}>
            <TextField
              label="Port"
              name="port"
              value={connDetails.port}
              onChange={handleCDChange}
              fullWidth
              margin="normal"
            />
          </Grid>

          {plantForm.ERPType === "SAP" && (
            <>
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>sapsysnum :</span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  label="sapsysnum"
                  name="sapsysnum"
                  value={connDetails.sapsysnum}
                  onChange={handleCDChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>

              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>sapclientid :</span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  label="sapclientid"
                  name="sapclientid"
                  value={connDetails.sapclientid}
                  onChange={handleCDChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>
            </>
          )}
        </Grid>
        <p style={{ color: "red" }}>{connectivityError}</p>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleDialogClose}>Cancel</Button>
        <Button onClick={handleAddCD}>Add</Button>
      </DialogActions>
    </Dialog>
  );
}
