import React, { useState, useEffect, useRef } from "react";
import { Scrollbars } from 'react-custom-scrollbars-2';
import ReactDataGrid from "@inovua/reactdatagrid-community";
import "@inovua/reactdatagrid-community/index.css";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  Select,
  MenuItem,
  Button,
  Tooltip
} from "@mui/material";
import Constant from '../../../constant'
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import { sortDataByKeyAllowableOrgTemplates } from "app/utils/mcoUtils/commonUtils";
const gridStyle = { minHeight: 248 }
const MVOtemplCompDialog = ({
  open,
  handleorgAllowedDialogClose,
  orgAllowedForm,
  handleorgAllowedFormChange,
  handleAddMVOtempComp,
  isEdit,
  orgAllowedTempList,
  onSelectionChange,
  selected,
  filteredBUOptions,
  setFilteredBUOptions,
  formError,
  RDO,
  selectedRow,
  handleStatusChange
}) => {
  const [tempData, setTempData] = useState([]);
  const [selectedTemplateValues, setSelectedTemplateValues] = useState({});
  sortDataByKeyAllowableOrgTemplates(tempData, "ALLOWABLE_ORG_TEMPLATES");

  useEffect(() => {
    if (orgAllowedForm?.BUID) {
      let buoption = [orgAllowedForm?.BUID] || [];
      setFilteredBUOptions([RDO])
    } else {
      setFilteredBUOptions([RDO])
    }

    if (orgAllowedTempList) {
      const newTempList = orgAllowedTempList?.filter((fitem) => {
        return fitem?.BUID === RDO;
      })
      if (selectedRow) {
        newTempList.map(item => {
          selectedRow?.details?.allowableOrgTemplates.map(item1 => {
            if (item.details.ALLOWABLE_ORG_TEMPLATES === item1.ALLOWABLE_ORG_TEMPLATE) {
              item.status = item1.status
            }
          })
          return item;
        })
      }

      let newdata = newTempList.map(item => {
        const newitem = { ...item, ...item?.details }
        return newitem
      }) || [];

      console.log(newdata, "================table data");

      setTempData(newdata);
    }
  }, [orgAllowedTempList, orgAllowedForm]);

  useEffect(() => {
    console.log(formError, 'formError');
  }, [])

  const gridRef = useRef(null);
  const columns = [
    { name: 'id', header: 'Id', defaultVisible: false, defaultWidth: 60, type: 'number' },
    {
      name: "ALLOWABLE_ORG_TEMPLATES",
      defaultWidth: 265,
      header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.AllowableOrgTemplate}>ALLOWABLE ORG TEMPLATES</Tooltip>,
      render: ({ data }) => <div>{data?.details?.ALLOWABLE_ORG_TEMPLATES}</div>,
    },
    {
      name: "Has_MBOM",
      header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.HasMBom}>Has MBOM</Tooltip>,
      defaultWidth: 120,
      render: ({ data }) => <div>{data?.details?.Has_MBOM}</div>,
    },
    {
      name: "EBOM_Allowed",
      header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.EBOM}>EBOM</Tooltip>,
      defaultWidth: 120,

      render: ({ data }) => <div>{data?.details?.EBOM_Allowed}</div>,
    },
    {
      name: "Optional",
      header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.Optional}>Optional</Tooltip>,
      defaultWidth: 120,

      render: ({ data }) => <div>{data?.details?.Optional}</div>,
    },
    {
      name: "PO_Org_Exist_Check",
      header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.PoCheck}>PO Check</Tooltip>,
      defaultWidth: 120,

      render: ({ data }) => <div>{data?.details?.PO_Org_Exist_Check}</div>,
    },
    {
      name: "On_MBOM",
      header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.OnMbom}>On MBOM</Tooltip>,
      defaultWidth: 120,

      render: ({ data }) => <div>{data?.details?.On_MBOM}</div>,
    },
    {
      name: "Status",
      header: <Tooltip title={Constant.adminConstant.orgAllowedList.tooltip.status}>Status</Tooltip>,
      defaultWidth: 200,
      render: ({ data }) => {
        return (
          <div>
            <FormControl>
              <RadioGroup
                row
                aria-labelledby="demo-row-radio-buttons-group-label"
                name={data?.details?.ALLOWABLE_ORG_TEMPLATES}
                // defaultValue={data.status === undefined ? 'active' : data.status}
                value={((selectedTemplateValues[data?.details?.ALLOWABLE_ORG_TEMPLATES]) ? (selectedTemplateValues[data?.details?.ALLOWABLE_ORG_TEMPLATES])?.trim() : "active")}
                onChange={(e) => {
                  const updatedStatus = {
                    ...selectedTemplateValues,
                    [e.target.name]: e.target.value,
                  };
                  setSelectedTemplateValues(updatedStatus);
                  handleStatusChange(e);
                }}
              >
                <FormControlLabel value="active" control={<Radio />} label="Active" />
                <FormControlLabel value="disabled" control={<Radio />} label="Disable" />
              </RadioGroup>
            </FormControl>
          </div>)
      }
    }
  ];
  const transformArray = (array) => {
    let output = {};
    array.forEach(item => {
      output[item?.ALLOWABLE_ORG_TEMPLATE] = item?.status?.toLowerCase();
    });
    return output;
  };
  useEffect(() => {
    if (selectedRow) {
      const getSelTempVal = transformArray(selectedRow?.details?.allowableOrgTemplates);
      setSelectedTemplateValues(getSelTempVal);
    }
  }, [selectedRow])
  return (
    <Dialog open={open}
      fullWidth={true}
      maxWidth='xl'
    >
      <DialogTitle>
        {" "}
        {isEdit
          ? "Edit MVO Template Compatibility"
          : "Add MVO Template Compatibility"}
      </DialogTitle>
      <Scrollbars
        autoHeight
        autoHeightMin={100}
        autoHeightMax={500}
        autoHide
        autoHideTimeout={1000}
        autoHideDuration={300}
      >
        <DialogContent>
          <Grid container spacing={3} alignItems="center">
          <Grid item md={3} sm={4} xs={12}>
              <span>
                Design Organization:
              </span>
            </Grid>
            <Grid item md={9} sm={8} xs={12}>
              <Select
                id="DataType-select"
                name="BUID"
                fullWidth
                value={RDO}
                onChange={handleorgAllowedFormChange}
                disabled={true}
              >
                {filteredBUOptions?.map((option) => (
                  <MenuItem key={option} value={option}>
                    {option}
                  </MenuItem>
                ))}
              </Select>
              {
                (formError?.RDOError) ? (<>
                  <p style={{ color: "red" }}>{formError?.RDOError}</p>
                </>) : (<></>)
              }
            </Grid>
            <Grid item md={3} sm={4} xs={12}>
              <span>
                Template MVO:
              </span>
            </Grid>
            <Grid item md={9} sm={8} xs={12}>
              <Select
                name="MVO_TEMPLATE_OPTION"
                fullWidth
                value={orgAllowedForm.MVO_TEMPLATE_OPTION}
                onChange={handleorgAllowedFormChange}
                disabled={isEdit}
              >
                {tempData?.map((option, index) => (
                  <MenuItem key={index} value={option.details.ALLOWABLE_ORG_TEMPLATES}>
                    {option.details.ALLOWABLE_ORG_TEMPLATES}
                  </MenuItem>
                ))}
              </Select>
              {
                (formError?.templateError) ? (<>
                  <p style={{ color: "red" }}>{formError?.templateError}</p>
                </>) : (<></>)
              }
            </Grid>

            <Grid item md={3} sm={4} xs={12}>
              <span>
              Org Allowed Templates:{" "}
              </span>
            </Grid>
            <Grid item md={9} sm={8} xs={12}>
              {
                (formError?.AllowableOrgTempError) ? (<>
                  <p style={{ color: "red" }}>{formError?.AllowableOrgTempError}</p>
                </>) : (<></>)
              }
              <ReactDataGrid
                idProperty="id"
                showZebraRows={false}
                handle={ref => gridRef.current = ref ? ref.current : null}
                selected={selected}
                checkboxColumn={true}
                onSelectionChange={onSelectionChange}
                checkboxOnlyRowSelect={true}
                style={{ ...gridStyle, height: '500px' }} // Add height here
                columns={columns}
                dataSource={tempData}
                pagination
              />
            </Grid>
          </Grid>
        </DialogContent>
      </Scrollbars>
      <DialogActions>
        <Button onClick={handleorgAllowedDialogClose}>Cancel</Button>
        <Button onClick={handleAddMVOtempComp}>
          {isEdit ? "Update" : "Add"}
        </Button>
      </DialogActions>

    </Dialog>
  );
};

export default MVOtemplCompDialog;
