import React, { createContext, useState, useEffect } from 'react';
import { useOktaAuth } from '@okta/okta-react';
import { useNavigate, useLocation } from "react-router-dom"
import { TemplateMappingService } from "./../../services/templateMappingService";
import { UserService } from "services/usersService";
import Loader from 'app/components/Loader';
import CircularProgress from "@mui/material/CircularProgress";
import SomethingWrong from "app/components/McoPages/PageError";
import BuConfirmationBox from "app/components/BuConfirmationBox";
import { useDispatch, useSelector, shallowEqual } from 'react-redux';

const OracleSettingsContext = createContext({
    isLoading: false,
    userDetails: null,
    bussinessUnit: null,
    bussinessUnitLists: null,
    templatesInfo: [],
    allTemplatesInfo: [],
    mappingTemplatesInfo: [],
    refreshData: true,
    updateBussinessUnit: () => null,
    updateBussinessUnitLists: () => null,
    updateTemplateLists: () => [],
    updateAllTemplate: () => [],
    updateMappingTemplatesInfo: () => [],
    updateRefreshData: () => null,
})

export const OracleMappingsProvider = ({ children }) => {
    const { authState, authService } = useOktaAuth();
    const {
        globalBussinessUnit,
    } = useSelector((state) => ({
        globalBussinessUnit: state.globalBUandInstance.bu,
    }), shallowEqual);

    // const tempService = new templateService();
    const tempMapService = new TemplateMappingService();
    const userService = new UserService();
    const navigate = useNavigate();
    const location = useLocation();
    const [userDetails, setUserDetails] = useState(null);
    const [isPageHavingError, setIsPageHavingError] = useState(false);
    const [isLoading, setIsLoading] = useState(false);

    const [bussinessUnit, setBussinessUnit] = useState(localStorage.getItem('bussinessUnit') || null);
    const [isBUModified, setIsBUModified] = useState(false);
    const [bussinessUnitLists, setBussinessUnitLists] = useState(null);
    const [templatesInfo, setTemplatesInfo] = useState([]);
    const [allTemplatesInfo, setAllTemplatesInfo] = useState([]);
    const [mappingTemplatesInfo, setMappingTemplatesInfo] = useState([]);
    const [refreshData, setRefreshData] = useState(true);

    const updateBussinessUnit = (values) => {
        setBussinessUnit(values);
    };
    const updateBussinessUnitLists = (values) => {
        setBussinessUnitLists(values);
    };
    const updateTemplateLists = (values) => {
        setTemplatesInfo(values);
        setAllTemplatesInfo(values);
    };
    const updateAllTemplate = (values) => {
        setAllTemplatesInfo(values);
        setTemplatesInfo(values);
    };
    const updateMappingTemplatesInfo = (values) => {
        setMappingTemplatesInfo(values);
    };
    const updateRefreshData = (values) => {
        setRefreshData(values);
    };

    const updateBUModifiedData = () => {
        if (location?.pathname?.includes("mcodetail")) {
            window.location.reload();
        } else {
            const bu = localStorage.getItem('bussinessUnit');
            if (bu) {
                setBussinessUnit(bu);
            }
        }
        setIsBUModified(false);
    };

    useEffect(() => {
        if (userDetails && mappingTemplatesInfo?.length <= 1) {
            if (authState && authState.isAuthenticated) {
                // tempService.getAllTemplateList().then((result) => {
                //     updateAllTemplate(result.template);
                // });
                updateRefreshData(true);
                tempMapService.getAlltemplateMappings().then((result) => {
                    updateMappingTemplatesInfo(result?.data?.templateMappingList);
                    updateTemplateLists(result?.data?.templateList);
                    updateBussinessUnitLists(result?.data?.bulist);

                    updateAllTemplate(result?.data?.templateList);

                    updateRefreshData(false);
                });
            }
        }
        if (authState && authState.isAuthenticated && authService) {
            authService.getAccessToken().then((accessToken) => {
                console.log('Access Token: ', accessToken);
            });
        }
    }, [authState, authService, userDetails]);

    useEffect(() => {
        if (authState?.isAuthenticated && !userDetails) {
            setIsLoading(true);
            userService.validateUser().then((result) => {
                if (result.status === 'error') {
                    navigate("/accessDenied", { state: { userInfo: result?.data[0], NotAuthorized: true } });
                } else {
                    if (result?.data?.length) {
                        setUserDetails(result?.data[0]);
                        setIsLoading(false);
                    }
                }
            }).catch(err => {
                setIsLoading(false);
                setIsPageHavingError(true);
            });
        }
    }, [authState]);

    useEffect(() => {
        console.log(globalBussinessUnit, "=====globalBussinessUnit");

        const checkLocalStorage = (event) => {
            // for avoid the check for mcodetails screen
            if (location?.pathname?.includes("mcodetail")) {
                return false;
            }
            const bu = localStorage.getItem('bussinessUnit');

            console.log(bussinessUnit, "===localStorageBusinessUnit");
            console.log(bu, "===localStorageBusinessUnit 2");
            // if (event.key === 'bussinessUnit') { // Check if the specific key you're interested in changed
            //     return;
            // }
            if (bu && bu !== bussinessUnit) {
                // setBussinessUnit(bu);
                setIsBUModified(true);
                console.log(bu, "===localStorageBusinessUnit 3");
            }
        };

        window.addEventListener('storage', checkLocalStorage);
        // checkLocalStorage(); // Check initially

        return () => {
            window.removeEventListener('storage', checkLocalStorage);
        };
    });


    return (
        <OracleSettingsContext.Provider
            value={{
                isLoading,
                userDetails,
                bussinessUnit,
                bussinessUnitLists,
                templatesInfo,
                allTemplatesInfo,
                mappingTemplatesInfo,
                refreshData,
                updateBussinessUnit,
                updateBussinessUnitLists,
                updateTemplateLists,
                updateAllTemplate,
                updateMappingTemplatesInfo,
                updateRefreshData
            }}
        >
            {
                (!authState?.isAuthenticated) ? (children) : (
                    <>
                        {
                            (!isPageHavingError) ? (
                                <>
                                    {(userDetails && !isLoading) ? children : (
                                        <div style={{ textAlign: "center", position: "absolute", width: "100%", top: "50%" }}>
                                            <CircularProgress />
                                        </div>
                                    )}
                                </>
                            ) : (<SomethingWrong />)
                        }
                    </>
                )
            }
            {
                (isBUModified) ? (<>
                    <BuConfirmationBox isBUModified={isBUModified} updateBUModifiedData={updateBUModifiedData} />
                </>) : (null)
            }
        </OracleSettingsContext.Provider>
    );
};

export default OracleSettingsContext;