import React, { useEffect, useState } from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import shortId from "shortid";
import { MCOService } from "services/mcoService";
import CloudUploadIcon from "@mui/icons-material/CloudUpload";
import {
  Table,
  TableBody,
  TableCell,
  TableRow,
  Box,
  styled,
} from "@mui/material";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import AlertTitle from "@mui/material/AlertTitle";
import CustomAlertBox from "app/components/CustomAlertBox";
import { UserService } from "services/usersService";
// import download from 'downloadjs';
// import CustomSelect from "./CustomSelect";
const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const StyledTable = styled(Table)(() => ({
  whiteSpace: "pre",
  "& thead": {
    "& tr": { "& th": { paddingLeft: 0, paddingRight: 0 } },
  },
  "& tbody": {
    "& tr": { "& td": { paddingLeft: 0, textTransform: "capitalize" } },
  },
}));

const VisuallyHiddenInput = styled("input")({
  clip: "rect(0 0 0 0)",
  clipPath: "inset(50%)",
  height: 1,
  overflow: "hidden",
  position: "absolute",
  bottom: 0,
  left: 0,
  whiteSpace: "nowrap",
  width: 1,
});

const BulkImportUsers = ({
  userImportDialog,
  setUserImportDialog,
  setUserData,
}) => {
  const mcoService = new MCOService();
  // const { importBulkUpdateFile, setImportBulkUpdateFile, savePartAndOracleTempInRedux } = props;
  const userService = new UserService();
  const [noFileError, setNoFileError] = useState(false);
  const [importResponse, setImportResponse] = useState(false);
  const [importfileBar, setimportfileBar] = useState(false);
  const [importedFileData, setImportedFileData] = useState(null);
  const [importedFileName, setImportedFileName] = useState(null);
  const instance = localStorage.getItem("instance");
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const importFileData = (e) => {
    console.log("file selected");
    const file = e.target.files[0];
    if (file) {
      const fileType = file.name.split('.').pop();
      if (fileType === 'xlsx' || fileType === 'xls') {
        // if (
        //   e.target.files[0].name.split(".").pop() == "xlsx" ||
        //   e.target.files[0].name.split(".").pop() === "xls"
        // ) {
        setImportedFileData(e.target.files[0]);
        setImportedFileName(e.target.files[0].name);
        setNoFileError(false);
      } else {
        setNoFileError(true);
        setImportedFileName(null);
        setImportedFileData(null);
      }
    } else {
      setNoFileError(true);
      setImportedFileName(null);
      setImportedFileData(null);
    }
  };

  // const handleCloseImport = () => {
  //     setImportedFileData(null)
  //     setImportBulkUpdateFile(false);
  // };
  const handleImportFile1 = () => {
    let hasError = false;
    if (importedFileData === null || importedFileData.length < 1) {
      setNoFileError(true);
      hasError = true;
    }
    if (hasError) {
      return;
    }

    if (importedFileData !== null) {
      //alert('file is upl7oaded')
      let postData = {
        file: importedFileData,
        data: {
          Instance: instance,
        },
      };

      // console.log(importSelectionValues, 'importSelectionValues');
      console.log(postData, "postData");
      userService.importUser(postData).then((response) => {
        console.log(response, "response 11111");
        if (response.status === "success") {
          console.log(
            response,
            "response 22222 file uploaded successfully and user added"
          );
        }
        setImportResponse(response);
        setimportfileBar(true);
        setImportedFileData(null);
        setImportedFileName(null);
        //   setImportBulkUpdateFile(false)
        handleCloseImportDialog();
        setOpenAlert(true);
        setOpenDesc({
          severity: response?.status,
          desc: response?.msg,
        });
      });
      userService.getAllUsersList().then((result) => {
        setUserData(result.data);
      });
    }
  };

  const handleImportFile = async () => {
    if (!importedFileData || importedFileData.length < 1) {
      setNoFileError(true);
      return;
    }

    try {
      const postData = {
        file: importedFileData,
        data: {
          Instance: instance,
        },
      };

      console.log(postData, "postData");

      const response = await userService.importUser(postData);
      console.log(response, "response 11111");

      // Determine the status from the response
      const status = response.status || "error";

      // Common tasks for both success and failure cases
      setImportResponse(response);
      setimportfileBar(true);
      setImportedFileData(null);
      setImportedFileName(null);
      handleCloseImportDialog();

      setOpenAlert(true);
      setOpenDesc({
        severity: status,
        desc: status === "success" ? response?.msg : response.errors.join(", "),
      });

      if (status === "success") {
        console.log(
          response,
          "response 22222 file uploaded successfully and user added"
        );
        const result = await userService.getAllUsersList();
        setUserData(result.data);
      } else {
        console.log(status, "response status");
      }
    } catch (error) {
      console.error("Error importing file:", error);
      setOpenAlert(true);
      setOpenDesc({
        severity: "error",
        desc: "An error occurred while importing the file.",
      });
    }
  };

  const handleDownload = () => {
    let filelocation = "sample-bulkUserUpload.xlsx";
    mcoService.downloadSampleFile(filelocation);
  };
  const handleCloseImportDialog = () => {
    setUserImportDialog(false);
    setImportedFileData(null);
    setImportedFileName(null);
  };
  return (
    <>
      <Dialog
        open={userImportDialog}
        onClose={handleCloseImportDialog}
        PaperProps={{
          style: {
            backgroundColor: "#fff",
            boxShadow: "none",
          },
        }}
      >
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Bulk Upload Required Users
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <Box width="100%" overflow="auto">
              <StyledTable>
                <TableBody>
                  <TableRow>
                    <TableCell align="left">Upload File (Excel File)</TableCell>
                    <TableCell align="left">
                      <div style={{ paddingTop: "10px" }}>
                        <Button
                          component="label"
                          variant="contained"
                          startIcon={<CloudUploadIcon />}
                        >
                          Upload file
                          <VisuallyHiddenInput
                            type="file"
                            name="file"
                            accept=".xls, .xlsx"
                            onChange={importFileData}
                          />
                        </Button>
                        {noFileError && (
                          <p>Please Select xlsx file to Upload </p>
                        )}
                      </div>
                      {importedFileName && <p>{importedFileName}</p>}
                    </TableCell>
                  </TableRow>
                  <TableRow>
                    <TableCell colSpan={2}>
                      <div style={{ paddingTop: "10px" }}>
                        <a
                          // href={sampleFileUrl}
                          onClick={handleDownload}
                          download="Example-XLSX-document"
                          rel="noreferrer"
                          // target="_blank"
                          //style={{ color: "blue" }}
                          style={{ color: "blue", cursor: "pointer" }}
                        >
                          Click Here
                        </a>{" "}
                        to download the sample Excel template for bulk user
                        upload.
                        {/* <Button component="label" variant="contained" startIcon={<CloudDownloadIcon />} onClick={() => { download(sampleFileUrl) }}>
                                                Download sample file
                                            </Button> */}
                        {/* </a> */}
                      </div>
                      <div style={{ paddingTop: "10px" }}>
                        <b>Note:</b> Upload file must contain six columns with
                        header labels in following order.
                      </div>
                      <div style={{ paddingTop: "10px" }}>
                        1. User Name
                        <br />
                        3. Name
                        <br />
                        4. Email
                        <br />
                        5. Is Authorized
                        <br />
                        6. User Group
                        <br />
                      </div>
                    </TableCell>
                  </TableRow>
                </TableBody>
              </StyledTable>
            </Box>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleImportFile}>Upload</Button>
          <Button onClick={handleCloseImportDialog}>Cancel</Button>
        </DialogActions>
      </Dialog>
      {openDesc ? (
        <CustomAlertBox
          openAlert={openAlert}
          openDesc={openDesc}
          setOpenAlert={setOpenAlert}
        />
      ) : (
        <></>
      )}
    </>
  );
};

export default BulkImportUsers;
