import React from 'react';
import PrivateRoute from "app/privateRoutes";
import USERList from "./usersList";
import AddEditUser from "./addEditUsers";

const userListRoutes = [
  {
    path: "/userList",
    element: (
      <PrivateRoute>
        <USERList />
      </PrivateRoute>
    )
  },
  {
    path: "/addEditUser",
    element: (
      <PrivateRoute>
        <AddEditUser />
      </PrivateRoute>
    )
  }
];

export default userListRoutes;
