import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Breadcrumb, SimpleCard } from "app/components";
import {
  Box,
  styled,
  Grid,
  TextField,
  Select,
  MenuItem,
  Paper,
  IconButton,
} from "@mui/material";

import { styled as makeStyles } from '@mui/material/styles';

import { ERPtypeOptions } from "../adminConfig/common";
import Button from "@mui/material/Button";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import { SourceListService } from "services/sourceListService";
import { MappingsService } from "services/mappingsService";
import DeleteIcon from "@mui/icons-material/Delete";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));
const useStyles = makeStyles({
  spanRed: {
    color: "#FF3D57",
  },
});

export default function CreateMapping() {
  const [sourceForm, setSourceForm] = useState({});
  const [mappingForm, setMappingForm] = useState({});
  const [snackBar, setsnackBar] = useState(false);
  const [snackbarmessage, setSnackbarMessage] = useState();
  const [snackbarseverity, setSnackbarSeverity] = useState();
  const [valuesInputList, setValuesInputList] = useState([]);
  const [keyerror, setKeyerror] = useState("");
  const [newKey, setNewKey] = useState("");
  const [newValue, setNewValue] = useState("");
  const [tempObject, setTempObject] = useState({});

  const classes = useStyles();
  let navigate = useNavigate();
  const sourceListService = new SourceListService();
  const mappingService = new MappingsService();
  const handleAddMapping = () => {
    if (
      mappingForm.Name?.trim() === "" ||
      !mappingForm.Name ||
      mappingForm.ERPType?.trim() === "" ||
      !mappingForm.ERPType ||
      valuesInputList.length === 0
    ) {
      handlesnackBarClick("Please fill the require details", "error");
      return;
    }

    const newMaping = {
      Name: mappingForm.Name,
      ERPType: mappingForm.ERPType,
      List: valuesInputList.reduce((acc, curr) => {
        acc[curr.key] = curr.value;
        return acc;
      }, {}),
    };
    mappingService.addnewMapping(newMaping).then((resp) => {
      if (resp && resp?.data && resp?.data?.length) {
        if (resp?.data[0]?.id) {
          handlesnackBarClick("Mapping added successfully!", "success");

          setTimeout(() => {
            navigate("/mappings");
          }, 200);
        } else if (resp?.data[0]?.msg) {
          handlesnackBarClick(resp?.data[0]?.msg, "error");
        }
      } else {
        console.log("No data in resp.");
      }
    });
  };

  //*********mapping values*******/
  const handleAddRow = () => {
    const keyExists = valuesInputList.some((row) => row.key === newKey);
    const valueExists = valuesInputList.some((row) => row.value === newValue);

    if (keyExists || valueExists) {
      setKeyerror("The key or value already exists");
      return;
    }

    setValuesInputList((prevList) => [
      ...prevList,
      { key: newKey, value: newValue },
    ]);
    setNewKey("");
    setNewValue("");
    setKeyerror("");
  };

  const handleDeleteRow = (index) => {
    setValuesInputList((prevList) => {
      const newList = [...prevList];
      newList.splice(index, 1);
      return newList;
    });
  };

  const handleMappingvalueChange = (e) => {
    const { name, value } = e.target;
    if (name === "newKey") {
      setNewKey(value);
    } else if (name === "newValue") {
      setNewValue(value);
    } else {
      setTempObject((prevObject) => ({
        ...prevObject,
        [name]: value,
      }));
    }
  };

  // console.log(
  //   valuesInputList,
  //   "values"
  // );
  //**********values code end******/

  const handleMappingFormChange = (event) => {
    const { name, value } = event.target;
    const updatedForm = {
      ...mappingForm,
      [name]: value,
    };
    setMappingForm(updatedForm);
  };

  const handlesnackBarClick = (message, severity) => {
    setSnackbarMessage(message);
    setSnackbarSeverity(severity);
    setsnackBar(true);
  };

  const handlesnackbarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setsnackBar(false);
  };

  //console.log(mappingForm, "mappingForm");

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Create Mapping" }]} />
      </Box>

      <form>
        <SimpleCard>
          <Grid item md={9} sm={6} xs={6}>
            <h4>Create Mapping</h4>
            <Grid container spacing={3} alignItems="center">
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Name <span className={classes.spanRed}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <TextField
                  label="Name"
                  name="Name"
                  value={mappingForm.Name}
                  onChange={handleMappingFormChange}
                  fullWidth
                  margin="normal"
                />
              </Grid>
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>ERP Type </span>{" "}
                <span className={classes.spanRed}>*</span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Select
                  labelId="ERPType-label"
                  id="ERPType-select"
                  name="ERPType"
                  fullWidth
                  value={mappingForm.ERPType}
                  onChange={handleMappingFormChange}
                >
                  {ERPtypeOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </Grid>
              <Grid item md={3} sm={4} xs={12} className="font-medium">
                <span>
                  Mapping values <span className={classes.spanRed}>*</span>
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Grid container alignItems="center">
                  <Grid item xs={3} style={{ marginLeft: "3px" }}>
                    <TextField
                      name="newKey"
                      label="key"
                      value={newKey}
                      variant="outlined"
                      fullWidth
                      margin="normal"
                      onChange={handleMappingvalueChange}
                    />
                  </Grid>
                  &nbsp; &nbsp;
                  <Grid item xs={3}>
                    <TextField
                      name="newValue"
                      label="value"
                      value={newValue}
                      variant="outlined"
                      fullWidth
                      margin="normal"
                      onChange={handleMappingvalueChange}
                    />
                  </Grid>
                  &nbsp; &nbsp;
                  <Grid item xs={3}>
                    <Button
                      style={{ fontSize: "30px", marginTop: "6px" }}
                      onClick={handleAddRow}
                    >
                      <b>+</b>
                    </Button>
                  </Grid>
                </Grid>
                <p style={{ color: "red" }}>{keyerror}</p>
                <div
                  style={{
                    height: "100px",
                    overflowY: "auto",
                    border: "1px solid #ccc",
                    borderRadius: "4px",
                    alignContent: "center",
                    width: "51%",
                  }}
                >
                  {valuesInputList.map((row, index) => (
                    <Paper
                      key={index}
                      style={{
                        paddingLeft: "50px",
                        marginBottom: "8px",
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                      }}
                    >
                      <Grid
                        container
                        alignItems="center"
                        style={{ marginLeft: "2px" }}
                      >
                        <Grid item xs={6}>
                          <p>
                            <b>{row.key}</b>:<b>{row.value}</b>
                          </p>
                        </Grid>
                        <Grid item xs={2}>
                          <IconButton onClick={() => handleDeleteRow(index)}>
                            <DeleteIcon />
                          </IconButton>
                        </Grid>
                      </Grid>
                    </Paper>
                  ))}
                </div>
              </Grid>
            </Grid>
            <Grid item md={3} sm={4} xs={12} className="font-medium">
              <span>
                <Button
                  variant="contained"
                  color="primary"
                  margin="2px"
                  onClick={handleAddMapping}
                >
                  Submit
                </Button>
              </span>
              {/* <span className={classes.spanRed}>*</span> */}
            </Grid>
          </Grid>
        </SimpleCard>
        <Snackbar
          open={snackBar}
          autoHideDuration={3000}
          onClose={handlesnackbarClose}
          anchorOrigin={{ vertical: "top", horizontal: "right" }}
        >
          <Alert
            onClose={handlesnackbarClose}
            severity={snackbarseverity}
            sx={{ width: "100%" }}
          >
            {snackbarmessage}
          </Alert>
        </Snackbar>
      </form>
    </Container>
  );
}
