import React, { useState, useEffect } from 'react';
import { Outlet } from 'react-router-dom';

import { useOktaAuth } from '@okta/okta-react';
import { toRelativeUrl } from '@okta/okta-auth-js';

const LoginScreen = () => {
    const { authState, oktaAuth } = useOktaAuth();

    const login = async () => {
        // try {
        //     oktaAuth.tokenManager.clear();
        //     const response = await oktaAuth.signInWithRedirect();
        //     // handle successful authentication here
        // } catch (error) {
        //     console.error('Authentication error:', error);
        //     // handle error appropriately, e.g., display error message or redirect to login page
        // }
        try {
            await oktaAuth.signInWithRedirect();
        } catch (error) {
            if (error.name === 'AuthApiError') {
                // Handle AuthApiError
                console.error('Authentication failed:', error.message);
                // Display an error message to the user
            } else {
                // Handle other errors
                console.error('An unexpected error occurred:', error);
                // Display a generic error message to the user
            }
        }
    };

    useEffect(() => {
        if (!authState) {
            return;
        }

        if (!authState?.isAuthenticated) {
            // const originalUri = toRelativeUrl(window.location.href, window.location.origin);
            // oktaAuth.setOriginalUri(originalUri);
            // oktaAuth.signInWithRedirect();
                       // Instead of relying on browser URL, set a static path for MemoryRouter
                       const fallbackUri = "/calist"; // Or any page you want to land on post-login
                       oktaAuth.setOriginalUri(fallbackUri);

            login();
        }
    }, [oktaAuth, !!authState, authState?.isAuthenticated]);

    if (!authState || !authState?.isAuthenticated) {
        return null;
    }

    return (<Outlet />);
}

export default LoginScreen;