import { createContext, useState, useEffect } from 'react'

const MVOallowedchangesContext = createContext({
    mvoallowedchangesinfo: [],
    updateMVOallowedchanges: () => [],
})

export const MVOallowedchangesProvider = ({ children }) => {
    const [mvoallowedchangesinfo, setmvoallowedchangesInfo] = useState([]);

    const updateMVOallowedchanges = (values) => {
        setmvoallowedchangesInfo(values);
       // console.log(values, "inside update context");
    };

    useEffect(() => {
       // console.log(mvoallowedchangesinfo, "templatesContext");
    }, [mvoallowedchangesinfo]);

    return (
        <MVOallowedchangesContext.Provider
            value={{
                mvoallowedchangesinfo,
                updateMVOallowedchanges,
            }}
        >
            {children}
        </MVOallowedchangesContext.Provider>
    );
};

export default MVOallowedchangesContext;