import { BACKEND_API_URL, REACT_APP_BACKEND_API_HEADER } from "../config";
import ApiInstance from './ApiInstance';

export class MVOtempcompService {
  getAlltempcompMappings = () => {
    return ApiInstance
      .get(
        `${BACKEND_API_URL}/newtemplateMappings/getTemplateMappings`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getTeamplateMappings", e);
        return [];
      });
  };

  deleteTempCompMapping = (tempMappingId) => {
    return ApiInstance
      .delete(
        `${BACKEND_API_URL}/newtemplateMappings/deleteTemplateMappings/${tempMappingId}`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error("Error deleting deleteTemplateMappings:", error);
        throw error;
      });
  };

  addnewTempCompMapping = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/newtemplateMappings/addTemplateMappings`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        // console.log(data, "data in post call");
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("addTemplateMappings", e);
        return [];
      });
  };

  updateTempCompMapping = (data) => {
    return ApiInstance
      .put(
        `${BACKEND_API_URL}/newtemplateMappings/updateTemplateMappings`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        // console.log(data, "data in post call");
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("updateTemplateMappings", e);
        return [];
      });
  };

  softDeleteTempCompMapping = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/newtemplateMappings/softDeleteTemplateMappings`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("softDeleteTemplateMappings", e);
        return [];
      });
  };
}
