import { SimpleCard } from "app/components";
import React, { useState, useEffect } from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import { Grid, Box } from "@mui/material";
//import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import { templateService } from "services/temlplateService";
import CustomAlertBox from "app/components/CustomAlertBox";
import { getUserInfo } from "@okta/okta-auth-js";
import { UserService } from 'services/usersService';
//import { isArray } from "lodash";
const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});
const BulkExportUsers = ({
  userExportDialog,
  setUserExportDialog,
  userData,
  selectedRows,
}) => {
  //console.log(props.templateData, 'props')
  const tempService = new templateService();
  const [dataToExport, setDataToExport] = useState(userData || []);
  const [value, setValue] = useState("allData");
  const userService = new UserService();
  const [exportfileBar, setexportfileBar] = useState(false);
  const [exportResponse, setExportResponse] = useState(false);
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);


  const handleCloseExportUser = () => {
    setUserExportDialog(false);
  };
  const handleExportfileBarClick = () => {
    setexportfileBar(true);
  };
  const handleImportfileBarClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }
    setexportfileBar(false);
  };

  /* export template data start */
  const exportTemplateHandle = () => {
    console.log(userData, "userData in bulk export inside export template.");

    let data = (value !== "allData" ? selectedRows : userData) || [];
    let exportData = data?.map((item) => {
      let res = {
        // userId: item.userId,
        "Name": item.name,
        "User Name": item.userName,
        Email: item.email,
        "Is Authorized": item.is_authorized,
        "User Group": item.userinfo.join(", "), // Convert array to comma-separated string
      };
      return res;
    });

    console.log(data, 'data')
    userService
      .exportUser({ data: exportData, sheet_name: "Users" })
      .then((response) => {
        setExportResponse(response);
        //console.log(response, ';resprespresp')
        handleExportfileBarClick("User exported successfully!", "success");

        setOpenAlert(true);
        setOpenDesc({
          severity: response?.status,
          desc: response?.msg,
        });
      });
    handleCloseExportUser();
  };

  const handleChange = (event) => {
    const newValue = event.target.value;
    setValue(newValue);

    if (newValue === "allData") {
      setDataToExport(userData);
    } else {
      setDataToExport(selectedRows);
    }
  };


  /* export template data end */
  return (
    <>
      <Dialog open={userExportDialog}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Export Users
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <Box width="100%" overflow="auto">
              <SimpleCard>
                <Grid item md={12} sm={12} xs={12}>
                  <Grid container alignItems="center">
                    <FormControl>
                      <FormLabel id="demo-controlled-radio-buttons-group">
                        Select Data to Export
                      </FormLabel>
                      <RadioGroup
                        aria-labelledby="demo-controlled-radio-buttons-group"
                        name="controlled-radio-buttons-group"
                        value={value}
                        onChange={handleChange}
                      >
                        <FormControlLabel
                          value="allData"
                          control={<Radio />}
                          label="All Data"
                        />
                        <FormControlLabel
                          value="selectedData"
                          disabled={selectedRows.length === 0}
                          control={<Radio />}
                          label="Selected Data"
                        />
                      </RadioGroup>
                    </FormControl>
                  </Grid>
                </Grid>
              </SimpleCard>
            </Box>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseExportUser}>Cancel</Button>
          <Button onClick={exportTemplateHandle}>Export</Button>
        </DialogActions>
      </Dialog>
      {/* <Snackbar
        open={exportfileBar}
        autoHideDuration={3000}
        onClose={handleImportfileBarClose}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
      >
        <Alert
          onClose={handleImportfileBarClose}
          severity="success"
          sx={{ width: "100%" }}
        >
          {exportResponse && (
            <div>
              {exportResponse.msg} <br />
            </div>
          )}
        </Alert>
      </Snackbar> */}
      {openDesc ? (
        <CustomAlertBox
          openAlert={openAlert}
          openDesc={openDesc}
          setOpenAlert={setOpenAlert}
        />
      ) : (
        <></>
      )}
    </>
  );
};

export default BulkExportUsers;
