import React from 'react';
import { authRoles } from "../../auth/authRoles";
import PrivateRoute from "app/privateRoutes";
import CreateMapping from "./createMappings";
import EditMapping from "./editMappings";

import Mappings from "./mappings";

const mappingsroutes = [
  {
    path: "/mappings",
    element: (
      <PrivateRoute>
        <Mappings />
      </PrivateRoute>
    ),
    auth: authRoles.admin
  },
  {
    path: "/createMappings",
    element: (
      <PrivateRoute>
        <CreateMapping />
      </PrivateRoute>
    ),
    auth: authRoles.admin,
  },
  {
    path: "/editMappings",
    element: (
      <PrivateRoute>
        <EditMapping />
      </PrivateRoute>
    ),
    auth: authRoles.admin,
  },
];

export default mappingsroutes;
