import { BACKEND_API_URL, REACT_APP_BACKEND_API_HEADER } from "../config";
import ApiInstance from './ApiInstance';

export class SourceListService {
  getAllSourceList = () => {
    return ApiInstance
      .get(
        `${BACKEND_API_URL}/sourceList/getSource`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getSourceDetails", e);
        return [];
      });
  };

  deleteSource = (ids) => {
    return ApiInstance.delete(
      `${BACKEND_API_URL}/sourceList/deleteSource`,
      { data: { ids } },
      REACT_APP_BACKEND_API_HEADER
    )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error("Error deleting source:", error);
        throw error;
      });
  };

  addnewSource = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/sourceList/addSource`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("addnewSource", e);
        return [];
      });
  };

  updateSource = (data) => {
    return ApiInstance
      .put(
        `${BACKEND_API_URL}/sourceList/updateSource`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("addnewPlant", e);
        return [];
      });
  };

  getSourceById = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/sourceList/getSourceById`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getPlantById", e);
        return [];
      });
  };
}
