import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Breadcrumb, SimpleCard } from "app/components";
import { styled as makeStyles } from '@mui/material/styles';
import { Formik, Form } from "formik";
import EditIcon from "@mui/icons-material/Edit";
import Button from "@mui/material/Button";
import CustomAlertBox from "app/components/CustomAlertBox";
import { PlantService } from "services/plantService";
import {
  Grid,
  TextField,
  Select,
  MenuItem,
  Box,
  styled
} from "@mui/material";
import ConnectivityDetailsDialog from "./connectivityDetailsDialog";
import {
  instanceOptions,
  ERPtypeOptions,
  bomusageOptions,
  exportToERPOptions,
} from "../adminConfig/common";
import useOracleSettings from "app/hooks/useOracleSettings";
const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));
const useStyles = makeStyles({
  spanRed: {
    color: "#FF3D57",
  },
});
export default function CreatePlant() {
  const plantService = new PlantService();
  const { bussinessUnitLists } = useOracleSettings();
  const [allPlantData, setallPlantData] = useState();
  const [diaBox, setDiabox] = useState(false);
  const classes = useStyles();
  const initialValues = {
    id: "",
    plants: "",
    ERPType: "",
    instance: "",
    Connectivitydetails: {},
  };
  const [plantForm, setPlantForm] = useState({
    BU: [],
    exportToERP: "Y", // Initialize BU as an empty array
    ERPType: "",
    instance: "",
    orgId: "",
    plantName: "",
    SalesOrg: "",
    aliasPlant: "",
    material_master: "",
    ECN_required: "",
    bomusage: "",
    // ... other fields
  });
  const [connDetails, setconnDetails] = useState({});
  const [connectivityError, setconnectivityError] = useState();
  const [selectedBU, setSelectedBU] = useState([]);
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  let navigate = useNavigate();

  const materialMasterOption = [
    {
      value: "Y",
      label: "Yes",
    },
    {
      value: "N",
      label: "No",
    },
  ];

  useEffect(() => {
    plantService.getAllPlantsList().then((result) => {
      setallPlantData(result.plant);
    });
  }, []);

  //console.log(diaBox, "dialogbox status");

  const handleAddPlant = () => {
    const newPlant = {
      id:
        plantForm.plantName +
        "_" +
        plantForm.ERPType +
        "_" +
        plantForm.instance,
      plants: plantForm.plantName + "_" + plantForm.ERPType,
      plantName: plantForm.plantName,
      ERPType: plantForm.ERPType,
      orgId: plantForm.orgId,
      SalesOrg: plantForm.SalesOrg,
      aliasPlant: plantForm.aliasPlant,
      material_master: plantForm.material_master,
      ECN_required: plantForm.ECN_required,
      bomusage: plantForm.bomusage,
      // BU: plantForm.ERPType === "SAP" ? "" : selectedBU,
      // exportToERP: plantForm.ERPType === "SAP" ? "" : plantForm.exportToERP,
      exportToERP: plantForm.exportToERP,
      ConnectivityDetails: connDetails,
    };

    if (plantForm?.instance) {
      newPlant["instance"] = plantForm?.instance;
      newPlant["id"] =
        plantForm.plantName +
        "_" +
        plantForm.ERPType +
        "_" +
        plantForm.instance;
      newPlant["plants"] =
        plantForm.plantName +
        "_" +
        plantForm.ERPType +
        "_" +
        plantForm.instance;
    }

    if (
      plantForm.plantName?.trim() === "" || !plantForm.plantName ||
      plantForm.ERPType?.trim() === "" || !plantForm.ERPType ||
      plantForm.instance?.trim() === "" || !plantForm.instance ||
      plantForm.exportToERP?.trim() === "" || !plantForm.exportToERP ||
      (plantForm.ERPType === "SAP" && (plantForm.plantName?.trim() === "" || !plantForm.plantName)) ||
      (plantForm.ERPType === "SAP" && (plantForm.material_master?.trim() === "" || !plantForm.material_master)) ||
      (plantForm.ERPType === "SAP" && (plantForm.bomusage?.trim() === "" || !plantForm.bomusage)) ||
      (plantForm.ERPType === "SAP" && !plantForm.instance)
    ) {
      handlesnackBarClick("Please fill required details ", "error");
    } else {
      plantService.addnewPlant(newPlant).then((resp) => {
        if (resp && resp?.data && resp?.data?.length) {
          if (resp?.data[0]?.id) {
            handlesnackBarClick("Plant added successfully!", "success");

            setTimeout(() => {
              navigate("/plants");
            }, 2000);
          } else if (resp?.data[0]?.msg) {
            handlesnackBarClick(resp?.data[0]?.msg, "error");
          }
        } else {
          //console.log("No data in resp.");
        }
      });
    }
  };

  const handleCDChange = (e) => {
    setconnDetails({
      ...connDetails,
      [e.target.name]: e.target.value,
    });
  };

  const handleAddCD = () => {
    setDiabox(false);
  };

  const handlePlantFromChange = (event) => {
    const { name, value } = event.target;
    const updatedForm = {
      ...plantForm,
      [name]: value,
    };
    setPlantForm(updatedForm);
  };

  const handleBUChange = (event) => {
    setSelectedBU(event.target.value);
  };
  //connectivity form methods
  const handleDialogClose = () => {
    setDiabox(false);
    setconnectivityError("");
    setconnDetails("");
  };

  // snackbar code
  const handlesnackBarClick = (message, severity) => {
    setOpenAlert(true);
    setOpenDesc({
      severity: severity,
      desc: message,
    });
  };
  const handleCancel = () => {
    setTimeout(() => {
      navigate(-1)
    }, 200);
  }
  //console.log(plantForm, "plantform");
  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Create Plant" }]} />
      </Box>
      <Formik
        initialValues={initialValues}
        onSubmit={() => {
        }}
      >
        <Form>
          <SimpleCard>
            <Grid item md={9} sm={6} xs={6}>
              <h4>Create Plant</h4>

              <Grid container spacing={3} alignItems="center">
                <Grid item md={3} sm={4} xs={12} className="font-medium">
                  <span>
                    Plant Name <span className={classes.spanRed}>*</span>
                  </span>
                </Grid>

                <Grid item md={9} sm={8} xs={12}>
                  <TextField
                    name="plantName"
                    value={plantForm.plantName}
                    onChange={handlePlantFromChange}
                    fullWidth
                    margin="normal"
                  />
                </Grid>



                <Grid item md={3} sm={4} xs={12} className="font-medium">
                  <span>
                    ERP Type <span className={classes.spanRed}>*</span>
                  </span>
                </Grid>

                <Grid item md={9} sm={8} xs={12}>
                  <Select
                    id="ERPType"
                    name="ERPType"
                    fullWidth
                    displayEmpty
                    value={plantForm.ERPType}
                    onChange={handlePlantFromChange}
                  >
                    {ERPtypeOptions.map((option) => (
                      <MenuItem key={option.value} value={option.value}>
                        {option.label}
                      </MenuItem>
                    ))}
                  </Select>
                </Grid>

                {(plantForm.ERPType === "SAP" || plantForm.ERPType === "ORACLE" || plantForm.ERPType === "JDE") && (
                  <>
                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>
                        Organization Id<span className={classes.spanRed}>*</span>
                      </span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <TextField
                        label="Organization Id"
                        name="orgId"
                        value={plantForm.orgId}
                        onChange={handlePlantFromChange}
                        fullWidth
                        margin="normal"
                      />
                    </Grid>
                  </>
                )}

                {(plantForm.ERPType === "SAP" ||
                  plantForm.ERPType === "ORACLE" || plantForm.ERPType === "JDE") && (
                    <>
                      <Grid item md={3} sm={4} xs={12} className="font-medium">
                        <span>
                          Instance <span className={classes.spanRed}>*</span>
                        </span>
                      </Grid>

                      <Grid item md={9} sm={8} xs={12}>
                        <Select
                          id="instance"
                          name="instance"
                          fullWidth
                          displayEmpty
                          value={plantForm.instance}
                          onChange={handlePlantFromChange}
                        >
                          {instanceOptions.map((option) => (
                            <MenuItem key={option.value} value={option.value}>
                              {option.label}
                            </MenuItem>
                          ))}
                        </Select>
                      </Grid>
                    </>
                  )}

                {["ORACLE", "SAP", "JDE"]?.includes(plantForm.ERPType) && (
                  <>
                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>Export to ERP</span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <Select
                        id="exportToERP"
                        name="exportToERP"
                        fullWidth
                        displayEmpty
                        value={plantForm.exportToERP}
                        onChange={handlePlantFromChange}
                      >
                        {exportToERPOptions.map((option) => (
                          <MenuItem key={option.value} value={option.value}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </Grid>
                  </>
                )}

                {/* {plantForm.ERPType === "ORACLE" && (
                  <>
                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>
                        Design Organization <span className={classes.spanRed}>*</span>
                      </span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <Select
                        style={{ width: "100%" }}
                        multiple
                        value={selectedBU}
                        onChange={handleBUChange}
                        renderValue={(selected) => selected.join(", ")}
                        MenuProps={{
                          sx: {
                            "&& .Mui-selected": {
                              backgroundColor: "rgb(25 118 210 / 67%)"
                            }
                          }
                        }}
                      >
                        {bussinessUnitLists.map((option) => (
                          <MenuItem key={option.businessUnit} value={option.businessUnit}>
                            {option.businessUnit}
                          </MenuItem>
                        ))}
                      </Select>
                    </Grid>
                  </>
                )} */}

                {plantForm.ERPType === "SAP" && (
                  <>
                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>
                        Default Sales Org<span className={classes.spanRed}>*</span>
                      </span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <TextField
                        label="Sales Org"
                        name="SalesOrg"
                        value={plantForm.SalesOrg}
                        onChange={handlePlantFromChange}
                        fullWidth
                        margin="normal"
                      />
                    </Grid>

                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>
                        Material Master <span className={classes.spanRed}>*</span>
                      </span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <Select
                        id="material_master"
                        name="material_master"
                        fullWidth
                        displayEmpty
                        value={plantForm.material_master}
                        onChange={handlePlantFromChange}
                      >
                        {materialMasterOption.map((option) => (
                          <MenuItem key={option.value} value={option.value}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </Grid>

                    {plantForm.material_master === "Y" && (
                      <>
                        <Grid item md={3} sm={4} xs={12} className="font-medium">
                          <span>
                            Plant Alias Name
                            <span className={classes.spanRed}>*</span>
                          </span>
                        </Grid>

                        <Grid item md={9} sm={8} xs={12}>
                          <TextField
                            name="aliasPlant"
                            value={plantForm.aliasPlant}
                            onChange={handlePlantFromChange}
                            fullWidth
                            margin="normal"
                          />
                        </Grid>
                      </>
                    )}

                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>
                        ECN Required <span className={classes.spanRed}>*</span>
                      </span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <Select
                        id="ECN_required"
                        name="ECN_required"
                        fullWidth
                        displayEmpty
                        value={plantForm.ECN_required}
                        onChange={handlePlantFromChange}
                      >
                        {materialMasterOption.map((option) => (
                          <MenuItem key={option.value} value={option.value}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </Grid>

                    <Grid item md={3} sm={4} xs={12} className="font-medium">
                      <span>
                        BOM Usage <span className={classes.spanRed}>*</span>
                      </span>
                    </Grid>

                    <Grid item md={9} sm={8} xs={12}>
                      <Select
                        id="bomusage"
                        name="bomusage"
                        fullWidth
                        displayEmpty
                        value={plantForm.bomusage}
                        onChange={handlePlantFromChange}
                      >
                        {bomusageOptions.map((option) => (
                          <MenuItem key={option.value} value={option.value}>
                            {option.label}
                          </MenuItem>
                        ))}
                      </Select>
                    </Grid>
                    {console.log(plantForm.ERPType, 'plantForm.ERPType')}
                    {plantForm.ERPType === "SAP" && (
                      <>
                        <Grid item md={3} sm={4} xs={12} className="font-medium">
                          <span>Connectivity Details </span>
                        </Grid>

                        <Grid item md={9} sm={8} xs={12}>
                          <span>
                            {Object.keys(connDetails).length === 0 && (
                              <Button
                                variant="contained"
                                color="primary"
                                margin="2px"
                                onClick={() => {
                                  setDiabox(true);
                                }}
                              >
                                Add Connectivity Details
                              </Button>
                            )}
                            {Object.keys(connDetails).length > 0 && (
                              <Grid item md={12} sm={8} xs={12}>
                                <>
                                  <div style={{ display: "flex" }}>
                                    <p>
                                      <b>URL :</b> {connDetails.url}
                                    </p>{" "}
                                    &nbsp;
                                    <p>
                                      <b>Username :</b> {connDetails.username}
                                    </p>{" "}
                                    &nbsp;
                                    <p>
                                      <b>Pass :</b> {connDetails.password}
                                    </p>{" "}
                                    &nbsp;
                                    <p>
                                      <b>AuthKey :</b> {connDetails.authkey}
                                    </p>{" "}
                                    &nbsp;
                                    <p>
                                      <b>Port :</b> {connDetails.port}
                                    </p>{" "}
                                    &nbsp;
                                    {plantForm.ERPType === "SAP" && (
                                      <>
                                        <p>
                                          <b>sapsysnum :</b> {connDetails.sapsysnum}
                                        </p>{" "}
                                        &nbsp;
                                        <p>
                                          <b>sapclientid :</b> {connDetails.sapclientid}
                                        </p>{" "}
                                      </>
                                    )}
                                    <p>
                                      {" "}
                                      <EditIcon
                                        onClick={() => {
                                          setDiabox(true);
                                        }}
                                      />
                                    </p>
                                  </div>
                                </>
                              </Grid>
                            )}
                          </span>
                        </Grid>
                      </>
                    )}
                  </>
                )}

                <Grid item md={3} sm={4} xs={12} className="font-medium">
                  <span>
                    <Button
                      type="submit"
                      variant="contained"
                      color="primary"
                      margin="2px"
                      onClick={handleAddPlant}
                    >
                      Submit
                    </Button>
                  </span>
                </Grid>
                <Grid item md={3} sm={4} xs={12} className="font-medium">
                  <span>
                    <Button
                      variant="contained"
                      color="primary"
                      margin="2px"
                      onClick={handleCancel}
                    >
                      Cancel
                    </Button>
                  </span>
                </Grid>
              </Grid>
            </Grid>
          </SimpleCard>
        </Form>
      </Formik>
      {(openDesc) ? (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      ) : (<></>)}
      {/* dialogBox */}

      <ConnectivityDetailsDialog
        open={diaBox}
        onClose={handleDialogClose}
        connDetails={connDetails}
        handleDialogClose={handleDialogClose}
        handleCDChange={handleCDChange}
        handleAddCD={handleAddCD}
        connectivityError={connectivityError}
        plantForm={plantForm}
        classes={classes}
      />
    </Container>
  );
}
