import React from 'react';
import DraftChangesWraper from "./draftChangesWrapper";
import PrivateRoute from "app/privateRoutes";

const draftChangesroutes = [
  {
    path: "/draftChanges",
    element: (
      <PrivateRoute>
        <DraftChangesWraper />
      </PrivateRoute>
    ),
  },
];

export default draftChangesroutes;
