import React from "react";
import "./Fallback.css";

const Fallback = ({ componentStack, error }) => {
  if (process.env.NODE_ENV === 'production') {
    // In production, render null to disable the error message
    return null;
  } else {
    // In development or other environments, render a default error message
    return (<>
      <section className="fallback">
        <header className="fallback__header">
          <h3 className="fallback__title">Oops! An error occured!</h3>
        </header>
        <div className="fallback__body">
          <p>
            <strong>Error:</strong> {error.toString()}
          </p>
          <p>
            <strong>Stacktrace:</strong> {componentStack}
          </p>
        </div>
      </section>
    </>)
  }
};

export default Fallback;
