import React from 'react';
import MVOtemplateCompatibilityWrapper from "./mvocompatibilityWrapper";
import PrivateRoute from "app/privateRoutes";
const mvocompatibilityRoutes = [
  {
    path: "/mvotemplateList",
    element: (
      <PrivateRoute>
        <MVOtemplateCompatibilityWrapper />
      </PrivateRoute>
    ),
  },
];

export default mvocompatibilityRoutes;
