import React, { useState, useEffect, useImperativeHandle, forwardRef } from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import { Grid, TextField, Select, MenuItem } from "@mui/material";
import shortId from 'shortid';
import CustomAlertBox from "app/components/CustomAlertBox";

const formData = [
    {
        "name": "ERP Type",
        "mandatory": "Y",
        "dataType": "string",
        "inputType": "select",
        "defaultValues": "",
        "values": [
            {
                "sap": "SAP",
                "oracle": "Oracle",
            }
        ]
    },
    {
        "name": "instance",
        "mandatory": "Y",
        "dataType": "string",
        "inputType": "select",
        "defaultValues": "",
        "values": [
            {
                "onPrem": "OnPrem",
                "SaaS": "SaaS",
            }
        ]
    },
    {
        "name": "Validation Key 1",
        "mandatory": "Y",
        "dataType": "string",
        "inputType": "text",
        "defaultValues": "",
        "values": ""
    },
    {
        "name": "Validation Key 2",
        "mandatory": "Y",
        "dataType": "string",
        "inputType": "text",
        "defaultValues": "",
        "values": ""
    },
]

const SystemSetupForm = forwardRef((props, ref) => {
    const [openAlert, setOpenAlert] = useState(false);
    const [openDesc, setOpenDesc] = useState(null);
    const [formAllData, setFormAllData] = useState([]);
    const initialValues = {
        // settingName: '',
        "ERP Type": null,
        instance: null,
        "Validation Key 1": null,
        "Validation Key 2": null,
    };

    const validate = (values) => {
        console.log(values, "validate");
        const errors = {};
        // Validation logic for each field
        // if (!values.settingName) {
        //     errors.settingName = 'Required';
        // }
        if (!values["ERP Type"]) {
            errors["ERP Type"] = 'Required';
        }
        if (!values.instance) {
            errors.instance = 'Required';
        }
        if (!values["Validation Key 1"]) {
            errors["Validation Key 1"] = 'Required';
        }
        if (!values["Validation Key 2"]) {
            errors["Validation Key 2"] = 'Required';
        }
        return errors;
    };

    const onSubmit = (values, isValid, { resetForm }) => {
        // Handle form submission
        console.log(values, 'values 111');
        if (isValid) {
            // Map Validation Key fields to file1 and file2
            const backendValues = {
                ...values,
                ERPType: values["ERP Type"],
                file1: values["Validation Key 1"],
                file2: values["Validation Key 2"],
            };
            delete backendValues["ERP Type"];
            delete backendValues["Validation Key 1"];
            delete backendValues["Validation Key 2"];

            resetForm(); // Reset the form using the resetForm function from Formik context
            // Check if the entire form has changed
            const isFormChanged = Object.keys(values).some((fieldName) => {
                return values[fieldName] !== initialValues[fieldName];
            });
            if (isFormChanged) {
                props.saveForm({ formisValid: isValid, formData: backendValues }); // Close the modal box after successful submission
            } else {
                setOpenAlert(true);
                setOpenDesc({
                    severity: "error",
                    desc: "No changes made!",
                });
            }
        } else {
            setOpenAlert(true);
            setOpenDesc({
                severity: "error",
                desc: "Form submitted with invalid data",
            });
        }
    };

    useEffect(() => {
        setFormAllData(formData);
    }, []);

    return (
        <div>
            <Formik
                initialValues={initialValues}
                validate={validate}
                onSubmit={(values, { setSubmitting }) => {
                    onSubmit(values, !Object.keys(validate(values)).length, { resetForm: () => setSubmitting(false) });
                }}
                innerRef={ref}
            >
                {
                    function Showform({ isValid, values, errors, setFieldValue, resetForm, handleSubmit, submitForm }) {
                        useEffect(() => {
                            if (props?.editForm === "Edit" && props?.selectedRows?.length) {
                                props?.selectedRows?.forEach(thisItem => {
                                    setFieldValue('ERP Type', thisItem?.ERPType);
                                    setFieldValue('instance', thisItem?.instance);
                                    setFieldValue('Validation Key 1', thisItem?.file1);
                                    setFieldValue('Validation Key 2', thisItem?.file2);
                                });
                            }
                        }, [setFieldValue])

                        // Expose the submitForm function to the parent component using useImperativeHandle
                        useImperativeHandle(ref, () => ({
                            submitForm() {
                                console.log("triggering here");
                                // Call the submitForm function when triggered from the parent component
                                // return new Promise((resolve, reject) => {
                                //     // Submit the form using the Formik handleSubmit function
                                //     handleSubmit();
                                //     // After the form submission, resolve the promise with the form values
                                //     resolve({ formisValid: isValid, formData: values });
                                // });

                                // Call the submitForm function when triggered from the parent component
                                handleSubmit();
                            }
                        }));

                        return (
                            <Form>
                                {
                                    (formAllData).length ? formAllData?.map((item, fkey) => {
                                        const thisInputName = item?.name;
                                        const currentFieldValue = values[thisInputName] || item?.defaultValues;

                                        let fieldType = item?.inputType;
                                        let fieldkey = values[formAllData[0]?.name];

                                        return (
                                            <div className="attribute-section" key={fkey}>
                                                <Grid
                                                    container
                                                    spacing={3}
                                                    alignItems="center"
                                                >
                                                    <Grid item md={4} sm={6} xs={12} className="font-medium">
                                                        <label htmlFor="firstName">
                                                            <span >
                                                                {item?.name}
                                                                {item?.mandatory === "Y" ? <span style={{ color: "red" }}>*</span> : ''}
                                                            </span>
                                                        </label>
                                                    </Grid>
                                                    <Grid item md={8} sm={6} xs={12} key={fkey}>
                                                        {
                                                            (fieldType === 'text') && (
                                                                <TextField
                                                                    className={"mb-4 w-full "}
                                                                    color={(errors?.[thisInputName] ? ('warning') : (''))}
                                                                    fullWidth
                                                                    size="small"
                                                                    variant="outlined"
                                                                    id={thisInputName}
                                                                    name={thisInputName}
                                                                    value={currentFieldValue}
                                                                    onChange={(e) => {
                                                                        setFieldValue(thisInputName, e.target.value);
                                                                    }}
                                                                    type="text"
                                                                    style={{ marginBottom: '10px' }}
                                                                />
                                                            )
                                                        }
                                                        {
                                                            (fieldType === 'select') && (
                                                                <>
                                                                    <Select
                                                                        disabled={props?.editForm === "Edit" && ["ERP Type", "instance"].includes(thisInputName)}
                                                                        value={currentFieldValue}
                                                                        onChange={(e) => {
                                                                            setFieldValue(thisInputName, e.target.value);
                                                                        }}
                                                                        displayEmpty
                                                                        renderValue={(selected) => {
                                                                            if (!selected) {
                                                                                return <em>--Select--</em>; // Placeholder text
                                                                            }
                                                                            return item?.values.length && item?.values[0][selected];
                                                                        }}
                                                                        style={{ height: '37.13px', marginBottom: '1rem', width: "100%" }}
                                                                    >
                                                                        <MenuItem>Select</MenuItem>
                                                                        {
                                                                            item?.values.length && Object.keys(item?.values[0]).map((vitem) => {
                                                                                return (
                                                                                    <MenuItem value={vitem} key={shortId.generate()}>{item?.values[0][vitem]}</MenuItem>
                                                                                )
                                                                            })
                                                                        }
                                                                    </Select>
                                                                </>
                                                            )
                                                        }
                                                        <ErrorMessage name={thisInputName} component="div" />
                                                    </Grid>
                                                </Grid>
                                            </div>
                                        )
                                    }) : (<></>)
                                }
                                {/* <button type="submit" style={{ display: 'none' }}></button> Hidden submit button */}
                            </Form>
                        )
                    }
                }
            </Formik>
            {openDesc && (
                <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
            )}
        </div>
    );
});

export default SystemSetupForm;