import { BACKEND_API_URL, REACT_APP_BACKEND_API_HEADER } from "../config";
import ApiInstance from './ApiInstance';

export class HistoryManagementService {

  // draftChanges services
  getAlldraftChanges = () => {
    return ApiInstance
      .get(
        `${BACKEND_API_URL}/draftChanges/getDraftChanges`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getTeamplateMappings", e);
        return [];
      });
  };

  deleteTemplateMapping = (tempMappingId) => {
    return ApiInstance
      .delete(
        `${BACKEND_API_URL}/draftChanges/deletedraftChanges/${tempMappingId}`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error("Error deleting deletedraftChanges:", error);
        throw error;
      });
  };

  addnewTemplateMapping = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/draftChanges/adddraftChanges`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        //console.log(data, "data in post call");
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("adddraftChanges", e);
        return [];
      });
  };

  updateTemplateMapping = (data) => {
    return ApiInstance
      .put(
        `${BACKEND_API_URL}/draftChanges/updatedraftChanges`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("updatedraftChanges", e);
        return [];
      });
  };

  applyDraftChanges = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/draftChanges/acceptChanges`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("applyDraftChanges", e);
        return [];
      });
  };

  discardDraftChanges = (dataId) => {
    return ApiInstance
      .delete(
        `${BACKEND_API_URL}/draftChanges/deletedraftChanges/${dataId}`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error("Error deleting discardDraftChanges:", error);
        throw error;
      });
  };

  // mappingHistory services
  getAllmappingHistory = () => {
    return ApiInstance
      .get(
        `${BACKEND_API_URL}/mappingHistory/getMappingHistory`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getTeamplateMappings", e);
        return [];
      });
  };

}
