import React, { useEffect, useState, useRef, useCallback } from "react";
import { Breadcrumb, SimpleCard } from "app/components";
import Constant from '../../../constant'
import Button from "@mui/material/Button";
import { Link as RouterLink } from "react-router-dom";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import CircularProgress from "@mui/material/CircularProgress";
import DataGrid from "@inovua/reactdatagrid-community";
import "@inovua/reactdatagrid-community/index.css";
import {
  Box,
  styled,
  Grid,
  TextField
} from "@mui/material";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import useOracleSettings from "app/hooks/useOracleSettings";
import { TemplateMappingService } from "services/templateMappingService";
import { HistoryManagementService } from "services/historyManagementService";
import CustomSelect from "../../../components/CustomSelect";
import { BUOptions } from "../../adminConfig/common";
import BUPlantDialog from "./buplantDialog";
import { MVOtempcompService } from "services/mvotempcompService";
import ImportMappingsDialog from '../../../components/importMappingsDialog';
import ExportMappingDialog from '../../../components/exportMappingTemplate'; //export functionality
import CustomAlertBox from "app/components/CustomAlertBox";
const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));

const BUPlantList = () => {
  const {
    refreshData,
    bussinessUnit,
    bussinessUnitLists,
    mappingTemplatesInfo,
    updateBussinessUnit,
    updateMappingTemplatesInfo
  } = useOracleSettings();

  const [importTemplate, setImportTemplate] = useState(false);
  const [searchValue, setSearchValue] = useState("");
  const [snackBar, setsnackBar] = useState(false);
  const [snackbarmessage, setSnackbarMessage] = useState();
  const [snackbarseverity, setSnackbarSeverity] = useState();
  const [deleteDialog, setDeleteDialog] = useState(false);
  const [orgallowedaddDialog, setOrgallowedaddDialog] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [orgAllowedForm, setOrgAllowedForm] = useState({});
  const tempMapService = new TemplateMappingService();
  const mvotempcompservice = new MVOtempcompService();
  const draftMapService = new HistoryManagementService();
  const [selectedDeleteMapping, setSelectedDeleteMapping] = useState();
  const [isEdit, setIsEdit] = useState(false);
  const [selected, setSelected] = useState({});
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [plantValueList, setPlantValueList] = useState([]);
  const [filteredDraftTabledata, setFilteredDraftTabledata] = useState([]);
  const [displayOrder, setDisplayOrder] = useState(0);
  const gridRef = useRef(null);
  const dataMap = gridRef && gridRef.current && gridRef.current.dataMap ? gridRef.current.dataMap : null;

  const handleSearchInputChange = (event) => {
    const newSearchValue = event.target.value;
    setSearchValue(newSearchValue);
  };

  const handleSearch = () => {
  };

  const handleCloseDelete = () => {
    setDeleteDialog(false);
  };

  const handlesnackBarClick = (message, severity) => {
    setOpenAlert(true);
    setOpenDesc({
      severity: severity,
      desc: message,
    });
  };

  const handlesnackbarClose = (event, reason) => {
    if (reason === "clickaway") { return; }
    setsnackBar(false);
  };

  //********Dialog code ********
  const handleorgAllowedFormChange = (event) => {
    const { name, value } = event.target;
    const updatedForm = {
      ...orgAllowedForm,
      [name]: value,
    };
    setOrgAllowedForm(updatedForm);
  };

  const handleAddBuPlant = () => {
    if (!orgAllowedForm.Entity || !orgAllowedForm.BUID || plantValueList.length < 1) {
      handlesnackBarClick("Please Enter the required fields", "error");
      return;
    }
    const resultPlantPayload = plantValueList.reduce((acc, curr) => {
      acc[curr.plantName] = curr.value;
      return acc;
    }, {});
    if (orgAllowedForm) {
      const neworgallowedvalue = {
        MappingType: "buplantmapping",
        BUID: orgAllowedForm.BUID,
        details: {
          Entity: orgAllowedForm.Entity,
          displayOrder: displayOrder,
          PLANT: resultPlantPayload
        },
        id: `${orgAllowedForm.Entity}_${orgAllowedForm.BUID}_buplantmapping`,
      };
      if (isEdit === false) {
        mvotempcompservice
          .addnewTempCompMapping(neworgallowedvalue)
          .then((resp) => {
            if (resp?.status === 'success') {
              fetchData();
              setTimeout(() => {
                setOrgallowedaddDialog(false);
              }, 200);
              setSelected({})
              handlesnackBarClick("RDO Plant Mapping added successfully", "success");
            } else {
              handlesnackBarClick(resp?.msg, resp?.status);
            }
          });
      } else {
        mvotempcompservice
          .updateTempCompMapping(neworgallowedvalue)
          .then((resp) => {
            if (resp.status === "success") {
              setTimeout(() => {
                setOrgallowedaddDialog(false);
                fetchDraftData();
                fetchData();
              }, 200);
              setIsEdit(false);
              setSelected({})
              handlesnackBarClick("RDO Plant Mapping updated successfully", "success");
            } else {
              handlesnackBarClick(resp?.msg, resp?.status);
            }

          });
      }
    }
    handleorgAllowedDialogClose();
  };

  const handleorgAllowedDialogOpen = () => {
    setOrgAllowedForm({ BUID: bussinessUnit });
    setOrgallowedaddDialog(true);
  };
  const handleorgAllowedDialogClose = () => {
    setOrgallowedaddDialog(false);
    setOrgAllowedForm("");
    setPlantValueList([])
    setIsEdit(false);
    setSelectedRows([])
    setSelected({})
  };

  const handleorgAllowedDialogAdd = () => {
    setOrgallowedaddDialog(false);
  };

  const handleDeleteDialog = () => {
    setSelectedDeleteMapping(selectedRows[0])
    setDeleteDialog(true);
  };
  const handleEditMappingDialog = () => {
    const Entity = { Entity: selectedRows[0]?.details?.Entity }
    const BUID = { BUID: bussinessUnit }
    setOrgAllowedForm({
      ...BUID,
      ...Entity
    });
    const plantValuesarr = Object.entries(
      selectedRows[0]?.details?.PLANT
    ).map(([key, value], index) => {
      return { 'plantName': key, 'value': value, 'id': index };
    });
    setDisplayOrder(selectedRows[0]?.details?.displayOrder)
    setPlantValueList(plantValuesarr)
    setIsEdit(true);
    setOrgallowedaddDialog(true);
  };
  const handleDeleteMVOtempcomp = async () => {
    try {
      await mvotempcompservice.softDeleteTempCompMapping({ data: selectedRows }).then(result => {
        handleCloseDelete(false)
        setSelectedRows([])
        setSelectedDeleteMapping([])
        setSelected({})
        setOpenAlert(true);
        setOpenDesc({
          severity: result?.status,
          desc: result?.msg,
        })
      });
    } catch (error) {
      console.error("Failed to delete mapping:", error);
    }
    fetchDraftData();
    fetchData();
    setSelectedRows([]);
  };

  const fetchData = () => {
    setIsLoading(true)
    tempMapService.getAlltemplateMappings().then((result) => {
      updateMappingTemplatesInfo(result?.data?.templateMappingList);
      setIsLoading(false);
    });
  }

  useEffect(() => {
    setIsLoading(refreshData);
  }, [refreshData]);

  const [plantColumns, setPlantColumns] = useState([]);
  const [plantMappingList, setPlantMappingList] = useState([])

  console.log("plantMappingList===",plantMappingList)

  useEffect(() => {
    fetchDraftData();
  }, []);

  useEffect(() => {
    setIsLoading(true)
    if (mappingTemplatesInfo) {
      let finalTempMapList = mappingTemplatesInfo;
      // Filter the data based on MappingType
      finalTempMapList = finalTempMapList.filter(
        (item) => item.MappingType === "buplantmapping" && item.actionFlag !== "Delete"
      );

      if (bussinessUnit) {
        finalTempMapList = finalTempMapList?.filter((fitem) => {
          return fitem.BUID === bussinessUnit;
        });
      }
      if (searchValue && searchValue !== "") {
        finalTempMapList = finalTempMapList?.filter((fitem) => {
          return (
            JSON.stringify(fitem)?.includes(searchValue) ||
            JSON.stringify(fitem)?.toLocaleLowerCase()?.includes(searchValue)
          );
        });
      }
      finalTempMapList = finalTempMapList.sort((a, b) => a.details.displayOrder - b.details.displayOrder)
      if (bussinessUnitLists) {
        // Function to sort an object by its values
        function sortObjectByValues(obj) {
          if (obj) {
            return Object.entries(obj).sort((a, b) => a[1].localeCompare(b[1])).reduce((acc, [key, value]) => {
              acc[key] = value;
              return acc;
            }, {});
          } else {
            return false;
          }
        }

        // Sorting the PLANT data by values
        let sortedList = sortObjectByValues(finalTempMapList[0]?.details?.PLANT);
        let plist = bussinessUnitLists.find(bu => bu.buName === bussinessUnit)?.plants || []
        const newList = Object.keys(sortedList).map(plantName => {
          const originalItem = plist.find(item => item?.plantName === plantName);
          return { plantName, id: originalItem?.id, value: sortedList[plantName] };
        });
        /* mycode start */
        const valueCounts = {};

        // Iterate over the data to populate valueCounts
        newList.forEach((item) => {
          finalTempMapList.forEach((pdata) => {
            if (pdata?.details?.Entity === "PlantGroup" && pdata.BUID === bussinessUnit) {
              const plantValue = pdata?.details?.PLANT[item.plantName];
              console.log("plantvalue===",plantValue)
              valueCounts[plantValue] = (valueCounts[plantValue] || 0) + 1;
            }
          });
        });
        let passedColumn = []
        const headerColumns = newList.map((item) => {
          return {
            name: item.plantName,
            header: item.plantName,
            defaultWidth: 150,

            // colspan: ({ data }) => {
            //   if (data?.details?.Entity === "PlantGroup" && data.BUID === bussinessUnit) {
            //     const plantValue = data?.details?.PLANT[item.plantName];
            //     if (passedColumn?.includes(plantValue)) {
            //       return 0
            //     } else {
            //       passedColumn.push(plantValue);
            //       return valueCounts[plantValue];
            //     }
            //   }
            //   return 1; // Default colspan is 1
            // },
            render: (params) => (
              <div dangerouslySetInnerHTML={{ __html: params.value }} />
            )
          };
        });

        setPlantColumns(headerColumns);

        /* mycode end */
      }

      if (filteredDraftTabledata.length > 0) {
        const filteredRDODraftData = filteredDraftTabledata.filter((i) => { return i.MappingType == "buplantmapping" });
        if (filteredRDODraftData.length > 0 && filteredRDODraftData[0].MappingType === 'buplantmapping') {
          finalTempMapList.forEach(obj1 => {
            filteredRDODraftData[0].changes.forEach(obj2 => {
              if (obj1['id'] == obj2['id']) {
                // obj1.details.draft_Plants = {
                //   "MME": obj2.details.PLANT.MME !== obj1.details.PLANT.MME ? obj2.details.PLANT.MME : null,
                //   "MMC": obj2.details.PLANT.MMC !== obj1.details.PLANT.MMC ? obj2.details.PLANT.MMC : null,
                //   "MVO": obj2.details.PLANT.MVO !== obj1.details.PLANT.MVO ? obj2.details.PLANT.MVO : null,
                //   "NA3": obj2.details.PLANT.NA3 !== obj1.details.PLANT.NA3 ? obj2.details.PLANT.NA3 : null,
                //   "NAE": obj2.details.PLANT.NAE !== obj1.details.PLANT.NAE ? obj2.details.PLANT.NAE : null,
                //   "CL2": obj2.details.PLANT.CL2 !== obj1.details.PLANT.CL2 ? obj2.details.PLANT.CL2 : null,
                //   "PVE": obj2.details.PLANT.PVE !== obj1.details.PLANT.PVE ? obj2.details.PLANT.PVE : null,
                //   "MMR": obj2.details.PLANT.MMR !== obj1.details.PLANT.MMR ? obj2.details.PLANT.MMR : null,
                //   "MFS": obj2.details.PLANT.MFS !== obj1.details.PLANT.MFS ? obj2.details.PLANT.MFS : null,
                //   "MET": obj2.details.PLANT.MET !== obj1.details.PLANT.MET ? obj2.details.PLANT.MET : null,
                //   "MMB": obj2.details.PLANT.MMB !== obj1.details.PLANT.MMB ? obj2.details.PLANT.MMB : null
                // }
                obj1.details.draft_Plants = {};
                for (let key in obj1.details.PLANT) {
                  console.log('obj2.details.PLANT[key]', obj2.details.PLANT[key]);
                  console.log('obj1.details.PLANT[key]', obj1.details.PLANT[key]);
                  obj1.details.draft_Plants[key] = obj2.details.PLANT[key].toLocaleLowerCase() !== obj1.details.PLANT[key].toLocaleLowerCase() ? obj2.details.PLANT[key] : null;
                }
              }
            });
          });
        }
      }

      setTimeout(() => {
        let data = finalTempMapList.map(item => {
          // Object.keys(item?.details?.PLANT).map(plant => {
          //   item?.details?.draft_Plants ? Object.keys(item?.details?.draft_Plants).map(draft_Plant =>{
          //       //item[plant] = item?.details?.draft_Plants[draft_Plant] ? item?.details?.draft_Plants[draft_Plant] + ' ' + item?.details?.PLANT[plant] : item?.details?.PLANT[plant]
          //       item["MME"] = item?.details?.draft_Plants["MME"] ? `${item?.details?.draft_Plants["MME"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.PLANT["MME"]} <span>` : item?.details?.PLANT["MME"]
          //       item["MMC"]= item?.details?.draft_Plants["MMC"] ? `${item?.details?.draft_Plants["MMC"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.PLANT["MMC"]} <span>` : item?.details?.PLANT["MMC"]
          //       item["MVO"]= item?.details?.draft_Plants["MVO"] ? `${item?.details?.draft_Plants["MVO"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.PLANT["MVO"]} <span>` : item?.details?.PLANT["MVO"]
          //       item["NA3"]= item?.details?.draft_Plants["NA3"] ? `${item?.details?.draft_Plants["NA3"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.PLANT["NA3"]} <span>` : item?.details?.PLANT["NA3"]
          //       item["NAE"]= item?.details?.draft_Plants["NAE"] ? `${item?.details?.draft_Plants["NAE"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.PLANT["NAE"]} <span>` : item?.details?.PLANT["NAE"]
          //       item["CL2"]= item?.details?.draft_Plants["CL2"] ? `${item?.details?.draft_Plants["CL2"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.PLANT["CL2"]} <span>` : item?.details?.PLANT["CL2"]
          //       item["PVE"]= item?.details?.draft_Plants["PVE"] ? `${item?.details?.draft_Plants["PVE"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.PLANT["PVE"]} <span>` : item?.details?.PLANT["PVE"]
          //       item["MMR"]= item?.details?.draft_Plants["MMR"] ? `${item?.details?.draft_Plants["MMR"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.PLANT["MMR"]} <span>` : item?.details?.PLANT["MMR"]
          //       item["MFS"]= item?.details?.draft_Plants["MFS"] ? `${item?.details?.draft_Plants["MFS"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.PLANT["MFS"]} <span>` : item?.details?.PLANT["MFS"]
          //       item["MET"]= item?.details?.draft_Plants["MET"] ? `${item?.details?.draft_Plants["MET"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.PLANT["MET"]} <span>` : item?.details?.PLANT["MET"]
          //       item["MMB"]= item?.details?.draft_Plants["MMB"] ? `${item?.details?.draft_Plants["MMB"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.PLANT["MMB"]} <span>`: item?.details?.PLANT["MMB"]
          //     }) : item[plant] = item?.details?.PLANT[plant]          
          //   }            
          // )   
          Object.keys(item?.details?.PLANT).map(plant => {
            item?.details?.draft_Plants ? item[plant] = item?.details?.draft_Plants[plant] ? `${item?.details?.draft_Plants[plant]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.PLANT[plant]} <span>` : item?.details?.PLANT[plant]
              : item[plant] = item?.details?.PLANT[plant]
          }
          )
          return item
        })

        setPlantMappingList(data)
        setIsLoading(false)

      }, 500)
    } else {
      if (!bussinessUnit) {
        updateBussinessUnit(BUOptions ? BUOptions[0]?.value : "");
      }
    }
  }, [mappingTemplatesInfo, bussinessUnit, searchValue, updateBussinessUnit, bussinessUnitLists, filteredDraftTabledata]);

  const fetchDraftData = () => {
    setIsLoading(true);
    draftMapService.getAlldraftChanges().then((result) => {
      let BUID = bussinessUnit ? bussinessUnit : 'Micro Motion';
      let filterData = result.data.filter(element => element.BUID === BUID);
      setFilteredDraftTabledata(filterData);
      setIsLoading(false);
    });
  }

  // *******react data grid table code *******//
  const gridStyle = { minHeight: 550 };
  const [selectedRows, setSelectedRows] = useState([]);
  const columns = [
    {
      name: "Entity",
      header: "Entity",
      render: ({ data }) => <div><b>{data?.details?.Entity}</b></div>,
      defaultWidth: 150,
    },
    ...plantColumns
  ];

  const onSelectionChange = useCallback(({ selected }) => {
    setSelected(selected);
  }, []);

  useEffect(() => {
    const selRowsData = selected === true ? dataMap : selected;
    const selRowsDetails = (selRowsData) && Object.keys(selRowsData).map(key => (selRowsData[key]));
    setSelectedRows(selRowsDetails);
  }, [selected, dataMap]);
  const rowStyle = ({ data }) => {
    const isSelected = Object.keys(selected).includes(data.id);
    return {
      backgroundColor: data.actionFlag !== undefined ? 'rgb(25 118 210 / 20%)' : '',
      zIndex: 99,
      background: (isSelected || selected === true) ? '#dee1f2' : 'initial',
      width: 'auto'
    }
  }
  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "RDO Plant Mapping " }]} />
      </Box>

      <SimpleCard>
        <Grid container spacing={1} alignItems="top">
          {/* Search Input and Search Button */}
          <Grid item md={4} sm={6} xs={12} className="font-medium">
            <Grid container spacing={1} alignItems="center">
              <Grid item md={6} sm={6} xs={12} className="font-medium">
                <TextField
                  type="text"
                  name="search"
                  size="small"
                  onChange={handleSearchInputChange}
                  value={searchValue}
                  style={{ width: "100%" }}
                />
              </Grid>
              <Grid item md={6} sm={6} xs={12} className="font-medium">
                <Button variant="contained" onClick={handleSearch}>
                  Search
                </Button>
              </Grid>
            </Grid>
          </Grid>

          {/* Design Organization Text and Dropdown */}
          <Grid item md={8} sm={6} xs={12} className="font-medium">
            <Grid container alignItems="center" justify="flex-end" style={{ display: "none" }}>
              <Grid item xs={12} sm={6} md={6} className="font-medium" style={{ textAlign: 'right' }}>
                <label style={{ whiteSpace: "nowrap", paddingTop: "11px" }}>Design Organization:</label>
              </Grid>
              <Grid item xs={12} sm={6} md={6} className="font-medium" style={{ textAlign: 'left' }}>
                <CustomSelect
                  onChange={handleSearchInputChange}
                  selectedvalue={bussinessUnit}
                  style={{ width: "100%" }}
                  optionsValues={BUOptions}
                  // defaultValue={
                  //   BUOptions && !bussinessUnit ? BUOptions[0]?.value : ""
                  // }
                  onChangeFunc={(selectedOptionValue) => {
                    setSelected({});
                    setSelectedRows([]);
                    updateBussinessUnit(selectedOptionValue);
                    setSearchValue("");
                    setSelectedRows([]);
                    setPlantValueList([]);
                  }}
                />
              </Grid>
            </Grid>

            {/* Right-aligned Buttons */}
            <Grid item xs={12} className="font-medium" style={{ marginTop: '0px', textAlign: 'right' }}>
              <Button
                variant="contained"
                component={RouterLink}
                onClick={handleorgAllowedDialogOpen}
                style={{ margin: '0 0px 0 10px' }}
              >
                Add
              </Button>
              <Button
                variant="contained"
                disabled={selectedRows?.length === 1 ? false : true}
                onClick={handleEditMappingDialog}
                style={{ margin: '0 0px 0 10px' }}
              >
                Edit
              </Button>
              <Button
                variant="contained"
                disabled={selectedRows?.length !== 0 ? false : true}
                onClick={handleDeleteDialog}
                style={{ margin: '0 0px 0 10px' }}
              >
                Delete
              </Button>
              <Button
                variant="contained"
                onClick={() => {
                  setImportTemplate(true)
                }}
                style={{ margin: '0 10px 0 10px' }}
              >
                Import
              </Button>
              {/* export btn */}
              <ExportMappingDialog
                title={"RDO Plant Mapping"}
                MappingType={"buplantmapping"}
                filterTemplateData={selectedRows}
              />
            </Grid>
          </Grid>

          {/* DataGrid */}
          <Grid item xs={12} style={{ marginTop: '10px' }}>
            <Box width="100%" overflow="auto" marginTop="10px">
              {isLoading && (
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    height: "100vh",
                  }}
                >
                  <CircularProgress />
                </Box>
              )}
              {!isLoading && (
                <DataGrid
                  idProperty="id"
                  showZebraRows={false}
                  style={gridStyle}
                  rowStyle={rowStyle}
                  columns={columns}
                  dataSource={plantMappingList}
                  defaultLimit={100}
                  // rowHeight={null}
                  checkboxColumn
                  selected={selected}
                  sortable={false}
                  onSelectionChange={onSelectionChange}
                  enableColumnAutosize={true}
                  handle={ref => gridRef.current = ref ? ref.current : null}
                  pagination
                  renderScroller={()=> gridRef?.current?.setScrollTop(selectedRows?.length==0 ? 0 : gridRef?.current?.getScrollTop())}
                  />
              )}
            </Box>
          </Grid>
        </Grid>
      </SimpleCard>

      <Snackbar
        open={snackBar}
        autoHideDuration={3000}
        onClose={handlesnackbarClose}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
      >
        <Alert
          onClose={handlesnackbarClose}
          severity={snackbarseverity}
          sx={{ width: "100%" }}
        >
          {snackbarmessage}
        </Alert>
      </Snackbar>
      {(openDesc) ? (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      ) : (<></>)}
      <Dialog open={deleteDialog}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Delete Mapping
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            {'Do you want to delete the RDO Plant Mapping(s) for Design Organization '}<b>{selectedDeleteMapping?.BUID}</b>
            <br />
            <br />
            <b>Note:</b> {Constant.adminConstant.common.deleteNote}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDelete}>Cancel</Button>
          <Button onClick={handleDeleteMVOtempcomp}>delete</Button>
        </DialogActions>
      </Dialog>

      <BUPlantDialog
        open={orgallowedaddDialog}
        handleorgAllowedDialogAdd={handleorgAllowedDialogAdd}
        handleorgAllowedDialogClose={handleorgAllowedDialogClose}
        orgAllowedForm={orgAllowedForm}
        handleorgAllowedFormChange={handleorgAllowedFormChange}
        handleAddBuPlant={handleAddBuPlant}
        isEdit={isEdit}
        bussinessUnitLists={bussinessUnitLists}
        bussinessUnit={bussinessUnit}
        setPlantValueList={setPlantValueList}
        plantValueList={plantValueList}
        setDisplayOrder={setDisplayOrder}
      />

      <ImportMappingsDialog
        title={"Import RDO Plant Mappings"}
        MappingType={"buplantmapping"}
        selectedBU={bussinessUnit}
        importTemplate={importTemplate}
        fetchData={fetchData}
        setImportTemplate={setImportTemplate} />
    </Container>
  );
};

export default BUPlantList;