import React, { useState, useEffect } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  Select,
  MenuItem,
  Button,
} from "@mui/material";

const SetCompTempDialog = ({
  open,
  handleorgAllowedDialogClose,
  modifytempdata,
  orgAllowedForm,
  handleorgAllowedFormChange,
  handleAddMVOtempComp,
  isEdit,
  templatesInfo,
  selectedBU
}) => {
  const [templateOption, setTemplateOption] = useState([])
  const [filteredBUOptions, setFilteredBUOptions] = useState([])
  useEffect(() => {
    if (selectedBU) {
      let tempData = templatesInfo.filter(temp => temp?.BU?.includes(selectedBU))
      setTemplateOption(tempData)
    }
    setFilteredBUOptions([selectedBU])
  }, [selectedBU, templatesInfo, orgAllowedForm.BUID]);
  return (
    <Dialog open={open}>
      <DialogTitle>
        {" "}
        {isEdit ? "Edit Set Compatible Templates" : "Add Set Compatible Templates"}
      </DialogTitle>
      <DialogContent>
        <Grid container spacing={3} alignItems="center">
          <Grid item md={3} sm={4} xs={12}>
            <span>
              Template For Org Existing <span style={{ color: "red" }}>*</span>
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <Select
              name="TEMPLATE_ORG_EXISTING"
              fullWidth
              value={orgAllowedForm.TEMPLATE_ORG_EXISTING}
              onChange={handleorgAllowedFormChange}
              disabled={isEdit}
            >
              {templateOption?.map((option) => (
                <MenuItem key={option.templateName} value={option.templateName}>
                  {option.templateName}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              Design Organization <span style={{ color: "red" }}>*</span>
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <Select
              id="DataType-select"
              name="BUID"
              fullWidth
              value={selectedBU}
              onChange={handleorgAllowedFormChange}
              disabled
            >
              {filteredBUOptions?.map((option) => (
                <MenuItem key={option} value={option}>
                  {option}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              Template For Org Modifying <span style={{ color: "red" }}>*</span>
            </span>
          </Grid>

          <Grid item md={9} sm={8} xs={12}>
            <Select
              style={{ width: "100%" }}
              multiple
              name="TEMPLATE_ORG_MODIFYING"
              value={modifytempdata}
              onChange={handleorgAllowedFormChange}
              renderValue={(selected) => selected?.join(", ")}
              MenuProps={{
                sx: {
                  "&& .Mui-selected": {
                    backgroundColor: "rgb(25 118 210 / 67%)"
                  }
                }
              }}
            >
              {templateOption?.map((option) => (
                <MenuItem key={option.templateName} value={option.templateName}>
                  {option.templateName}
                </MenuItem>
              ))}
            </Select>
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleorgAllowedDialogClose}>Cancel</Button>
        <Button onClick={handleAddMVOtempComp}>
          {isEdit ? "Update" : "Add"}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default SetCompTempDialog;
