import {
    BACKEND_API_URL,
    REACT_APP_BACKEND_API_HEADER,
} from '../config'
import ApiInstance from './ApiInstance';


export class MappingsService {


    getAllMappings = () => {
        return ApiInstance.get(`${BACKEND_API_URL}/mappings/getMappings`, REACT_APP_BACKEND_API_HEADER).then((res) => {
            if (res.status === 200) {
                return res.data;
            }
            throw res.data;
        }).catch((e) => {
            console.error("getSourceDetails", e);
            return [];
        });
    };

    deleteMapping = (mappingId) => {
        return ApiInstance.delete(`${BACKEND_API_URL}/mappings/deleteMapping/${mappingId}`, REACT_APP_BACKEND_API_HEADER)
            .then((res) => {
                if (res.status === 200) {
                    return res.data;
                }
                throw res.data;
            })
            .catch((error) => {
                console.error('Error deleting source:', error);
                throw error;
            });
    };

    addnewMapping = (data) => {
        return ApiInstance.post(`${BACKEND_API_URL}/mappings/addMapping`, data, REACT_APP_BACKEND_API_HEADER).then((res) => {
            // console.log(data, 'data in post call');
            if (res.status === 200) {
                return res.data;
            }
            throw res.data;
        }).catch((e) => {
            console.error('addnewSource', e);
            return [];
        });
    };

    updateMapping = (data) => {
        return ApiInstance.put(`${BACKEND_API_URL}/mappings/updateMapping`, data, REACT_APP_BACKEND_API_HEADER).then((res) => {
            //console.log(data, 'data in post call');
            if (res.status === 200) {
                return res.data;
            }
            throw res.data;
        }).catch((e) => {
            console.error('addnewPlant', e);
            return [];
        });
    };
}