import { Breadcrumb, SimpleCard } from "app/components";
import React, { useEffect, useState, useRef, useCallback } from "react";
import Button from "@mui/material/Button";
import { Link as RouterLink, useNavigate, useLocation } from "react-router-dom";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import { CircularProgress } from "@mui/material";
import {
  Box,
  styled,
  Grid,
  TextField,
  Tooltip
} from "@mui/material";
import { BUService } from "services/buService";
import CustomAlertBox from "app/components/CustomAlertBox";
import DataGrid from "@inovua/reactdatagrid-community";
import ExportDialogBox from "./exportBUList";
import "@inovua/reactdatagrid-community/index.css";
import useOracleSettings from "app/hooks/useOracleSettings";
import sortedExportDataHOC from "app/components/sortedExportDataHOC";
import { sortDataByKey } from "app/utils/mcoUtils/commonUtils";

const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));

const BUList = () => {
  const { bussinessUnitLists, updateBussinessUnitLists } = useOracleSettings();
  const location = useLocation();
  let toastState = location.state;
  const [selectedSource, setselectedSource] = useState();
  const [buData, setBUData] = useState([]);
  const [searchValue, setSearchValue] = useState("");
  const [deleteDialog, setDeleteDialog] = useState(false);
  const buService = new BUService();
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [sortInfo, setSortInfo] = useState({}); // State to keep track of sorting information
  // ==============table===========
  const [selected, setSelected] = useState({});
  const [selectedRows, setSelectedRows] = useState([]);
  const gridRef = useRef(null);
  const dataMap = gridRef && gridRef.current && gridRef.current.dataMap ? gridRef.current.dataMap : null;
  const [isLoading, setIsLoading] = useState(true);
  const [initialData, setInitialData] = useState([]);
  
  const rowStyle = ({ data }) => {
    const isSelected = Object.keys(selected).includes(data.id);
    const styles = 
    {
          backgroundColor: data.actionFlag !== undefined ? 'rgb(25 118 210 / 20%)' : '',
          zIndex: 99,
          background: (isSelected || selected === true) ? '#dee1f2' : 'initial',
          height: "auto !important",
          whiteSpace: "normal"
        }
    return styles;
  };
 
  const onSelectionChange = useCallback(({ selected }) => {
    setSelected(selected);
  }, []);
  const handleDeleteDialog = () => {
    handleDeleteSourceDilaog(selectedRows[0])
    setDeleteDialog(true);
    // Perform delete operation here using the rowId or rowData
  };
  const handleEditMappingDialog = () => {
    handleEditSource(selectedRows[0]);
  };
  // ==============table===========

  useEffect(() => {
    setIsLoading(true);
    if (!bussinessUnitLists) {
      buService.getAllBuList()
        .then((result) => {
          setBUData(result.data);
          updateBussinessUnitLists(result.data);
          setInitialData(result.data); // Set initial data
          setFilteredBUData(result.data);
        })
        .finally(() => {
          setIsLoading(false);
        });
    } else {
      setTimeout(() => {
        setBUData(bussinessUnitLists);
        setInitialData(bussinessUnitLists);
        setIsLoading(false);
      }, 500);

    }
  }, [bussinessUnitLists]);
  const [filteredBUData, setFilteredBUData] = useState([]);
  useEffect(() => {
    setFilteredBUData(buData);
  }, [buData]);

  const sortedFilteredBuData =  sortDataByKey([...filteredBUData], "buName");

  const handleSearchInputChange = (event) => {
    const newSearchValue = event.target.value;
    setSearchValue(newSearchValue);
  };

  const handleSearch = () => {
  };

  const navigate = useNavigate();
  const plantModeRef = useRef("initialValue");
  const handleCreateBUList = () => {
    plantModeRef.current = "create";
    setTimeout(() => {
      navigate("/createBU", {
        state: { plantMode: plantModeRef.current },
      });
    }, 200);
  };
  const handleEditSource = (editSelectedSource) => {
    plantModeRef.current = "edit";
    const selectedBUData = editSelectedSource;
    setTimeout(() => {
      navigate(`/editBU/${selectedBUData?.id}`, {
        state: { plantMode: plantModeRef.current, selectedBUData },
      });
    }, 200);
  };

  const handleDeleteSourceDilaog = (DeleteSource) => {
    setDeleteDialog(true);
    setselectedSource(DeleteSource);
  };
  const handleCloseDelete = () => {
    setDeleteDialog(false);
  };

  const handleDeleteSource = async () => {
    setDeleteDialog(false);
    try {
      const selRowsId = selectedRows?.map(item => (item?.id));
      await buService.deleteBU(selRowsId).then(response => {
        console.log(response, 'delete response')
        setOpenAlert(true);
        setOpenDesc({
          severity: response?.status,
          desc: response?.msg,
        });
        setSelected({});
        setSelectedRows([]);
      });
    } catch (error) {
      console.error("Failed to delete BU:", error);
    }
    buService.getAllBuList().then((result) => {
      setBUData(result.data);
      updateBussinessUnitLists(result.data)
      setInitialData(result.data);
    });
  };

  // ****************filters**********************
  useEffect(() => {
    const selRowsData = selected === true ? dataMap : selected;
    const selRowsDetails = (selRowsData) && Object.keys(selRowsData).map(key => (selRowsData[key]));
    setSelectedRows(selRowsDetails);
  }, [selected, dataMap]);

  useEffect(() => {
    if (buData) {
      const new_FilteredData = buData.filter((subscriber) => {
        return (searchValue === "" ||
          subscriber.buName
            .toLowerCase()
            .includes(searchValue.toLowerCase())
        )
      })
      setFilteredBUData(new_FilteredData);
    }
  }, [searchValue, buData]);
  useEffect(() => {
    if (toastState?.status) {
      setOpenAlert(true);
      setOpenDesc({
        severity: toastState?.status,
        desc: toastState?.message,
      });
      buService.getAllBuList().then((result) => {
        setBUData(result.data);
        updateBussinessUnitLists(result.data)
        setInitialData(result.data); 
      });
    }
    toastState = {};

    // Clear the state value on page refresh
    window.onbeforeunload = () => {
      window.history.replaceState({}, document.title);
    };
  }, [toastState]);

  const handleSortInfoChange = (sortInfo) => {
    setSortInfo(sortInfo);
    const SortedbuList = sortedExportDataHOC(BUList);
    const sortedData = SortedbuList({ data: buData, sortInfo, sortedFilteredBuData, onSortInfoChange: handleSortInfoChange, initialData });
    setBUData(sortedData.props.data);
    setFilteredBUData(sortedData.props.data);
  };

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Design Organization" }]} />
      </Box>

      <SimpleCard>
        <Grid container spacing={1} alignItems="center">
          <Grid item md={3} sm={6} xs={12} className="font-medium">
            <TextField
              type="text"
              name="search"
              size="small"
              onChange={handleSearchInputChange}
              value={searchValue}
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid item container md={9} sm={6} xs={12} alignItems="center">
            <Button variant="contained" onClick={handleSearch}>
              Search
            </Button>
            <div style={{ flex: 1 }} />
            <Button
              variant="contained"
              component={RouterLink}
              onClick={handleCreateBUList}
            >
              Create
            </Button>
            <Button
              variant="contained"
              disabled={selectedRows?.length === 1 ? false : true}
              onClick={handleEditMappingDialog}
              style={{ margin: '0 0px 0 10px' }}
            >
              Edit
            </Button>
            <Button
              variant="contained"
              disabled={selectedRows?.length ? false : true}
              onClick={handleDeleteDialog}
              style={{ margin: '0 10px 0 10px' }}
            >
              Delete
            </Button>
            <ExportDialogBox sortInfo={sortInfo} buData={buData} filterTemplateData={selectedRows} />
          </Grid>
          <Box width="100%" overflow="auto">
            <br />
            {isLoading ? (
              <Box
                sx={{
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  minHeight: 550,
                }}
              >
                <CircularProgress />
              </Box>
            ) : (
              <DataGrid
                idProperty="id"
                showZebraRows={false}
                defaultLimit={100}
                rowStyle={rowStyle}
                // style={{ minHeight: 550 }}
                columns={[
                  {
                    name: "buName",
                    header: <Tooltip title={'Design Organization'}>Design Organization</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                    render: ({ data }) => <div>{data?.buName}</div>,
                  },
                  {
                    name: "exportToERPdisabled",
                    header: <Tooltip title={"Export To ERP Disabled"}>Export To ERP Disabled</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                  },
                  {
                    name: "exportToERPbyPassed",
                    header: <Tooltip title={"Export To ERP Bypassed"}>Export To ERP Bypassed</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                  },
                  {
                    name: "enableBOSTransfer",
                    header: <Tooltip title={"Enable BOS Transfer"}>Enable BOS Transfer</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                  },
                  {
                    name: "contextDFF",
                    header: <Tooltip title={"Context DFF"}>Context DFF</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                    render: ({ data }) => <div>{data?.contextDEF}</div>,
                  },
                  {
                    name: "EMRDivisionORGValue",
                    header: <Tooltip title={"EMR Division ORG Value"}>EMR Division ARG</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                    render: ({ data }) => <div>{data?.EMRDivisionORGValue}</div>,
                  },
                  {
                    name: "productHierarchy",
                    header: <Tooltip title={"Product Hierarchy"}>Product Hierarchy</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                    render: ({ data }) => <div>{data?.productHierarchy}</div>,
                  },
                  {
                    name: "plants",
                    header: <Tooltip title={"Plant Name"}>Plant Name</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                    render: ({ data }) => (
                      <div>
                        {data?.plants
                          .sort((a, b) => a.plantName.localeCompare(b.plantName))
                          .map((item, index) => (
                            <div key={index}>{item?.plantName}</div>
                          ))}
                      </div>
                    ),
                  },
                  {
                    name: "DoNotSendToERP",
                    header: <Tooltip title={"DoNotSendToERP"}>Do Not Send To ERP</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                    render: ({ data }) => (
                      <div>
                        {data?.doNotSendToERP
                          .sort((a, b) => a.localeCompare(b)) 
                          .map((item, index) => (
                            <div key={index}>{item}</div>
                          ))}
                      </div>
                    ),
                  },
                  {
                    name: "OracleExisting",
                    header: <Tooltip title={"OracleExisting"}>Oracle Existing</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 50,
                    render: ({ data }) => (
                      <div>
                        {data?.oracleExisting.sort((a, b)=>a.localeCompare(b))
                        .map((item, index) => (
                          <div key={index}>{item}</div> 
                        ))}
                      </div>
                    ),
                  },
                  {
                    name: "MakeWithoutBOMList",
                    header: <Tooltip title={"Make Without BOMList"}>Make Without BOM List</Tooltip>,
                    defaultFlex: 0,
                    defaultWidth: 170,
                    render: ({ data }) => (
                      <div>
                        {data?.makeWithoutBOMList.sort((a, b)=>a.localeCompare(b))
                        .map((item, index) => (
                          <div key={index}>{item}</div>
                        ))}
                      </div>
                    ),
                  },
                  {
                    name: "NotOwningDivisionList",
                    header: <Tooltip title={"Not Owning DivisionList"}>Not Owning Division List</Tooltip>,
                    defaultFlex: 1,
                    defaultWidth: 100,
                    render: ({ data }) => (
                      <div>
                        {data?.notOwningDivisionList.sort((a, b)=>a.localeCompare(b))
                        .map((item, index) => (
                          <div key={index}>{item}</div> 
                        ))}
                      </div>
                    ),
                  },
                ]}
                dataSource={sortedFilteredBuData}
                // rowHeight={null}
                checkboxColumn
                selected={selected}
                onSelectionChange={onSelectionChange}
                handle={ref => gridRef.current = ref ? ref.current : null}
                pagination
                renderScroller={() => gridRef?.current?.setScrollTop(selectedRows?.length == 0 ? 0 : gridRef?.current?.getScrollTop())}
                onSortInfoChange={handleSortInfoChange}
              />
            )}
          </Box>
        </Grid>
      </SimpleCard>
      {(openDesc) ? (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      ) : (<></>)}
      <Dialog open={deleteDialog}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Delete Design Organization
        </DialogTitle>
        <DialogContent>
          {
            (selectedRows.length === 1) ? (<>
              <DialogContentText>
                Do you want to delete the RDO <b>{selectedSource?.buName}</b> having
                plants{" "}
                <b>
                  {" "}
                  {selectedSource?.plants
                    .map((plant) => {
                      return plant.plantName;
                    })
                    .join(",")}{" "}
                </b>
              </DialogContentText>
            </>) : (<>
              <DialogContentText>
                Do you want to delete the seleted Design Organization?
              </DialogContentText>
            </>)
          }
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCloseDelete}>
            Cancel
          </Button>
          <Button onClick={handleDeleteSource}>delete</Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default BUList;