import { BACKEND_API_URL, REACT_APP_BACKEND_API_HEADER } from "../config";
import ApiInstance from './ApiInstance';

export class PlantService {
  getAllPlantsList = () => {
    return ApiInstance
      .get(`${BACKEND_API_URL}/plant/getplants`, REACT_APP_BACKEND_API_HEADER)
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getAllPlantsList", e);
        return [];
      });
  };
  getPlantById = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/plant/getPlantById`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getPlantById", e);
        return [];
      });
  };
  addnewPlant = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/plant/addPlant`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("addnewPlant", e);
        return [];
      });
  };

  updatePlant = (data) => {
    return ApiInstance
      .put(
        `${BACKEND_API_URL}/plant/updatePlant`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("addnewPlant", e);
        return [];
      });
  };

  deletePlant = (ids) => {
    return ApiInstance.delete(
      `${BACKEND_API_URL}/plant/deletePlant`,
      { data: { ids } },
      REACT_APP_BACKEND_API_HEADER
    )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error("Error deleting plant:", error);
        throw error;
      });
  };
}
