import React from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
  TextField,
  FormControl,
  RadioGroup,
  FormControlLabel,
  Radio,
  Select,
  MenuItem,
  Button,
  IconButton,
  Paper,
} from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";

const AttributeDialog = ({
  open,
  handleDialogClose,
  tempObject,
  handleAttributeChange,
  handleAddAttribute,
  newKey,
  newValue,
  handleAddRow,
  handleDeleteRow,
  keyerror,
  attributeForm,
  valuesInputList,
}) => {
  const InputTypeOptions = [
    { value: "text", label: "text" },
    { value: "select", label: "select" },
  ];
  const DataTypeOptions = [
    { value: "int", label: "int" },
    { value: "string", label: "string" },
    { value: "long", label: "long" },
    { value: "bool", label: "bool" },
  ];
  return (
    <Dialog open={open}>
      <DialogTitle>
        {attributeForm === null ? "Add Attribute" : "Edit Attribute"}
      </DialogTitle>
      <DialogContent>
        <Grid container spacing={3} alignItems="center">
          <Grid item md={3} sm={4} xs={12}>
            <span>
              Name:
              {/* <span className={classes.spanRed}>*</span>:*/}
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <TextField
              name="name"
              value={tempObject.name || ""}
              onChange={handleAttributeChange}
              fullWidth
              margin="normal"
            />
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              Mandatory: {/* <span className={classes.spanRed}>*</span>:*/}
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <FormControl>
              <RadioGroup
                row
                value={tempObject.mandatory || "YES"}
                aria-labelledby="demo-row-radio-buttons-group-label"
                name="mandatory"
                onChange={handleAttributeChange}
              >
                <FormControlLabel value="Y" control={<Radio />} label="Yes" />
                <FormControlLabel value="N" control={<Radio />} label="No" />
              </RadioGroup>
            </FormControl>
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              Material master:{" "}
              {/* <span className={classes.spanRed}>*</span>:*/}
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <FormControl>
              <RadioGroup
                row
                value={tempObject.material_master || "Y"}
                aria-labelledby="demo-row-radio-buttons-group-label"
                name="material_master"
                onChange={handleAttributeChange}
              >
                <FormControlLabel value="Y" control={<Radio />} label="Yes" />
                <FormControlLabel value="N" control={<Radio />} label="No" />
              </RadioGroup>
            </FormControl>
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              Data type: {/* <span className={classes.spanRed}>*</span>:*/}
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <Select
              labelId="DataType-label"
              id="DataType-select"
              name="dataType"
              fullWidth
              value={tempObject.dataType || ""}
              onChange={handleAttributeChange}
            >
              {DataTypeOptions.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.value}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item md={3} sm={4} xs={12}>
            <span>
              Input Type: {/* <span className={classes.spanRed}>*</span>:*/}
            </span>
          </Grid>
          <Grid item md={9} sm={8} xs={12}>
            <Select
              labelId="InputType-label"
              id="InputType-select"
              name="inputType"
              fullWidth
              value={tempObject.inputType || ""}
              onChange={handleAttributeChange}
            >
              {InputTypeOptions.map((option) => (
                <MenuItem key={option.value} value={option.value}>
                  {option.label}
                </MenuItem>
              ))}
            </Select>
          </Grid>
          {tempObject.inputType === "select" && (
            <>
              <Grid item md={3} sm={4} xs={12}>
                <span>
                  Values: {/* <span className={classes.spanRed}>*</span>:*/}
                </span>
              </Grid>

              <Grid item md={9} sm={8} xs={12}>
                <Grid container alignItems="center">
                  <Grid item xs={3} style={{ marginLeft: "3px" }}>
                    <TextField
                      name="newKey"
                      label="value"
                      value={newKey}
                      variant="outlined"
                      fullWidth
                      margin="normal"
                      onChange={handleAttributeChange}
                    />
                  </Grid>
                  &nbsp; &nbsp;
                  <Grid item xs={3}>
                    <TextField
                      name="newValue"
                      label="display value"
                      value={newValue}
                      variant="outlined"
                      fullWidth
                      margin="normal"
                      onChange={handleAttributeChange}
                    />
                  </Grid>
                  &nbsp; &nbsp;
                  <Grid item xs={3}>
                    <Button
                      style={{ fontSize: "30px", marginTop: "6px" }}
                      onClick={handleAddRow}
                    >
                      <b>+</b>
                    </Button>
                  </Grid>
                </Grid>
                <p style={{ color: "red" }}>{keyerror}</p>
                <div
                  style={{
                    height: "100px",
                    overflowY: "auto",
                    border: "1px solid #ccc",
                    borderRadius: "4px",
                    alignContent: "center",
                  }}
                >
                  {valuesInputList.map((row, index) => (
                    <Paper
                      key={index}
                      style={{
                        paddingLeft: "50px",
                        marginBottom: "8px",
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                      }}
                    >
                      <Grid
                        container
                        alignItems="center"
                        style={{ marginLeft: "2px" }}
                      >
                        <Grid item xs={6}>
                          <p>
                            <b>{row.key}</b>:<b>{row.value}</b>
                          </p>
                        </Grid>
                        <Grid item xs={2}>
                          <IconButton onClick={() => handleDeleteRow(index)}>
                            <DeleteIcon />
                          </IconButton>
                        </Grid>
                      </Grid>
                    </Paper>
                  ))}
                </div>
              </Grid>
            </>
          )}

          <Grid item md={3} sm={4} xs={12}>
            <span>
              Default values: {/* <span className={classes.spanRed}>*</span>:*/}
            </span>
          </Grid>

          <Grid item md={9} sm={8} xs={12}>
            <TextField
              name="defaultValues"
              value={tempObject.defaultValues || ""}
              onChange={handleAttributeChange}
              fullWidth
              margin="normal"
            />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleDialogClose}>Cancel</Button>
        <Button onClick={handleAddAttribute}>
          {attributeForm === null ? "Add" : "Update"}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default AttributeDialog;
