import React, { useEffect, useState, useRef, useCallback } from "react";
import { Breadcrumb, SimpleCard } from "app/components";
import Constant from '../../../constant'
import Button from "@mui/material/Button";
import { Link as RouterLink } from "react-router-dom";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import ExpenseMappingDialog from "./expensemapDialog";
import DataGrid from "@inovua/reactdatagrid-community";
import "@inovua/reactdatagrid-community/index.css";
import { Box, styled, Grid, TextField, Tooltip } from "@mui/material";
import useOracleSettings from "app/hooks/useOracleSettings";
import { TemplateMappingService } from "services/templateMappingService";
import { HistoryManagementService } from "services/historyManagementService";
import CustomSelect from "../../../components/CustomSelect";
import { BUOptions } from "../../adminConfig/common";
import { MVOtempcompService } from "services/mvotempcompService";
import CircularProgress from "@mui/material/CircularProgress";
import ImportMappingsDialog from "../../../components/importMappingsDialog";
import ExportMappingDialog from '../../../components/exportMappingTemplate'; //export functionality
import CustomAlertBox from "app/components/CustomAlertBox";
import { sortDataByKey } from "app/utils/mcoUtils/commonUtils";

const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));

const ExpenseMappingList = () => {
  const {
    refreshData,
    templatesInfo,
    bussinessUnit,
    bussinessUnitLists,
    mappingTemplatesInfo,
    updateBussinessUnit,
    updateMappingTemplatesInfo
  } = useOracleSettings();

  const [importTemplate, setImportTemplate] = useState(false);
  const [searchValue, setSearchValue] = useState("");
  const [deleteDialog, setDeleteDialog] = useState(false);
  const [orgallowedaddDialog, setOrgallowedaddDialog] = useState(false);
  const [orgAllowedForm, setOrgAllowedForm] = useState({});
  const gridStyle = { minHeight: 550 };
  const mvotempcompservice = new MVOtempcompService();
  const tempMapService = new TemplateMappingService();
  const draftMapService = new HistoryManagementService();
  const [selectedDeleteMapping, setSelectedDeleteMapping] = useState();
  const [isEdit, setIsEdit] = useState(false);
  const [expensemapData, setExpenseMapData] = useState([]);
  const [selectedRows, setSelectedRows] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [selected, setSelected] = useState({});
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [plantValueList, setPlantValueList] = useState([]);
  const [filteredDraftTabledata, setFilteredDraftTabledata] = useState([]);
  const [sortInfo, setSortInfo] = useState({});
  const gridRef = useRef(null);
  const dataMap = gridRef && gridRef.current && gridRef.current.dataMap ? gridRef.current.dataMap : null;
  const handleSearchInputChange = (event) => {
    const newSearchValue = event.target.value;
    setSearchValue(newSearchValue);

  };
  const handleSearch = () => {
  };
  const handleCloseDelete = () => {
    setDeleteDialog(false);
  };

  const handlesnackBarClick = (message, severity) => {
    setOpenAlert(true);
    setOpenDesc({
      severity: severity,
      desc: message,
    })
  };
  let plantColumnsSet = new Set(); // Define it outside
  if (expensemapData && expensemapData.length > 0) {
    expensemapData.forEach((dataItem) => {
      const plants = dataItem.details?.EXPENSE_PLANTS;
      if (plants) {
        const plantKeys = Object.keys(plants);

        plantKeys.forEach((plant) => {
          plantColumnsSet.add(plant);
        });
      }
    });
  }

  const plantColumns = Array.from(plantColumnsSet).map((plant) => {
    return {
      name: plant,
      header: plant,
      sortable: false,
      defaultWidth: 90,
      //render: ({ data }) => <div>{data?.details?.EXPENSE_PLANTS[plant]}</div>,
      render: (params) => (
        <div dangerouslySetInnerHTML={{ __html: params.value }} />
      ),
    };
  });
  const handleorgAllowedFormChange = (event) => {
    const { name, value } = event.target;
    // If it's a form field change
    const updatedForm = {
      ...orgAllowedForm,
      [name]: value,
    };
    setOrgAllowedForm(updatedForm);
  };
  const handleAddOrgAllowedValues = () => {
    const templateid = templatesInfo?.filter(
      (template) => template.templateName === orgAllowedForm.EXPENSE_TEMPLATE
    );
    const resultPlantPayload = plantValueList.reduce((acc, curr) => {
      acc[curr.plantName] = Number(curr.expense) !== 0 && !isNaN(Number(curr.expense)) ? Number(curr.expense) : '';
      return acc;
    }, {});
    if (
      !orgAllowedForm.EXPENSE_TEMPLATE ||
      !orgAllowedForm.BUID ||
      plantValueList.length < 1
    ) {
      handlesnackBarClick("Please Enter the required fields", "error");
      return;
    }
    if (orgAllowedForm) {
      const neworgallowedvalue = {
        BUID: orgAllowedForm.BUID,
        MappingType: "expensemapping",
        id:
          templateid[0].id +
          "_" +
          orgAllowedForm.BUID.trim() +
          "_" +
          "expensemapping",
        details: {
          EXPENSE_TEMPLATE: orgAllowedForm.EXPENSE_TEMPLATE,
          EXPENSE_TEMPLATE_ID: templateid[0].id,
          EXPENSE_PLANTS: resultPlantPayload,
        },
      };
      if (isEdit === false) {
        mvotempcompservice
          .addnewTempCompMapping(neworgallowedvalue)
          .then((resp) => {
            if (resp?.status === 'success') {
              fetchData();
              setTimeout(() => {
                setOrgallowedaddDialog(false);
              }, 200);
              setSelected({})
              handlesnackBarClick("Expense Mapping added successfully", "success");
            } else {
              handlesnackBarClick(resp?.msg, resp?.status)
            }
          });
      } else {
        mvotempcompservice
          .updateTempCompMapping(neworgallowedvalue)
          .then((resp) => {
            if (resp.status === "success") {
              setTimeout(() => {
                setOrgallowedaddDialog(false);
                fetchData();
              }, 200);
              setIsEdit(false);
              setSelected({})
              handlesnackBarClick("Expense Mapping updated successfully", "success");
            } else {
              handlesnackBarClick(resp?.msg, resp?.status)
            }
          });
      }
    }
    handleorgAllowedDialogClose();
  };
  const handleorgAllowedDialogOpen = () => {
    setOrgAllowedForm({ BUID: bussinessUnit });
    setOrgallowedaddDialog(true);
  };
  const handleorgAllowedDialogClose = () => {
    setOrgallowedaddDialog(false);
    setOrgAllowedForm("");
    setPlantValueList([])
    setIsEdit(false);
    setSelectedRows([])
    setSelected({})
  };

  const handleorgAllowedDialogAdd = () => {
    setOrgallowedaddDialog(false);
  };
  const handleDeleteDialog = () => {
    setSelectedDeleteMapping(selectedRows[0]);
    setDeleteDialog(true);
  };
  const handleEditExpenseMapping = () => {
    const selectedDetails = selectedRows[0]?.details;
    const BUID = { BUID: selectedRows[0]?.BUID };
    setOrgAllowedForm({
      ...selectedDetails,
      ...BUID,
    });
    const plantValuesarr = Object.entries(
      selectedRows[0]?.details?.EXPENSE_PLANTS
    ).map(([key, value], index) => {
      return { 'plantName': key, 'expense': value, 'id': index };
    });
    sortDataByKey(plantValuesarr, "plantName");
    setPlantValueList(plantValuesarr)
    setIsEdit(true);
    setOrgallowedaddDialog(true);
  };

  const handleDeleteOrgAllowedValues = async () => {
    try {
      await mvotempcompservice.softDeleteTempCompMapping({ data: selectedRows }).then(result => {
        handleCloseDelete(false)
        setSelectedRows([])
        setSelectedDeleteMapping([])
        setSelected({})
      });
    } catch (error) {
      console.error("Failed to delete mapping:", error);
    }
    handlesnackBarClick("Expense Mapping deleted Successfully", "success");
    fetchData();
    setSelectedRows([]);
  };

  const columns = [
    {
      name: "EXPENSE_TEMPLATE",
      header: <Tooltip title={Constant.adminConstant.expensemapList.tooltip.EXPENSE_TEMPLATE}>Template</Tooltip>,
      defaultWidth: 250,
      // render: (params) => (
      //   <div dangerouslySetInnerHTML={{ __html: params.value }} />
      // )
      render: ({ data }) => <div>{data?.EXPENSE_TEMPLATE}</div>,
    },

    {
      name: "BUID",
      header: <Tooltip title={Constant.adminConstant.expensemapList.tooltip.RDO}>Design Organization</Tooltip>,
      defaultWidth: 150,
    },

    ...plantColumns,

    // Add a custom column for checkboxes
  ];

  const fetchData = () => {
    setIsLoading(true);
    tempMapService.getAlltemplateMappings().then((result) => {
      updateMappingTemplatesInfo(result?.data?.templateMappingList);
      setIsLoading(false);
    });
  }

  useEffect(() => {
    setIsLoading(refreshData);
  }, [refreshData]);
  const [tableTempData, setTableTempData] = useState([])

  const sortedFilterExpenseMappingData = sortDataByKey(tableTempData, "EXPENSE_TEMPLATE");

  useEffect(() => {
    fetchDraftData();
  }, []);

  useEffect(() => {
    setIsLoading(true);
    if (mappingTemplatesInfo) {
      let finalTempMapList = mappingTemplatesInfo;
      finalTempMapList = finalTempMapList.filter(
        (item) => item.MappingType === "expensemapping" && item.actionFlag !== "Delete"
      );

      if (bussinessUnit) {
        finalTempMapList = finalTempMapList?.filter((fitem) => {
          return fitem.BUID === bussinessUnit;
        });
      }
      if (searchValue && searchValue !== "") {
        finalTempMapList = finalTempMapList?.filter((fitem) => {
          return (
            JSON.stringify(fitem)?.includes(searchValue) ||
            JSON.stringify(fitem)?.toLocaleLowerCase()?.includes(searchValue)
          );
        });
      }
      if (filteredDraftTabledata.length > 0) {
        const filteredExpenseDraftData = filteredDraftTabledata.filter((i) => { return i.MappingType == "expensemapping" });
        if (filteredExpenseDraftData.length > 0 && filteredExpenseDraftData[0].MappingType === 'expensemapping') {
          finalTempMapList.forEach(obj1 => {
            filteredExpenseDraftData[0].changes.forEach(obj2 => {
              if (obj1['id'] === obj2['id']) {
                obj1.details.draftValue_EXPENSE_TEMPLATE = obj2.details.EXPENSE_TEMPLATE !== obj1.details.EXPENSE_TEMPLATE ? obj2.details.EXPENSE_TEMPLATE : null;
                obj1.details.draft_Plants = {};
                for (let key in obj1.details.EXPENSE_PLANTS) {
                  obj1.details.draft_Plants[key] = obj2.details.EXPENSE_PLANTS[key] !== obj1.details.EXPENSE_PLANTS[key] ? obj2.details.EXPENSE_PLANTS[key] : null;
                }
                // obj1.details.draft_Plants = {
                //   "MME": obj2.details.EXPENSE_PLANTS.MME !== obj1.details.EXPENSE_PLANTS.MME ? obj2.details.EXPENSE_PLANTS.MME : null,
                //   "MMC": obj2.details.EXPENSE_PLANTS.MMC !== obj1.details.EXPENSE_PLANTS.MMC ? obj2.details.EXPENSE_PLANTS.MMC : null,
                //   "MVO": obj2.details.EXPENSE_PLANTS.MVO !== obj1.details.EXPENSE_PLANTS.MVO ? obj2.details.EXPENSE_PLANTS.MVO : null,
                //   "NA3": obj2.details.EXPENSE_PLANTS.NA3 !== obj1.details.EXPENSE_PLANTS.NA3 ? obj2.details.EXPENSE_PLANTS.NA3 : null,
                //   "NAE": obj2.details.EXPENSE_PLANTS.NAE !== obj1.details.EXPENSE_PLANTS.NAE ? obj2.details.EXPENSE_PLANTS.NAE : null,
                //   "CL2": obj2.details.EXPENSE_PLANTS.CL2 !== obj1.details.EXPENSE_PLANTS.CL2 ? obj2.details.EXPENSE_PLANTS.CL2 : null,
                //   "PVE": obj2.details.EXPENSE_PLANTS.PVE !== obj1.details.EXPENSE_PLANTS.PVE ? obj2.details.EXPENSE_PLANTS.PVE : null,
                //   "MMR": obj2.details.EXPENSE_PLANTS.MMR !== obj1.details.EXPENSE_PLANTS.MMR ? obj2.details.EXPENSE_PLANTS.MMR : null,
                //   "MFS": obj2.details.EXPENSE_PLANTS.MFS !== obj1.details.EXPENSE_PLANTS.MFS ? obj2.details.EXPENSE_PLANTS.MFS : null,
                //   "MET": obj2.details.EXPENSE_PLANTS.MET !== obj1.details.EXPENSE_PLANTS.MET ? obj2.details.EXPENSE_PLANTS.MET : null,
                //   "MMB": obj2.details.EXPENSE_PLANTS.MMB !== obj1.details.EXPENSE_PLANTS.MMB ? obj2.details.EXPENSE_PLANTS.MMB : null
                // }
              }
            });
          });
        }
      }
      setExpenseMapData(finalTempMapList);
      let data = finalTempMapList.map(item => {
        item["EXPENSE_TEMPLATE"] = item?.details?.EXPENSE_TEMPLATE;
        // Object.keys(item?.details?.EXPENSE_PLANTS).map(plant => {
        //   item?.details?.draft_Plants ? Object.keys(item?.details?.draft_Plants).map(draft_Plant =>{
        //       item["MME"] = item?.details?.draft_Plants["MME"] ? `${item?.details?.draft_Plants["MME"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.EXPENSE_PLANTS["MME"]} <span>` : item?.details?.EXPENSE_PLANTS["MME"]
        //       item["MMC"]= item?.details?.draft_Plants["MMC"] ? `${item?.details?.draft_Plants["MMC"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.EXPENSE_PLANTS["MMC"]} <span>` : item?.details?.EXPENSE_PLANTS["MMC"]
        //       item["MVO"]= item?.details?.draft_Plants["MVO"] ? `${item?.details?.draft_Plants["MVO"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.EXPENSE_PLANTS["MVO"]} <span>` : item?.details?.EXPENSE_PLANTS["MVO"]
        //       item["NA3"]= item?.details?.draft_Plants["NA3"] ? `${item?.details?.draft_Plants["NA3"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.EXPENSE_PLANTS["NA3"]} <span>` : item?.details?.EXPENSE_PLANTS["NA3"]
        //       item["NAE"]= item?.details?.draft_Plants["NAE"] ? `${item?.details?.draft_Plants["NAE"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.EXPENSE_PLANTS["NAE"]} <span>` : item?.details?.EXPENSE_PLANTS["NAE"]
        //       item["CL2"]= item?.details?.draft_Plants["CL2"] ? `${item?.details?.draft_Plants["CL2"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.EXPENSE_PLANTS["CL2"]} <span>` : item?.details?.EXPENSE_PLANTS["CL2"]
        //       item["PVE"]= item?.details?.draft_Plants["PVE"] ? `${item?.details?.draft_Plants["PVE"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.EXPENSE_PLANTS["PVE"]} <span>` : item?.details?.EXPENSE_PLANTS["PVE"]
        //       item["MMR"]= item?.details?.draft_Plants["MMR"] ? `${item?.details?.draft_Plants["MMR"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.EXPENSE_PLANTS["MMR"]} <span>` : item?.details?.EXPENSE_PLANTS["MMR"]
        //       item["MFS"]= item?.details?.draft_Plants["MFS"] ? `${item?.details?.draft_Plants["MFS"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.EXPENSE_PLANTS["MFS"]} <span>` : item?.details?.EXPENSE_PLANTS["MFS"]
        //       item["MET"]= item?.details?.draft_Plants["MET"] ? `${item?.details?.draft_Plants["MET"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.EXPENSE_PLANTS["MET"]} <span>` : item?.details?.EXPENSE_PLANTS["MET"]
        //       item["MMB"]= item?.details?.draft_Plants["MMB"] ? `${item?.details?.draft_Plants["MMB"]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.EXPENSE_PLANTS["MMB"]} <span>`: item?.details?.EXPENSE_PLANTS["MMB"]
        //     }) : item[plant] = item?.details?.EXPENSE_PLANTS[plant]          
        //   }            
        // )
        Object.keys(item?.details?.EXPENSE_PLANTS).map(plant => {
          item?.details?.draft_Plants ? item[plant] = item?.details?.draft_Plants[plant] ? `${item?.details?.draft_Plants[plant]} &nbsp;&nbsp;&nbsp;&nbsp;<span className="previousSelection" style="color:#f00; font-weight:bold;text-decoration: line-through"> ${item?.details?.EXPENSE_PLANTS[plant]} <span>` : item?.details?.EXPENSE_PLANTS[plant]
            : item[plant] = item?.details?.EXPENSE_PLANTS[plant]
        }
        )
        return item
      })
      setTimeout(() => {
        setTableTempData(data);
        setIsLoading(false)
      }, 500)
    } else {
      if (!bussinessUnit) {
        updateBussinessUnit(BUOptions ? BUOptions[0]?.value : "");
      }
    }
  }, [mappingTemplatesInfo, bussinessUnit, searchValue, updateBussinessUnit, filteredDraftTabledata]);

  const fetchDraftData = () => {
    setIsLoading(true);
    draftMapService.getAlldraftChanges().then((result) => {
      let BUID = bussinessUnit ? bussinessUnit : 'Micro Motion';
      let filterData = [];
      if (result.data) {
        filterData = result.data.filter(element => element.BUID === BUID);
      }     
      setFilteredDraftTabledata(filterData);
      setIsLoading(false);
    });
  }

  const onSelectionChange = useCallback(({ selected }) => {
    setSelected(selected);
  }, []);

  useEffect(() => {
    const selRowsData = selected === true ? dataMap : selected;
    const selRowsDetails = (selRowsData) && Object.keys(selRowsData).map(key => (selRowsData[key]));
    setSelectedRows(selRowsDetails);
  }, [selected, dataMap]);
  const rowStyle = ({ data }) => {
    const isSelected = Object.keys(selected).includes(data.id);
    return {
      backgroundColor: data.actionFlag !== undefined ? 'rgb(25 118 210 / 20%)' : '',
      zIndex: 99,
      background: (isSelected || selected === true) ? '#dee1f2' : 'initial',
    }
  }
  
  const handleSortInfoChange = (sortInfo) => {
    setSortInfo(sortInfo);
  };

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Expense Mapping" }]} />
      </Box>
      <SimpleCard>
        <Grid container spacing={1} alignItems="top">
          {/* Search Input and Search Button */}
          <Grid item md={4} sm={6} xs={12} className="font-medium">
            <Grid container spacing={1} alignItems="center">
              <Grid item md={6} sm={6} xs={12} className="font-medium">
                <TextField
                  type="text"
                  name="search"
                  size="small"
                  onChange={handleSearchInputChange}
                  value={searchValue}
                  style={{ width: "100%" }}
                />
              </Grid>
              <Grid item md={6} sm={6} xs={12} className="font-medium">
                <Button variant="contained" onClick={handleSearch}>
                  Search
                </Button>
              </Grid>
            </Grid>
          </Grid>

          {/* Design Organization Text and Dropdown */}
          <Grid item md={8} sm={6} xs={12} className="font-medium">
            <Grid container alignItems="center" justify="flex-end" style={{ display: "none" }}>
              <Grid item xs={12} sm={6} md={6} className="font-medium" style={{ textAlign: 'right' }}>
                <label style={{ whiteSpace: "nowrap", paddingTop: "11px" }}>Design Organization:</label>
              </Grid>
              <Grid item xs={12} sm={6} md={6} className="font-medium" style={{ textAlign: 'left' }}>
                <CustomSelect
                  onChange={handleSearchInputChange}
                  selectedvalue={bussinessUnit}
                  style={{ width: "100%" }}
                  optionsValues={BUOptions}
                  // defaultValue={
                  //   BUOptions && !bussinessUnit ? BUOptions[0]?.value : ""
                  // }
                  onChangeFunc={(selectedOptionValue) => {
                    setSelected({});
                    setSelectedRows([]);
                    updateBussinessUnit(selectedOptionValue);
                    setSearchValue("");
                    setSelectedRows([]);
                  }}
                />
              </Grid>
            </Grid>

            {/* Right-aligned Buttons */}
            <Grid item xs={12} className="font-medium" style={{ marginTop: '0px', textAlign: 'right' }}>
              <Button
                variant="contained"
                component={RouterLink}
                onClick={handleorgAllowedDialogOpen}
                style={{ margin: "0 0px 0 10px" }}
              >
                Add
              </Button>
              <Button
                variant="contained"
                component={RouterLink}
                disabled={selectedRows?.length === 1 ? false : true}
                onClick={handleEditExpenseMapping}
                style={{ margin: "0 0px 0 10px" }}
              >
                Edit
              </Button>
              <Button
                variant="contained"
                component={RouterLink}
                disabled={selectedRows?.length !== 0 ? false : true}
                onClick={handleDeleteDialog}
                style={{ margin: "0 0px 0 10px" }}
              >
                Delete
              </Button>
              <Button
                variant="contained"
                onClick={() => {
                  setImportTemplate(true);
                }}
                style={{ margin: "0 10px 0 10px" }}
              >
                Import
              </Button>
              {/* export btn */}
              <ExportMappingDialog
                title={"Expense Mapping"}
                MappingType={"expensemapping"}
                filterTemplateData={selectedRows}
                sortInfo={sortInfo} // Pass sortInfo to ExportMappingDialog 
              />
            </Grid>
          </Grid>

          {/* DataGrid */}
          <Grid item xs={12} style={{ marginTop: '10px' }}>
            <Box width="100%" overflow="auto" marginTop="10px">
              {isLoading && (
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    height: "100vh",
                  }}
                >
                  <CircularProgress />
                </Box>
              )}
              {!isLoading && (
                <DataGrid
                  idProperty="id"
                  showZebraRows={false}
                  style={gridStyle}
                  columns={columns}
                  dataSource={sortInfo === null ? expensemapData : sortedFilterExpenseMappingData}
                  rowHeight={null}
                  checkboxColumn
                  defaultLimit={100}
                  selected={selected}
                  onSelectionChange={onSelectionChange}
                  handle={ref => gridRef.current = ref ? ref.current : null}
                  rowStyle={rowStyle}
                  pagination
                  renderScroller={()=> gridRef?.current?.setScrollTop(selectedRows?.length==0 ? 0 : gridRef?.current?.getScrollTop())}
                    onSortInfoChange={handleSortInfoChange}
                />
              )}
            </Box>
          </Grid>
        </Grid>
      </SimpleCard>
      {
        (openDesc) ? (
          <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
        ) : (<></>)
      }
      <Dialog open={deleteDialog}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Delete Mapping
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            {'Do you want to delete the ExpenseAcctByTemplateAndPlant Mapping(s) for Design Organization'} <b>{selectedDeleteMapping?.BUID}</b>

            <br />
            <br />
            <b>Note:</b> {Constant.adminConstant.common.deleteNote}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDelete}>Cancel</Button>
          <Button onClick={handleDeleteOrgAllowedValues}>delete</Button>
        </DialogActions>
      </Dialog>

      <ExpenseMappingDialog
        open={orgallowedaddDialog}
        handleorgAllowedDialogAdd={handleorgAllowedDialogAdd}
        handleorgAllowedDialogClose={handleorgAllowedDialogClose}
        orgAllowedForm={orgAllowedForm}
        handleorgAllowedFormChange={handleorgAllowedFormChange}
        handleAddOrgAllowedValues={handleAddOrgAllowedValues}
        isEdit={isEdit}
        templatesInfo={templatesInfo}
        bussinessUnitLists={bussinessUnitLists}
        selectedBU={bussinessUnit}
        setPlantValueList={setPlantValueList}
        plantValueList={plantValueList}
      />
      <ImportMappingsDialog
        title={"Import Expense Mappings"}
        MappingType={"expensemapping"}
        selectedBU={bussinessUnit}
        importTemplate={importTemplate}
        fetchData={fetchData}
        setImportTemplate={setImportTemplate}
      />
    </Container >
  );
};

export default ExpenseMappingList;
