import { BACKEND_API_URL, REACT_APP_BACKEND_API_HEADER, REACT_APP_BACKEND_API_HEADER_FOR_FILE } from "../config";
import ApiInstance from './ApiInstance';
import { getOKTAAccessKey } from "./../app/utils/commonFunctions";

export class TemplateMappingService {
  getAlltemplateMappings = () => {
    return ApiInstance
      .get(
        `${BACKEND_API_URL}/newtemplateMappings/getTemplateMappings`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getTeamplateMappings", e);
        return [];
      });
  };

  deleteTemplateMapping = (tempMappingId) => {
    return ApiInstance
      .delete(
        `${BACKEND_API_URL}/newtemplateMappings/deleteTemplateMappings/${tempMappingId}`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error("Error deleting deleteTemplateMappings:", error);
        throw error;
      });
  };

  addnewTemplateMapping = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/newtemplateMappings/addTemplateMappings`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        //console.log(data, "data in post call");
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("addTemplateMappings", e);
        return [];
      });
  };

  updateTemplateMapping = (data) => {
    return ApiInstance
      .put(
        `${BACKEND_API_URL}/newtemplateMappings/updateTemplateMappings`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        // console.log(data, "data in post call");
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("updateTemplateMappings", e);
        return [];
      });
  };
  importTemplate = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/newtemplateMappings/bulkAddTemplate`,
        data,
        REACT_APP_BACKEND_API_HEADER_FOR_FILE
      )
      .then((res) => {
        // console.log(data, "data in post call");
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("bulkAddTemplate", e);
        return [];
      });
  };
  
  downloadSampleFile=(fileName)=>{
    window.open(`${BACKEND_API_URL}/${fileName}`);
  }

  exportAllTemplateMapping = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/newtemplateMappings/exportAllTemplateMapping`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then(async (res) => {
        if (res.status === 200) {
          const fileUrl = `${BACKEND_API_URL}/${res.data.filePath}`;
          try {
            const response = await fetch(fileUrl, {
              method: 'GET',
              headers: {
                'Content-Type': 'application/octet-stream',
                'Authorization': 'Bearer ' + getOKTAAccessKey()
              }
            });  
            if (!response.ok) {
              throw new Error('Failed to fetch the file');
            }  
            const fileBlob = await response.blob();
            const downloadUrl = URL.createObjectURL(fileBlob);
  
            const link = document.createElement('a');
            link.href = downloadUrl;
            link.download = res.data.filePath;
            link.click();
            URL.revokeObjectURL(downloadUrl);
          } catch (error) {
            console.error('Error downloading the file:', error);
          }  
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error('Error in exportAllData:', error);
        throw error;
      });
  };
  

  exportAllAdminGeneralProperties = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/newtemplateMappings/exportAllAdminGeneralProperties`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then(async (res) => {
        if (res.status === 200) {
          const fileUrl = `${BACKEND_API_URL}/${res.data.filePath}`;
          try {
            const response = await fetch(fileUrl, {
              method: 'GET',
              headers: {
                'Content-Type': 'application/octet-stream',
                'Authorization': 'Bearer ' + getOKTAAccessKey()
              }
            });  
            if (!response.ok) {
              throw new Error('Failed to fetch the file');
            }  
            const fileBlob = await response.blob();
            const downloadUrl = URL.createObjectURL(fileBlob);
  
            const link = document.createElement('a');
            link.href = downloadUrl;
            link.download = res.data.filePath;
            link.click();
            URL.revokeObjectURL(downloadUrl);
          } catch (error) {
            console.error('Error downloading the file:', error);
          }  
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error('Error in exportAllData:', error);
        throw error;
      });
  };

  exportAllAdminSystemSetups = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/newtemplateMappings/exportAllAdminSystemSetups`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then(async (res) => {
        if (res.status === 200) {
          const fileUrl = `${BACKEND_API_URL}/${res.data.filePath}`;
          try {
            const response = await fetch(fileUrl, {
              method: 'GET',
              headers: {
                'Content-Type': 'application/octet-stream',
                'Authorization': 'Bearer ' + getOKTAAccessKey()
              }
            });  
            if (!response.ok) {
              throw new Error('Failed to fetch the file');
            }  
            const fileBlob = await response.blob();
            const downloadUrl = URL.createObjectURL(fileBlob);
  
            const link = document.createElement('a');
            link.href = downloadUrl;
            link.download = res.data.filePath;
            link.click();
            URL.revokeObjectURL(downloadUrl);
          } catch (error) {
            console.error('Error downloading the file:', error);
          }  
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error('Error in exportAllData:', error);
        throw error;
      });
  };
}
