//import React from "react";

export const getBasename = () => {
    const path = window.location.pathname;
    let currentInstance = "";
    if (path === "/") {
        return {
            segment: path,
            instanceType: currentInstance,
        };
    }
    if (path.includes("login")) {
        return {
            segment: "/",
            instanceType: currentInstance,
        };
    }

    let pathArray = path.split('/');
    pathArray = pathArray.filter(fitem => fitem !== "");

    let returnVal = '/';
    if (pathArray.length > 1) {
        if (pathArray[1] === "Cloud" || pathArray[1] === "onPrem") {
            if (pathArray[0] === "Dev") {
                currentInstance = pathArray[1];
                returnVal = pathArray[0] + "/";
            }
        }
    } else if (pathArray.length === 1 && pathArray[0] === "Dev") {
        return {
            segment: "/",
            instanceType: currentInstance,
        };
    }
    return {
        segment: returnVal,
        instanceType: currentInstance,
    }
};