import { BACKEND_API_URL, REACT_APP_BACKEND_API_HEADER, REACT_APP_BACKEND_API_HEADER_FOR_FILE  } from "../config";
import ApiInstance from './ApiInstance';
import { Store } from "app/redux/Store";
import { update_generalValues } from 'app/redux/actions/MCODetailsAction';
import { getOKTAAccessKey } from "./../app/utils/commonFunctions";
export class UserService {
  getAllUsersList = () => {
    return ApiInstance
      .get(
        `${BACKEND_API_URL}/users/getUsers`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getUsers", e);
        return [];
      });
  };

  addnewUser = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/users/addUser`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("addnewUser", e);
        return [];
      });
  };

  updateUser = (data) => {
    return ApiInstance
      .put(
        `${BACKEND_API_URL}/users/updateUser`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("updateUser", e);
        return [];
      });
  };

  deleteUser = (ids) => {
    return ApiInstance
      .delete(
        `${BACKEND_API_URL}/users/deleteUser`,
        { data: { ids } },
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error("Error deleting users:", error);
        throw error;
      });
  };
  validateUser = () => {
    console.log("validateUser called with URL:", `${BACKEND_API_URL}/users/validateUser`);
    console.log("Headers being sent:", REACT_APP_BACKEND_API_HEADER);
  
    return ApiInstance
      .get(
        `${BACKEND_API_URL}/users/validateUser`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        console.log("validateUser response:", res);
  
        if (res.status === 200) {
          console.log("validateUser success:", res.data);
          return res.data;
        }
  
        console.warn("validateUser unexpected status:", res.status);
        throw res.data;
      })
      .catch((error) => {
        console.error("validateUser request failed:", error);
  
        Store.dispatch(update_generalValues({
          isPageHavingIssue: true
        }));
  
        throw error;
      });
  };
  
  requestForAccess = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/users/requestForAccessApp`,
        { data: data },
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error("Error requestForAccessApp users:", error);
        throw error;
      });
  };

  importUser = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/users/bulkAddUser`,
        data,
        REACT_APP_BACKEND_API_HEADER_FOR_FILE
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("bulkAddUser", e);
        return [];
      });
  };
  exportUser = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/users/bulkExportUser`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then(async (res) => {
        if (res.status === 200) {
          const fileUrl = `${BACKEND_API_URL}/${res.data.filePath}`;
          try {
            const response = await fetch(fileUrl, {
              method: 'GET',
              headers: {
                'Content-Type': 'application/octet-stream',
                'Authorization': 'Bearer ' + getOKTAAccessKey()
              }
            });  
            if (!response.ok) {
              throw new Error('Failed to fetch the file');
            }  
            const fileBlob = await response.blob();
            const downloadUrl = URL.createObjectURL(fileBlob);
  
            const link = document.createElement('a');
            link.href = downloadUrl;
            link.download = res.data.filePath.replace("downloads/", "") 
            link.click();
            URL.revokeObjectURL(downloadUrl);
          } catch (error) {
            console.error('Error downloading the file:', error);
          }  
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error('Error in exportTemplate:', error);
        throw error;
      });
  };
  downloadSampleFile = (fileName) => {
    window.open(`http://localhost:8086/users/bulkExportUser/${fileName}`);
  }
}
