    getDataForClassificationyMCONames = (data) => {        
        //  Filter out invalid entries (where `name` or `id` is undefined)
        const validData = data.filter(item => item?.name && item?.id);
        if (validData.length === 0) {
            console.error("No valid data to send in API request!");
            return Promise.reject("Invalid data - No valid MCO entries.");
        }
    

        let payloadBody = {
            "queryParams": {
                "ids": validData,
                "operation": "CA-Details",
                "instance": "SaaS"
            }
        }
     

        let headersBody = {
            'client_id': 123,
            'client_secret': 456
        }
        return axios.post(`${SAP_API_BASE_URL}/loginLTToken/caDetails`, payloadBody, headersBody).then((res) => {            
            if (res.status === 200) {                              
                return res.data;
            }               
            }).then((data)=>{                
                const formattedResponse = {};
                data.forEach(item => {
                    if (item?.mcoPropeties?.name) {
                        formattedResponse[item.mcoPropeties.name] = item;
                    }
                });
                return formattedResponse;
            }).catch((e) => {
                throw e;
            });        
    };
