import { Breadcrumb, SimpleCard } from "app/components";
import React, { useEffect, useState, useRef, useCallback } from "react";
import Button from "@mui/material/Button";
import { Link as RouterLink, useNavigate, useLocation } from "react-router-dom";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import CircularProgress from "@mui/material/CircularProgress";
import BulkImportUsers from "./bulkImportUsersDialog";
import { Box, styled, Grid, TextField } from "@mui/material";

import { UserService } from "services/usersService";
import CustomAlertBox from "app/components/CustomAlertBox";
import DataGrid from "@inovua/reactdatagrid-community";
import "@inovua/reactdatagrid-community/index.css";
import BulkExportUsers from "./bulkExportUsersDialog";
import sortedExportDataHOC from "app/components/sortedExportDataHOC";
import { sortDataByKey } from "app/utils/mcoUtils/commonUtils";

const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));
const UserList = () => {
  const location = useLocation();
  let toastState = location.state;
  const [userData, setUserData] = useState([]);
  const [searchValue, setSearchValue] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [deleteDialog, setDeleteDialog] = useState(false);
  const userService = new UserService();
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);
  const [userImportDialog, setUserImportDialog] = useState(false);
  const [userExportDialog, setUserExportDialog] = useState(false);
  const [initialData, setInitialData] = useState([]);
  const [sortInfo, setSortInfo] = useState({}); // Add this line

  // ==============table===========
  const [selected, setSelected] = useState({});
  const [selectedRows, setSelectedRows] = useState([]);
  const gridRef = useRef(null);
  const dataMap =
    gridRef && gridRef.current && gridRef.current.dataMap
      ? gridRef.current.dataMap
      : null;
  const onSelectionChange = useCallback(({ selected }) => {
    setSelected(selected);
  }, []);

  const columns = [
    {
      name: "name",
      header: "Name",
      defaultFlex: 1,
      defaultWidth: 50,
      render: ({ data }) => <div>{data?.name}</div>,
    },
    {
      name: "userName",
      header: "User Name",
      defaultFlex: 1,
      defaultWidth: 50,
      render: ({ data }) => <div>{data?.userName}</div>,
    },
    {
      name: "email",
      header: "Email",
      defaultFlex: 1,
      defaultWidth: 50,
      render: ({ data }) => <div>{data?.email}</div>,
    },
    {
      name: "is_authorized",
      header: "Is Authorized",
      defaultFlex: 1,
      defaultWidth: 50,
      render: ({ data }) => (
        <div>{data?.is_authorized === "Y" ? "Yes" : "No"}</div>
      ),
    },
    {
      name: "userinfo",
      header: "User Group",
      defaultFlex: 1,
      defaultWidth: 50,
      render: ({ data }) => (
        <div>
          {data?.userinfo ? data.userinfo.map((item) => item).join(", ") : ""}
        </div>
      ),
    },
  ];

  const handleDeleteDialog = () => {
    handleDeleteUserDilaog(selectedRows[0]);
    setDeleteDialog(true);
    // Perform delete operation here using the rowId or rowData
  };
  const handleEditMappingDialog = () => {
    handleEditUser(selectedRows[0]);
  };

  // ===========Import Export Functions==========//
  const handleImportDialog = () => {
    setUserImportDialog(true);
  };
  const handleExportDialog = () => {
    setUserExportDialog(true);
  };
  const handleCloseImportDialog = () => {
    console.log("close dialog");
    setUserImportDialog(false);
  };
  const handleCloseExportDialog = () => {
    setUserExportDialog(false);
  };
  // ===========Import Export Functions==========//

  // ==============table===========

  useEffect(() => {
    console.log(userData, "userData inside useEffect");
    if (userData.length === 0) {
      setIsLoading(true);
      userService.getAllUsersList().then((result) => {
        setUserData(result.data);
        setInitialData(result.data); // Set initial data
        setIsLoading(false);
      });
    } else {
      setUserData(userData);
    }
  }, []);
  const [filteredBUData, setFilteredBUData] = useState([]);

// Ensure filteredBUData is an array
const validFilteredBUData = Array.isArray(filteredBUData) ? filteredBUData : [];
let sortedUsersData = sortDataByKey([...validFilteredBUData], "name");

  useEffect(() => {
    setFilteredBUData(userData);
    setInitialData(sortedUsersData);
  }, [userData]);

  const handleSearchInputChange = (event) => {
    const newSearchValue = event.target.value;
    setSearchValue(newSearchValue);
  };

  const handleSearch = () => { };

  const navigate = useNavigate();

  const handleCreateUser = () => {
    setTimeout(() => {
      navigate("/addEditUser", {
        state: { isEdit: false },
      });
    }, 200);
  };

  const handleEditUser = (editSelectedUser) => {
    const selectedUserData = editSelectedUser;
    setTimeout(() => {
      navigate("/addEditUser", {
        state: { isEdit: true, selectedUserData },
      });
    }, 200);
  };

  const handleDeleteUserDilaog = (DeleteSource) => {
    setDeleteDialog(true);
  };
  const handleCloseDelete = () => {
    setDeleteDialog(false);
  };

  const handleDeleteUser = async () => {
    setDeleteDialog(false);
    try {
      setIsLoading(true);
      console.log(selectedRows, "selectedRows");
      const selRowsId = selectedRows?.map((item) => item?.id);
      await userService.deleteUser(selRowsId).then((response) => {
        console.log(response, "delete response");
        setIsLoading(false);
        setOpenAlert(true);
        setOpenDesc({
          severity: response?.status,
          desc: response?.msg,
        });
        setSelected({});
        setSelectedRows([]);
      });
    } catch (error) {
      console.error("Failed to delete User:", error);
    }
    userService.getAllUsersList().then((result) => {
      setUserData(result.data);
    });
  };

  useEffect(() => {
    const selRowsData = selected === true ? dataMap : selected;
    const selRowsDetails =
      selRowsData && Object.keys(selRowsData).map((key) => selRowsData[key]);
    setSelectedRows(selRowsDetails);
  }, [selected, dataMap]);

  useEffect(() => {
    if (userData) {
      const new_FilteredData = userData.filter((subscriber) => {
        return (
          searchValue === "" ||
          subscriber.name.toLowerCase().includes(searchValue.toLowerCase()) ||
          subscriber.email.toLowerCase().includes(searchValue.toLowerCase()) ||
          subscriber.userName.toLowerCase().includes(searchValue.toLowerCase())
        );
      });
      setFilteredBUData(new_FilteredData);
    }
  }, [searchValue, userData]);
  useEffect(() => {
    if (toastState?.status) {
      setOpenAlert(true);
      setOpenDesc({
        severity: toastState?.status,
        desc: toastState?.message,
      });
      userService.getAllUsersList().then((result) => {
        setUserData(result.data);
        setInitialData(result.data); // Set initial data
      });
    }
    toastState = {};

    // Clear the state value on page refresh
    window.onbeforeunload = () => {
      window.history.replaceState({}, document.title);
    };
  }, [toastState]);

  const handleSortInfoChange = (sortInfo) => {
    setSortInfo(sortInfo);
    const SortedUserData = sortedExportDataHOC(UserList);
    const sortedData = SortedUserData({ data: userData, sortedUsersData, sortInfo, onSortInfoChange: handleSortInfoChange, initialData });
    setUserData(sortedData.props.data);
    setFilteredBUData(sortedData.props.data);
  };

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Users" }]} />
      </Box>

      <SimpleCard>
        <Grid container spacing={1} alignItems="center">
          <Grid item md={3} sm={6} xs={12} className="font-medium">
            <TextField
              type="text"
              name="search"
              size="small"
              onChange={handleSearchInputChange}
              value={searchValue}
              style={{ width: "100%" }}
            />
          </Grid>
          <Grid item container md={9} sm={6} xs={12} alignItems="center">
            <Button variant="contained" onClick={handleSearch}>
              Search
            </Button>
            <div style={{ flex: 1 }} />
            <Button
              variant="contained"
              component={RouterLink}
              onClick={handleCreateUser}
            >
              Create
            </Button>
            <Button
              variant="contained"
              disabled={selectedRows?.length === 1 ? false : true}
              onClick={handleEditMappingDialog}
              style={{ margin: "0 0px 0 10px" }}
            >
              Edit
            </Button>
            <Button
              variant="contained"
              disabled={selectedRows?.length ? false : true}
              onClick={handleDeleteDialog}
              style={{ margin: "0 0px 0 10px" }}
            >
              Delete
            </Button>
            <Button
              variant="contained"
              onClick={handleImportDialog}
              style={{ margin: "0 0px 0 10px" }}
            >
              Import
            </Button>

            <Button
              variant="contained"
              onClick={handleExportDialog}
              style={{ margin: "0 0px 0 10px" }}
            >
              Export
            </Button>
          </Grid>
          <Box width="100%" overflow="auto">
            <br></br>
            {isLoading ? (
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "100vh",
                }}
              >
                <CircularProgress />
              </Box>
            ) : (
              filteredBUData && (
                <DataGrid
                  idProperty="id"
                  showZebraRows={false}
                  style={{ minHeight: 550 }}
                  defaultLimit={100}
                  columns={columns}
                    dataSource={sortInfo === null ? filteredBUData : sortedUsersData}
                  // rowHeight={null}
                  checkboxColumn
                  selected={selected}
                  onSelectionChange={onSelectionChange}
                  handle={(ref) => (gridRef.current = ref ? ref.current : null)}
                  pagination
                  renderScroller={()=> gridRef?.current?.setScrollTop(selectedRows?.length==0 ? 0 : gridRef?.current?.getScrollTop())}
                  onSortInfoChange={handleSortInfoChange}
                  />
              )
            )}
          </Box>
        </Grid>
      </SimpleCard>
      {openDesc ? (
        <CustomAlertBox
          openAlert={openAlert}
          openDesc={openDesc}
          setOpenAlert={setOpenAlert}
        />
      ) : (
        <></>
      )}
      <Dialog open={deleteDialog}>
        <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
          Delete Design Organization
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            Do you want to delete the seleted User(s)?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleCloseDelete}>
            Cancel
          </Button>
          <Button onClick={handleDeleteUser}>delete</Button>
        </DialogActions>
      </Dialog>
      <BulkExportUsers
        userExportDialog={userExportDialog}
        handleCloseExportDialog={handleCloseExportDialog}
        setUserImportDialog={setUserImportDialog}
        setUserExportDialog={setUserExportDialog}
        selectedRows={selectedRows}
        userData={sortInfo && Object.keys(sortInfo).length === 0 ? sortedUsersData : userData} />
        <BulkImportUsers
        userImportDialog={userImportDialog}
        setUserImportDialog={setUserImportDialog}
        setUserData={setUserData}

      />
    </Container>
  );
};

export default UserList;
