import React from 'react';
import OrgAllowedValuesWrapper from "./orgallowedWrapper";
import PrivateRoute from "app/privateRoutes";
const orgallowedroutes = [
  {
    path: "/orgallowedList",
    element: (
      <PrivateRoute>
        <OrgAllowedValuesWrapper />
      </PrivateRoute>
    ),
  },
];

export default orgallowedroutes;