import { Breadcrumb, SimpleCard } from "app/components";

import React, { useEffect, useState, useRef } from "react";
import Button from "@mui/material/Button";

import CircularProgress from "@mui/material/CircularProgress";

import DataGrid from "@inovua/reactdatagrid-community";
import "@inovua/reactdatagrid-community/index.css";
import {
  Box,
  // styled,
  Grid,
  TextField,
} from "@mui/material";


import { styled } from '@mui/system';

import MuiAlert from "@mui/material/Alert";

import useOracleSettings from "app/hooks/useOracleSettings";

import { HistoryManagementService } from "services/historyManagementService";

import CustomSelect from "./../../components/CustomSelect";
import { BUOptions } from "../adminConfig/common";
import CommitChangesForm from "./commitChangesForm";

import CustomAlertBox from "app/components/CustomAlertBox";
import { TemplateMappingService } from "services/templateMappingService";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const Container = styled("div")(({ theme }) => ({
  margin: "30px",
  [theme.breakpoints.down("sm")]: { margin: "16px" },
  "& .breadcrumb": {
    marginBottom: "30px",
    [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
  },
}));



const DraftChangesList = () => {
  const { bussinessUnit, updateBussinessUnit, updateMappingTemplatesInfo } = useOracleSettings();

  const [tabledata, setTabledata] = useState([]);
  const [filteredTabledata, setFilteredTabledata] = useState([]);
  const [searchValue, setSearchValue] = useState("");
  const [applyChangesDialog, setApplyChangesDialog] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const gridStyle = { minHeight: 550 };
  const [selectedRows, setSelectedRows] = useState([]);
  const gridRef = useRef(null);
  const [openAlert, setOpenAlert] = useState(false);
  const [openDesc, setOpenDesc] = useState(null);

  const historyManagementService = new HistoryManagementService();
  const tempMapService = new TemplateMappingService();
  const columns = [
    {
      name: "userName",
      header: "User Name",
      defaultFlex: 1,
      defaultWidth: 50,
    },
    {
      name: "MappingType",
      header: "Mapping Type",
      defaultFlex: 1,
      render: ({ data }) => <div>{data?.MappingType}</div>,
    },
    {
      name: "BUID",
      header: "Design Organization",
      defaultFlex: 1,
      render: ({ data }) => <div>{data?.BUID}</div>,
    },
    // {
    //   name: "modifiedTemplate",
    //   header: "Modified Templates",
    //   defaultFlex: 1,
    //   render: ({ data }) => <div>{data?.modifiedTemplate.join(", ")}</div>,
    // },
    {
      name: "modifiedDateTime",
      header: "Modified Date & Time ",
      defaultFlex: 1,
      render: ({ data }) => <div>{(data?.modifiedDateTime) ? data?.modifiedDateTime : "-"}</div>,
    },
    {
      name: "Action",
      header: "Action",
      defaultFlex: 1,
      render: ({ data }) => {
        return (
          <div>
            <Button variant="contained"
              style={{ margin: "0px 10px 0px 0px" }}
              onClick={() => {
                setSelectedRows([data]);
                setApplyChangesDialog(true);
              }}
            >
              Apply
            </Button>
            <Button variant="contained"
              onClick={() => {
                discardDraftChanges(data.id);
              }}>
              Discard
            </Button>
          </div>
        )
      }
    },
  ];

  const handleSearch = () => {
    let filteredData = [];
    // input box filter
    if (searchValue === "") {
      filteredData = tabledata;
    } else {
      filteredData = tabledata?.filter((thisItem) => {
        const thisRowData = Object.values(thisItem).join().toLowerCase();
        return thisRowData.includes(searchValue.toLowerCase());
      });
    }

    // dropdown filter
    filteredData = filteredData?.filter((thisItem) => {
      const thisRowData = Object.values(thisItem).join().toLowerCase();
      return thisRowData.includes(bussinessUnit.toLowerCase());
    });

    setFilteredTabledata(filteredData);
  };

  const handleRowClick = (rowData) => {
    const isSelected = selectedRows.some((row) => row.id === rowData.id);

    if (isSelected) {
      setSelectedRows((prevSelectedRows) =>
        prevSelectedRows.filter((row) => row.id !== rowData.id)
      );
    } else {
      setSelectedRows([rowData]);
    }
  };

  const updateTemplateMappingData = () => {
    tempMapService.getAlltemplateMappings().then((result) => {
      updateMappingTemplatesInfo(result?.data?.templateMappingList);
      //setIsLoading(false);
    });
  }
  const saveApplyChanges = (values) => {
    try {
      setIsLoading(true);

      if (!selectedRows.length) {
        setOpenAlert(true);
        setOpenDesc({
          severity: "error",
          desc: "Please select row.",
        });
        return;
      }

      const payload = {
        id: selectedRows[0]?.id,
        reason: values.description,
        BUID: selectedRows[0]?.BUID,
        MappingType: selectedRows[0]?.MappingType,
      }
      historyManagementService.applyDraftChanges(payload).then((result) => {
        setApplyChangesDialog(false); // close dialog box
        setSelectedRows([]); // reset selected row value

        updateTemplateMappingData();
        fetchData();
        setIsLoading(false);

        if (result?.status === "success") {
          setOpenAlert(true);
          setOpenDesc({
            severity: "success",
            desc: result?.msg,
          });
        } else if (result?.status === "failed") {
          setOpenAlert(true);
          setOpenDesc({
            severity: "error",
            desc: result?.msg,
          });
        }
      });
    } catch (err) {
      //console.log(err, 'errors 1 ==================');
    }
  }
  const discardDraftChanges = (rowId) => {
    try {
      setIsLoading(true);
      historyManagementService.discardDraftChanges(rowId).then((result) => {
        setApplyChangesDialog(false); // close dialog box
        setSelectedRows([]); // reset selected row value

        updateTemplateMappingData();
        fetchData();
        setIsLoading(false);

        if (result?.status === "success") {
          setOpenAlert(true);
          setOpenDesc({
            severity: "success",
            desc: result?.msg,
          });
        } else if (result?.status === "failed") {
          setOpenAlert(true);
          setOpenDesc({
            severity: "error",
            desc: result?.msg,
          });
        }
      });
    } catch (err) {
      //console.log(err, 'errors 1 ==================');
    }
  }

  const fetchData = () => {
    setIsLoading(true);
    historyManagementService.getAlldraftChanges().then((result) => {
      setTabledata(result?.data);
      let BUID = bussinessUnit ? bussinessUnit : 'Micro Motion';
      let filterData = result.data.filter(element => element.BUID === BUID);
      setFilteredTabledata(filterData);
      //setTabledata(result?.data);
      //setFilteredTabledata(result?.data);
      setIsLoading(false);
    });
  }
  useEffect(() => {
    fetchData();
  }, []);

  useEffect(() => {
    handleSearch();
  }, [searchValue, bussinessUnit]);

  return (
    <Container>
      <Box className="breadcrumb">
        <Breadcrumb routeSegments={[{ name: "Draft Changes" }]} />
      </Box>
      <SimpleCard>
        <Grid container spacing={1} alignItems="center">
          <Grid item md={4} sm={6} xs={12} className="font-medium">
            <Grid container spacing={1} alignItems="center">
              <Grid item md={6} sm={6} xs={12} className="font-medium">
                <TextField
                  type="text"
                  name="search"
                  size="small"
                  onChange={(e) => {
                    setSearchValue(e.target.value);
                  }}
                  value={searchValue}
                  style={{ width: "100%" }}
                />
              </Grid>
              <Grid item md={5} sm={6} xs={12} className="font-medium">
                <Button variant="contained" onClick={handleSearch}>
                  Search
                </Button>
              </Grid>
            </Grid>
          </Grid>
          <Grid item md={8} sm={6} xs={12} className="font-medium">
            <Grid container alignItems="center"
              style={{
                // marginLeft: "198px",
                textAlign: "right"
              }}
            >
              <Grid
                item
                md={12}
                sm={12}
                xs={12}
                className="font-medium"
                style={{ paddingRight: "10px" }}
              >
                <label style={{ textWrap: "nowrap", paddingTop: "10px" }}>Design Organization:</label>
                <CustomSelect
                  selectedvalue={bussinessUnit}
                  style={{ width: "25%" }}
                  //optionsValues={BUOptions}
                  // defaultValue={
                  //   BUOptions && !bussinessUnit ? BUOptions[0]?.value : ""
                  // }
                  onChangeFunc={(selectedOptionValue) => {
                    updateBussinessUnit(selectedOptionValue);
                    setSearchValue("");
                  }}
                />
              </Grid>
            </Grid>
          </Grid>

          <Box width="100%" overflow="auto" marginTop="10px">
            {isLoading && (
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "100vh",
                }}
              >
                <CircularProgress />
              </Box>
            )}
            {!isLoading && (
              <DataGrid
                showZebraRows={false}
                defaultLimit={100}
                style={gridStyle}
                columns={columns}
                dataSource={filteredTabledata}
                // rowHeight={null}
                onRowClick={(rowData) => handleRowClick(rowData.data)}
                pagination
                renderScroller={()=> gridRef?.current?.setScrollTop(selectedRows?.length==0 ? 0 : gridRef?.current?.getScrollTop())}
                />
            )}
          </Box>
        </Grid>
      </SimpleCard>

      <CommitChangesForm applyChangesDialog={applyChangesDialog} setApplyChangesDialog={setApplyChangesDialog} saveApplyChanges={saveApplyChanges} />

      {(openDesc) ? (
        <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} />
      ) : (<></>)}
    </Container>
  );
};

export default DraftChangesList;
