import { createContext, useState, useEffect } from 'react'

const ExpenseMappingContext = createContext({
    expensemapInfo: [],
    updateExpenseMap: () => [],
})

export const ExpenseMapProvider = ({ children }) => {
    const [expensemapInfo, setExpensemapInfo] = useState([]);

    const updateExpenseMap = (values) => {
        setExpensemapInfo(values);
       // console.log(values, "inside update context");
    };

    useEffect(() => {
       // console.log(expensemapInfo, "templatesContext");
    }, [expensemapInfo]);

    return (
        <ExpenseMappingContext.Provider
            value={{
                expensemapInfo,
                updateExpenseMap,
            }}
        >
            {children}
        </ExpenseMappingContext.Provider>
    );
};

export default ExpenseMappingContext;