import React, { useEffect } from "react";
import { useOktaAuth } from "@okta/okta-react";
import { useNavigate } from "react-router-dom";

const CustomLoginCallback = () => {
  const { oktaAuth, authState } = useOktaAuth();
  let navigate = useNavigate();

  useEffect(() => {
    console.log(" Current URL after login:", window.location.href);

    if (authState?.isAuthenticated) {
      const originalUrl = sessionStorage.getItem("original_uwaUrl");
      console.log(" Redirecting back to:", originalUrl);
      if (originalUrl) {
        window.location.replace(originalUrl); // Ensures widget reloads in same frame
      }
    }
  }, [authState]);

  const onSuccess = (user) => {
    // Redirect the user to their intended destination or the home page
    oktaAuth.redirect("/");
    // navigate('/');
  };

  const onError = (error) => {
    // Handle any errors that may occur during the login process
    console.error("Error during login callback:", error);
  };

  useEffect(() => {
    oktaAuth.handleLoginRedirect().then(onSuccess).catch(onError);
  }, [oktaAuth]);

  return (
    <div>
      <h4>Loading...</h4>
    </div>
  );
};

export default CustomLoginCallback;
