import React from 'react';
import { Navigate } from 'react-router-dom';
import { useOktaAuth } from '@okta/okta-react';
import useOracleSettings from "app/hooks/useOracleSettings";

const PrivateRoute = ({ children }) => {
    const { userDetails } = useOracleSettings();
    const { authState } = useOktaAuth();
    return (
        (authState?.isAuthenticated && userDetails?.userinfo?.includes("admin")) ? (children) : (<Navigate to="/accessDenied" />)
        // (authState?.isAuthenticated && userRoles?.includes("admin")) ? (children) : (<Navigate to="/accessDenied" />)
        // (authState?.isAuthenticated && authState?.idToken?.claims?.userinfo?.includes("admin")) ? (children) : (<Navigate to="/accessDenied" />)
        // (authState?.isAuthenticated) ? (children) : (<Navigate to="/accessDenied" />)

    );
};

export default PrivateRoute;