import React, { useEffect, useState } from "react";

import Button from "@mui/material/Button";

import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import shortId from 'shortid';

import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import CloudDownloadIcon from '@mui/icons-material/CloudDownload';

import {
    Grid,
    TextField,
    Table,
    TableBody,
    TableCell,
    TableHead,
    TablePagination,
    TableRow,
    Box,
    styled,
    InputLabel,
    Autocomplete,
} from "@mui/material";


import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import AlertTitle from '@mui/material/AlertTitle';

import { TemplateMappingService } from "services/templateMappingService";
import useOracleSettings from "app/hooks/useOracleSettings";
import Loader from 'app/components/Loader';

import download from 'downloadjs';
import CustomSelect from "./CustomSelect";
import CustomAlertBox from "./CustomAlertBox";
const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const StyledTable = styled(Table)(() => ({
    whiteSpace: "pre",
    "& thead": {
        "& tr": { "& th": { paddingLeft: 0, paddingRight: 0 } },
    },
    "& tbody": {
        "& tr": { "& td": { paddingLeft: 0, textTransform: "capitalize" } },
    },
}));

const VisuallyHiddenInput = styled('input')({
    clip: 'rect(0 0 0 0)',
    clipPath: 'inset(50%)',
    height: 1,
    overflow: 'hidden',
    position: 'absolute',
    bottom: 0,
    left: 0,
    whiteSpace: 'nowrap',
    width: 1,
});

const ImportMappingsDialog = (props) => {
    const {
        allTemplatesInfo,
        // updateTemplateLists,
        updateRefreshData,
        bussinessUnitLists
    } = useOracleSettings();

    const templateMappingService = new TemplateMappingService();
    const { importTemplate, setImportTemplate, fetchData } = props;

    const [isLoading, setIsLoading] = useState(false);

    const [noFileError, setNoFileError] = useState(false);
    const [importResponse, setImportResponse] = useState(false);

    const [importfileBar, setimportfileBar] = useState(false);
    const [importedFileData, setImportedFileData] = useState(null)
    const [importedFileName, setImportedFileName] = useState(null);

    const [sampleFileUrl, setSampleFileUrl] = useState(null);

    const [ERPTypeError, setERPTypeError] = useState(false);
    const [instanceError, setinstanceError] = useState(false);
    const [BUError, setBUError] = useState(false);
    const [importSelectionValues, setImportSelectionValues] = useState({
        ERPType: [],
        instance: [],
        BU: [],
    });
    const [openAlert, setOpenAlert] = useState(false);
    const [openDesc, setOpenDesc] = useState(null);

    const importFileData = (e) => {
        const file = e.target.files[0];
        if (file) {
            const fileType = file.name.split('.').pop();
            if (fileType === 'xlsx' || fileType === 'xls') {
                // if (e.target.files[0].name.split('.').pop() === 'xlsx' || e.target.files[0].name.split('.').pop() === 'xls') {
                setImportedFileData(e.target.files[0])
                setImportedFileName(e.target.files[0].name)
                setNoFileError(false)
            } else {
                setNoFileError(true)
                setImportedFileName(null)
                setImportedFileData(null);
            }
        } else {
            setNoFileError(true)
            setImportedFileName(null)
            setImportedFileData(null);
        }
    }

    const handleCloseImportTemplate = () => {
        setImportedFileData(null)
        setImportTemplate(false);
    };

    const handleImportTemplate = () => {
        if (importSelectionValues.BU === null || importSelectionValues.BU.length < 1) {
            setBUError(true);
            return;
        }

        if (importedFileData === null || importedFileData.length < 1) {
            setNoFileError(true);
            return;
        }
        if (importedFileData !== null) {
            let postData = {
                'file': importedFileData,
                data: {
                    BU: importSelectionValues.BU,
                    MappingType: props?.MappingType,
                }
            };

            // console.log(importSelectionValues, 'importSelectionValues');
            // console.log(postData, 'postData');
            // return;
            setIsLoading(true);
            templateMappingService.importTemplate(postData).then((response) => {
                // console.log(response, 'response 11111');
                setImportResponse(response)
                setimportfileBar(true);
                setImportedFileData(null)
                setImportedFileName(null)
                setImportTemplate(false);
                let desc = {};
                let severity='';
                if (response && response.status === 'error') {
                    desc = (
                        <div style={{ color: 'red' }}>
                            {response?.msg} <br />
                            {response?.errorInTemplateCount && (
                                <>
                                    Total Template Count: {response.errorInTemplateCount} <br />
                                    Templates are: <br />
                                    <ul>
                                        {response.errorInTemplate.map((eitem) => (
                                            <li key={shortId.generate()}>{eitem}</li>
                                        ))}
                                    </ul>
                                </>
                            )}

                            {response?.errorInPlantCount && (
                                <>
                                    Total Plant Count: {response.errorInPlantCount} <br />
                                    Plants are: <br />
                                    <ul>
                                        {response.errorInPlants.map((eitem) => (
                                            <li key={shortId.generate()}>{eitem}</li>
                                        ))}
                                    </ul>
                                </>
                            )}
                        </div>
                    );
                    severity = 'error';
                } else {
                    if (response.errorCount > 0 || (!response.newTemplateAdded && !response.updateTemplates)) {
                        desc = (
                            <div style={{ color: 'red' }}>
                                Failure <br />
                                New Template Added: {response.newTemplateAdded} <br />
                                Template Updated: {response.updateTemplates} <br />
                                {response.errorCount > 0 && <>Error in Template: {response.errorCount} <br /></>}
                            </div>
                        );
                        severity = 'error';
                    } else {
                        desc = (
                            <div>
                                {response.msg} <br />
                                New Template Added: {response.newTemplateAdded} <br />
                                Template Updated: {response.updateTemplates} <br />
                            </div>
                        );
                        severity = 'success';
                    }
                }

                setOpenAlert(true);
                setOpenDesc({
                    severity: severity,
                    desc: desc,
                });
                setIsLoading(false);
                if (severity === 'error') return; // Stop further execution if there is an error
                if (!response?.errorInTemplateCount || !response?.errorInPlantCount) {
                    updateRefreshData(true);
                    fetchData();
                }
            });
        }
    }
    const handleImportfileBarClose = (event, reason) => {
        if (reason === "clickaway") {
            return;
        }
        setimportfileBar(false);
    };

    const handleImportTemplateSelection = (column, selectedValues) => {
        setImportSelectionValues((prevselectedValues) => {
            return {
                ...prevselectedValues,
                [column]: selectedValues,
            };
        })
        if (column === 'ERPType') { setERPTypeError(false) }
        if (column === 'instance') { setinstanceError(false) }
        if (column === 'BU') { setBUError(false) }
        console.info(importSelectionValues, 'importSelectionValues')
    }

    const getUniqueValues = (column) => {
        const values = new Set();

        // console.log(allTemplatesInfo, 'allTemplatesInfo 11111');

        // allTemplatesInfo?.forEach((subscriber) => {
        //     if (column === "plants") {
        //         subscriber[column]?.forEach((plant) => {
        //             values.add(plant.plantName);
        //         });
        //     } else if (column === "BU") {
        //         subscriber[column]?.forEach((BU) => {
        //             values.add(BU);
        //         });
        //     } else {
        //         values.add(subscriber[column]);
        //     }
        // });
        bussinessUnitLists?.forEach(item => {
            values.add(item[column]);
        })
        return Array.from(values);
    };

    useEffect(() => {
        let getsampleFileUrl = null;
        switch (props?.MappingType) {
            case "orgallowedvalues":
                getsampleFileUrl = "sample-orgAllowedTemplate.xlsx";
                break;
            case "mvoTemplateCompatibility":
                getsampleFileUrl = "sample-mvotemplatecompatibility.xlsx";
                break;
            case "parttypetotemplate":
                getsampleFileUrl = "sample-partTypeToTemplate.xlsx";
                break;
            case "mvoallowedchanges":
                getsampleFileUrl = "sample-mvoallowedchanges.xlsx";
                break;
            case "setcompatibletemplates":
                getsampleFileUrl = "sample-setcompatibletemplates.xlsx";
                break;
            case "buplantmapping":
                getsampleFileUrl = "sample-RDOPlantMapping.xlsx";
                break;
            case "expensemapping":
                getsampleFileUrl = "sample-expensemapping.xlsx";
                break;
            default:
                break;
        }
        setSampleFileUrl(getsampleFileUrl);
    }, [props.MappingType]);

    // useEffect(() => {
    //     templateMappingService.getAlltemplateMappings().then((result) => {
    //         updateTemplateLists(result?.data?.templateMappingList);
    //     });
    // }, [])
    const handleDownload = () => {
        templateMappingService.downloadSampleFile(sampleFileUrl)
    };
    return (
        <>
            <Dialog open={importTemplate}
                style={{
                    zIndex: 999, // Set your desired z-index value
                }}>
                <DialogTitle style={{ cursor: "move" }} id="draggable-dialog-title">
                    {props.title}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        <Box width="100%" overflow="auto">
                            <StyledTable>
                                <TableBody>
                                    <TableRow>
                                        <TableCell align="left">
                                            <InputLabel
                                                style={{
                                                    fontWeight: "bold",
                                                    color: "rgba(52, 49, 76, 1)",
                                                }}
                                            >
                                                Design Organization
                                            </InputLabel>
                                        </TableCell>
                                        <TableCell align="left">
                                            <Autocomplete
                                                style={{ width: "70%" }}
                                                multiple={false}
                                                options={getUniqueValues("buName")}
                                                // options={getBuList}
                                                value={importSelectionValues.BU}
                                                onChange={(_, newValue) =>
                                                    handleImportTemplateSelection("BU", newValue)
                                                }
                                                renderValue={() => null}
                                                renderInput={(params) => (
                                                    <TextField
                                                        {...params}
                                                        variant="outlined"
                                                        label="Select RDO"
                                                        placeholder="Search..."
                                                    />
                                                )}
                                                PaperProps={{
                                                    style: {
                                                        maxHeight: "200px",
                                                    },
                                                }}
                                                disableCloseOnSelect={false}
                                                disableClearable
                                                isOptionEqualToValue={(option, value) => option === value}
                                                getOptionSelected={(option, value) =>
                                                    importSelectionValues.BU.includes(value)
                                                }
                                                ChipProps={{
                                                    style: {
                                                        fontSize: "8px",
                                                    },
                                                }}
                                            />
                                            {BUError && (<p style={{ color: 'red' }}>please select Design Organization</p>)}
                                        </TableCell>
                                    </TableRow>

                                    <div style={{ paddingTop: '10px' }}>
                                        <Button component="label" variant="contained" startIcon={<CloudUploadIcon />}>
                                            Upload file
                                            <VisuallyHiddenInput type="file" name="file" accept=".xls, .xlsx" onChange={importFileData} />
                                        </Button>
                                        {noFileError && (<p>Please Select xlsx file to Upload </p>)}
                                    </div>
                                    {importedFileName && (<p>{importedFileName}</p>)}
                                    <div style={{ paddingTop: '10px' }}>
                                        {/* <a
                                            href={sampleFileUrl}
                                            download="Example-XLSX-document"
                                            target="_blank"
                                            rel="noreferrer"
                                        > */}
                                        <Button component="label" variant="contained" startIcon={<CloudDownloadIcon />} onClick={handleDownload}>
                                            Download sample file
                                        </Button>
                                        {/* </a> */}
                                    </div>
                                </TableBody>
                            </StyledTable>

                        </Box>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={handleCloseImportTemplate}>
                        Cancel
                    </Button>
                    <Button onClick={handleImportTemplate}>Import</Button>
                </DialogActions>
            </Dialog>

            {/* <Snackbar
                open={importfileBar}
                autoHideDuration={6000}
                onClose={handleImportfileBarClose}
                anchorOrigin={{ vertical: "top", horizontal: "right" }}
            >
                <Alert
                    onClose={handleImportfileBarClose}
                    severity={(importResponse && (!importResponse?.errorInTemplate?.length && !importResponse?.errorInPlants?.length)) ? "success" : "error"}
                    sx={{ width: "100%" }}
                >
                    <AlertTitle>{(importResponse && (!importResponse?.errorInTemplate?.length && !importResponse?.errorInPlants?.length)) ? "Success" : "Error"}</AlertTitle>
                    {
                        (importResponse && (importResponse?.errorInTemplate?.length || importResponse?.errorInPlants?.length)) ? (
                            <>
                                <div>
                                    {importResponse?.msg} <br />
                                    {
                                        (importResponse?.errorInTemplateCount) ? (<>
                                            Total Template Count: {importResponse?.errorInTemplateCount} <br />
                                            Templates are: <br />
                                            <ul>
                                                {
                                                    importResponse?.errorInTemplate?.map((eitem) => {
                                                        return (
                                                            <li key={shortId.generate()}>
                                                                {eitem}
                                                            </li>
                                                        )
                                                    })
                                                }
                                            </ul>
                                        </>) : (<></>)
                                    }

                                    {
                                        (importResponse?.errorInPlantCount) ? (
                                            <>
                                                Total Plant Count: {importResponse?.errorInPlantCount} <br />
                                                Plants are: <br />
                                                <ul>
                                                    {
                                                        importResponse?.errorInPlants?.map((eitem) => {
                                                            return (
                                                                <li key={shortId.generate()}>
                                                                    {eitem}
                                                                </li>
                                                            )
                                                        })
                                                    }
                                                </ul>
                                            </>
                                        ) : (<></>)
                                    }

                                </div>
                            </>
                        ) : (
                            <>
                                <div>
                                    {importResponse.msg} <br />
                                    New Template Added: {importResponse.newTemplateAdded} <br />
                                    Template Updated: {importResponse.updateTemplates} <br />
                                </div>
                            </>
                        )
                    }
                </Alert>
            </Snackbar > */}
            {(openDesc) ? (
                <CustomAlertBox openAlert={openAlert} openDesc={openDesc} setOpenAlert={setOpenAlert} sx={{ '& .MuiAlertTitle-root': { backgroundColor: 'red' } }} />
            ) : (<></>)}

            {
                isLoading ? (
                    <div style={{ textAlign: 'center' }}>
                        <Loader show={isLoading} />
                    </div>
                ) : <></>
            }
        </>
    )
}

export default ImportMappingsDialog;