import { BACKEND_API_URL, REACT_APP_BACKEND_API_HEADER, REACT_APP_BACKEND_API_HEADER_FOR_FILE } from "../config";
import ApiInstance from './ApiInstance';
import { getOKTAAccessKey } from "./../app/utils/commonFunctions";

export class templateService {
  // ===============Template=====================

  // get the templateList
  getAllTemplateList = () => {
    return ApiInstance
      .get(
        `${BACKEND_API_URL}/template/gettemplate`,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getTemplateDetails", e);
        return [];
      });
  };

  addnewTemplate = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/template/addTemplate`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("addnewPlant", e);
        return [];
      });
  };
  getTemplateById = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/template/getTemplateById`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("getTemplateById", e);
        return [];
      });
  };

  updateTemplate = (data) => {
    return ApiInstance
      .put(
        `${BACKEND_API_URL}/template/updateTemplate`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("addnewPlant", e);
        return [];
      });
  };

  deleteTemplate = (ids) => {
    return ApiInstance.delete(
      `${BACKEND_API_URL}/template/deleteTemplate`,
      { data: { ids } },
      REACT_APP_BACKEND_API_HEADER
    )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error("Error deleting plant:", error);
        throw error;
      });
  };

  importTemplate = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/template/bulkAddTemplate`,
        data,
        REACT_APP_BACKEND_API_HEADER_FOR_FILE
      )
      .then((res) => {
        if (res.status === 200) {
          return res.data;
        }
        throw res.data;
      })
      .catch((e) => {
        console.error("bulkAddTemplate", e);
        return [];
      });
  };
  exportTemplate = (data) => {
    return ApiInstance
      .post(
        `${BACKEND_API_URL}/newtemplateMappings/bulkExportTemplate`,
        data,
        REACT_APP_BACKEND_API_HEADER
      )
      .then(async (res) => {
        if (res.status === 200) {
          const fileUrl = `${BACKEND_API_URL}/${res.data.filePath}`;
          try {
            const response = await fetch(fileUrl, {
              method: 'GET',
              headers: {
                'Content-Type': 'application/octet-stream',
                'Authorization': 'Bearer ' + getOKTAAccessKey()
              }
            });  
            if (!response.ok) {
              throw new Error('Failed to fetch the file');
            }  
            const fileBlob = await response.blob();
            const downloadUrl = URL.createObjectURL(fileBlob);
  
            const link = document.createElement('a');
            link.href = downloadUrl;
            link.download = res.data.filePath.replace("downloads/", "") 
            link.click();
            URL.revokeObjectURL(downloadUrl);
          } catch (error) {
            console.error('Error downloading the file:', error);
          }  
          return res.data;
        }
        throw res.data;
      })
      .catch((error) => {
        console.error('Error in exportTemplate:', error);
        throw error;
      });
  };
  downloadSampleFile = (fileName) => {
    window.open(`${BACKEND_API_URL}/${fileName}`);
  }
  // ===============Plant=====================
}
