import React, { useEffect } from 'react';
import { Select, MenuItem } from '@mui/material';
import useOracleSettings from "app/hooks/useOracleSettings";
//import {bussinessUnitLists} from '../contexts/OracleSettingsContext'
//export default function CustomSelect({ isdisabled = false, selectedvalue, defaultValue, optionValues, onChangeFunc, style = {} }) {
export default function CustomSelect({ isdisabled = false, selectedvalue, defaultValue, onChangeFunc, style = {}, size }) {
    const { bussinessUnitLists } = useOracleSettings();
    //console.log(bussinessUnitLists, 'bussinessUnitLists')
    useEffect(() => {
        if (defaultValue) {
            onChangeFunc(defaultValue);
        }
    }, [defaultValue]);

    return (
        <Select
            disabled={isdisabled}
            className="form-control"
            defaultValue={defaultValue}
            value={selectedvalue}
            onChange={(e) => onChangeFunc(e.target.value)}
            style={style}
            size={size?size:'medium'}
            displayEmpty // Enable displayEmpty prop
            renderValue={(selected) => {
                // Render the selected value
                let selectedOption = [];
                selectedOption = bussinessUnitLists?.find(
                    (option) => (option.businessUnit === selected)
                );

                return (selectedOption?.businessUnit) ? selectedOption.buName : '';
                //return (selectedOption?.value) ? selectedOption.label : '';
            }}
        >
            {bussinessUnitLists?.map((option, index) => {
                const listKey = "dpKey" + index;
                return (
                    <MenuItem value={option.businessUnit} key={listKey}>
                        {/* {option.templateName ? option.instance + "_" + option.templateName + "_" + option.desc : ''} */}
                        {/*option.templateName ? option.desc : ''*/}
                        {option.buName}
                    </MenuItem>
                )
            })}
        </Select>
    );
}