import { differenceInSeconds } from 'date-fns';

export const convertHexToRGB = (hex) => {
  // check if it's a rgba
  if (hex.match('rgba')) {
    let triplet = hex.slice(5).split(',').slice(0, -1).join(',');
    return triplet;
  }

  let c;
  if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
    c = hex.substring(1).split('');
    if (c.length === 3) {
      c = [c[0], c[0], c[1], c[1], c[2], c[2]];
    }
    c = '0x' + c.join('');

    return [(c >> 16) & 255, (c >> 8) & 255, c & 255].join(',');
  }
};

export function debounce(func, wait, immediate) {
  let timeout;
  return function () {
    let context = this,
      args = arguments;
    clearTimeout(timeout);
    timeout = setTimeout(function () {
      timeout = null;
      if (!immediate) func.apply(context, args);
    }, wait);
    if (immediate && !timeout) func.apply(context, args);
  };
}

export function isMobile() {
  if (window) {
    return window.matchMedia(`(max-width: 767px)`).matches;
  }
  return false;
}

export function isMdScreen() {
  if (window) {
    return window.matchMedia(`(max-width: 1199px)`).matches;
  }
  return false;
}

function currentYPosition(elm) {
  if (!window && !elm) {
    return;
  }
  if (elm) return elm.scrollTop;
  // Firefox, Chrome, Opera, Safari
  if (window.pageYOffset) return window.pageYOffset;
  // Internet Explorer 6 - standards mode
  if (document.documentElement && document.documentElement.scrollTop)
    return document.documentElement.scrollTop;
  // Internet Explorer 6, 7 and 8
  if (document.body.scrollTop) return document.body.scrollTop;
  return 0;
}

function elmYPosition(elm) {
  let y = elm.offsetTop;
  let node = elm;
  while (node.offsetParent && node.offsetParent !== document.body) {
    node = node.offsetParent;
    y += node.offsetTop;
  }
  return y;
}

export function scrollTo(scrollableElement, elmID) {
  let elm = document.getElementById(elmID);

  if (!elmID || !elm) {
    return;
  }

  let startY = currentYPosition(scrollableElement);
  let stopY = elmYPosition(elm);

  let distance = stopY > startY ? stopY - startY : startY - stopY;
  if (distance < 100) {
    scrollTo(0, stopY);
    return;
  }
  let speed = Math.round(distance / 50);
  if (speed >= 20) speed = 20;
  let step = Math.round(distance / 25);
  let leapY = stopY > startY ? startY + step : startY - step;
  let timer = 0;
  if (stopY > startY) {
    for (let i = startY; i < stopY; i += step) {
      setTimeout(
        (function (leapY) {
          return () => {
            scrollableElement.scrollTo(0, leapY);
          };
        })(leapY),
        timer * speed
      );
      leapY += step;
      if (leapY > stopY) leapY = stopY;
      timer++;
    }
    return;
  }
  for (let i = startY; i > stopY; i -= step) {
    setTimeout(
      (function (leapY) {
        return () => {
          scrollableElement.scrollTo(0, leapY);
        };
      })(leapY),
      timer * speed
    );
    leapY -= step;
    if (leapY < stopY) leapY = stopY;
    timer++;
  }
  return false;
}

export function getTimeDifference(date) {
  let difference = differenceInSeconds(new Date(), date);

  if (difference < 60) return `${Math.floor(difference)} sec`;
  else if (difference < 3600) return `${Math.floor(difference / 60)} min`;
  else if (difference < 86400) return `${Math.floor(difference / 3660)} h`;
  else if (difference < 86400 * 30) return `${Math.floor(difference / 86400)} d`;
  else if (difference < 86400 * 30 * 12) return `${Math.floor(difference / 86400 / 30)} mon`;
  else return `${(difference / 86400 / 30 / 12).toFixed(1)} y`;
}

export function generateRandomId() {
  const crypto = window.crypto || window.msCrypto;
  let array = new Uint32Array(1);
  crypto.getRandomValues(array); // Compliant for security-sensitive use cases
  const uid = array[0];
  return uid;
}

export function getQueryParam(prop) {
  let params = {};
  let search = decodeURIComponent(
    window.location.href.slice(window.location.href.indexOf('?') + 1)
  );
  let definitions = search.split('&');
  definitions.forEach(function (val, key) {
    let parts = val.split('=', 2);
    params[parts[0]] = parts[1];
  });
  return prop && prop in params ? params[prop] : params;
}

export function classList(classes) {
  return Object.entries(classes)
    .filter((entry) => entry[1])
    .map((entry) => entry[0])
    .join(' ');
}

export const flat = (array) => {
  let result = [];
  array.forEach(function (a) {
    result.push(a);
    if (Array.isArray(a.children)) {
      result = result.concat(flat(a.children));
    }
  });
  return result;
};
// Sorted latest data get passed to bulkExport function
export const sortExportData = (data, sortInfo) => {
  if (!sortInfo || !sortInfo.id) {
    // If sortInfo is null or does not have an id, return original order
    return data;
  }

  return [...data].sort((a, b) => {
    const field = sortInfo.id || sortInfo.name || sortInfo.columnName;
    const aValue = a[field]?.toLowerCase() || '';
    const bValue = b[field]?.toLowerCase() || '';
    if (aValue < bValue) {
      return sortInfo.dir === 1 ? -1 : 1;
    }
    if (aValue > bValue) {
      return sortInfo.dir === 1 ? 1 : -1;
    }
    return 0;
  });
};

export const sortInsideRowData = (data) => {
  let sortedNameDivs = [];
  let sortedMvoTemplateToDivs = [];

  return data.map(item => {
    if (item.name) {
      const nameDivs = item.name.match(/<div>.*?<\/div>/g) || [];
      sortedNameDivs = nameDivs.sort((a, b) => {
        const textA = a.replace(/<div>|<\/div>/g, '').toUpperCase();
        const textB = b.replace(/<div>|<\/div>/g, '').toUpperCase();
        return textA.localeCompare(textB);
      });
    }

    if (item.MVO_TEMPLATE_TO) {
      const mvoTemplateToDivs = item.MVO_TEMPLATE_TO.match(/<div>.*?<\/div>/g) || [];
      sortedMvoTemplateToDivs = mvoTemplateToDivs.sort((a, b) => {
        const textA = a.replace(/<div>|<\/div>/g, '').toUpperCase();
        const textB = b.replace(/<div>|<\/div>/g, '').toUpperCase();
        return textA.localeCompare(textB);
      });
    }

    return {
      ...item,
      name: sortedNameDivs.join(''),
      MVO_TEMPLATE_TO: sortedMvoTemplateToDivs.join('')
    };
  });
};

export function sortAndReorderData(filterData) {
  // Function to sort object values alphabetically (excluding "Entity")
  function sortObjectValues(obj) {
      return {
          Entity: obj.Entity,
          ...Object.fromEntries(
              Object.entries(obj)
                  .filter(([key]) => key !== "Entity")
                  .sort((a, b) => a[1].localeCompare(b[1])) // Sort values alphabetically
          )
      };
  }

  // Find the "PlantGroup" object without removing it
  const plantGroup = filterData.find(item => item.Entity === "PlantGroup");

  // Sort the "PlantGroup" object (if found)
  const sortedPlantGroup = plantGroup ? sortObjectValues(plantGroup) : null;

  // Sort the rest of the objects based on "Entity" alphabetically, excluding "PlantGroup"
  const sortedOtherEntities = filterData
      .filter(item => item.Entity !== "PlantGroup") // Keep everything except "PlantGroup"
      .map(sortObjectValues)
      .sort((a, b) => a.Entity.localeCompare(b.Entity));

  // Return the final array with "PlantGroup" on top (if exists)
  return sortedPlantGroup ? [sortedPlantGroup, ...sortedOtherEntities] : sortedOtherEntities;
}

export const caseInsensitiveSort = (a, b) => {
  return a.localeCompare(b, undefined, { sensitivity: 'base' });
};